/* eslint-disable no-console */
import URLSearchParams from '@ungap/url-search-params';
import debounce from 'lodash.debounce';
import nullFunc from '@auth/components/src/nullFunc';
import {
  ATTEMPTED, INITIALIZED, TERMINATED, COMPLETED,
} from './tincanVerbs';
import iso8601TimeInterval from './iso8601TimeInterval';
import interactionsXapi from './interactionsXapi';
import xapiEnsureJson from './xapiEnsureJson';

let lrs;
let agent;
let activityId;
let registration;
let score;
let completion;
let success;
let accumulatedTime;
let elapsedTime = 0;
let canBatchingStatements = true;
let inDebugMode = false;
let succesStatusCalled = false;
let completionStatusCalled = false;
let lmsSupportGetStatements = true;

function debug(...params) {
  if (inDebugMode) {
    console.info(...params);
  }
}

function createStatement(obj) {
  return new window.TinCan.Statement({
    actor: agent,
    target: {
      id: activityId,
    },
    ...obj,
  });
}

function syncSendStatement(statement) {
  const ret = lrs.saveStatement(statement);
  debug('saveStatement', statement, ret);
  return ret;
}

const sendStatementCallback = {
  callback(err, xhr) {
    if (err !== null) {
      if (xhr !== null) {
        console.error(`Failed to save statement: ${xhr.responseText} (${xhr.status})`);
        return;
      }
      console.error(`Failed to save statement: ${err}`);
    }
  },
};

function sendStatement(statement) {
  debug('saveStatement', statement);
  lrs.saveStatement(
    statement,
    sendStatementCallback,
  );
}

function sendStatements(statements) {
  debug('saveStatements', statements);
  lrs.saveStatements(
    statements,
    sendStatementCallback,
  );
}

function sendState(id, state, cfg) {
  const config = {
    activity: {
      id: activityId,
    },
    agent,
    registration,
    callback: nullFunc,
    contentType: 'application/json',
    ...cfg,
  };
  debug('saveState', id, state, config);
  lrs.saveState(
    id,
    state,
    config,
  );
}

function syncSendState(id, state, cfg) {
  const config = {
    activity: {
      id: activityId,
    },
    agent,
    registration,
    contentType: 'application/json',
    ...cfg,
  };
  debug('saveState', id, state, config);
  const ret = lrs.saveState(
    id,
    state,
    config,
  );
  debug('syncSendState', id, state);
  return ret;
}

function syncGetState(id) {
  const retrievedState = lrs.retrieveState(
    id,
    {
      activity: {
        id: activityId,
      },
      agent,
      registration,
    },
  );
  const ret = xapiEnsureJson(retrievedState.state && retrievedState.state.contents);
  debug('retrieveState', id, ret);
  return ret;
}

function getContext() {
  return ({
    contextActivities: {
      grouping: [
        {
          id: activityId,
          objectType: 'Activity',
        },
      ],
    },
    registration,
  });
}

function createCourseStatement(statement) {
  return createStatement({
    ...statement,
    context: getContext(),
  });
}

function generateResult() {
  const ret = {
    duration: iso8601TimeInterval(elapsedTime + accumulatedTime),
    score: {
      scaled: score / 100,
      raw: score,
      min: 0,
      max: 100,
    },
  };
  if (completionStatusCalled) {
    ret.completion = completion;
  }
  if (succesStatusCalled) {
    ret.success = success;
  }
  return ret;
}

function getResult() {
  return syncGetState('result');
}

function getLmsData() {
  const params = {
    agent,
    verb: TERMINATED,
    activity: {
      id: activityId,
    },
    registration,
    ascending: false,
  };
  const statements = lrs.queryStatements({
    params,
  });

  if (statements?.err) {
    lmsSupportGetStatements = false;
    const fallbackStatements = getResult();
    if (fallbackStatements) {
      const fallback = JSON.parse(fallbackStatements);
      return [
        fallback?.score?.raw,
        fallback?.completion,
        fallback?.duration || 'PT0S',
      ];
    }
    return [undefined, undefined, 'PT0S'];
  }

  if (statements?.statementsResult?.statements?.length) {
    const {
      result,
    } = statements.statementsResult.statements[0];
    debug('queryStatements', params, result);
    return [
      result.score.raw,
      result.completion,
      result.duration || 'PT0S',
    ];
  }
  debug('queryStatements', params, undefined);
  return [undefined, undefined, 'PT0S'];
}

function init(config) {
  const { debugMode, batchingStatements } = config;
  inDebugMode = debugMode;
  canBatchingStatements = batchingStatements !== false;
  const queryString = window.location.search;
  const searchParams = new URLSearchParams(queryString);
  const endpoint = searchParams.get('endpoint');
  const auth = searchParams.get('auth');
  const actor = searchParams.get('actor');
  const languagePreference = searchParams.get('languagePreference');
  activityId = searchParams.get('activity_id');
  registration = searchParams.get('registration');
  try {
    lrs = new window.TinCan.LRS({
      endpoint,
      auth,
      allowFail: false,
    });
    const parseActor = JSON.parse(actor);
    agent = new window.TinCan.Agent({
      account: parseActor.account,
      name: parseActor.name,
    });
    const initStatement = createCourseStatement({
      verb: INITIALIZED,
    });
    const [
      lmsScore, lmsCompletion, lmsDuration,
    ] = getLmsData();
    score = lmsScore;
    completion = lmsCompletion;
    accumulatedTime = window.TinCan.Utils.convertISO8601DurationToMilliseconds(
      lmsDuration,
    );
    sendStatement(initStatement);
    return [
      true,
      lmsScore,
      lmsCompletion,
      null,
      languagePreference,
    ];
  } catch (err) {
    console.error(err);
    return [false];
  }
}

function setSuspend(suspend) {
  sendState('suspend', suspend);
}

function getSuspend() {
  return syncGetState('suspend');
}

function setLocation(location) {
  sendState('location', { location });
}

function getLocation() {
  const { location = '' } = syncGetState('location') || {};
  return location;
}

function consoleOnError(sendStatementResponse) {
  if (sendStatementResponse.err !== null) {
    if (/^\d+$/.test(sendStatementResponse.err)) {
      if (sendStatementResponse.err === 0) {
        console.error('Failed to save statement: aborted, offline, or invalid CORS endpoint');
      } else {
        console.error(`Failed to save statement: ${sendStatementResponse.xhr.responseText}`);
      }
    } else {
      console.error(`Failed to save statement: ${sendStatementResponse.err}`);
    }
  }
}

function exit({ sessionTime }) {
  elapsedTime = sessionTime;
  const result = generateResult();
  consoleOnError(
    syncSendState('result', result),
  );
  const exitStatement = createCourseStatement({
    verb: TERMINATED,
    result,
  });
  consoleOnError(
    syncSendStatement(exitStatement),
  );
}

function getInteractionStatement(data) {
  return createStatement(interactionsXapi(data, activityId, getContext()));
}

function sendInteraction(data) {
  const statement = getInteractionStatement(data);
  sendStatement(statement);
}

function sendMultipleInteractions(data) {
  if (canBatchingStatements) {
    const statements = data.map(
      (interactionData) => getInteractionStatement(interactionData),
    );
    sendStatements(statements);
  } else {
    data.forEach(
      (interactionData) => {
        sendInteraction(interactionData);
      },
    );
  }
}

const sendResult = debounce(() => {
  const result = generateResult();
  const statement = createCourseStatement({
    verb: completion ? COMPLETED : ATTEMPTED,
    result,
  });
  sendStatement(statement);
  if (!lmsSupportGetStatements) {
    sendState('result', JSON.stringify(result));
  }
}, 500);

function setScore(newScore, newElapsedTime) {
  score = newScore;
  elapsedTime = newElapsedTime;
  sendResult();
}

function setCompletion(newCompletion, newElapsedTime) {
  completion = newCompletion;
  elapsedTime = newElapsedTime;
  completionStatusCalled = true;
  if (completion) { sendResult(); }
}

function setSuccessStatus(newSuccess, newElapsedTime) {
  success = newSuccess;
  elapsedTime = newElapsedTime;
  succesStatusCalled = true;
  sendResult();
}

const getName = () => {
  const { name } = agent;
  return name;
};

const getId = () => {
  const { name } = agent.account;
  return name;
};

const xapi = {
  init,
  setSuspend,
  getSuspend,
  setLocation,
  getLocation,
  exit,
  sendInteraction,
  sendMultipleInteractions,
  setScore,
  setCompletion,
  setSuccessStatus,
  getName,
  getId,
};

export default xapi;
