import { useEffect, useRef } from 'react';
import { COMPLETING_CONTENT } from '@auth/lib/src/events/triggers';
import { useSelector } from 'react-redux';
import useDispatchEvent from '../hooks/useDispatchEvent';
import completionSelector from '../selectors/completionSelector';

function ContentCompleteDispatcher() {
  const dispatchEvent = useDispatchEvent();
  const completion = useSelector(completionSelector);
  const completionRef = useRef(false);
  useEffect(
    () => {
      if (!completionRef.current && completion) {
        dispatchEvent(COMPLETING_CONTENT);
        completionRef.current = true;
      }
    },
    [completion, dispatchEvent],
  );
  return null;
}

ContentCompleteDispatcher.propTypes = {};

ContentCompleteDispatcher.defaultProps = {};

export default ContentCompleteDispatcher;
