import evaluableActivitiesSelector from './evaluableActivitiesSelector';
import suspendDataSelector from './suspendDataSelector';

function scoreByPassedActivitySelector(state) {
  const scorableActivies = evaluableActivitiesSelector(state);
  const correct = scorableActivies.filter(
    (act) => suspendDataSelector(act, 'correct', state) === 1,
  );
  return Math.round(correct.length * (100 / scorableActivies.length));
}

export default scoreByPassedActivitySelector;
