import React, { useEffect, useMemo } from 'react';
import PropTypes from 'prop-types';
import { css } from '@emotion/css';
import { Global } from '@auth/shared/src/utils/generateClassReact';
import useGetPreserveAspectRatio from '@auth/shared/src/hooks/useGetPreserveAspectRatio';
import MaintainAspectRatioContext from '@auth/shared/src/contexts/MaintainAspectRatioContext';
import useMantainAspectRatioRender from '../hooks/useMantainAspectRatioRender';

function MaintainAspectRatio(props) {
  const {
    children,
  } = props;
  const aspect = useGetPreserveAspectRatio();
  const {
    scale, translateX, translateY, ...rest
  } = useMantainAspectRatioRender(aspect);

  useEffect(() => {
    if (aspect === 'none') return undefined;
    const myClass = css({ overflow: 'hidden' });
    document.querySelector('body').classList.add(myClass);
    return () => {
      document.querySelector('body').classList.remove(myClass);
    };
  }, [aspect]);
  const contextValue = useMemo(
    () => ({
      scale, translateX, translateY,
    }),
    [scale, translateX, translateY],
  );
  return (
    <>
      <Global
        styles={{
          '.mantain-aspect-ratio': { ...rest },
        }}
      />
      <MaintainAspectRatioContext.Provider value={contextValue}>
        {children}
      </MaintainAspectRatioContext.Provider>
    </>
  );
}

MaintainAspectRatio.propTypes = {
  children: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]),
};

MaintainAspectRatio.defaultProps = {
  children: null,
};

export default MaintainAspectRatio;
