import React from 'react';
import PropTypes from 'prop-types';
import { Provider } from 'react-redux';
import useAppData from '@auth/shared/src/hooks/useAppData';
import ensureHaveDefault from '@auth/lib/src/utils/ensureHaveDefault';
import cssReducerInitialState from '@auth/lib/src/features/css/cssReducerInitialState';
import configureStore from './configureStore';
import InteractiveRender from './InteractiveRender/InteractiveRender';
import getPage from './StoreProvider/getPage';
import getDefaultLang from './StoreProvider/getDefaultLang';
import getLocalStorageZoom from './StoreProvider/getLocalStorageZoom';

function StoreProvider(props) {
  const { data } = props;
  const { unitId } = useAppData();
  const newData = {
    ...data,
    page: getPage(data),
    currentLang: getDefaultLang(data.langs),
    css: ensureHaveDefault(data.css, cssReducerInitialState),
    zoom: getLocalStorageZoom(),
  };
  const store = configureStore(newData, unitId);
  return (
    <Provider store={store}>
      <InteractiveRender />
    </Provider>
  );
}

StoreProvider.propTypes = {
  // eslint-disable-next-line react/forbid-prop-types
  data: PropTypes.objectOf(PropTypes.any).isRequired,
};

StoreProvider.defaultProps = {};

export default React.memo(StoreProvider);
