import React from 'react';
import PropTypes from 'prop-types';
import useInteractiveProps from '../useInteractiveProps';

function CompositeAccordion(props) {
  const {
    visibleChilds = [],
    children = null,
    className,
    accordionGridTemplateAreas,
    accordionHeaderId,
    firstCompositeItem,
  } = props;
  const patchChildren = React.Children.toArray(children.props.children).filter(
    (childComp) => {
      const childId = childComp.props.id;
      if (childId === accordionHeaderId) return true;
      if (visibleChilds.length) {
        return visibleChilds.includes(childId);
      }
      return childId === firstCompositeItem;
    },
  );
  return (
    <div
      className={className}
      {...useInteractiveProps(props)}
    >
      {React.cloneElement(children, {
        children: patchChildren,
        style: {
          gridTemplateAreas: accordionGridTemplateAreas,
        },
      })}
    </div>
  );
}

CompositeAccordion.propTypes = {
  id: PropTypes.string.isRequired,
  accordionHeaderId: PropTypes.string.isRequired,
  firstCompositeItem: PropTypes.string.isRequired,
  className: PropTypes.string,
  visibleChilds: PropTypes.arrayOf(PropTypes.string),
  children: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]),
  AccordionHeaders: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]),
  accordionGridTemplateAreas: PropTypes.string,
};

CompositeAccordion.defaultProps = {
  visibleChilds: [],
  children: null,
  className: '',
  AccordionHeaders: null,
  accordionGridTemplateAreas: '',
};

export default CompositeAccordion;
