import selectorStateStructure from './selectorStateStructure';

function selectorNextPageId(state, customPage) {
  const structure = selectorStateStructure(state);
  const page = customPage || state?.page;
  const currentIndex = structure.indexOf(page);
  if (currentIndex === -1) return null;
  const newIndex = currentIndex + 1;
  if (newIndex >= structure.length) return null;
  return structure[newIndex];
}

export default selectorNextPageId;
