import React from 'react';
import PropTypes from 'prop-types';
import { FontAwesomeIcon } from '@auth/shared/src/utils/integrationFortawesome';
import { ClassNames } from '@auth/shared/src/utils/generateClassReact';
import useInteractiveProps from '../useInteractiveProps';
import pseudoSelectorProp from '../pseudoSelectorProp';

const cssContainerValues = {
  display: 'flex',
  height: '100%',
  cursor: 'pointer',
  '.ntx-option-container-input': {
    display: 'grid',
    alignItems: 'center',
    justifyItems: 'center',
    gridTemplateAreas: '"my-area"',
    position: 'relative',
    userSelect: 'none',
    input: {
      position: 'absolute',
      opacity: 0,
      height: 0,
      width: 0,
      margin: 0,
      cursor: 'pointer',
      '~ span': {
        gridArea: 'my-area',
        display: 'block',
        cursor: 'pointer',
      },
    },
  },
  '.ntx-option-icon-mark': {
    gridArea: 'my-area',
    display: 'block',
    cursor: 'pointer',
  },
  '.classContainerChildren': {
    width: '100%',
  },
};

const optionPositionInput = {
  positionInputTop: {
    flexDirection: 'column',
  },
  positionInputBottom: {
    flexDirection: 'column-reverse',
  },
  positionInputLeft: {
    flexDirection: 'row',
  },
  positionInputRight: {
    flexDirection: 'row-reverse',
  },
};

function LikertOption(props) {
  const {
    children,
    onChange,
    checked,
    className,
    optionShowRadioButton,
    optionMarkDisplay,
    optionIconMarkDisplay,
    pseudoSelector,
    iconSrc,
    htmlFor,
    inputId,
    positionInput,
  } = props;
  const interactiveProps = useInteractiveProps(props);
  return (
    <ClassNames>
      {({ css }) => (
        <section
          className={className}
          {...pseudoSelectorProp(pseudoSelector)}
          {...interactiveProps}
        >
          <label htmlFor={htmlFor} className={`${css(cssContainerValues)} ${css(optionPositionInput[positionInput])} ntx-option-label`}>
            <div>
              <div className="ntx-option-container-input">
                <input type="radio" id={inputId} onChange={onChange} checked={checked} />
                {optionShowRadioButton === 'true' && (
                  <>
                    <span className="ntx-option-base" />
                    {optionMarkDisplay === 'true' && (<span className="ntx-option-mark" />)}
                    {optionIconMarkDisplay === 'true' && (<FontAwesomeIcon icon={['fas', iconSrc]} className="ntx-option-icon-mark ntx-apply-transition" />)}
                  </>
                )}
              </div>
            </div>
            <div className="classContainerChildren">
              {children}
            </div>
          </label>
        </section>
      )}
    </ClassNames>
  );
}

LikertOption.propTypes = {
  htmlFor: PropTypes.string,
  inputId: PropTypes.string,
  className: PropTypes.string,
  children: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]),
  onChange: PropTypes.func,
  checked: PropTypes.bool,
  optionShowRadioButton: PropTypes.string,
  optionMarkDisplay: PropTypes.string,
  optionIconMarkDisplay: PropTypes.string,
  pseudoSelector: PropTypes.oneOf([
    '',
    'selected',
  ]),
  iconSrc: PropTypes.string,
  positionInput: PropTypes.oneOf([
    'positionInputTop',
    'positionInputRight',
    'positionInputBottom',
    'positionInputLeft',
  ]),
};

LikertOption.defaultProps = {
  htmlFor: 'empty', // ponemos empty para no poder seleccionar en el Editor
  inputId: null,
  children: null,
  onChange: () => null,
  checked: false,
  className: '',
  optionShowRadioButton: 'true',
  optionMarkDisplay: 'false',
  optionIconMarkDisplay: 'false',
  pseudoSelector: '',
  iconSrc: 'check',
  positionInput: 'positionInputBottom',
};

export default LikertOption;
