import { REQUIREMENTS_MET } from '@auth/lib/src/events/triggers';
import usePrevious from '@auth/shared/src/hooks/usePrevious';
import { useEffect } from 'react';
import { useSelector } from 'react-redux';
import useDispatchEvent from './useDispatchEvent';
import launchRequirementsEventSelector from './useRequirementsDispatchEvent/launchRequirementsEventSelector';

function useRequirementsDispatchEvent(id) {
  const dispatchEvent = useDispatchEvent();
  const launchRequirementsEvent = useSelector(
    (state) => launchRequirementsEventSelector(state, id),
  );
  const prevLaunchRequirementsEvent = usePrevious(launchRequirementsEvent);
  useEffect(
    () => {
      if (launchRequirementsEvent === true && prevLaunchRequirementsEvent === false) {
        dispatchEvent(REQUIREMENTS_MET, id);
      }
    },
    [launchRequirementsEvent, dispatchEvent, id, prevLaunchRequirementsEvent],
  );
}

export default useRequirementsDispatchEvent;
