import { useCallback } from 'react';
import { useDispatch } from 'react-redux';
import { sectionScrolledAction } from '../reducers/sectionToScrollReducer';

function useSetSectionScrolled() {
  const dispatch = useDispatch();
  return useCallback(
    () => dispatch(sectionScrolledAction()),
    [dispatch],
  );
}

export default useSetSectionScrolled;
