import React from 'react';
import PropTypes from 'prop-types';
import FlipCard from '@auth/components/src/FlipCard/FlipCard';
import getFlipCardCss from './interactiveRenderFlipCard/getFlipCardCss';
import useGetSuspendData from '../../hooks/useGetSuspendData';
import useFlipCard from '../../hooks/useFlipCard';

const initialState = {
  showFront: true,
};

function InteractiveRenderFlipCard(props) {
  const {
    id,
    transitionType,
    transitionDuration,
    transitionDelay,
    transitionTimingFunction,
    flipCardOnClick,
  } = props;
  const suspendShowBack = useGetSuspendData(id, 'showBack');
  const flipCard = useFlipCard();
  const cssCustom = getFlipCardCss({
    ...initialState,
    showFront: suspendShowBack !== 1,
    transitionType,
    transitionDuration,
    transitionTimingFunction,
    transitionDelay,
  });
  const handleClick = () => {
    if (flipCardOnClick) {
      flipCard(id);
    }
  };

  return (
    <FlipCard
      {...props}
      cssCustom={{
        cursor: flipCardOnClick ? 'pointer' : 'auto',
        ...cssCustom,
      }}
      onClick={handleClick}
    />
  );
}

InteractiveRenderFlipCard.propTypes = {
  id: PropTypes.string.isRequired,
  transitionType: PropTypes.oneOf(['ltr', 'rtl', 'ttb', 'btt']),
  transitionDuration: PropTypes.string,
  transitionTimingFunction: PropTypes.oneOf(['ease', 'linear', 'ease-in', 'ease-out', 'ease-in-out']),
  transitionDelay: PropTypes.string,
  flipCardOnClick: PropTypes.bool,
};
InteractiveRenderFlipCard.defaultProps = {
  transitionType: 'ltr',
  transitionDuration: '1000ms',
  transitionTimingFunction: 'ease',
  transitionDelay: '0ms',
  flipCardOnClick: true,
};

export default InteractiveRenderFlipCard;
