import getGridId from '@auth/components/src/RenderContent/getChildAreas/getGridId';
import selectorChildsByType from '@auth/lib/src/selectors/selectorChildsByType';
import selectorProp from '@auth/lib/src/selectors/selectorProp';
import selectorRealType from '@auth/lib/src/selectors/selectorRealType';
import { useSelector } from 'react-redux';

function selectorRelatedAccordionIds(state, id) {
  const headers = selectorChildsByType(state, id, 'AccordionHeaders');
  const headerBtns = headers.reduce(
    (memo, header) => [...memo, ...(selectorProp(state, header, 'childs') || [])],
    [],
  );
  const childs = selectorProp(state, id, 'childs') || [];
  const compositeItems = childs.filter(
    (child) => selectorRealType(state, child) === 'CompositeItem',
  );
  return compositeItems.reduce(
    (memo, compositeItem) => ({
      ...memo,
      [compositeItem]: headerBtns.find((header) => selectorProp(state, header, 'isRelatedComp') === compositeItem),
    }),
    {},
  );
}

function useGetAccordionGridTemplateAreas(id) {
  return useSelector(
    (state) => {
      const relatedId = selectorRelatedAccordionIds(state, id);
      return Object.entries(relatedId).reduce(
        (memo, [k, v]) => `${memo} "${getGridId(v)}" "${getGridId(k)}"`,
        '',
      );
    },
  );
}

export default useGetAccordionGridTemplateAreas;
