import React, { useContext } from 'react';
import PropTypes from 'prop-types';
import { CSSTransition } from 'react-transition-group';
import generateClass from '@auth/shared/src/utils/generateClass';
import Panel from '@auth/components/src/Panel/Panel';
import InteractiveRenderStateContext from '../InteractiveRenderStateContext';

const outLeft = 'translateX(-100%)';
const outRight = 'translateX(100vw)';
const inLeft = 'translateX(0%)';
const inRight = 'translateX(calc(100vw - 100%))';

const getIn = (type) => (type === 'slideLeft' ? inLeft : inRight);
const getOut = (type) => (type === 'slideLeft' ? outLeft : outRight);
const strMsToNumber = (str) => +str.replace('ms', '');

function InteractiveRenderPanel(props) {
  const {
    in: inProp,
    transitionTypeIn,
    transitionTypeOut,
    transitionInDuration = '300ms',
    transitionOutDuration,
  } = props;
  const { closePanel } = useContext(InteractiveRenderStateContext);
  const enter = (() => {
    if (transitionTypeIn === 'none') return {};
    return {
      enter: generateClass({ transform: getOut(transitionTypeIn) }),
      enterActive: generateClass({
        transform: getIn(transitionTypeIn),
        transition: `transform ${transitionInDuration} ease-out`,
      }),
      enterDone: generateClass({
        transform: getIn(transitionTypeIn),
      }),
    };
  })();
  const exit = (() => {
    if (transitionTypeOut === 'none') return {};
    return {
      exit: generateClass({ transform: getIn(transitionTypeIn) }),
      exitActive: generateClass({
        transform: getOut(transitionTypeOut),
        transition: `transform ${transitionOutDuration} ease-out`,
      }),
      exitDone: generateClass({
        transform: getOut(transitionTypeOut),
      }),
    };
  })();
  const enterTimeout = (() => {
    if (transitionTypeIn === 'none') return {};
    return { enter: strMsToNumber(transitionInDuration) };
  })();
  const exitTimeout = (() => {
    if (transitionTypeIn === 'none') return {};
    return { exit: strMsToNumber(transitionOutDuration) };
  })();

  return (
    <CSSTransition
      in={inProp}
      unmountOnExit
      classNames={{
        ...enter,
        ...exit,
      }}
      timeout={{
        ...enterTimeout,
        ...exitTimeout,
      }}
    >
      <Panel
        {...props}
        onClose={closePanel}
      />
    </CSSTransition>
  );
}

InteractiveRenderPanel.propTypes = {
  in: PropTypes.bool.isRequired,
  transitionTypeIn: PropTypes.oneOf(['slideLeft', 'slideRight']).isRequired,
  transitionTypeOut: PropTypes.oneOf(['slideLeft', 'slideRight']).isRequired,
  transitionInDuration: PropTypes.string,
  transitionOutDuration: PropTypes.string,
};

InteractiveRenderPanel.defaultProps = {
  transitionInDuration: '300ms',
  transitionOutDuration: '300ms',
};

export default InteractiveRenderPanel;
