import selectorProp from '@auth/lib/src/selectors/selectorProp';
import changeDisplay from './changeDisplay';
import getAreasInColumns from './getAreasInColumns';
import getAreasInRows from './getAreasInRows';
import getDisplayPropName from './getDisplayPropName';

function row({
  childs, state, id, cssDisplay, type,
}) {
  const inRows = getAreasInRows(childs);
  const inCols = getAreasInColumns(childs);
  const gap = selectorProp(state, id, 'gap') || '0px';
  const rowGap = selectorProp(state, id, 'rowGap') || gap;
  const columnGap = selectorProp(state, id, 'columnGap') || gap;
  const gridTemplateColumns = selectorProp(state, id, 'gridTemplateColumns');
  const display = selectorProp(state, id, getDisplayPropName(state));
  const objGridTemplateColumns = display === 'vertical' ? {} : {
    gridTemplateColumns,
  };
  return {
    display: changeDisplay(cssDisplay, type),
    gridTemplateAreas: display === 'vertical' ? inRows : inCols,
    ...objGridTemplateColumns,
    gridTemplateRows: '1fr',
    rowGap,
    columnGap,
  };
}

export default row;
