import { CURRENTLANG } from '../../editor/stateKeys';
import { setAllState } from '../../stateChanger/stateChanger';

function currentLangReducerSetDefaultLangOnUnitCreation(state, action, allState) {
  const key = action?.payload?.key;
  if (!key) return state;
  return setAllState(state, key, allState, CURRENTLANG);
}

export default currentLangReducerSetDefaultLangOnUnitCreation;
