import React, { useCallback, useEffect, useRef } from 'react';
import PropTypes from 'prop-types';
import { useSwipeable } from 'react-swipeable';
import { ClassNames } from '@auth/shared/src/utils/generateClassReact';
import './Slides.css';
import { CSSTransition, TransitionGroup } from 'react-transition-group';
import useInteractiveProps from '../useInteractiveProps';
import isVisibleChild from './isVisibleChild';

function usePrevious(value) {
  const ref = useRef();
  useEffect(() => {
    ref.current = value;
  });
  return ref.current;
}

function getClassPositionArrows(position) {
  switch (position) {
    case 'middle':
      return 'composite composite-slides composite-arrows-middle composite-arrows-middle-middle';
    case 'top':
      return 'composite composite-slides composite-arrows-top composite-arrows-top-middle';
    case 'bottom':
      return 'composite composite-slides composite-arrows-bottom composite-arrows-bottom-middle';
    case 'middle inner':
      return 'composite composite-slides composite-arrows-middle composite-arrows-middle-inner';
    case 'top inner':
      return 'composite composite-slides composite-arrows-top composite-arrows-top-inner';
    case 'bottom inner':
      return 'composite composite-slides composite-arrows-bottom composite-arrows-bottom-inner';
    case 'middle outer':
      return 'composite composite-slides composite-arrows-middle composite-arrows-middle-outer';
    case 'top outer':
      return 'composite composite-slides composite-arrows-top composite-arrows-top-outer';
    case 'bottom outer':
      return 'composite composite-slides composite-arrows-bottom composite-arrows-bottom-outer';
    default:
      return 'composite composite-slides composite-arrows-middle composite-arrows-middle-middle';
  }
}

function CompositeSlides(props) {
  const {
    childs,
    children = null,
    Dots = null,
    Button = null,
    visibleChilds,
    showDots,
    showArrows,
    compositeSlidesArrowsPosition,
    compositeSlidesDotsPosition,
    className,
    transitionDuration,
    transitionDelay,
    transitionType,
    goNextSlide,
    goPrevSlide,
  } = props;
  const prevVisibleChilds = usePrevious(visibleChilds) || [visibleChilds[0]] || [];
  const pos = childs.indexOf(visibleChilds[0]);
  const prevPos = childs.indexOf(prevVisibleChilds[0]);
  const next = pos > prevPos;
  const nTransitionDuration = +(transitionDuration.replace('ms', ''));
  const nTransitionDelay = +(transitionDelay.replace('ms', ''));
  const compositeItems = React.Children.toArray(children.props.children).filter(
    (childComp) => ['MasterContainer', 'CompositeItem'].includes(childComp.props.componentType),
  );
  const filterChildrens = React.Children.toArray(compositeItems).filter(
    (childComponent, index) => (
      isVisibleChild(childComponent.props.id, index, visibleChilds)
    ),
  );
  const handlers = useSwipeable({
    onSwipedRight: () => goPrevSlide(),
    onSwipedLeft: () => goNextSlide(),
    preventDefaultTouchmoveEvent: true,
  });
  const { ref, ...others } = { ...useInteractiveProps(props) };
  const setRefs = useCallback(
    (node) => {
      handlers.ref(node);
      if (typeof ref === 'function') {
        ref(node);
      }
    },
    [handlers, ref],
  );
  return (
    <ClassNames>
      {({ css }) => (
        <div
          {...handlers}
          {...others}
          ref={setRefs}
          className={`${className} ${getClassPositionArrows(compositeSlidesArrowsPosition)}`}
        >
          {showDots && compositeSlidesDotsPosition === 'top' && Dots}
          {React.cloneElement(children, {
            children: (() => {
              if (transitionType === 'none') {
                return filterChildrens;
              }
              const [classNames, timeout] = (() => {
                if (transitionType === 'fade') {
                  const durationFade = `${nTransitionDuration}ms`;
                  const enterDelay = `${nTransitionDuration + nTransitionDelay}ms`;
                  const exitDelay = `${nTransitionDelay}ms`;
                  return [
                    {
                      enter: css({
                        opacity: 0,
                      }),
                      enterActive: css({
                        opacity: 1,
                        transition: `opacity ${durationFade} ease-in ${enterDelay}`,
                      }),
                      exit: css({
                        opacity: 1,
                      }),
                      exitActive: css({
                        opacity: 0,
                        transition: `opacity ${durationFade} ease-in ${exitDelay}`,
                      }),
                    },
                    {
                      enter: nTransitionDuration + nTransitionDuration + nTransitionDelay,
                      exit: nTransitionDuration + nTransitionDelay,
                    },
                  ];
                }
                const slideTimeout = {
                  enter: nTransitionDuration + nTransitionDelay,
                  exit: nTransitionDuration + nTransitionDelay,
                };
                const duration = `${nTransitionDuration}ms`;
                const delay = `${nTransitionDelay}ms`;
                if (next) {
                  return [
                    {
                      enter: css({
                        transform: 'translateX(100%)',
                        label: 'prevEnter',
                      }),
                      enterActive: css({
                        transform: 'translateX(0%)',
                        transition: `transform ${duration} ease-in ${delay}`,
                        label: 'prevEnterActive',
                      }),
                      exit: css({
                        transform: 'translateX(0%)',
                        label: 'prevExit',
                      }),
                      exitActive: css({
                        transform: 'translateX(-100%)',
                        transition: `transform ${duration} ease-in ${delay}`,
                        label: 'prevExitActive',
                      }),
                    },
                    slideTimeout,
                  ];
                }
                return [
                  {
                    enter: css({
                      transform: 'translateX(-100%)',
                      label: 'nextEnter',
                    }),
                    enterActive: css({
                      transform: 'translateX(0%)',
                      transition: `transform ${duration} ease-in ${delay}`,
                      label: 'nextEnterActive',
                    }),
                    exit: css({
                      transform: 'translateX(0%)',
                      label: 'nextExit',
                    }),
                    exitActive: css({
                      transform: 'translateX(100%)',
                      transition: `transform ${duration} ease-in ${delay}`,
                      label: 'nextExitActive',
                    }),
                  },
                  slideTimeout,
                ];
              })();
              const childrenWithTransition = filterChildrens.map(
                (childComponent) => {
                  const newKey = `${next}_${childComponent.props.id}`;
                  return (
                    <CSSTransition
                      key={newKey}
                      timeout={timeout}
                      classNames={classNames}
                    >
                      {childComponent}
                    </CSSTransition>
                  );
                },
              );
              return (
                <TransitionGroup
                  component={null}
                  childFactory={(child) => React.cloneElement(child, {
                    type: 'CompositeItem',
                    classNames,
                    timeout,
                  })}
                >
                  {childrenWithTransition}
                </TransitionGroup>
              );
            })(),
          })}
          {showDots && compositeSlidesDotsPosition === 'bottom' && Dots}
          {
            showArrows && React.Children.map(Button, (ButtonItem, buttonIndex) => (
              <div className={`composite-arrow composite-arrow-${buttonIndex}`}>{ButtonItem}</div>
            ))
          }
        </div>
      )}
    </ClassNames>
  );
}

CompositeSlides.propTypes = {
  childs: PropTypes.arrayOf(PropTypes.string).isRequired,
  className: PropTypes.string,
  visibleChilds: PropTypes.arrayOf(PropTypes.string),
  children: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]),
  Dots: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]),
  Button: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]),
  showDots: PropTypes.bool,
  compositeSlidesDotsPosition: PropTypes.string,
  showArrows: PropTypes.bool,
  compositeSlidesArrowsPosition: PropTypes.string,
  transitionType: PropTypes.string,
  transitionDuration: PropTypes.string,
  transitionDelay: PropTypes.string,
  goNextSlide: PropTypes.func,
  goPrevSlide: PropTypes.func,
};

CompositeSlides.defaultProps = {
  visibleChilds: [],
  className: '',
  children: null,
  Dots: null,
  Button: null,
  showDots: true,
  compositeSlidesDotsPosition: 'bottom',
  showArrows: true,
  compositeSlidesArrowsPosition: '',
  transitionType: 'fade',
  transitionDuration: '1000ms',
  transitionDelay: '0ms',
  goNextSlide: () => null,
  goPrevSlide: () => null,
};

export default CompositeSlides;
