import canNavigateToPageSelector from '../../selectors/canNavigateToPageSelector';
import completionSelector from '../../selectors/completionSelector';
import isDescendantOfFirstPageSelector from '../../selectors/isDescendantOfFirstPageSelector';
import isDescendantOfLastPageSelector from '../../selectors/isDescendantOfLastPageSelector';

const visibilityAndEnabledConditions = {
  firstPage: (id, state) => isDescendantOfFirstPageSelector(state, id),
  lastPage: (id, state) => isDescendantOfLastPageSelector(state, id),
  completionRequirementsMet: (id, state) => completionSelector(state, [id]),
  canNavigate: (id, state) => canNavigateToPageSelector(state, id),
};

export default visibilityAndEnabledConditions;
