import activityTypes from '../data/activityTypes';

export const COMPLETING_CONTENT = 'COMPLETING_CONTENT';
export const CONTENT_INIT = 'CONTENT_INIT';
export const CONTENT_EXIT = 'CONTENT_EXIT';
export const EXCEEDING_CONTENT = 'EXCEEDING_CONTENT';
export const PAGE_SCROLL_END = 'PAGE_SCROLL_END';
export const VIDEO_INDICATED_END = 'VIDEO_INDICATED_END';
export const VIDEO_INDICATED_TIME = 'VIDEO_INDICATED_TIME';
export const AUDIO_INDICATED_END = 'AUDIO_INDICATED_END';
export const AUDIO_INDICATED_TIME = 'AUDIO_INDICATED_TIME';
export const FLIPCARD_CHANGE_TO_REVERSE = 'FLIPCARD_CHANGE_TO_REVERSE';
export const FLIPCARD_CHANGE_TO_FRONT = 'FLIPCARD_CHANGE_TO_FRONT';
export const QUIZ_PASSED = 'QUIZ_PASSED';
export const QUIZ_FAILED = 'QUIZ_FAILED';
export const ACTIVITY_PASSED = 'ACTIVITY_PASSED';
export const ACTIVITY_FAILED = 'ACTIVITY_FAILED';
export const REQUIREMENTS_MET = 'REQUIREMENTS_MET';

const triggersData = {
  [CONTENT_INIT]: {
  },
  [CONTENT_EXIT]: {
  },
  [COMPLETING_CONTENT]: {
  },
  [EXCEEDING_CONTENT]: {
  },
  [PAGE_SCROLL_END]: {
    types: ['PageContent'],
  },
  [VIDEO_INDICATED_END]: {
    types: ['Video'],
  },
  [VIDEO_INDICATED_TIME]: {
    types: ['Video'],
  },
  [AUDIO_INDICATED_END]: {
    types: ['Audio'],
  },
  [AUDIO_INDICATED_TIME]: {
    types: ['Audio'],
  },
  [FLIPCARD_CHANGE_TO_REVERSE]: {
    types: ['FlipCard'],
  },
  [FLIPCARD_CHANGE_TO_FRONT]: {
    types: ['FlipCard'],
  },
  [QUIZ_PASSED]: {
    types: ['Quiz'],
  },
  [QUIZ_FAILED]: {
    types: ['Quiz'],
  },
  [ACTIVITY_PASSED]: {
    types: activityTypes,
  },
  [ACTIVITY_FAILED]: {
    types: activityTypes,
  },
  [REQUIREMENTS_MET]: {
    types: ['PageContent', 'Quiz', 'Video', 'Audio', 'Composite', 'Image', 'FlipCard'],
  },
};

const triggers = Object.keys(triggersData);

export const isValidTrigger = (trigger) => triggers.includes(trigger);

export const getTriggersByType = (type) => triggers.filter(
  (trigger) => {
    if (!type) {
      return !triggersData[trigger].types;
    }
    const { types = [] } = triggersData[trigger];
    return types.includes(type);
  },
);

export default triggers;
