import selectorFirstAscendantByType from '@auth/lib/src/selectors/selectorFirstAscendantByType';
import sequenceOptionPseudoSelector from '../InteractiveRender/customComps/interactiveRenderSequenceOption/sequenceOptionPseudoSelector';

function sequenceOptionCorrectSelector(sourceId, state) {
  const activityId = selectorFirstAscendantByType(state, sourceId, 'ActivitySequence');
  const isCorrect = sequenceOptionPseudoSelector(state, activityId, sourceId);
  if (isCorrect === 'answeredright') return 1;
  if (isCorrect === 'answeredwrong') return 0;
  return undefined;
}

export default sequenceOptionCorrectSelector;
