import completionSelector from '../../selectors/completionSelector';
import compositeIsInFirstChild from '../../selectors/compositeIsInFirstChild';
import compositeIsInLastChild from '../../selectors/compositeIsInLastChild';

const visibilityAndEnabledConditions = {
  firstBlock: (id, state) => compositeIsInFirstChild(state, id),
  lastBlock: (id, state) => compositeIsInLastChild(state, id),
  completionRequirementsMet: (id, state) => completionSelector(state, [id]),
};

export default visibilityAndEnabledConditions;
