import React, { useEffect, useRef } from 'react';
import PropTypes from 'prop-types';
import { useDispatch } from 'react-redux';
import PageContent from '@auth/components/src/PageContent/PageContent';
import { PAGE_SCROLL_END } from '@auth/lib/src/events/triggers';
import usePrevious from '@auth/shared/src/hooks/usePrevious';
import useSetVisited from '../../hooks/useSetVisited';
import isScrollAtEnd from '../../utils/isScrollAtEnd';
import useSetSuspendData from '../../hooks/useSetSuspendData';
import useDispatchEvent from '../../hooks/useDispatchEvent';
import useSectionToScroll from '../../hooks/useSectionToScroll';
import useSetSectionScrolled from '../../hooks/useSetSectionScrolled';

function InteractiveRenderPageContent(props) {
  const { id, title } = props;
  const dispatch = useDispatch();
  const setSectionScrolled = useSetSectionScrolled();
  const setSuspend = useSetSuspendData();
  const dispatchEvent = useDispatchEvent();
  const sectionToScroll = useSectionToScroll();
  const scrollEndRef = useRef(false);
  useSetVisited(id);

  const prevSectionToScroll = usePrevious(sectionToScroll);
  const prevId = usePrevious(id);
  useEffect(() => {
    if (prevSectionToScroll && sectionToScroll === null) return;
    if (!sectionToScroll && prevId !== id) window.scroll({ top: 0 });
    if (sectionToScroll) {
      const section = document.querySelector(`[data-ntx-component="${sectionToScroll}"]`);
      if (section) {
        section.scrollIntoView({ behavior: 'smooth' });
        setSectionScrolled();
      }
    }
  }, [id, sectionToScroll, setSectionScrolled, prevSectionToScroll, prevId]);

  useEffect(() => {
    const checkScrollEnd = () => {
      if (isScrollAtEnd()) {
        if (!scrollEndRef.current) {
          scrollEndRef.current = true;
          setSuspend(id, 'scrollEnd', 1);
          dispatchEvent(PAGE_SCROLL_END, id);
        }
      }
    };
    checkScrollEnd();
    const handleScroll = () => {
      checkScrollEnd();
    };
    window.addEventListener('scroll', handleScroll);
    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, [id, dispatch, setSuspend, dispatchEvent]);
  useEffect(() => {
    document.title = title;
  }, [title]);

  return (
    <PageContent
      {...props}
    />
  );
}

InteractiveRenderPageContent.propTypes = {
  id: PropTypes.string.isRequired,
  title: PropTypes.string,
};
InteractiveRenderPageContent.defaultProps = {
  title: '',
};

InteractiveRenderPageContent.slots = PageContent.slots;

export default InteractiveRenderPageContent;
