import selectorAllDescendantIdsByTypes from '@auth/lib/src/selectors/selectorAllDescendantIdsByTypes';
import selectorFindDescendantByTypes from '@auth/lib/src/selectors/selectorFindDescendantByTypes';
import { getSortedLangKeys } from '@auth/lib/src/selectors/selectorInstance';
import selectorProp from '@auth/lib/src/selectors/selectorProp';
import selectorStateInstances from '@auth/lib/src/selectors/selectorStateInstances';
import removeLineBreaks from '@auth/shared/src/utils/removeLineBreaks';
import stripTags from '@auth/shared/src/utils/stripTags';

function getValByLang(lang, state, id, propName, invalidTypes) {
  const instances = selectorStateInstances(state);
  const instance = instances[id];
  if (!instance) return null;
  const description = instance[`${propName}__${lang}`];
  if (description) return description;
  const textComp = selectorFindDescendantByTypes(state, id, ['Text'], invalidTypes);
  if (textComp) {
    const text = instances[textComp][`text__${lang}`];
    if (text) return removeLineBreaks(stripTags(text));
  }
  const altComps = selectorAllDescendantIdsByTypes(id, ['Image', 'Video', 'Audio'], state, invalidTypes);
  const alts = altComps.map(
    (altComp) => instances[altComp][`alt__${lang}`],
  );
  const alt = alts.find((currentAlt) => !!currentAlt);
  if (alt) return alt;
  if (textComp) {
    const textInOtherLangs = selectorProp(state, textComp, 'text', lang);
    if (textInOtherLangs) return removeLineBreaks(stripTags(textInOtherLangs));
  }
  const altsInOtherLangs = altComps.map(
    (altComp) => selectorProp(state, altComp, 'alt', lang),
  );
  return altsInOtherLangs.find((currentAlt) => !!currentAlt);
}

function getDescriptionByPropName(state, id, propName, invalidTypes) {
  const langKeys = getSortedLangKeys(state);
  return langKeys.reduce(
    (memo, langKey) => {
      const wording = getValByLang(langKey, state, id, propName, invalidTypes);
      if (!wording) return memo;
      return {
        ...memo,
        [langKey]: wording,
      };
    },
    {},
  );
}

export default getDescriptionByPropName;
