import React from 'react';
import PropTypes from 'prop-types';
import { ClassNames } from '@auth/shared/src/utils/generateClassReact';
import useInteractiveProps from '../useInteractiveProps';

const cssDefault = {
  gap: '0.5rem',
  justifyContent: 'center',
};
function Dots(props) {
  const {
    children,
    className,
  } = props;
  const interactiveProps = useInteractiveProps(props);
  return (
    <ClassNames>
      {({ css, cx }) => (
        <div
          className={cx(css(cssDefault), className)}
          {...interactiveProps}
        >
          {children}
        </div>
      )}
    </ClassNames>
  );
}

Dots.propTypes = {
  className: PropTypes.string,
  children: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]),
};

Dots.defaultProps = {
  children: null,
  className: '',
};

export default Dots;
