import selectorConfig from '@auth/lib/src/selectors/selectorConfig';
import selectorStateStructure from '@auth/lib/src/selectors/selectorStateStructure';
import selectorType from '@auth/lib/src/selectors/selectorType';
import completionSelector from './completionSelector';

export default function canNavigateToPageSelector(state, id) {
  const isPage = selectorType(state, id) === 'PageContent';
  if (!isPage) return false;
  const contentNavigation = selectorConfig(state, 'contentNavigation');
  if (contentNavigation === 'free') return true;
  const structure = selectorStateStructure(state);
  if (!structure.includes(id)) return true;
  const firstPageNoComplete = structure.findIndex(
    (page) => !completionSelector(state, [page]),
  );
  if (firstPageNoComplete === -1) return true;
  const pageIndex = structure.indexOf(id);
  return pageIndex <= firstPageNoComplete;
}
