import { useSelector } from 'react-redux';
import selectorProp from '@auth/lib/src/selectors/selectorProp';
import selectorType from '@auth/lib/src/selectors/selectorType';
import getCompositeVisibleChildsSelector from '../selectors/getCompositeVisibleChildsSelector';
import suspendDataSelector from '../selectors/suspendDataSelector';

function Composite(state, myContainer, id, isRelatedComp) {
  const visibleChilds = getCompositeVisibleChildsSelector(state, myContainer);
  return visibleChilds.includes(isRelatedComp);
}

function Image(state, myContainer, id) {
  const suspend = suspendDataSelector(myContainer, 'sel', state);
  return id === suspend;
}

function Launcher(state, myContainer) {
  const suspend = suspendDataSelector(myContainer, 'clicked', state);
  return suspend === 1;
}

const config = {
  Launcher,
  Composite,
  Image,
};

const valids = Object.keys(config);

function selectedSelector(state, id, isRelatedComp, ascendants) {
  const myContainer = [id, ...ascendants].find(
    (ascendant) => valids.includes(selectorProp(state, ascendant, 'type')),
  );
  const myContainerType = selectorType(state, myContainer);
  const func = config[myContainerType];
  if (!func) return false;
  return func(state, myContainer, id, isRelatedComp);
}

function useSelected({ id, ascendants, isRelatedComp }) {
  return useSelector(
    (state) => selectedSelector(state, id, isRelatedComp, ascendants),
  );
}

export default useSelected;
