import React from 'react';
import PropTypes from 'prop-types';
import useInteractiveProps from '../useInteractiveProps';

function ActivityTextInput(props) {
  const {
    children,
    className,
    gridStyle = {},
  } = props;
  return (
    <div
      className={className}
      style={gridStyle}
      {...useInteractiveProps(props)}
    >
      {children}
    </div>
  );
}

ActivityTextInput.propTypes = {
  className: PropTypes.string,
  children: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]),
  gridStyle: PropTypes.objectOf(PropTypes.string),
  score: PropTypes.string,
};

ActivityTextInput.defaultProps = {
  children: null,
  className: '',
  gridStyle: {},
  score: '100',
};

export default ActivityTextInput;
