import activityTypes from '@auth/lib/src/data/activityTypes';
import selectorClosestType from '@auth/lib/src/selectors/selectorClosestType';
import selectorClosestTypes from '@auth/lib/src/selectors/selectorClosestTypes';
import quizActivitiesSelector from './quizActivitiesSelector';

const quizNumCurrentActivitySelector = (state, descendantId) => {
  const quiz = selectorClosestType(state, descendantId, 'Quiz');
  if (!quiz) return -1;
  const activities = quizActivitiesSelector(state, quiz);
  if (!activities.length) return 0;
  const activity = selectorClosestTypes(state, descendantId, activityTypes);
  return activities.indexOf(activity) + 1;
};

export default quizNumCurrentActivitySelector;
