import quizDescendantMaxScoreSelector from './quizDescendantMaxScoreSelector';
import quizDescendantScoreSelector from './quizDescendantScoreSelector';

function quizScorePercentSelector(state, descendantId) {
  const score = quizDescendantScoreSelector(state, descendantId) || 0;
  const maxScore = quizDescendantMaxScoreSelector(state, descendantId);
  if (maxScore === 0) return 100;
  return (100 * score) / maxScore;
}

export default quizScorePercentSelector;
