import selectorFirstAscendantByType from '@auth/lib/src/selectors/selectorFirstAscendantByType';
import { selector } from '../hooks/useArrowPseudoselector';
import optionActivityIsFinished from '../InteractiveRender/customComps/interactiveRenderChoiceOption/optionActivityIsFinished';

function arrowSourceCorrectSelector(sourceId, state) {
  const quizId = selectorFirstAscendantByType(state, sourceId, 'Quiz');
  const activityFinished = optionActivityIsFinished(state, quizId);
  const groupId = selectorFirstAscendantByType(state, sourceId, 'ArrowsGroupOption');
  const activitId = selectorFirstAscendantByType(state, sourceId, 'ActivityArrows');
  const current = selector(state, activitId, groupId, sourceId, true, activityFinished);
  if (current === 'answeredright') return 1;
  if (current === 'answeredwrong') return 0;
  return undefined;
}

export default arrowSourceCorrectSelector;
