import selectorClosestType from '@auth/lib/src/selectors/selectorClosestType';
import activityScoreSelector from './activityScoreSelector';
import quizActivitiesSelector from './quizActivitiesSelector';

function quizDescendantScoreSelector(state, descendantId) {
  const quiz = selectorClosestType(state, descendantId, 'Quiz');
  if (!quiz) { return undefined; }
  const activities = quizActivitiesSelector(state, quiz);
  return activities.reduce(
    (memo, activityId) => {
      const actScore = activityScoreSelector(state, activityId);
      if (actScore !== null) { return memo + actScore; }
      return memo;
    },
    0,
  );
}

export default quizDescendantScoreSelector;
