import activityTypes from '@auth/lib/src/data/activityTypes';
import selectorType from '@auth/lib/src/selectors/selectorType';
import activityPointsSelector from './activityPointsSelector';
import { calculateArrowsTotPoints } from './checkActivityArrowsSelector';
import { calculateSequenceTotPoints } from './checkActivitySequenceSelector';
import { calculateMultipleChoiceTotPoints } from './checkMultipleChoiceActivitySelector';
import suspendDataSelector from './suspendDataSelector';

function normalBehaviour(state, activityId) {
  const correct = suspendDataSelector(activityId, 'correct', state);
  if (correct === 1) {
    return activityPointsSelector(state, activityId);
  }
  return 0;
}

function multipleChoice(state, activityId) {
  const correct = suspendDataSelector(activityId, 'correct', state);
  if ([0, 1].includes(correct)) return calculateMultipleChoiceTotPoints(state, activityId);
  return 0;
}

function arrows(state, activityId) {
  const correct = suspendDataSelector(activityId, 'correct', state);
  if ([0, 1].includes(correct)) {
    const response = suspendDataSelector(activityId, 'response', state);
    return calculateArrowsTotPoints(
      state,
      activityId,
      response,
    );
  }
  return 0;
}

function sequence(state, activityId) {
  const correct = suspendDataSelector(activityId, 'correct', state);
  if ([0, 1].includes(correct)) {
    const response = suspendDataSelector(activityId, 'response', state);
    return calculateSequenceTotPoints(
      state,
      activityId,
      response,
    );
  }
  return 0;
}

const config = {
  ActivityArrows: arrows,
  ActivityLikert: normalBehaviour,
  ActivityMultipleChoice: multipleChoice,
  ActivitySequence: sequence,
  ActivitySingleChoice: normalBehaviour,
  ActivityTextInput: normalBehaviour,
};

function activityScoreSelector(state, activityId) {
  const type = selectorType(state, activityId);
  if (activityTypes.includes(type)) {
    return config[type](state, activityId);
  }
  return null;
}

export default activityScoreSelector;
