import { contentData } from '@auth/lib/src/data/conditions';
import selectorConfig from '@auth/lib/src/selectors/selectorConfig';
import selectorStateLangs from '@auth/lib/src/selectors/selectorStateLangs';
import completionSelector from '../selectors/completionSelector';
import scoreSelector from '../selectors/scoreSelector';
import check from './check';

const contentValueSelector = {
  contentStatus: (state) => (scoreSelector(state) >= selectorConfig(state, 'contentCutOffScore')
    ? 'passed'
    : 'failed'),
  contentCompletion: (state) => completionSelector(state).toString(),
  multiLang: (state) => (Object.keys(selectorStateLangs(state)).length > 1),
};

function checkContentCondition(condition, state) {
  const { trigger } = condition;
  const {
    field,
    operator,
    value,
  } = contentData[trigger] || {};
  const currentValue = contentValueSelector[field] ? contentValueSelector[field](state) : null;
  return check(currentValue, value, operator);
}

export default checkContentCondition;
