import React from 'react';
import PropTypes from 'prop-types';
import useInteractiveProps from '../useInteractiveProps';
import getGridId from '../RenderContent/getChildAreas/getGridId';

function SequenceGroupOption(props) {
  const {
    className = '',
    children = null,
    gridStyle = {},
  } = props;
  const myChilds = React.Children.toArray(children.props.children);
  return (
    <div
      className={className}
      style={gridStyle}
      {...useInteractiveProps(props)}
    >
      {
        myChilds.map(
          (child) => {
            const { id } = child.props;
            const gridId = getGridId(id);
            return (
              <div
                key={id}
                style={{
                  justifySelf: 'stretch',
                  display: 'grid',
                  gridArea: gridId,
                  gridTemplateAreas: `"${gridId}"`,
                }}
              >
                {child}
              </div>
            );
          },
        )
      }
    </div>
  );
}

SequenceGroupOption.propTypes = {
  className: PropTypes.string,
  children: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]),
  gridStyle: PropTypes.objectOf(PropTypes.string),
};

SequenceGroupOption.defaultProps = {
  children: null,
  className: '',
  gridStyle: {},
};

export default SequenceGroupOption;
