import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import { ClassNames } from '@auth/shared/src/utils/generateClassReact';
import progressTransition from './progressTransition';

function findYInCircle(cy, rad, cornerGrad) {
  const calc = cornerGrad - 90;
  const cornerRad = (calc * Math.PI) / 180;
  return Math.sin(cornerRad) * rad + cy;
}

function CircleProgressBar(props) {
  const {
    start,
    end,
    value,
    initialValue,
    progressBarBaseStrokeWidth,
    progressBarBaseStroke,
    progressBarBaseStrokeLinecap,
    progressBarFillStrokeWidth,
    progressBarFillStroke,
    progressBarFillStrokeLinecap,
    transitionType,
    transitionDuration,
    transitionDelay,
  } = props;
  const nStart = +start;
  const nEnd = +end;
  const strokeWidthBase = +progressBarBaseStrokeWidth;
  const strokeWidthFill = +progressBarFillStrokeWidth;
  const strokeWidth = Math.max(strokeWidthBase, strokeWidthFill);
  const radius = 100 + strokeWidth;
  const normalizedRadius = radius - (strokeWidth / 2);
  const patchStart = nStart > 180 && nStart > nEnd ? (nStart - 360) : nStart;
  const patchEnd = nStart > 180 && nStart > nEnd ? nEnd + 360 : nEnd;
  const circumference = normalizedRadius * 2 * Math.PI;
  const arc = ((patchEnd - nStart) / 360);
  const fillOffsetEnd = circumference - ((arc * value) / 100) * circumference;
  const fillOffsetInit = circumference - ((arc * initialValue) / 100) * circumference;
  const baseOffset = circumference - arc * circumference;
  const getYMinMax = () => {
    const angleMin = Math.min(patchStart, patchEnd);
    const angleMax = Math.max(patchStart, patchEnd);
    const touch = (val) => {
      if (nStart < nEnd) return nStart < val && nEnd > val;
      if (nStart > 180) {
        return (nStart - 360) < val && nEnd > val;
      }
      return true;
    };

    const findY = (angle, type, stroke) => {
      if (type === 'round') {
        const y1 = findYInCircle(radius, 100 + stroke / 2, angle);
        return [y1 - stroke / 2, y1 + stroke / 2];
      }
      return [
        findYInCircle(radius, 100, angle),
        findYInCircle(radius, 100 + stroke, angle),
      ];
    };

    const extremes = [0, 90, 180, 270].filter((val) => touch(val));
    const angles = [angleMin, angleMax, ...extremes];
    const yValues = angles.reduce(
      (memo, angle) => [
        ...memo,
        ...findY(angle, progressBarBaseStrokeLinecap, strokeWidthBase),
        ...findY(angle, progressBarFillStrokeLinecap, strokeWidthFill),
      ],
      [],
    );
    const yMinGetMinMax = Math.min(...yValues);
    const yMaxGetMinMax = Math.max(...yValues);
    return [yMinGetMinMax, yMaxGetMinMax];
  };
  const [yMin, yMax] = getYMinMax();
  const height = (yMax - yMin);
  const circleStyle = {
    transform: `rotate(${nStart - 90}deg)`,
    transformOrigin: '50% 50%',
  };
  const [showStrokeDashoffset, setShowStrokeDashoffset] = useState(false);
  useEffect(() => {
    /*
           * Este useEffect se mete para ver la animación la primera vez.
           * También es necesario para que la animación la primera vez no sea a la inversa.
          */
    setTimeout(() => {
      setShowStrokeDashoffset(true);
    }, 100);
  }, [setShowStrokeDashoffset]);
  const commonCircleProps = {
    strokeDasharray: `${circumference} ${circumference}`,
    style: circleStyle,
    r: normalizedRadius,
    cx: radius,
    cy: radius,
    fill: 'transparent',
  };
  const cssTransition = progressTransition(transitionDuration, transitionDelay, transitionType);
  return (
    <ClassNames>
      {({ css }) => (
        <div className={css({ fontSize: 0 })}>
          <svg
            xmlns="http://www.w3.org/2000/svg"
            preserveAspectRatio="xMinYMin slice"
            style={{ width: '100%', height: '100%' }}
            height={height}
            width={radius * 2}
            viewBox={`0 ${yMin} ${radius * 2} ${radius * 2}`}
          >
            <circle
              stroke={progressBarBaseStroke}
              strokeWidth={strokeWidthBase}
              strokeLinecap={progressBarBaseStrokeLinecap}
              strokeDashoffset={baseOffset}
              {...commonCircleProps}
            />
            <circle
              className={css(cssTransition)}
              stroke={progressBarFillStroke}
              strokeWidth={strokeWidthFill}
              strokeDashoffset={
                showStrokeDashoffset ? fillOffsetEnd : fillOffsetInit
              }
              strokeLinecap={progressBarFillStrokeLinecap}
              {...commonCircleProps}
            />

          </svg>
        </div>
      )}
    </ClassNames>
  );
}

CircleProgressBar.propTypes = {
  start: PropTypes.string,
  end: PropTypes.string,
  value: PropTypes.number,
  initialValue: PropTypes.number,
  progressBarBaseStrokeWidth: PropTypes.string,
  progressBarBaseStroke: PropTypes.string,
  progressBarBaseStrokeLinecap: PropTypes.string,
  progressBarFillStrokeWidth: PropTypes.string,
  progressBarFillStroke: PropTypes.string,
  progressBarFillStrokeLinecap: PropTypes.string,
  transitionType: PropTypes.string,
  transitionDuration: PropTypes.string,
  transitionDelay: PropTypes.string,
};

CircleProgressBar.defaultProps = {
  start: '270',
  end: '90',
  value: 50,
  initialValue: 0,
  progressBarBaseStrokeWidth: '10',
  progressBarBaseStroke: '#eee',
  progressBarBaseStrokeLinecap: 'butt',
  progressBarFillStrokeWidth: '20',
  progressBarFillStroke: '#1AA9E2',
  progressBarFillStrokeLinecap: 'butt',
  transitionType: 'linear',
  transitionDuration: '1000ms',
  transitionDelay: '0ms',
};

export default CircleProgressBar;
