const addedUrls = [];

function getFontFace(url, family, weight, style) {
  if (addedUrls.includes(url)) { return ''; }
  addedUrls.push(url);
  return `
    @font-face {
      font-family: "${family}";
      font-weight: ${weight};
      font-style: ${style};
      src: url('${url}');
      font-display: block;
    }`;
}

export default getFontFace;
