import selectorProp from '@auth/lib/src/selectors/selectorProp';
import quizzesAllSelector from './quizzesAllSelector';

function quizzesEvaluableSelector(state, id) {
  const evaluable = selectorProp(state, id, 'evaluable');
  if (!evaluable) return true;
  return quizzesAllSelector(state, id);
}

export default quizzesEvaluableSelector;
