import selectorAllDescendantIds from '@auth/lib/src/selectors/selectorAllDescendantIds';
import selectorType from '@auth/lib/src/selectors/selectorType';
import getDescription from './getDescription';
import getId from './getId';

function getDescendantsDescriptionByType(state, id, type) {
  return selectorAllDescendantIds(id, state).reduce(
    (memo, descendant) => {
      const typeDescendant = selectorType(state, descendant);
      if (typeDescendant !== type) return memo;
      return {
        ...memo,
        [getId(state, descendant)]: {
          description: getDescription(state, descendant),
        },
      };
    },
    {},
  );
}

export default getDescendantsDescriptionByType;
