function likert(data) {
  const {
    id,
    response,
    wording,
    scoreMax,
  } = data;
  return [
    ['id', id],
    ['type', 'likert'],
    ['learner_response', response],
    ['weighting', scoreMax],
    ['result', 'neutral'],
    ['description', wording ? Object.values(wording)[0] : ''],
  ];
}

export default likert;
