import React, { useRef } from 'react';
import PropTypes from 'prop-types';
import { shallowEqual, useSelector } from 'react-redux';
import Video, { getSubtitlesVideo } from '@auth/components/src/Video/Video';
import { VIDEO_INDICATED_TIME, VIDEO_INDICATED_END } from '@auth/lib/src/events/triggers';
import interactiveRenderMultimediaSelector from '@auth/shared/src/selectors/interactiveRenderMultimediaSelector';
import useGetCurrentLang from '@auth/shared/src/hooks/useGetCurrentLang';
import { getSortedLangKeys } from '@auth/lib/src/selectors/selectorInstance';
import selectorEventsIdsByTriggerCompIdAndTriggerType from '@auth/lib/src/selectors/selectorEventsIdsByTriggerCompIdAndTriggerType';
import useSetSuspendData from '../../hooks/useSetSuspendData';
import useGetSuspendDataThunk from '../../hooks/useGetSuspendDataThunk';
import useSetCompleted from '../../hooks/useSetCompleted';
import useDispatchEvent from '../../hooks/useDispatchEvent';

const translateBookmarkProps = (props, langKeys) => Object.entries(props).filter(
  ([propName]) => propName.startsWith('bookmark'),
).reduce(
  (memo, [propName, strPropValue]) => {
    const propValue = strPropValue ? JSON.parse(strPropValue) : {};
    const { second } = propValue;
    const validLang = langKeys.find(
      (lang) => propValue[`title_${lang}`],
    );
    const title = propValue[`title_${validLang}`] || '';
    return {
      ...memo,
      [propName]: JSON.stringify({ second, title }),
    };
  },
  {},
);

function InteractiveRenderVideo(props) {
  const { id, srcOrigin } = props;
  const currentRef = useRef(0);
  const lastRef = useRef(null);
  const currentLang = useGetCurrentLang();
  const setSuspend = useSetSuspendData();
  const getSuspend = useGetSuspendDataThunk();
  const initTime = getSuspend(id, `currentTime.${currentLang}`) || 0;
  const setCompleted = useSetCompleted(id);
  const dispatchEvent = useDispatchEvent();
  const subtitlesInCCR = getSubtitlesVideo(props);
  const videoIndicatedTimeEvents = useSelector(
    (state) => interactiveRenderMultimediaSelector(state, id),
    shallowEqual,
  );
  const checkVideoIndicatedTimeEvents = (events, current) => {
    const valid = events.find(
      (time) => current === time && lastRef.current !== time,
    );
    lastRef.current = current;
    return valid;
  };
  const handleTimeUpdate = (currentTime) => {
    currentRef.current = currentTime;
  };

  const handleReady = (player, dispatchEvents) => {
    if (dispatchEvents) {
      setInterval(
        () => {
          if (player) {
            const currentTime = player.getCurrentTime();
            const validVideoIndicatedEvent = checkVideoIndicatedTimeEvents(
              videoIndicatedTimeEvents,
              Math.floor(currentTime),
            );
            if (validVideoIndicatedEvent || validVideoIndicatedEvent === 0) {
              dispatchEvent(
                VIDEO_INDICATED_TIME,
                id,
                {
                  time: validVideoIndicatedEvent,
                  player,
                },
              );
            }
          }
        },
        200,
      );
    }

    if (initTime && srcOrigin !== 'youtube') {
      player.seek(initTime, 'seconds');
    }
  };

  const handlePlay = () => {
    setSuspend(id, 'play', 1);
  };

  const handlePause = () => {
    setSuspend(id, `currentTime.${currentLang}`, currentRef.current);
  };

  const eventsIdsTriggerCompIdAndTriggerType = useSelector(
    (state) => selectorEventsIdsByTriggerCompIdAndTriggerType(state, id, VIDEO_INDICATED_END),
    shallowEqual,
  );

  const handleEnded = () => {
    if (eventsIdsTriggerCompIdAndTriggerType.length) {
      dispatchEvent(VIDEO_INDICATED_END, id);
    }
    setSuspend(id, `currentTime.${currentLang}`, currentRef.current);
    setCompleted();
  };

  const handleClick = (e) => {
    e.stopPropagation();
  };
  const langKeys = useSelector(
    (state) => getSortedLangKeys(state),
    shallowEqual,
  );
  const translatedBookmarkProps = translateBookmarkProps(props, langKeys);
  return (
    <Video
      {...props}
      {...translatedBookmarkProps}
      subtitlesInCCR={JSON.stringify(subtitlesInCCR)}
      currentTime={initTime}
      onClick={handleClick}
      onTimeUpdate={handleTimeUpdate}
      onPlay={handlePlay}
      onPause={handlePause}
      onEnded={handleEnded}
      onReady={handleReady}
      timeEvents={videoIndicatedTimeEvents}
    />
  );
}

InteractiveRenderVideo.propTypes = {
  id: PropTypes.string.isRequired,
  srcOrigin: PropTypes.string,
};
InteractiveRenderVideo.defaultProps = {
  srcOrigin: 'ccr',
};

export default InteractiveRenderVideo;
