import strFirstCharToLowerCase from '@auth/lib/src/utils/strFirstCharToLowerCase';
import getMq from './getMq';
import getPseudo from './getPseudo';
import getRelatedProp from './getRelatedProp';

const propsByProp = (prop) => {
  const ret = [prop];
  const pseudo = getPseudo(prop);
  const mq = getMq(prop);
  if (mq === ':mobile') {
    ret.push(prop.replace(':mobile', ':tablet'));
  }
  if (mq) {
    ret.push(prop.replace(mq, ''));
  }
  if (pseudo) {
    const withoutPseudo = strFirstCharToLowerCase(prop.replace(pseudo, ''));
    ret.push(withoutPseudo);
    if (mq === ':mobile') {
      ret.push(withoutPseudo.replace(':mobile', ':tablet'));
    }
  }
  if (pseudo && mq) {
    ret.push(getRelatedProp(prop));
  }
  return ret;
};

export default propsByProp;
