import selectorType from '@auth/lib/src/selectors/selectorType';
import getCurrent from './getCurrent';
import isChecked from './isChecked';

function getTodo(id, activityId, state) {
  if (isChecked(activityId, state)) return 'none';
  const current = getCurrent(activityId, state);
  if (!current) return 'setCurrent';
  const currentType = selectorType(state, current);
  const myType = selectorType(state, id);
  if (currentType === myType) return 'setCurrent';
  return 'setResponse';
}

export default getTodo;
