export const SET_ZOOM = 'SET_ZOOM';

export function setZoomAction(zoom) {
  return ({
    type: SET_ZOOM,
    payload: {
      zoom,
    },
  });
}

const initialState = 100;

function zoomReducer(state = initialState, action = {}) {
  const { type, payload } = action;
  if (type === SET_ZOOM) {
    const { zoom } = payload;
    return zoom;
  }
  return state;
}

export default zoomReducer;
