import selectorType from '@auth/lib/src/selectors/selectorType';
import activityPointsSelector from './activityPointsSelector';
import activityScoreSelector from './activityScoreSelector';
import getCorrectResponses from './interactionSelector/getCorrectResponses';
import getWording from './interactionSelector/getWording';
import getChoices from './interactionSelector/getChoices';
import getSuccess from './interactionSelector/getSuccess';
import getResponse from './interactionSelector/getResponse';
import getSource from './interactionSelector/getSource';
import getTarget from './interactionSelector/getTarget';
import getId from './interactionSelector/getId';
import quizCurrentQuizTimesSelector from './quizCurrentQuizTimesSelector';

function interactionSelector(state, id) {
  const type = selectorType(state, id);
  const ret = {
    attempt: quizCurrentQuizTimesSelector(state, id),
    id: getId(state, id),
    type,
    wording: getWording(state, id),
    response: getResponse(state, id, type),
    success: getSuccess(state, id),
    scoreMin: 0,
    scoreMax: activityPointsSelector(state, id),
    scoreRaw: activityScoreSelector(state, id),
  };

  const correctResponses = getCorrectResponses(state, id, type);
  if (correctResponses) ret.correctResponses = correctResponses;

  const choices = getChoices(state, id, type);
  if (choices) ret.choices = choices;

  const source = getSource(state, id, type);
  if (source) ret.source = source;

  const target = getTarget(state, id, type);
  if (target) ret.target = target;

  return ret;
}

export default interactionSelector;
