import { EDITOR_RESOLUTION_WIDTH_DESKTOP_EXTRA, EDITOR_RESOLUTION_WIDTH_MOBILE, EDITOR_RESOLUTION_WIDTH_TABLET } from '@auth/lib/src/consts';
import selectorStateEditorResolutionWidth from '@auth/lib/src/selectors/selectorStateEditorResolutionWidth';

function getDisplayPropName(state) {
  const resolution = selectorStateEditorResolutionWidth(state);
  switch (resolution) {
    case EDITOR_RESOLUTION_WIDTH_DESKTOP_EXTRA:
      return 'columnDisplayWidescreen';
    case EDITOR_RESOLUTION_WIDTH_TABLET:
      return 'columnDisplayTablet';
    case EDITOR_RESOLUTION_WIDTH_MOBILE:
      return 'columnDisplayMobile';
    default:
      return 'columnDisplayDesktop';
  }
}

export default getDisplayPropName;
