import ntxCssProps from './data/ntxCssProps';
import selectorConfig from './selectors/selectorConfig';

export const isInstanceResourceProp = (propName) => {
  if (propName.startsWith('backgroundImageUrl')) return true;
  if (propName === 'src') return true;
  if (propName.startsWith('src__')) return true;
  if (propName.startsWith('subtitle_')) return true;
  if (propName.startsWith('poster')) return true;
  return false;
};

const getExtension = (str = '') => (str.split('.').pop()).toLowerCase();

export function patchResourceId(id = '', filename = '') {
  const idExtension = getExtension(id);
  if (idExtension === id) return id;
  if (!filename) return `${id.split('.')[0]}.${idExtension}`;
  const filenameExtension = getExtension(filename);
  return `${id.split('.')[0]}.${filenameExtension}`;
}

function getInstanceResourcePropValue(propName, propValue) {
  if ((isInstanceResourceProp(propName)) && propValue && propValue !== 'none') {
    const { id, filename } = JSON.parse(propValue);
    if (!id) return null;
    return patchResourceId(id, filename);
  }
  return null;
}

export const isCssResourceProp = (propName) => (
  propName.startsWith('backgroundImageUrl')
  || propName.endsWith('LocalFontSrc')
);

const getCssResourcePropValue = (propName, obj, mqConfig) => {
  const propValue = obj[propName];
  if (isCssResourceProp(propName) && propValue && propValue !== 'none') {
    if (propName.startsWith('backgroundImageUrl')) {
      const { id, filename } = JSON.parse(propValue);
      if (!id) return null;
      return patchResourceId(id, filename);
    }
    const { source } = ntxCssProps(propName, mqConfig);
    if (obj[source] === 'local') {
      const { id, filename } = JSON.parse(propValue);
      if (!id) return null;
      return patchResourceId(id, filename);
    }
  }
  return null;
};

function resources(renderState) {
  const { instances, css, config } = renderState;
  const mqConfig = {
    desktop: selectorConfig(renderState, 'resolutionDesktop'),
    tablet: selectorConfig(renderState, 'resolutionTablet'),
    mobile: selectorConfig(renderState, 'resolutionMobile'),
  };
  const instancesResources = Object.keys(instances).reduce(
    (m, k) => {
      const values = Object.entries(instances[k]).reduce(
        (memo, [key, value]) => {
          if (isInstanceResourceProp(key) && value) {
            if (key.startsWith('src__') && instances[k].srcSource === 'srcUrl') { return memo; }
            if (key.startsWith('subtitle_')
              && instances[k].srcSource === 'srcUrl' && instances[k].subtitlesSource !== 'local') { return memo; }
            const ret = getInstanceResourcePropValue(key, value);
            return [...memo, ret];
          }
          if (key.startsWith('textResources')) {
            const ids = JSON.parse(value).map(({ id }) => id);
            return [...memo, ...ids];
          }
          return memo;
        },
        [],
      );
      return [...m, ...values];
    },
    [],
  );
  const cssResources = Object.values(css)
    .filter(
      (obj) => !!obj,
    )
    .reduce(
      (m, obj) => [...m, ...Object.keys(obj).filter(
        (k) => isCssResourceProp(k),
      ).map(
        (k) => getCssResourcePropValue(k, obj, mqConfig),
      )],
      [],
    );
  const configResources = (() => {
    const { favicon } = config;
    if (!favicon) { return []; }
    const { id, filename } = JSON.parse(favicon);
    if (!id) { return []; }
    const calcId = patchResourceId(id, filename);
    return [calcId];
  })();

  return [
    ...new Set(
      [
        ...instancesResources,
        ...cssResources,
        ...configResources,
      ].filter(
        (v) => !!v,
      ),
    ),
  ];
}

export default resources;
