import strFirstCharToUpperCase from '../utils/strFirstCharToUpperCase';
import selectorStateEditorResolutionWidth from './selectorStateEditorResolutionWidth';
import {
  EDITOR_RESOLUTION_WIDTH_DESKTOP_EXTRA,
  EDITOR_RESOLUTION_WIDTH_TABLET,
  EDITOR_RESOLUTION_WIDTH_MOBILE,
} from '../consts';

export const getCalcPropNameButtonState = (
  editorButtonState, propName,
) => (editorButtonState ? `${editorButtonState}${strFirstCharToUpperCase(propName)}` : propName);

export function getCalcPropNameResolution(
  resolution,
  propName,
) {
  const suffix = (() => {
    switch (resolution) {
      case EDITOR_RESOLUTION_WIDTH_DESKTOP_EXTRA:
        return ':widescreen';
      case EDITOR_RESOLUTION_WIDTH_TABLET:
        return ':tablet';
      case EDITOR_RESOLUTION_WIDTH_MOBILE:
        return ':mobile';
      default:
        return '';
    }
  })();
  return `${propName}${suffix}`;
}

export function getCalcPropName(editorButtonState, propName, resolution) {
  const propWithButtonState = getCalcPropNameButtonState(editorButtonState, propName);
  return getCalcPropNameResolution(resolution, propWithButtonState);
}

function selectorGetCalcPropName(propName, state, editorButtonState) {
  const resolution = selectorStateEditorResolutionWidth(state);
  return getCalcPropName(editorButtonState, propName, resolution);
}

export default selectorGetCalcPropName;
