import activitiesCheckedSelector from './activitiesCheckedSelector';
import evaluableActivitiesSelector from './evaluableActivitiesSelector';

const evaluableQuizzesDoneSelector = (state) => {
  if (evaluableActivitiesSelector(state).length) return true;
  const activities = evaluableActivitiesSelector(state);
  return activitiesCheckedSelector(activities, state);
};

export default evaluableQuizzesDoneSelector;
