import apiUrl from './apiUrl';
import getQueryParam from './getQueryParam';

const keys = [
  'api_content_managers_get_collection',
  'api_authoring_unit_contents_front_review_app_item',
  'api_authoring_unit_contents_post_editor_event_collection',
  'api_authoring_unit_contents_resource_url_item',
  'api_content_managers_get_item',
  'api_authoring_unit_contents_get_projection_item',
];

function getStrParams(params = {}) {
  if (!Object.keys(params).length) { return ''; }
  return Object.entries(params).reduce(
    (memo, [k, v], index) => `${memo}${k}=${v}${index === Object.entries(params).length - 1 ? '' : '&'}`,
    '',
  );
}

function getBackRoute(routeKey, routeData = {}, params = []) {
  if (!keys.includes(routeKey)) { throw new Error(`${routeKey} not found`); }
  const base = apiUrl;
  const previewToken = getQueryParam('previewToken');
  if (previewToken) {
    routeData.previewToken = previewToken;
  }
  const route = window.Routing.generate(routeKey, routeData);
  const strParams = getStrParams(params);
  const separator = (() => {
    if (!Object.keys(params).length) { return ''; }
    if (route.includes('?')) { return '&'; }
    return '?';
  })();
  return `${base}${route}${separator}${strParams}`;
}

export default getBackRoute;
