import React from 'react';
import PropTypes from 'prop-types';
import ActivitySingleChoice from '@auth/components/src/ActivitySingleChoice/ActivitySingleChoice';
import ActivityContext from './ActivityContext';

function InteractiveRenderActivitySingleChoice(props) {
  const { id } = props;
  return (
    <ActivityContext.Provider value={id}>
      <ActivitySingleChoice {...props} />
    </ActivityContext.Provider>
  );
}

InteractiveRenderActivitySingleChoice.propTypes = {
  id: PropTypes.string.isRequired,
};
InteractiveRenderActivitySingleChoice.defaultProps = {
};

export default InteractiveRenderActivitySingleChoice;
