import { CURRENTLANG } from '../../editor/stateKeys';
import selectorDefaultLang from '../../selectors/selectorDefaultLang';
import { setAllState } from '../../stateChanger/stateChanger';

function currentLangReducerRemove(state, action, allState) {
  const key = action?.payload?.key;
  if (key !== state) return state;
  const defaultLang = selectorDefaultLang(allState);
  return setAllState(state, defaultLang, allState, CURRENTLANG);
}

export default currentLangReducerRemove;
