import selectorCompIdsByType from '@auth/lib/src/selectors/selectorCompIdsByType';
import isVisitedSelector from './isVisitedSelector';

function visitedByTypeSelector(state, type) {
  const idsOfType = selectorCompIdsByType(state, type, false);
  const idsVisited = idsOfType.filter(
    (id) => isVisitedSelector(state, id),
  );
  return idsVisited.length;
}

export default visitedByTypeSelector;
