import selectorClosestType from '@auth/lib/src/selectors/selectorClosestType';
import quizElapsedTimeSelector from './quizElapsedTimeSelector';
import quizLimitTimeSelector from './quizLimitTimeSelector';

function quizRemainingTimeSelector(state, descendantId) {
  const quiz = selectorClosestType(state, descendantId, 'Quiz');
  if (!quiz) return undefined;
  const elapsed = quizElapsedTimeSelector(state, quiz);
  if (elapsed === undefined) return undefined;
  const timeLimit = quizLimitTimeSelector(state, quiz);
  return (Math.max(timeLimit - elapsed, 0));
}

export default quizRemainingTimeSelector;
