import React, { useEffect, useRef } from 'react';
import PropTypes from 'prop-types';
import RangeTouch from 'rangetouch';
import useInteractiveProps from '../useInteractiveProps';
import './skin1/MultimediaProgressBarSkin1.css';
import MilestoneMultimediaProgressBar1 from './MilestoneMultimediaProgressBar1';

function MultimediaProgressBar(props) {
  const {
    className,
    seek,
    played,
    loaded,
    setSeeking,
    timeEvents,
    duration,
    fastFordward,
  } = props;
  const newPlayed = played * 100;
  const newLoaded = loaded * 100;
  const componentElRange = useRef(null);

  const handleSeekMouseDown = () => {
    setSeeking(true);
  };
  const handleSeekChange = (e) => {
    const value = parseFloat(e.target.value);
    const valid = fastFordward || value <= newPlayed;
    if (valid) {
      seek(parseFloat(value) / 100);
    }
  };
  const handleSeekMouseUp = () => {
    setSeeking(false);
  };

  // En Chrome necesitamos pintar la barra de reproducción desde el inicio hasta el tiempo actual.
  useEffect(() => {
    if (componentElRange.current) {
      componentElRange.current.style.setProperty('--seek-before-width', `${newPlayed}%`);
    }
  }, [componentElRange, newPlayed]);

  // Rellenamos la barra de buffer.
  useEffect(() => {
    componentElRange.current.style.setProperty('--buffered-width', `${newLoaded}%`);
  }, [componentElRange, newLoaded]);
  React.useEffect(() => {
    const range = new RangeTouch(componentElRange.current);
    return () => {
      range.destroy();
    };
  }, []);
  return (
    <div
      className={`ntx-multimedia-progress-bar ${className}`}
      {...useInteractiveProps(props)}
    >
      <input
        ref={componentElRange}
        type="range"
        max={99}
        min={0}
        value={newPlayed}
        step="any"
        onMouseDown={handleSeekMouseDown}
        onChange={handleSeekChange}
        onMouseUp={handleSeekMouseUp}
        onInput={handleSeekChange}
        className="ntx-multimedia-range-seek"
      />
      {timeEvents.map(
        (time) => (
          <MilestoneMultimediaProgressBar1
            key={time}
            left={time / duration}
            onClick={(eventTime) => { seek(eventTime); }}
          />
        ),
      )}
    </div>
  );
}

MultimediaProgressBar.propTypes = {
  id: PropTypes.string.isRequired,
  className: PropTypes.string,
  seek: PropTypes.func,
  loaded: PropTypes.number,
  played: PropTypes.number,
  setSeeking: PropTypes.func,
  timeEvents: PropTypes.arrayOf(PropTypes.number),
  duration: PropTypes.number,
  fastFordward: PropTypes.bool,
};

MultimediaProgressBar.defaultProps = {
  className: '',
  seek: () => null,
  loaded: 0,
  played: 0,
  setSeeking: () => null,
  timeEvents: [],
  duration: 0,
  fastFordward: true,
};

export default MultimediaProgressBar;
