import selectorAllDescendantIds from '@auth/lib/src/selectors/selectorAllDescendantIds';
import selectorProp from '@auth/lib/src/selectors/selectorProp';
import selectorType from '@auth/lib/src/selectors/selectorType';
import getId from './getId';

function correctResponsesActivityMultipleChoice(state, id) {
  const optionsCorrects = selectorAllDescendantIds(id, state).filter(
    (descendant) => selectorType(state, descendant) === 'MultipleChoiceOption'
      && selectorProp(state, descendant, 'correct') === 'true',
  );
  const mapOptionCorrects = optionsCorrects.map((v) => getId(state, v));
  return mapOptionCorrects.toString();
}

export default correctResponsesActivityMultipleChoice;
