import { cssEnabledProps } from './cssEnabledProps';
import getMq from './getMq';
import getPropByRelated from './getPropByRelated';
import getPseudo from './getPseudo';
import getRelatedProp from './getRelatedProp';
import isEnabledProp from './isEnabledProp';

function addValuesOnDisabled(cssData) {
  const cssDataDisabledProps = Object.keys(cssData).filter(
    (propName) => {
      if (!isEnabledProp(propName)) return false;
      if (cssData[propName] !== 'false') return false;
      const mq = getMq(propName);
      const pseudo = getPseudo(propName);
      if (!mq && !pseudo) return false;
      return true;
    },
  );
  return cssDataDisabledProps.reduce(
    (memo, enabledProp) => {
      const relatedProp = getRelatedProp(enabledProp);
      const mq = getMq(enabledProp);
      const pseudo = getPseudo(enabledProp);
      const obj = cssEnabledProps[relatedProp].valuesOnDisabled;
      const objToAdd = Object.entries(obj).reduce(
        (m, [k, v]) => ({
          ...m,
          [getPropByRelated(k, pseudo, mq)]: v,
        }),
        {
          [enabledProp]: 'true',
        },
      );
      return {
        ...memo,
        ...objToAdd,
      };
    },
    cssData,
  );
}

export default addValuesOnDisabled;
