import selectorFindDescendantByTypes from '@auth/lib/src/selectors/selectorFindDescendantByTypes';
import selectorProp from '@auth/lib/src/selectors/selectorProp';
import { itemsCorrect } from '../../selectors/checkActivitySequenceSelector';
import suspendDataSelector from '../../selectors/suspendDataSelector';

function rightAnswersCount(state, activityId) {
  const groupId = selectorFindDescendantByTypes(state, activityId, ['SequenceGroupOption']);
  if (!groupId) return 0;
  const correctResponseInSuspend = selectorProp(state, groupId, 'correct');
  if (!correctResponseInSuspend) return 0;
  const correctResponse = correctResponseInSuspend ? correctResponseInSuspend.split('[,]') : [];
  const checked = [0, 1].includes(suspendDataSelector(activityId, 'correct', state));
  if (!checked) return 0;
  const response = suspendDataSelector(activityId, 'response', state);
  return itemsCorrect(response, groupId, state, correctResponse);
}

export default rightAnswersCount;
