import selectorChilds from './selectorChilds';
import selectorRealType from './selectorRealType';

function selectorChildsByTypes(state, id, validTypes = []) {
  const childs = selectorChilds(state, id);
  return childs.filter((child) => {
    const realType = selectorRealType(state, child);
    return validTypes.includes(realType);
  });
}

export default selectorChildsByTypes;
