import { getStateData } from '@auth/lib/src/data/conditions';
import selectorConditionId from '@auth/lib/src/selectors/selectorConditionId';
import selectorConditionType from '@auth/lib/src/selectors/selectorConditionType';
import check from './check';
import checkContentCondition from './checkContentCondition';
import conditionCurrentValue from './conditionCurrentValue';

function checkCondition(condition, state, instanceId, masterContainer) {
  const {
    compId,
    trigger,
  } = condition;
  if (compId === 'content') {
    return checkContentCondition(condition, state);
  }
  const compType = selectorConditionType(state, compId, instanceId, masterContainer);
  const {
    field,
    operator,
    value,
  } = getStateData(compType, trigger);
  const patchId = selectorConditionId(state, compId, instanceId, masterContainer);

  const currentValue = conditionCurrentValue(patchId, compType, field, state);
  return check(currentValue, value, operator);
}

export default checkCondition;
