import {
  useCallback, useEffect, useState,
} from 'react';

import getMantainAspectRatioClass from '@auth/shared/src/utils/getMantainAspectRatioClass';

const getCurrentState = () => ({
  width: window.innerWidth,
  height: window.innerHeight,
});

function useMantainAspectRatioRender(mantainAspectRatio = 'none') {
  const [state, setState] = useState(getCurrentState());
  const afterResizeEvent = useCallback(
    () => {
      setState(getCurrentState());
    },
    [],
  );

  useEffect(
    () => {
      if (mantainAspectRatio === 'none') return undefined;
      window.addEventListener('resize', afterResizeEvent);
      return () => {
        window.removeEventListener('resize', afterResizeEvent);
      };
    },
    [afterResizeEvent, mantainAspectRatio],
  );
  if (mantainAspectRatio === 'none') {
    return {
      scale: 1,
      translateX: 0,
      translateY: 0,
    };
  }
  return getMantainAspectRatioClass(
    state.width,
    state.height,
    mantainAspectRatio,
  );
}

export default useMantainAspectRatioRender;
