import {
  EDITOR_RESOLUTION_WIDTH_DESKTOP_EXTRA,
  EDITOR_RESOLUTION_WIDTH_DESKTOP,
  EDITOR_RESOLUTION_WIDTH_TABLET,
  EDITOR_RESOLUTION_WIDTH_MOBILE,
} from './consts';

/**
 * Mostramos el contenido con la resolución de escritorio extra grande
 */
export const setResolutionWidthDesktopExtraAction = () => ({
  type: 'SET_RESOLUTION_WIDTH_DESKTOP_EXTRA',
});
/**
 * Mostramos el contenido con la resolución de escritorio
 */
export const setResolutionWidthDesktopAction = () => ({
  type: 'SET_RESOLUTION_WIDTH_DESKTOP',
});
/**
 * Mostramos el contenido con la resolución de una tablet
 */
export const setResolutionWidthTabletAction = () => ({
  type: 'SET_RESOLUTION_WIDTH_TABLET',
});
/**
 * Mostramos el contenido con la resolución de un móvil
 */
export const setResolutionWidthMobileAction = () => ({
  type: 'SET_RESOLUTION_WIDTH_MOBILE',
});

function resolutionWidthReducer(state = EDITOR_RESOLUTION_WIDTH_DESKTOP, action = {}) {
  const { type } = action;
  switch (type) {
    case 'SET_RESOLUTION_WIDTH_DESKTOP_EXTRA':
      return EDITOR_RESOLUTION_WIDTH_DESKTOP_EXTRA;
    case 'SET_RESOLUTION_WIDTH_DESKTOP':
      return EDITOR_RESOLUTION_WIDTH_DESKTOP;
    case 'SET_RESOLUTION_WIDTH_TABLET':
      return EDITOR_RESOLUTION_WIDTH_TABLET;
    case 'SET_RESOLUTION_WIDTH_MOBILE':
      return EDITOR_RESOLUTION_WIDTH_MOBILE;
    default:
      return state;
  }
}

export default resolutionWidthReducer;
