const funcTypes = [
  'onClick',
  'onMouseOver',
  'onMouseEnter',
  'onMouseLeave',
  'onMouseOut',
  'onDoubleClick',
  'data-child-wrapper',
  'dropRef',
  'componentRef',
];

const useInteractiveProps = (props) => funcTypes.reduce(
  (memo, type) => {
    if (!props[type]) return memo;
    if (type === 'dropRef' || type === 'componentRef') {
      return {
        ...memo,
        ref: props[type],
      };
    }
    return {
      ...memo,
      [type]: props[type],
    };
  },
  {
    'data-ntx-type': props.type,
    'data-ntx-component': props.id,
  },
);

export default useInteractiveProps;
