import selectorClosestType from '@auth/lib/src/selectors/selectorClosestType';
import quizSuccessRatePercentSelector from './quizSuccessRatePercentSelector';

export default function quizDescendantSuccessRatePercentSelector(state, descendantId) {
  const quiz = selectorClosestType(state, descendantId, 'Quiz');
  if (!quiz) return undefined;
  return quizSuccessRatePercentSelector(state, quiz);
}
