import { useDispatch } from 'react-redux';
import selectorChildsByType from '@auth/lib/src/selectors/selectorChildsByType';
import selectorInstanceData from '@auth/lib/src/selectors/selectorInstanceData';
import selectorStateInstances from '@auth/lib/src/selectors/selectorStateInstances';
import selectorStateLangs from '@auth/lib/src/selectors/selectorStateLangs';
import selectorStateCurrentLang from '@auth/lib/src/selectors/selectorStateCurrentLang';
import useClickChangeLanguage from './useClickChangeLanguage';
import useClickCheckAllQuizzes from './useClickCheckAllQuizzes';
import useClickCheckQuiz from './useClickCheckQuiz';
import useClickCheckQuizzesOnCurrentPage from './useClickCheckQuizzesOnCurrentPage';
import useClickClearResponse from './useClickClearResponse';
import useClickCloseMenu from './useClickCloseMenu';
import useClickClosePopup from './useClickClosePopup';
import useClickDownloadResource from './useClickDownloadResource';
import useClickFinaliseAllQuizzes from './useClickFinaliseAllQuizzes';
import useClickFinaliseQuiz from './useClickFinaliseQuiz';
import useClickFinaliseQuizzesOnCurrentPage from './useClickFinaliseQuizzesOnCurrentPage';
import useClickFlipCard from './useClickFlipCard';
import useClickGotoSlide from './useClickGotoSlide';
import useClickGotoFirstActivity from './useClickGotoFirstActivity';
import useClickGotoPage from './useClickGotoPage';
import useClickNext from './useClickNext';
import useClickNextSlide from './useClickNextSlide';
import useClickOpenMenu from './useClickOpenMenu';
import useClickOpenNotification from './useClickOpenNotification';
import useClickOpenPopup from './useClickOpenPopup';
import useClickOpenResource from './useClickOpenResource';
import useClickPreviousSlide from './useClickPreviousSlide';
import useClickPrevious from './useClickPrevious';
import useClickRetryAllQuizzes from './useClickRetryAllQuizzes';
import useClickRetryAllQuizzesWithoutReset from './useClickRetryAllQuizzesWithoutReset';
import useClickRetryAndMantainCorrectAllQuizzes from './useClickRetryAndMantainCorrectAllQuizzes';
import useClickRetryCurrentActivity from './useClickRetryCurrentActivity';
import useClickRetryCurrentActivityWithoutReset from './useClickRetryCurrentActivityWithoutReset';
import useClickRetryQuiz from './useClickRetryQuiz';
import useClickRetryQuizMantainCorrect from './useClickRetryQuizMantainCorrect';
import useClickRetryQuizWithoutReset from './useClickRetryQuizWithoutReset';
import useClickRetryQuizzesOnCurrentPage from './useClickRetryQuizzesOnCurrentPage';
import useClickRetryQuizzesOnCurrentPageMantainCorrect from './useClickRetryQuizzesOnCurrentPageMantainCorrect';
import useClickRetryQuizzesOnCurrentPageWithoutReset from './useClickRetryQuizzesOnCurrentPageWithoutReset';
import useClickSaveAndExitContent from './useClickSaveAndExitContent';
import useClickSequenceMoveNext from './useClickSequenceMoveNext';
import useClickSequenceMovePrevious from './useClickSequenceMovePrevious';
import useClickStartQuiz from './useClickStartQuiz';
import useClickUrl from './useClickUrl';
import useClickZoomIn from './useClickZoomIn';
import useClickZoomOut from './useClickZoomOut';
import nullFunc from '../../../utils/nullFunc';
import useActionEnable from '../../../hooks/useActionEnable';
import useClickCheck from './useClickCheck';

function useInteractiveClick(props) {
  const { action, onClick = nullFunc } = props;
  const config = {
    changeLanguage: useClickChangeLanguage(),
    check: useClickCheck(),
    checkAllQuizzes: useClickCheckAllQuizzes(),
    checkQuiz: useClickCheckQuiz(),
    checkQuizzesOnCurrentPage: useClickCheckQuizzesOnCurrentPage(),
    clearResponse: useClickClearResponse(),
    closeMenu: useClickCloseMenu(),
    closePopup: useClickClosePopup(),
    downloadResource: useClickDownloadResource(),
    finaliseAllQuizzes: useClickFinaliseAllQuizzes(),
    finaliseQuiz: useClickFinaliseQuiz(),
    finaliseQuizOnCurrentPage: useClickFinaliseQuizzesOnCurrentPage(),
    flipCard: useClickFlipCard(),
    goToAccordion: useClickGotoSlide(),
    goToFirstActivity: useClickGotoFirstActivity(),
    gotoPage: useClickGotoPage(),
    goToSlide: useClickGotoSlide(),
    goToTab: useClickGotoSlide(),
    next: useClickNext(),
    nextActivity: useClickNextSlide(),
    nextSlide: useClickNextSlide(),
    nextTab: useClickNextSlide(),
    openMenu: useClickOpenMenu(),
    openNotification: useClickOpenNotification(),
    openPopup: useClickOpenPopup(),
    openResource: useClickOpenResource(),
    prevActivity: useClickPreviousSlide(),
    previous: useClickPrevious(),
    previousSlide: useClickPreviousSlide(),
    previousTab: useClickPreviousSlide(),
    retryAllQuizzes: useClickRetryAllQuizzes(),
    retryAllQuizzesWithoutReset: useClickRetryAllQuizzesWithoutReset(),
    retryAndMantainCorrectAllQuizzes: useClickRetryAndMantainCorrectAllQuizzes(),
    retryCurrentActivity: useClickRetryCurrentActivity(),
    retryCurrentActivityWithoutReset: useClickRetryCurrentActivityWithoutReset(),
    retryQuiz: useClickRetryQuiz(),
    retryQuizMantainCorrect: useClickRetryQuizMantainCorrect(),
    retryQuizWithoutReset: useClickRetryQuizWithoutReset(),
    retryQuizzesOnCurrentPage: useClickRetryQuizzesOnCurrentPage(),
    retryQuizzesOnCurrentPageMantainCorrect: useClickRetryQuizzesOnCurrentPageMantainCorrect(),
    retryQuizzesOnCurrentPageWithoutReset: useClickRetryQuizzesOnCurrentPageWithoutReset(),
    saveAndExitContent: useClickSaveAndExitContent(),
    sequenceMoveNext: useClickSequenceMoveNext(),
    sequenceMovePrevious: useClickSequenceMovePrevious(),
    startQuiz: useClickStartQuiz(),
    url: useClickUrl(),
    zoomIn: useClickZoomIn(),
    zoomOut: useClickZoomOut(),
  };
  const isActionEnable = useActionEnable();
  const dispatchThunk = useDispatch();
  return (e) => {
    e.stopPropagation();
    onClick();
    const mainAction = config[action];
    if (mainAction && isActionEnable(props)) mainAction(props);
    dispatchThunk(
      (dispatch, getState) => {
        const state = getState();
        const { id } = props;
        const otherActions = selectorChildsByType(state, id, 'ActionData');
        otherActions.forEach(
          (actionId) => {
            const instance = { id: actionId, ...selectorStateInstances(state)[actionId] };
            const langs = selectorStateLangs(state);
            const currentLang = selectorStateCurrentLang(state);
            const actionProps = selectorInstanceData(instance, langs, currentLang, null);
            const actionToExecute = config[actionProps.action];
            if (actionToExecute && isActionEnable(actionProps)) actionToExecute(actionProps);
          },
        );
      },
    );
  };
}

export default useInteractiveClick;
