import selectorPlaceholder from '@auth/components/src/RenderContent/selectorPlaceholder';
import selectorCssInstanceProp from '@auth/lib/src/selectors/selectorCssInstanceProp';
import selectorGetCalcPropName from '@auth/lib/src/selectors/selectorGetCalcPropName';
import selectorProp from '@auth/lib/src/selectors/selectorProp';
import selectorRealType from '@auth/lib/src/selectors/selectorRealType';
import suspendDataSelector from './suspendDataSelector';

export function selectorGetCssValueAndPlaceholder(
  state,
  instanceId,
  propName,
  defaultPlaceholder,
) {
  const propNameCalculated = selectorGetCalcPropName(propName, state, '');
  const value = selectorCssInstanceProp(
    state,
    instanceId,
    propNameCalculated,
  );
  const placeholder = selectorPlaceholder(
    state,
    instanceId,
    propName,
    defaultPlaceholder,
    '',
  );
  const modified = !!value && value !== placeholder;
  return [value, placeholder, modified];
}

function getCompositeVisibleChildsSelector(state, id) {
  const suspend = suspendDataSelector(id, 'visibleChilds', state);
  if (suspend) return suspend;
  const [type] = selectorGetCssValueAndPlaceholder(state, id, 'compositeNavigationType');
  const childs = selectorProp(state, id, 'childs') || [];
  const compositeItems = childs.filter((child) => selectorRealType(state, child) === 'CompositeItem');
  if (compositeItems.length && type !== 'accordion') {
    return [compositeItems[0]];
  }
  return [];
}

export default getCompositeVisibleChildsSelector;
