export const initialState = {
  active: false,
  over: false,
  overTimestamp: Date.now(),
};

function activeReducer(state, action) {
  const newValue = !!action.value;
  if (state.active === newValue) return state;
  return {
    ...state,
    active: newValue,
  };
}

function overReducer(state, action) {
  const {
    value, timestamp,
  } = action;

  if (state.over === value) return state;
  const delay = timestamp - state.overTimestamp;
  if (delay < 300) return state;
  return {
    ...state,
    over: value,
    overTimestamp: timestamp,
  };
}

const config = {
  active: activeReducer,
  over: overReducer,
};

function interactiveClickReducer(state, action) {
  const reducer = config[action.type];
  if (!reducer) return state;
  return reducer(state, action);
}

export default interactiveClickReducer;
