/* eslint-disable jsx-a11y/media-has-caption */
import React, {
  useEffect, useRef, useState,
} from 'react';
import PropTypes from 'prop-types';
import useInteractiveProps from '../useInteractiveProps';
import './skin1/AudioSkin1.css';

const calculateTime = (secs) => {
  const minutes = Math.floor(secs / 60);
  const seconds = Math.floor(secs % 60);
  const returnedSeconds = seconds < 10 ? `0${seconds}` : `${seconds}`;
  return `${minutes}:${returnedSeconds}`;
};

function Audio(props) {
  const {
    src,
    className,
    onInit,
    onExit,
  } = props;
  const componentEl = useRef(null);
  const audioEl = useRef(null);
  const seekEl = useRef(null);
  const [stateBtnPlayPause, setStateBtnPlayPause] = useState('play');
  const [currentTime, setCurrentTime] = useState(0);
  const [duration, setDuration] = useState(100);
  const [userIsSeeking, setUserIsSeeking] = useState(false);
  const handleClickPlayPause = () => {
    if (stateBtnPlayPause === 'play') {
      setStateBtnPlayPause('pause');
      audioEl.current.play();
    } else {
      setStateBtnPlayPause('play');
      audioEl.current.pause();
    }
  };
  useEffect(() => {
    const currentComponentEl = componentEl.current;
    const currentAudioEl = audioEl.current;
    const currentSeekEl = seekEl.current;
    const handleTimeupdate = () => {
      if (!userIsSeeking) {
        setCurrentTime(Math.floor(audioEl.current.currentTime));
      }
    };
    const handleChangeSeekEl = (e) => {
      currentAudioEl.currentTime = e.target.value;
      if (!currentAudioEl.paused) {
        setUserIsSeeking(false);
      }
    };
    const handlePlay = () => {
      setStateBtnPlayPause('pause');
    };
    const handlePause = () => {
      setStateBtnPlayPause('play');
    };
    const handleEnded = () => {
      setStateBtnPlayPause('play');
      setCurrentTime(duration);
    };
    const handleBufferedAmout = () => {
      const bufferedAmount = currentAudioEl.buffered.length ? Math.floor(
        currentAudioEl.buffered.end(currentAudioEl.buffered.length - 1),
      ) : 0;
      currentComponentEl.style.setProperty('--buffered-width', `${(bufferedAmount / duration) * 100}%`);
    };
    const handleLoadedmetadata = () => {
      setDuration(Math.floor(currentAudioEl.duration));
      handleBufferedAmout();
    };
    if (currentAudioEl.readyState > 0) {
      handleLoadedmetadata();
    } else {
      currentAudioEl.addEventListener('loadedmetadata', handleLoadedmetadata);
    }
    currentAudioEl.addEventListener('play', handlePlay);
    currentAudioEl.addEventListener('pause', handlePause);
    currentAudioEl.addEventListener('ended', handleEnded);
    currentAudioEl.addEventListener('progress', handleBufferedAmout);
    currentAudioEl.addEventListener('timeupdate', handleTimeupdate);
    currentSeekEl.addEventListener('change', handleChangeSeekEl);
    return () => {
      currentAudioEl.removeEventListener('loadedmetadata', handleLoadedmetadata);
      currentAudioEl.removeEventListener('play', handlePlay);
      currentAudioEl.removeEventListener('pause', handlePause);
      currentAudioEl.removeEventListener('ended', handleEnded);
      currentAudioEl.removeEventListener('progress', handleBufferedAmout);
      currentAudioEl.removeEventListener('timeupdate', handleTimeupdate);
      currentSeekEl.removeEventListener('change', handleChangeSeekEl);
    };
  }, [audioEl, seekEl, componentEl, duration, userIsSeeking, src]);
  useEffect(() => {
    if (componentEl.current) {
      componentEl.current.style.setProperty('--seek-before-width', `${(currentTime / duration) * 100}%`);
    }
  }, [componentEl, currentTime, duration]);
  useEffect(() => {
    const currentAudioEl = audioEl.current;
    if (!currentAudioEl) {
      return undefined;
    }
    onInit(currentAudioEl);
    return () => onExit(currentAudioEl);
  }, [audioEl, onInit, onExit, src]);
  return (
    <div
      key={src}
      className={className}
      {...useInteractiveProps(props)}
    >
      <figure ref={componentEl} className="ntx-audio-skin-1">
        <audio tabIndex="-1" ref={audioEl} src={src} preload="metadata" />
        <button
          type="button"
          aria-label="Play/Pause"
          onClick={handleClickPlayPause}
          className={`ntx-audio-btn-play-pause ${stateBtnPlayPause === 'play' ? 'play' : 'pause'}`}
        />
        <input
          ref={seekEl}
          type="range"
          max={duration}
          value={currentTime}
          step="0.1"
          onInput={(e) => {
            if (!audioEl.current.paused) {
              setUserIsSeeking(true);
            }
            setCurrentTime(e.target.value);
          }}
          className="ntx-audio-range-seek"
        />
        <span className="ntx-audio-time">
          {calculateTime(currentTime)}
          {' '}
          /
          {' '}
          {calculateTime(duration)}
        </span>
      </figure>
    </div>
  );
}

Audio.propTypes = {
  src: PropTypes.string,
  className: PropTypes.string,
  onInit: PropTypes.func,
  onExit: PropTypes.func,
};

Audio.defaultProps = {
  src: `${process.env.PUBLIC_URL}/resources/audio.mp3`,
  className: '',
  onInit: () => null,
  onExit: () => null,
};

export default Audio;
