import selectorClosestType from '@auth/lib/src/selectors/selectorClosestType';
import checkQuizTimesSelector from './checkQuizTimesSelector';
import quizMaxAttemptSelector from './quizMaxAttemptSelector';
import suspendDataSelector from './suspendDataSelector';

function quizRemainingAttemptsSelector(state, descendantId) {
  const quiz = selectorClosestType(state, descendantId, 'Quiz');
  if (!quiz) return 0;
  const finalise = suspendDataSelector(quiz, 'finalise', state) === '1';
  if (finalise) return 0;
  const maxAttempts = quizMaxAttemptSelector(state, quiz);
  const checkQuizTimes = checkQuizTimesSelector(state, quiz);
  const remainig = maxAttempts - checkQuizTimes;
  return Math.max(remainig, 0);
}

export default quizRemainingAttemptsSelector;
