export const cssEnabledPropsKeys = Object.keys(cssEnabledProps);

export const cssEnabledPropValues = [
  ...new Set(
    Object.values(cssEnabledProps).reduce(
      (m, { affectedProps }) => [...m, ...affectedProps],
      [],
    ),
  )];

export function getAffectedProps(propName) {
  return cssEnabledProps[propName].affectedProps;
}
