/* eslint-disable no-restricted-syntax */
import React, { useCallback, useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import ReactPlayer from 'react-player/lazy';

const VideoPlayer = React.memo(React.forwardRef((props, ref) => {
  const {
    playsinline,
    listenChangeSubtitles,
    url,
    typeResource,
    playing,
    muted,
    poster,
    volume,
    loadNativeSubtitles,
    currentSubtitles,
    subtitlesInCCR,
    setSubtitles,
    onPlay,
    onEnded,
    onReady,
    onDuration,
    onProgress,
    onPause,
  } = props;
  const [alreadyInit, setAlreadyInit] = useState(false);
  const tracks = Object.entries(JSON.parse(subtitlesInCCR)).map(
    ([srcLang, mySrc]) => ({
      kind: 'subtitles',
      src: mySrc,
      srcLang,
    }),
  );
  const checkExistInternalPlayer = useCallback(() => {
    if (ref && ref.current && ref.current.getInternalPlayer && ref.current.getInternalPlayer()) {
      return true;
    }
    return false;
  }, [ref]);
  const handleSetSubtitleYoutube = useCallback((lang) => {
    if (checkExistInternalPlayer()) {
      const player = ref.current.getInternalPlayer();
      try {
        player.setOption('captions', 'track', lang === 'none' ? {} : { languageCode: lang });
      } catch (err) {
        // eslint-disable-next-line no-console
        console.error(err);
      }
    }
  }, [checkExistInternalPlayer, ref]);
  const handleSetSubtitleVimeo = useCallback((lang) => {
    if (checkExistInternalPlayer()) {
      const player = ref.current.getInternalPlayer();
      if (!lang || lang === 'none') {
        player.disableTextTrack();
      } else {
        player.enableTextTrack(lang);
      }
    }
  }, [checkExistInternalPlayer, ref]);
  const handleSetSubtitleCCR = useCallback((lang) => {
    if (checkExistInternalPlayer()) {
      const player = ref.current.getInternalPlayer();
      for (const textTrack of player.textTracks) {
        const { language } = textTrack;
        textTrack.mode = language === lang ? 'showing' : 'hidden';
      }
    }
  }, [checkExistInternalPlayer, ref]);
  const initSubtitlesCCR = (lang) => {
    const player = ref.current.getInternalPlayer();
    for (let i = 0, L = player.textTracks.length; i < L; i += 1) {
      const { language } = player.textTracks[i];
      if (language === lang) {
        player.textTracks[i].mode = 'showing';
      } else {
        player.textTracks[i].mode = 'hidden';
      }
    }
  };
  const handleOnReady = () => {
    if (!alreadyInit) {
      if (typeResource === 'ccr') {
        if (tracks.length) {
          const newSubtitles = tracks.reduce((m, v) => ({
            ...m,
            [v.srcLang]: v.srcLang,
          }), {});
          setSubtitles(newSubtitles);
          initSubtitlesCCR(currentSubtitles);
        } else {
          setSubtitles({});
        }
      }
      if (typeResource === 'youtube') {
        const player = ref.current.getInternalPlayer();
        let loadCaptionsModuleYoutube = true;
        player.addEventListener('onApiChange', () => {
          if (!loadCaptionsModuleYoutube) {
            try {
              if (loadNativeSubtitles) {
                const trackList = player.getOption('captions', 'tracklist');
                if (trackList) {
                  const newSubtitles = trackList.reduce((m, v) => ({
                    ...m,
                    [v.languageCode]: v.displayName,
                  }), {});
                  setSubtitles(newSubtitles);
                }
              }
              handleSetSubtitleYoutube();
            } catch (err) {
              // eslint-disable-next-line no-console
              console.error(err);
            }
          }
          if (loadCaptionsModuleYoutube) {
            player.loadModule('captions');
            loadCaptionsModuleYoutube = false;
          }
        });
      }
      if (typeResource === 'vimeo') {
        const player = ref.current.getInternalPlayer();
        player.getTextTracks().then((tracksVimeo) => {
          if (loadNativeSubtitles) {
            const newSubtitles = tracksVimeo.reduce((m, v) => ({
              ...m,
              [v.language]: v.label,
            }), {});
            setSubtitles(newSubtitles);
          }
          handleSetSubtitleVimeo();
        });
      }
      onReady();
      setAlreadyInit(true);
    }
  };
  useEffect(() => {
    if (listenChangeSubtitles) {
      if (typeResource === 'youtube') {
        handleSetSubtitleYoutube(currentSubtitles);
      }
      if (typeResource === 'vimeo') {
        handleSetSubtitleVimeo(currentSubtitles);
      }
      if (typeResource === 'ccr') {
        handleSetSubtitleCCR(currentSubtitles);
      }
    }
  }, [
    currentSubtitles,
    handleSetSubtitleCCR,
    handleSetSubtitleVimeo,
    handleSetSubtitleYoutube,
    listenChangeSubtitles,
    typeResource,
  ]);
  if (!url) {
    return null;
  }
  return (
    <ReactPlayer
      className="ntx-video-player"
      width="100%"
      height="100%"
      loop={false}
      ref={ref}
      url={url}
      pip={false}
      playsinline={playsinline}
      config={{
        attributes: {
          tabIndex: '-1',
        },
        youtube: {
          playerVars: {
            playsinline: playsinline ? 1 : 0,
            autoplay: 0,
            enablejsapi: 1,
            showinfo: 0,
            rel: 0,
            cc_load_policy: 0,
            fs: 0,
            disablekb: 1,
          },
        },
        vimeo: {
          playerOptions: {
            playsinline,
            transparent: false,
            loop: false,
          },
        },
        file: {
          attributes: {
            poster,
          },
          pip: false,
          tracks,
        },
      }}
      playing={playing}
      muted={muted}
      volume={volume}
      onReady={handleOnReady}
      onDuration={onDuration}
      onProgress={onProgress}
      onPlay={onPlay}
      onPause={onPause}
      onEnded={onEnded}
    />
  );
}));

VideoPlayer.propTypes = {
  url: PropTypes.string.isRequired,
  typeResource: PropTypes.string.isRequired,
  playsinline: PropTypes.bool,
  listenChangeSubtitles: PropTypes.bool,
  playing: PropTypes.bool,
  volume: PropTypes.number,
  muted: PropTypes.bool,
  subtitlesInCCR: PropTypes.string,
  loadNativeSubtitles: PropTypes.bool,
  currentSubtitles: PropTypes.string,
  setSubtitles: PropTypes.func,
  poster: PropTypes.string,
  onReady: PropTypes.func,
  onPlay: PropTypes.func,
  onProgress: PropTypes.func,
  onDuration: PropTypes.func,
  onPause: PropTypes.func,
  onEnded: PropTypes.func,
};

VideoPlayer.defaultProps = {
  playsinline: true,
  listenChangeSubtitles: true,
  playing: false,
  volume: null,
  muted: false,
  poster: '',
  subtitlesInCCR: '{}',
  loadNativeSubtitles: false,
  currentSubtitles: 'none',
  setSubtitles: () => null,
  onReady: () => null,
  onPlay: () => null,
  onProgress: () => null,
  onDuration: () => null,
  onPause: () => null,
  onEnded: () => null,
};

export default VideoPlayer;
