import React, { useMemo, useReducer } from 'react';
import PropTypes from 'prop-types';
import InteractiveRenderStateContext from './InteractiveRenderStateContext';

const initialState = {
  isPanelOpen: false,
};

const closePanelReducer = (state) => ({ ...state, isPanelOpen: false });
const openPanelReducer = (state) => ({ ...state, isPanelOpen: true });

const config = {
  closePanel: closePanelReducer,
  openPanel: openPanelReducer,
};

const reducer = (state, action) => {
  const { type } = action;
  return config[type](state, action);
};

function InteractiveRenderState(props) {
  const { children } = props;
  const [
    state,
    dispatch,
  ] = useReducer(
    reducer,
    initialState,
  );
  const {
    isPanelOpen,
  } = state;
  const closePanel = () => dispatch({ type: 'closePanel' });
  const openPanel = () => dispatch({ type: 'openPanel' });
  const contextValue = useMemo(() => ({
    isPanelOpen,
    closePanel,
    openPanel,
  }), [isPanelOpen]);
  return (
    <InteractiveRenderStateContext.Provider value={contextValue}>
      {children}
    </InteractiveRenderStateContext.Provider>
  );
}

InteractiveRenderState.propTypes = {
  children: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]),
};

InteractiveRenderState.defaultProps = {
  children: null,
};

export default InteractiveRenderState;
