import activityTypes from '@auth/lib/src/data/activityTypes';
import selectorType from '@auth/lib/src/selectors/selectorType';
import activityPointsSelector from '@auth/render/src/selectors/activityPointsSelector';

function selectorActivityMaxScore(state, activityId) {
  const type = selectorType(state, activityId);
  if (activityTypes.includes(type)) {
    return activityPointsSelector(state, activityId);
  }
  return null;
}

export default selectorActivityMaxScore;
