import { useDispatch } from 'react-redux';
import selectorCompIdsByType from '@auth/lib/src/selectors/selectorCompIdsByType';
import useFinaliseQuizzes from '../../../hooks/useFinaliseQuizzes';

function useClickFinaliseAllQuizzes() {
  const dispatchThunk = useDispatch();
  const finaliseQuizzes = useFinaliseQuizzes();
  return () => {
    dispatchThunk(
      (dispatch, getState) => {
        const state = getState();
        const quizzes = selectorCompIdsByType(state, 'Quiz', true);
        finaliseQuizzes(quizzes);
      },
    );
  };
}

export default useClickFinaliseAllQuizzes;
