import getQueryParam from '@auth/shared/src/getQueryParam';
import debounce from 'lodash.debounce';
import lmsApi, { lmsApiInit, isScorm12InConfig } from './lmsApi';

let initTime;
let lastPageHideInit;
let mastery;
let lastScore;
let lastSuccess;
let isExitExecuted = false;
let accumulatedTimeInPageHide = 0;

const getElapsedTime = () => Date.now() - initTime - accumulatedTimeInPageHide;

const handleHidden = () => {
  lastPageHideInit = Date.now();
};

const handleVisible = () => {
  if (lastPageHideInit) {
    const elapsedHidden = (Date.now() - lastPageHideInit);
    accumulatedTimeInPageHide += elapsedHidden;
    lastPageHideInit = undefined;
  }
};

export function reset() {
  initTime = undefined;
  lastPageHideInit = undefined;
  mastery = undefined;
  lastScore = undefined;
  lastSuccess = undefined;
  isExitExecuted = false;
  accumulatedTimeInPageHide = 0;
}

export function init(storeConfig) {
  initTime = Date.now();
  const debugParam = getQueryParam('debug');
  const config = debugParam === 'true' || debugParam === 'false'
    ? { ...storeConfig, debugMode: debugParam === 'true' }
    : storeConfig;
  const [
    lmsScore,
    lmsCompletion,
    lmsMastery,
    languagePreference,
  ] = lmsApiInit(config);
  mastery = lmsMastery || storeConfig.contentCutOffScore;
  const ret = {
    languagePreference,
  };
  if (typeof lmsScore === 'number') {
    ret.score = lmsScore;
    lastScore = lmsScore;
  }
  if (typeof lmsCompletion === 'boolean') ret.completion = lmsCompletion;
  const suspend = lmsApi().getSuspend();
  if (suspend) ret.suspend = suspend;
  const strLocation = lmsApi().getLocation();
  if (strLocation) ret.location = strLocation;

  document.addEventListener('visibilitychange', () => {
    if (document.visibilityState === 'hidden') handleHidden();
    if (document.visibilityState === 'visible') handleVisible();
  });

  return ret;
}

const sendSuccess = (
  { score, completion, evaluableQuizzesDone },
  { sendingSuccessStatusCriteria },
) => {
  const successStatus = score >= mastery;
  if (sendingSuccessStatusCriteria === 'never') return;
  if (sendingSuccessStatusCriteria === 'content_completed' && !completion) return;
  if (sendingSuccessStatusCriteria === 'quizzes_completed' && !evaluableQuizzesDone) return;
  if (successStatus === lastSuccess) return;
  lastSuccess = successStatus;
  lmsApi().setSuccessStatus(successStatus, getElapsedTime());
};

const sendScore = (
  { score, completion, evaluableQuizzesDone },
  { sendingScoreCriteria },
) => {
  const newVal = score || 0;
  if (sendingScoreCriteria === 'never') return;
  if (sendingScoreCriteria === 'content_completed' && !completion) return;
  if (sendingScoreCriteria === 'quizzes_completed' && !evaluableQuizzesDone) return;
  if (newVal === lastScore) return;
  lastScore = newVal;
  lmsApi().setScore(lastScore, getElapsedTime());
};

function sendSetCompletion({
  sendComplete,
  scorm12SuccessLessonStatus,
  scorm12FailedLessonStatus,
  sendingSuccessStatusCriteria,
}) {
  if (isScorm12InConfig && (scorm12SuccessLessonStatus === 'passed' || scorm12FailedLessonStatus === 'failed')) {
    return sendingSuccessStatusCriteria !== 'never';
  }
  return sendComplete;
}

export function completionChange(data, config) {
  const { completion } = data;
  if (isExitExecuted) return;
  if (sendSetCompletion(config)) lmsApi().setCompletion(completion, getElapsedTime());
  sendScore(data, config);
  sendSuccess(data, config);
}

export function scoreChange(data, config) {
  if (isExitExecuted) return;
  sendScore(data, config);
  sendSuccess(data, config);
}

export function evaluableQuizzesDoneChange(data, config) {
  scoreChange(data, config);
}

const setSuspendFunc = debounce((suspend) => lmsApi().setSuspend(suspend), 1000);
export function suspendChange({ suspend }) {
  if (isExitExecuted) return;
  setSuspendFunc(suspend);
}

export function locationChange(
  { location },
) {
  if (isExitExecuted) return;
  lmsApi().setLocation(location);
}

export function sendInteraction(data) {
  if (isExitExecuted) return;
  lmsApi().sendInteraction(data);
}

export function sendMultipleInteractions(data) {
  if (isExitExecuted) return;
  lmsApi().sendMultipleInteractions(data);
}

function exitCourse() {
  if (!isExitExecuted) {
    lmsApi().exit({
      sessionTime: getElapsedTime(),
    });
    isExitExecuted = true;
  }
}

export function saveAndExitContent() {
  window.dispatchEvent(new Event('saveAndExitContent'));
  // el timeout es para asegurarnos de tener el último suspend
  setTimeout(() => {
    exitCourse();
    window.close();
  }, 1100);
}

window.addEventListener('beforeunload', exitCourse);
