import React from 'react';
import PropTypes from 'prop-types';
import { shallowEqual, useSelector } from 'react-redux';
import Audio from '@auth/components/src/Audio/Audio';
import { AUDIO_INDICATED_END, AUDIO_INDICATED_TIME } from '@auth/lib/src/events/triggers';
import interactiveRenderMultimediaSelector from '@auth/shared/src/selectors/interactiveRenderMultimediaSelector';
import selectorEventsIdsByTriggerCompIdAndTriggerType from '@auth/lib/src/selectors/selectorEventsIdsByTriggerCompIdAndTriggerType';
import useSetSuspendData from '../../hooks/useSetSuspendData';
import useGetSuspendDataThunk from '../../hooks/useGetSuspendDataThunk';
import useSetCompleted from '../../hooks/useSetCompleted';
import useDispatchEvent from '../../hooks/useDispatchEvent';

let last;
const checkVideoIndicatedTimeEvents = (events, current) => {
  const valid = events.find(
    (time) => current > time && last < time,
  );
  last = current;
  return valid;
};

function InteractiveRenderAudio(props) {
  const { id } = props;
  const setSuspend = useSetSuspendData();
  const getSuspend = useGetSuspendDataThunk();
  const setCompleted = useSetCompleted(id);
  const dispatchEvent = useDispatchEvent();
  const eventsIdsTriggerCompIdAndTriggerType = useSelector(
    (state) => selectorEventsIdsByTriggerCompIdAndTriggerType(state, id, AUDIO_INDICATED_END),
    shallowEqual,
  );
  const videoIndicatedTimeEvents = useSelector(
    (state) => interactiveRenderMultimediaSelector(state, id),
    shallowEqual,
  );
  const handleTimeupdate = (e) => {
    const { currentTime = 0 } = e.target;
    const validVideoIndicatedEvent = checkVideoIndicatedTimeEvents(
      videoIndicatedTimeEvents,
      currentTime,
    );
    if (validVideoIndicatedEvent) {
      dispatchEvent(AUDIO_INDICATED_TIME, id, { time: validVideoIndicatedEvent, player: e.target });
    }
    setSuspend(id, 'currentTime', currentTime);
  };
  const handleLoadeddata = (e) => {
    const initTime = getSuspend(id, 'currentTime') || 0;
    e.target.currentTime = initTime;
  };
  const handleEnded = () => {
    if (eventsIdsTriggerCompIdAndTriggerType.length) {
      dispatchEvent(AUDIO_INDICATED_END, id);
    }
    setCompleted();
  };
  const handlePlay = () => {
    setSuspend(id, 'play', 1);
  };
  const handleOnInit = (player) => {
    player.addEventListener('timeupdate', handleTimeupdate);
    player.addEventListener('loadeddata', handleLoadeddata);
    player.addEventListener('ended', handleEnded);
    player.addEventListener('play', handlePlay);
  };
  const handleOnExit = (player) => {
    player.removeEventListener('timeupdate', handleTimeupdate);
    player.removeEventListener('loadeddata', handleLoadeddata);
    player.removeEventListener('ended', handleEnded);
    player.removeEventListener('play', handlePlay);
  };
  const handleClick = (e) => {
    e.stopPropagation();
  };
  return (
    <Audio
      {...props}
      onInit={handleOnInit}
      onExit={handleOnExit}
      onClick={handleClick}
    />
  );
}

InteractiveRenderAudio.propTypes = {
  id: PropTypes.string.isRequired,
};

InteractiveRenderAudio.defaultProps = {
};

export default InteractiveRenderAudio;
