import React, { useContext } from 'react';
import PropTypes from 'prop-types';
import { ClassNames } from '@auth/shared/src/utils/generateClassReact';
import { createPortal } from 'react-dom';
import PopupOverlayContext from './PopupOverlayContext';

const cssDefault = {
  position: 'absolute',
  top: 0,
  right: 0,
  bottom: 0,
  left: 0,
};

function PopupOverlay(props) {
  const {
    popupOverlayBackground = null,
  } = props;
  const overlayCss = popupOverlayBackground
    ? { backgroundColor: popupOverlayBackground }
    : {};
  const { id, zIndex } = useContext(PopupOverlayContext) || {};
  const objZindex = zIndex !== undefined ? { zIndex } : {};
  const ret = (
    <ClassNames>
      {({ css }) => (
        <div
          className={css({ ...cssDefault, ...overlayCss, ...objZindex })}
        />
      )}
    </ClassNames>
  );

  const overlay = document.getElementById(id);
  if (overlay) return createPortal(ret, overlay);
  return ret;
}

PopupOverlay.propTypes = {
  popupOverlayBackground: PropTypes.string,
};

PopupOverlay.defaultProps = {
  popupOverlayBackground: null,
};

export default PopupOverlay;
