import {
  EDITOR_RESOLUTION_WIDTH_DESKTOP_EXTRA,
  EDITOR_RESOLUTION_WIDTH_MOBILE,
  EDITOR_RESOLUTION_WIDTH_TABLET,
} from '@auth/lib/src/consts';

function getMqByResolution(resolution) {
  switch (resolution) {
    case EDITOR_RESOLUTION_WIDTH_DESKTOP_EXTRA:
      return ':widescreen';
    case EDITOR_RESOLUTION_WIDTH_TABLET:
      return ':tablet';
    case EDITOR_RESOLUTION_WIDTH_MOBILE:
      return ':mobile';
    default:
      return '';
  }
}

export default getMqByResolution;
