import { CONTENT_EXIT } from '@auth/lib/src/events/triggers';
import useDispatchEvent from '../../../hooks/useDispatchEvent';
import { saveAndExitContent } from '../../../tracking/tracking';

function useClickSaveAndExitContent() {
  const dispatch = useDispatchEvent();
  return () => {
    saveAndExitContent();
    dispatch(CONTENT_EXIT);
  };
}

export default useClickSaveAndExitContent;
