import { useSelector } from 'react-redux';
import selectorStateEditorResolutionWidth from '@auth/lib/src/selectors/selectorStateEditorResolutionWidth';
import {
  EDITOR_RESOLUTION_WIDTH_DESKTOP,
  EDITOR_RESOLUTION_WIDTH_DESKTOP_EXTRA,
  EDITOR_RESOLUTION_WIDTH_MOBILE,
  EDITOR_RESOLUTION_WIDTH_TABLET,
} from '@auth/lib/src/consts';
import selectorConfig from '@auth/lib/src/selectors/selectorConfig';
import { useEffect, useState } from 'react';

export function resolutionToNumber(str) {
  if (!str) return 0;
  return +str.replace('px', '');
}

const selectorMax = (state) => {
  const currentResolution = selectorStateEditorResolutionWidth(state);
  switch (currentResolution) {
    case EDITOR_RESOLUTION_WIDTH_DESKTOP_EXTRA:
      return Infinity;
    case EDITOR_RESOLUTION_WIDTH_DESKTOP:
      return resolutionToNumber(selectorConfig(state, 'resolutionDesktop'));
    case EDITOR_RESOLUTION_WIDTH_TABLET:
      return resolutionToNumber(selectorConfig(state, 'resolutionTablet'));
    case EDITOR_RESOLUTION_WIDTH_MOBILE:
      return resolutionToNumber(selectorConfig(state, 'resolutionMobile'));
    default:
      return Infinity;
  }
};

const selectorMin = (state) => {
  const currentResolution = selectorStateEditorResolutionWidth(state);
  switch (currentResolution) {
    case EDITOR_RESOLUTION_WIDTH_DESKTOP_EXTRA:
      return resolutionToNumber(selectorConfig(state, 'resolutionDesktop')) + 1;
    case EDITOR_RESOLUTION_WIDTH_DESKTOP:
      return resolutionToNumber(selectorConfig(state, 'resolutionTablet')) + 1;
    case EDITOR_RESOLUTION_WIDTH_TABLET:
      return resolutionToNumber(selectorConfig(state, 'resolutionMobile')) + 1;
    case EDITOR_RESOLUTION_WIDTH_MOBILE:
      return 0;
    default:
      return 0;
  }
};

const getWidth = () => window.innerWidth
  || document.documentElement.clientWidth
  || document.body.clientWidth;

const propertiesWidth = 300;
const margins = 64;

const useEditorCanvasSize = () => {
  const max = useSelector(selectorMax);
  const min = useSelector(selectorMin);
  const [width, setWidth] = useState(getWidth());
  useEffect(() => {
    let timeoutId = null;
    const resizeListener = () => {
      clearTimeout(timeoutId);
      timeoutId = setTimeout(() => setWidth(getWidth()), 150);
    };
    window.addEventListener('resize', resizeListener);
    return () => {
      window.removeEventListener('resize', resizeListener);
    };
  }, []);

  return Math.max(
    Math.min(width - propertiesWidth - margins, max),
    min,
  );
};

export default useEditorCanvasSize;
