import arrows from './interactions2004/arrows';
import likert from './interactions2004/likert';
import multipleChoice from './interactions2004/multipleChoice';
import sequence from './interactions2004/sequence';
import singleChoice from './interactions2004/singleChoice';
import textInput from './interactions2004/textInput';

const config = {
  ActivityArrows: arrows,
  ActivityLikert: likert,
  ActivityMultipleChoice: multipleChoice,
  ActivitySequence: sequence,
  ActivitySingleChoice: singleChoice,
  ActivityTextInput: textInput,
};

function interactions2004(data) {
  const { type } = data;
  return config[type](data);
}

export default interactions2004;
