import selectorClosestType from '@auth/lib/src/selectors/selectorClosestType';
import selectorStateStructure from '@auth/lib/src/selectors/selectorStateStructure';

export default function isDescendantOfLastPageSelector(state, descendantId) {
  const pageId = selectorClosestType(state, descendantId, 'PageContent');
  const structure = selectorStateStructure(state);
  const position = structure.indexOf(pageId);
  return position === (structure.length - 1);
}
