import selectorFirstAscendantByType from '@auth/lib/src/selectors/selectorFirstAscendantByType';
import progressPropSelector from './progressPropSelector';

const textProgressByPropNameSelector = (state, id, propName) => {
  const myProgress = selectorFirstAscendantByType(state, id, 'ProgressBar');
  return progressPropSelector(state, myProgress, propName);
};

export default textProgressByPropNameSelector;
