import selectorStateCss from './selectorStateCss';
import selectorType from './selectorType';

function getId(state, instanceId) {
  const type = selectorType(state, instanceId);
  if (type === 'MenuListItem') {
    return `${instanceId.split('_')[0]}_items`;
  }
  return instanceId;
}

function selectorCssInstanceData(state, instanceId) {
  const css = selectorStateCss(state);
  const id = getId(state, instanceId);
  return css[id] || {};
}

export default selectorCssInstanceData;
