import { useContext } from 'react';
import VisbleChildsContext from '../VisbleChildsContext';
import usePrevSlide from '../../../hooks/usePrevSlide';

function useClickPreviousSlide() {
  const {
    componentId,
    visibleChild,
    childs = [],
  } = useContext(VisbleChildsContext) || {};
  const goPrevSlide = usePrevSlide();
  return () => {
    goPrevSlide(componentId, childs, visibleChild);
  };
}

export default useClickPreviousSlide;
