import selectorFindDescendantByTypes from '@auth/lib/src/selectors/selectorFindDescendantByTypes';
import selectorProp from '@auth/lib/src/selectors/selectorProp';
import getId from './getId';

export function mapArrowsIds(state, arrows) {
  return arrows ? arrows.split('[,]').map(
    (arrow) => {
      const [source, target] = arrow.split('[.]');
      const sourceId = getId(state, source);
      const targetId = getId(state, target);
      return `${sourceId}[.]${targetId}`;
    },
  ).join('[,]') : [];
}

function correctResponsesActivityArrows(state, id) {
  const groupOption = selectorFindDescendantByTypes(state, id, ['ArrowsGroupOption']);
  const correct = selectorProp(state, groupOption, 'correct');
  return mapArrowsIds(state, correct);
}

export default correctResponsesActivityArrows;
