export default function xapiEnsureJson(json) {
  try {
    const type = typeof json;
    if (type === 'object') return json;
    if (type === 'string') {
      const patch = JSON.parse(json);
      if (typeof patch === 'object') return patch;
    }
    return undefined;
  } catch {
    return undefined;
  }
}
