import selectorType from '@auth/lib/src/selectors/selectorType';
import checkActivityArrowsSelector from './checkActivityArrowsSelector';
import checkActivitySequenceSelector from './checkActivitySequenceSelector';
import checkActivityTextInputSelector from './checkActivityTextInputSelector';
import checkLikertActivitySelector from './checkLikertActivitySelector';
import checkMultipleChoiceActivitySelector from './checkMultipleChoiceActivitySelector';
import checkSingleChoiceActivitySelector from './checkSingleChoiceActivitySelector';

const config = {
  ActivityArrows: checkActivityArrowsSelector,
  ActivityLikert: checkLikertActivitySelector,
  ActivityMultipleChoice: checkMultipleChoiceActivitySelector,
  ActivitySequence: checkActivitySequenceSelector,
  ActivitySingleChoice: checkSingleChoiceActivitySelector,
  ActivityTextInput: checkActivityTextInputSelector,
};

function isCorrectActivitySelector(state, activityId) {
  const type = selectorType(state, activityId);
  return config[type](state, activityId);
}

export default isCorrectActivitySelector;
