import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import './skin1/VideoSkin1.css';
import VolumeControl from '../VolumeControl/VolumeControl';
import MultimediaProgressBar from '../MultimediaProgressBar/MultimediaProgressBar';
import SubtitleButton from './SubtitleButton';

const calculateTime = (secs) => {
  const minutes = Math.floor(secs / 60);
  const seconds = Math.floor(secs % 60);
  const returnedSeconds = seconds < 10 ? `0${seconds}` : `${seconds}`;
  return `${minutes}:${returnedSeconds}`;
};

const ToolbarControl = (props) => {
  const {
    id,
    duration,
    paused,
    loaded,
    setSeeking,
    played,
    muted,
    volume,
    play,
    pause,
    unmute,
    mute,
    seek,
    setVolume,
    setFullscreen,
    currentSubtitle,
    subtitles,
    setSubtitle,
    timeEvents,
    fastFordward,
    bookmarks,
    heightVideo,
  } = props;
  const [showPanelSubtitles, setShowPanelSubtitles] = useState(false);
  const [showPanelBookmarks, setShowPanelBookmarks] = useState(false);
  const handleClickPlayPause = () => {
    if (paused === true) {
      play();
    } else {
      pause();
    }
  };
  const handleSetMute = () => {
    if (muted) {
      unmute();
    } else {
      mute();
    }
  };
  const handleClickButtonSubtitles = () => {
    setShowPanelSubtitles(!showPanelSubtitles);
    setShowPanelBookmarks(false);
  };
  const handleClickButtonBookmarks = () => {
    setShowPanelBookmarks(!showPanelBookmarks);
    setShowPanelSubtitles(false);
  };
  const handleSubtitleClick = (lang) => {
    setShowPanelSubtitles(!showPanelSubtitles);
    setSubtitle(lang);
  };

  const handleBookmarkClick = (second) => {
    const normalizedSecond = second >= duration ? duration - 0.5 : second;
    setShowPanelBookmarks(false);
    setSeeking(true);
    seek(normalizedSecond / duration);
    setSeeking(false);
  };
  const [maxHeightChoose, setMaxHeightChoose] = useState('none');
  useEffect(() => {
    if (heightVideo > 0) {
      setMaxHeightChoose(heightVideo - 64);
    } else {
      setMaxHeightChoose('none');
    }
  }, [heightVideo]);
  return (
    <div className="ntx-video-toolbar-control">
      <MultimediaProgressBar
        id={`${id}_progressBar`}
        seek={seek}
        loaded={loaded}
        played={played}
        setSeeking={setSeeking}
        timeEvents={timeEvents}
        duration={duration}
        fastFordward={fastFordward}
      />
      <div className="ntx-video-controls">
        <div className="ntx-video-controls-left">
          <button
            type="button"
            aria-label="Play/Pause"
            onClick={handleClickPlayPause}
            className={`ntx-video-btn ntx-video-btn-play-pause ${paused === true ? 'play' : 'pause'}`}
          />
          <VolumeControl
            id={`${id}_volume`}
            mute={muted}
            setMute={handleSetMute}
            volume={volume}
            setVolume={(newVolume) => { setVolume(newVolume); }}
          />
          <span className="ntx-video-time">
            {calculateTime(duration * played)}
            {' '}
            /
            {' '}
            {calculateTime(duration)}
          </span>
        </div>
        <div className="ntx-video-controls-right">
          {Object.keys(bookmarks).length > 0
            && (
              <span className="ntx-video-container-bookmarks">
                <button
                  type="button"
                  aria-label="Bookmarks"
                  onClick={handleClickButtonBookmarks}
                  className="ntx-video-btn ntx-video-btn-bookmarks"
                />
                {showPanelBookmarks && (
                  <div role="menu" className="ntx-video-panel-choose-bookmarks">
                    <div className="ntx-video-panel-choose-bookmarks-container" style={{ maxHeight: maxHeightChoose }}>
                      {
                        Object.entries(bookmarks).map(
                          ([k, v]) => {
                            const { title, second } = JSON.parse(v);
                            return (
                              <button
                                type="button"
                                onClick={() => handleBookmarkClick(+second)}
                                title={title}
                                key={k}
                              >
                                {title}
                              </button>
                            );
                          },
                        )
                      }
                    </div>
                  </div>
                )}
              </span>
            )}
          {Object.keys(subtitles).length > 0
            && (
              <span className="ntx-video-container-subtitles">
                <button
                  type="button"
                  aria-label="Subtitles"
                  onClick={handleClickButtonSubtitles}
                  className="ntx-video-btn ntx-video-btn-subtitles"
                />
                {showPanelSubtitles && (
                  <div role="menu" className="ntx-video-panel-choose-subtitles">
                    <div className="ntx-video-panel-choose-subtitles-container" style={{ maxHeight: maxHeightChoose }}>
                      <button
                        type="button"
                        aria-label="disable subtitles"
                        onClick={() => handleSubtitleClick('none')}
                        className={currentSubtitle === 'none' ? 'ntx-subtitle-button-selected' : ''}
                      >
                        None
                      </button>
                      {
                        Object.keys(subtitles).map(
                          (lang) => (
                            <SubtitleButton
                              key={lang}
                              lang={lang}
                              defaultPlaceholder={subtitles[lang]}
                              onClick={handleSubtitleClick}
                              selected={lang === currentSubtitle}
                            />
                          ),
                        )
                      }
                    </div>
                  </div>
                )}
              </span>
            )}
          <button
            type="button"
            aria-label="Fullscreen"
            onClick={setFullscreen}
            className="ntx-video-btn ntx-video-btn-fullscreen"
          />
        </div>
      </div>
    </div>
  );
};

ToolbarControl.propTypes = {
  id: PropTypes.string.isRequired,
  duration: PropTypes.number,
  paused: PropTypes.bool,
  loaded: PropTypes.number,
  played: PropTypes.number,
  muted: PropTypes.bool,
  volume: PropTypes.number,
  play: PropTypes.func,
  pause: PropTypes.func,
  unmute: PropTypes.func,
  mute: PropTypes.func,
  setSeeking: PropTypes.func,
  seek: PropTypes.func,
  setVolume: PropTypes.func,
  setFullscreen: PropTypes.func,
  currentSubtitle: PropTypes.string,
  subtitles: PropTypes.objectOf(PropTypes.string),
  setSubtitle: PropTypes.func,
  timeEvents: PropTypes.arrayOf(PropTypes.number),
  fastFordward: PropTypes.bool,
  bookmarks: PropTypes.objectOf(PropTypes.string),
  heightVideo: PropTypes.number,
};

ToolbarControl.defaultProps = {
  duration: 0,
  paused: true,
  loaded: 0,
  played: 0,
  muted: false,
  volume: 1,
  play: () => null,
  pause: () => null,
  unmute: () => null,
  mute: () => null,
  setSeeking: () => null,
  seek: () => null,
  setVolume: () => null,
  setFullscreen: () => null,
  setSubtitle: () => null,
  currentSubtitle: 'none',
  subtitles: {},
  timeEvents: [],
  fastFordward: true,
  bookmarks: {},
  heightVideo: 0,
};

export default ToolbarControl;
