import { ANSWERED } from '../tincanVerbs';
import { xApiInteractionType } from '../xapi/conts';

function textInput(data, activityId, context) {
  const {
    id,
    response,
    courseId,
    correctResponses,
    wording,
    success,
    scoreMin,
    scoreMax,
    scoreRaw,
  } = data;
  const ret = {
    verb: ANSWERED,
    result: {
      response: response.split(',').join('[,]'),
      score: {
        min: scoreMin,
        max: scoreMax,
        raw: scoreRaw,
      },
    },
    context,
    object: {
      id: `${activityId || courseId}/interaction/${id}`,
      definition: {
        description: wording,
        type: xApiInteractionType,
        interactionType: correctResponses ? 'fill-in' : 'long-fill-in',
      },
      objectType: 'Activity',
    },
  };
  if (correctResponses) {
    ret.object.definition.correctResponsesPattern = correctResponses.split('[,]');
    ret.result.success = success;
  }
  return ret;
}

export default textInput;
