let inDebugMode;

const debug = (...params) => {
  if (inDebugMode) {
    // eslint-disable-next-line no-console
    console.info(...params);
  }
};

const init = (config) => {
  const { debugMode } = config;
  inDebugMode = debugMode;
  debug('init', config, []);
  return [true];
};

let suspend;
const setSuspend = (newSuspend) => {
  debug('setSuspend', newSuspend);
  suspend = newSuspend;
};

const getSuspend = () => {
  debug('getSuspend', suspend);
  return suspend;
};

const setLocation = (location) => {
  debug('setLocation', location);
};

const getLocation = () => {
  debug('getLocation', '');
  return '';
};

const exit = (data) => {
  debug('exit', data);
};

const sendInteraction = (data) => {
  debug('sendInteraction', data);
};

const sendMultipleInteractions = (data) => {
  debug('sendMultipleInteractions', data);
};

const setScore = (score) => {
  debug('setScore:', score);
};

const setSuccessStatus = (success) => {
  debug('successStatus: ', success);
};

const setCompletion = (completion) => {
  debug('setCompletion', completion);
};

const getName = () => {
  if (process.env.REACT_APP_BUILD_MODE === 'review') {
    return 'John Smith';
  }
  return '';
};

const getId = () => {
  if (process.env.REACT_APP_BUILD_MODE === 'review') {
    return 'dummy-id-1234';
  }
  return '';
};

const none = {
  init,
  setSuspend,
  getSuspend,
  setLocation,
  getLocation,
  exit,
  sendInteraction,
  sendMultipleInteractions,
  setScore,
  setCompletion,
  setSuccessStatus,
  getName,
  getId,
};

export default none;
