import React, { useCallback } from 'react';
import PropTypes from 'prop-types';
import { useSwipeable } from 'react-swipeable';
import useInteractiveProps from '../useInteractiveProps';

function getChildren(props) {
  const {
    children,
    visibleChilds,
    showIntroductionScreen,
    showFeedbackScreen,
    navigation,
  } = props;
  const arrayChildren = React.Children.toArray(children.props.children);
  if (navigation === 'continuously') {
    const filtered = arrayChildren.filter(
      (childComponent) => {
        const { componentType } = childComponent.props;
        if (componentType === 'IntroductionScreen') return showIntroductionScreen === 'true';
        if (componentType === 'FeedbackScreen') return showFeedbackScreen === 'true';
        return true;
      },
    );
    return React.cloneElement(children, {
      children: filtered,
    });
  }
  const patchVisibleChilds = (() => {
    if (visibleChilds.length) return visibleChilds;
    const visible = arrayChildren.find(
      (childComponent) => {
        const { componentType } = childComponent.props;
        const isIntruduction = componentType === 'IntroductionScreen';
        const showIntroduction = showIntroductionScreen === 'true';
        if (isIntruduction && !showIntroduction) return false;
        return true;
      },
    );
    if (visible) return [visible.props.id];
    return [];
  })();
  const filterChildrens = arrayChildren.filter(
    (childComponent) => {
      const childId = childComponent.props.id;
      return patchVisibleChilds.includes(childId);
    },
  );
  return React.cloneElement(children, {
    children: filterChildrens,
  });
}

function Quiz(props) {
  const {
    className,
    goPrevSlide,
    goNextSlide,
  } = props;
  const handlers = useSwipeable({
    onSwipedRight: () => goPrevSlide(),
    onSwipedLeft: () => goNextSlide(),
    preventDefaultTouchmoveEvent: true,
  });
  const { ref, ...others } = { ...useInteractiveProps(props) };
  const setRefs = useCallback(
    (node) => {
      handlers.ref(node);
      if (typeof ref === 'function') {
        ref(node);
      }
    },
    [handlers, ref],
  );
  return (
    <div
      {...handlers}
      {...others}
      ref={setRefs}
      className={className}
    >
      {getChildren(props)}
    </div>
  );
}

Quiz.propTypes = {
  id: PropTypes.string.isRequired,
  className: PropTypes.string,
  visibleChilds: PropTypes.arrayOf(PropTypes.string),
  children: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]),
  showIntroductionScreen: PropTypes.string.isRequired,
  goNextSlide: PropTypes.func,
  goPrevSlide: PropTypes.func,
  navigation: PropTypes.oneOf(['slides', 'continuously']),
};

Quiz.defaultProps = {
  className: '',
  visibleChilds: [],
  children: null,
  goNextSlide: () => null,
  goPrevSlide: () => null,
  navigation: 'slides',
};

export default Quiz;
