import React from 'react';
import PropTypes from 'prop-types';
import { ClassNames } from '@auth/shared/src/utils/generateClassReact';
import useInteractiveProps from '../useInteractiveProps';
import pseudoSelectorProp from '../pseudoSelectorProp';
import nullFunc from '../nullFunc';

const cssDefault = {
  cursor: 'pointer',
};

function ArrowSource(props) {
  const {
    children = null,
    className,
    id,
    pseudoSelector,
  } = props;
  const interactiveProps = useInteractiveProps(props);
  return (
    <ClassNames>
      {({ css, cx }) => (
        <div
          id={id}
          role="button"
          tabIndex="0"
          className={cx(css(cssDefault), className)}
          {...interactiveProps}
          {...pseudoSelectorProp(pseudoSelector)}
        >
          {children}
        </div>
      )}
    </ClassNames>
  );
}

ArrowSource.propTypes = {
  id: PropTypes.string.isRequired,
  className: PropTypes.string,
  children: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]),
  onClick: PropTypes.func,
  pseudoSelector: PropTypes.string,
};

ArrowSource.defaultProps = {
  children: null,
  className: '',
  onClick: nullFunc,
  pseudoSelector: '',
};

export default ArrowSource;
