import { xApiSmartExtensionContext } from '../xapi/conts';

function patchContext(context, activityId, data) {
  const { courseId, attempt } = data;
  const id = activityId || courseId;
  return {
    ...context,
    contextActivities: {
      ...context.contextActivities,
      parent: [
        {
          id,
          objectType: 'Activity',
        },
      ],
    },
    extensions: {
      ...context.extensions,
      [xApiSmartExtensionContext]: {
        attemptNumber: attempt,
      },
    },
  };
}

export default patchContext;
