import { SET_PAGE } from './pageReducer';

export const SET_POPUP = 'SET_POPUP';

export const setPopupAction = (popup) => ({
  type: SET_POPUP,
  payload: {
    popup,
  },
});

const popupReducer = (state = '', action = {}) => {
  const { type, payload } = action;
  if (type === SET_POPUP) {
    const { popup } = payload;
    return popup;
  }
  if (type === SET_PAGE) {
    return '';
  }
  return state;
};

export default popupReducer;
