import { VIDEO_INDICATED_TIME, AUDIO_INDICATED_TIME } from '@auth/lib/src/events/triggers';
import selectorStateEvents from '@auth/lib/src/selectors/selectorStateEvents';

const interactiveRenderMultimediaSelector = (state, id) => {
  const events = selectorStateEvents(state);
  const filteredEvents = Object.values(events).filter(
    ({ triggerType, triggerCompId }) => triggerCompId === id
      && (triggerType === VIDEO_INDICATED_TIME || triggerType === AUDIO_INDICATED_TIME),
  );
  return filteredEvents.map(
    (event) => +(event?.data?.time || 0),
  );
};

export default interactiveRenderMultimediaSelector;
