export function filterProps(prefixPropName, props) {
  return Object.entries(props).reduce(
    (memo, [k, v]) => {
      if (k.startsWith(`${prefixPropName}_`)) {
        return { ...memo, [k]: JSON.parse(v) };
      }
      return memo;
    },
    {},
  );
}

export const getStatesByType = (type) => (data[type] || {});

export const getStateData = (type, state) => getStatesByType(type)[state] || {};

export const stateCompTypes = Object.keys(data);
