import { useContext } from 'react';
import QuizContext from '../QuizContext';
import useFinaliseQuizzes from '../../../hooks/useFinaliseQuizzes';

function useClickFinaliseQuiz() {
  const quizId = useContext(QuizContext);
  const finaliseQuizzes = useFinaliseQuizzes();
  return () => {
    if (quizId) finaliseQuizzes([quizId]);
  };
}

export default useClickFinaliseQuiz;
