import React, { useContext, useEffect, useState } from 'react';
import TextInputOption from '@auth/components/src/TextInputOption/TextInputOption';
import useGetSuspendData from '../../hooks/useGetSuspendData';
import useSuspendOperations, {
  setOperation, setTempOperation, unsetOperation, unsetTempOperation,
} from '../../hooks/useSuspendOperations';
import ActivityContext from './ActivityContext';

function InteractiveRenderTextInputOption(props) {
  const activityId = useContext(ActivityContext);
  const value = useGetSuspendData(activityId, 'response');
  const suspendOperations = useSuspendOperations();
  const activityCorrect = useGetSuspendData(activityId, 'correct');
  const success = (() => {
    if (activityCorrect === 0) return false;
    if (activityCorrect === 1) return true;
    return undefined;
  })();
  const isChecked = [0, 1].includes(activityCorrect);
  const handleOnBlur = (e) => {
    if (!isChecked) {
      suspendOperations(
        [
          unsetTempOperation(activityId, 'response'),
          unsetOperation(activityId, 'correct'),
          setOperation(activityId, 'response', e.currentTarget.value),
        ],
      );
    }
  };
  const [currentVal, setCurrentVal] = useState(value);
  const handleOnChange = (e) => {
    if (!isChecked) {
      const newVal = e.currentTarget.value;
      setCurrentVal(newVal);
      const operations = [
        setTempOperation(activityId, 'response', newVal),
      ];
      if (!newVal) operations.push(setOperation(activityId, 'response', newVal));
      suspendOperations(operations);
    }
  };
  const correct = activityCorrect;
  const pseudoSelector = (() => {
    if (success !== undefined) {
      if (isChecked) {
        if (correct === 1) return 'answeredright';
        return 'answeredwrong';
      }
    }
    if (isChecked) return 'askingchosen';
    return '';
  })();
  useEffect(() => {
    setCurrentVal(value);
  }, [value]);
  return (
    <TextInputOption
      {...props}
      onBlur={handleOnBlur}
      onChange={handleOnChange}
      value={currentVal}
      pseudoSelector={pseudoSelector}
      disabled={isChecked}
    />
  );
}

export default InteractiveRenderTextInputOption;
