function sequence(data) {
  const {
    id,
    response,
    wording,
    correctResponses,
    success,
    scoreMax,
  } = data;
  return [
    ['id', id],
    ['type', 'sequence'],
    ['correct_responses.0.pattern', correctResponses],
    ['learner_response', response],
    ['weighting', scoreMax],
    ['result', success ? 'correct' : 'incorrect'],
    ['description', wording ? Object.values(wording)[0] : ''],
  ];
}

export default sequence;
