import selectorRealType from '@auth/lib/src/selectors/selectorRealType';
import { useSelector } from 'react-redux';

const selectorFirstCompositeItem = (state, childs) => childs.find(
  (child) => selectorRealType(state, child) === 'CompositeItem',
);

const useGetAccordionFirstCompositeItem = (childs) => useSelector(
  (state) => selectorFirstCompositeItem(state, childs),
);

export default useGetAccordionFirstCompositeItem;
