import { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import {
  setResolutionWidthDesktopExtraAction,
  setResolutionWidthDesktopAction,
  setResolutionWidthTabletAction,
  setResolutionWidthMobileAction,
} from '@auth/lib/src/resolutionWidthReducer';
import resolutionStoreUpdaterThunk from './useResolutionStoreUpdater/resolutionStoreUpdaterThunk';

function useResolutionStoreUpdater() {
  const dispatch = useDispatch();
  useEffect(() => {
    const {
      configDesktop, configTablet, configMobile,
    } = dispatch(resolutionStoreUpdaterThunk);
    const check = (e, action) => {
      if (e.matches) dispatch(action);
    };
    [
      [`(min-width: ${configDesktop})`, setResolutionWidthDesktopExtraAction],
      [`(max-width: ${configDesktop}) and (min-width:${configTablet})`, setResolutionWidthDesktopAction],
      [`(max-width: ${configTablet}) and (min-width:${configMobile})`, setResolutionWidthTabletAction],
      [`(max-width: ${configMobile})`, setResolutionWidthMobileAction],
    ].forEach(
      ([query, action]) => {
        const matchQuery = window.matchMedia(query);
        check(matchQuery, action());
        if (matchQuery.addEventListener) {
          matchQuery.addEventListener(
            'change',
            (e) => check(e, action()),
          );
        }
        if (!matchQuery.addEventListener && matchQuery.addListener) {
          matchQuery.addListener(
            (e) => check(e, action()),
          );
        }
      },
    );
  }, [dispatch]);
}

export default useResolutionStoreUpdater;
