import langValidKeys from '../../data/langValidKeys';
import { LANGS } from '../../editor/stateKeys';
import { remove } from '../../stateChanger/stateChanger';

export const LANGS_REMOVE = 'LANGS_REMOVE';

export const langsRemoveSchema = {
  type: {
    type: 'string',
  },
  payload: {
    type: 'object',
    shape: {
      key: {
        type: 'string',
      },
    },
  },
};

export const langsRemoveAction = (key) => ({
  type: LANGS_REMOVE,
  payload: {
    key,
  },
});

function langsReducerRemove(state, action, allState) {
  const mutable = action?.mutable;
  const key = action?.payload?.key;
  if (!langValidKeys.includes(key) || !state[key]) return state;
  return remove(
    state,
    key,
    mutable,
    allState,
    LANGS,
  );
}

export default langsReducerRemove;
