import selectorChilds from '@auth/lib/src/selectors/selectorChilds';
import selectorFindDescendantByTypes from '@auth/lib/src/selectors/selectorFindDescendantByTypes';
import selectorProp from '@auth/lib/src/selectors/selectorProp';
import activityPointsSelector from './activityPointsSelector';
import activitySuccessRatePercentSelector from './activitySuccessRatePercentSelector';
import suspendDataSelector from './suspendDataSelector';

export function getInitialResponse(state, activityId) {
  const groupId = selectorFindDescendantByTypes(state, activityId, ['SequenceGroupOption']);
  const orderChilds = suspendDataSelector(groupId, 'orderChilds', state);
  if (orderChilds) return orderChilds;
  return selectorChilds(state, groupId);
}

export function getCurrentResponse(state, activityId) {
  const responseInSuspend = suspendDataSelector(activityId, 'response', state);
  if (responseInSuspend) return responseInSuspend.split('[,]');
  return getInitialResponse(state, activityId);
}

export function getCorrectResponse(state, activityId) {
  const groupId = selectorFindDescendantByTypes(state, activityId, ['SequenceGroupOption']);
  const correctResponse = selectorProp(state, groupId, 'correct');
  if (!correctResponse) return [];
  return correctResponse.split('[,]');
}

export function itemsCorrect(responseInSuspend, groupId, state, correctResponse) {
  const responseRel = (() => {
    if (responseInSuspend) return responseInSuspend.split('[,]');
    const orderChilds = suspendDataSelector(groupId, 'orderChilds', state);
    if (orderChilds) return orderChilds;
    return selectorChilds(state, groupId);
  })();
  return responseRel.filter(
    (option, index) => option === correctResponse[index],
  ).length;
}

function percentCorrect(state, activityId, responseInSuspend) {
  const groupId = selectorFindDescendantByTypes(state, activityId, ['SequenceGroupOption']);
  if (!groupId) return 0;
  const correctResponse = selectorProp(state, groupId, 'correct');
  const correctResponseRel = correctResponse ? correctResponse.split('[,]') : [];
  const totCorrect = itemsCorrect(responseInSuspend, groupId, state, correctResponseRel);
  return (totCorrect / correctResponseRel.length) * 100;
}

export function calculateSequenceTotPoints(state, activityId, response) {
  const activityPoints = activityPointsSelector(state, activityId);
  const percent = percentCorrect(state, activityId, response);
  const points = (percent / 100) * activityPoints;
  return points > 1 ? Math.round(points) : points;
}

function checkActivitySequenceSelector(state, activityId) {
  const response = suspendDataSelector(
    activityId,
    'response',
    state,
  );

  const currentPercent = percentCorrect(state, activityId, response);
  const successRatePercent = activitySuccessRatePercentSelector(state, activityId);
  return currentPercent >= successRatePercent;
}

export default checkActivitySequenceSelector;
