import strFirstCharToUpperCase from '@auth/lib/src/utils/strFirstCharToUpperCase';
import {
  cssEnabledPropsKeys, cssEnabledPropValues, getAffectedProps,
} from './cssEnabledProps';
import getMq from './getMq';
import getPseudo from './getPseudo';
import getRelatedProp from './getRelatedProp';
import propValue from './propValue';

function isValidProp(searchProp, data) {
  const prop = getRelatedProp(searchProp);
  const pseudo = getPseudo(searchProp);
  const mq = getMq(searchProp);
  const search = cssEnabledPropsKeys.filter(
    (k) => getAffectedProps(k).includes(prop),
  ).map(
    (k) => {
      if (!pseudo) return `${k}${mq}`;
      return `${pseudo}${strFirstCharToUpperCase(k)}${mq}`;
    },
  );
  const values = search.map(
    (s) => propValue(s, data),
  );
  const disabled = values.find(
    (v) => v === 'false',
  );
  if (disabled) return false;
  if (cssEnabledPropValues.includes(prop)) {
    const enabled = values.find(
      (v) => v === 'true',
    );
    return !!enabled;
  }
  return true;
}

export default isValidProp;
