import { useDispatch } from 'react-redux';
import {
  PAUSE_MEDIA_AND_SHOW_NOTIFICATION, PAUSE_MEDIA_AND_SHOW_POPUP, SHOW_NOTIFICATION, SHOW_POPUP,
} from '@auth/lib/src/events/actions';
import { isValidTrigger, VIDEO_INDICATED_TIME, AUDIO_INDICATED_TIME } from '@auth/lib/src/events/triggers';
import selectorStateEvents from '@auth/lib/src/selectors/selectorStateEvents';
import { useCallback } from 'react';
import { subscribeSetPopupAction } from '../middlewares/setPopupMiddleware';
import useSetCurrentPopup from './useSetCurrentPopup';
import useShowNotification from './useShowNotification';
import { subscribeHideNotificationAction } from '../middlewares/hideNotificationMiddleware';

function filterEvents(events, eventData) {
  return events.filter(
    ({ triggerType, data }) => {
      if (triggerType === VIDEO_INDICATED_TIME || triggerType === AUDIO_INDICATED_TIME) {
        return data ? +(data.time) === eventData.time : eventData.time === 0;
      }
      return true;
    },
  );
}

function thunkGetEvents(type, compId) {
  return (dispatch, getState) => {
    const state = getState();
    const events = selectorStateEvents(state);
    return Object.values(events).reduce(
      (m, event) => {
        const {
          triggerType, triggerCompId,
        } = event;
        if (triggerType === type && triggerCompId === compId) {
          return [...m, event];
        }
        return m;
      },
      [],
    );
  };
}

function useDispatchEvent() {
  const dispatch = useDispatch();
  const setPopup = useSetCurrentPopup();
  const showNotification = useShowNotification();

  return useCallback(
    (triggerType, triggerCompId, data) => {
      if (isValidTrigger(triggerType)) {
        const events = dispatch(thunkGetEvents(triggerType, triggerCompId));
        const filteredEvents = filterEvents(events, data);
        filteredEvents.forEach(
          (event) => {
            const { actionType } = event;
            if (actionType === SHOW_POPUP) {
              setPopup(event.actionCompId);
              if (data?.player?.exitFullscreen) {
                data.player.exitFullscreen();
              }
            }
            if (actionType === PAUSE_MEDIA_AND_SHOW_POPUP) {
              data.player.pause();
              setPopup(event.actionCompId);
              if (data?.player?.exitFullscreen) {
                data.player.exitFullscreen();
              }
              subscribeSetPopupAction(
                (action, unsubscribe) => {
                  if (!action.payload.popup) {
                    data.player.play();
                    unsubscribe();
                  }
                },
              );
            }
            if (actionType === SHOW_NOTIFICATION) {
              showNotification(event.actionCompId);
              if (data?.player?.exitFullscreen) {
                data.player.exitFullscreen();
              }
            }
            if (actionType === PAUSE_MEDIA_AND_SHOW_NOTIFICATION) {
              data.player.pause();
              const action = showNotification(event.actionCompId);
              const { notification } = action.payload;
              if (data?.player?.exitFullscreen) {
                data.player.exitFullscreen();
              }
              subscribeHideNotificationAction(
                (act, unsubscribe) => {
                  if (act.payload.notification === notification) {
                    data.player.play();
                    unsubscribe();
                  }
                },
              );
            }
          },
        );
      }
    },
    [dispatch, setPopup, showNotification],
  );
}

export default useDispatchEvent;
