import React, { useState } from 'react';
import Button from '@auth/components/src/Button/Button';
import { useSelector } from 'react-redux';
import getValue from '@auth/components/src/RenderContent/selectorGetCss/getValue';
import selectorStateEditorResolutionWidth from '@auth/lib/src/selectors/selectorStateEditorResolutionWidth';
import InteractiveClick from './interactiveClick/InteractiveClick';
import getCssPropByButtonState from './interactiveRenderButton/getCssPropByButtonState';

function InteractiveRenderButton(props) {
  const [buttonState, setButtonState] = useState('');
  const handleOnStateChange = (newButtonState) => {
    setButtonState(newButtonState);
  };
  const iconSrc = useSelector(
    (state) => getValue(
      'iconSrc',
      buttonState,
      selectorStateEditorResolutionWidth(state),
      props,
    ),
  );

  const buttonTextDisplay = getCssPropByButtonState(buttonState, 'buttonTextDisplay', props);
  const buttonIconDisplay = getCssPropByButtonState(buttonState, 'buttonIconDisplay', props);

  return (
    <InteractiveClick
      {...props}
      onStateChange={handleOnStateChange}
      iconSrc={iconSrc}
      Comp={Button}
      buttonTextDisplay={buttonTextDisplay}
      buttonIconDisplay={buttonIconDisplay}
    />
  );
}

InteractiveRenderButton.propTypes = {
};

InteractiveRenderButton.defaultProps = {
};

export default InteractiveRenderButton;
