import selectorChildsByType from '@auth/lib/src/selectors/selectorChildsByType';
import selectorClosestType from '@auth/lib/src/selectors/selectorClosestType';
import getCompositeVisibleChildsSelector from './getCompositeVisibleChildsSelector';

function compositeIsInLastChild(state, descendantId) {
  const composite = selectorClosestType(state, descendantId, 'Composite');
  if (!composite) return false;
  const [currentVisible] = getCompositeVisibleChildsSelector(state, composite);
  const compositeItems = selectorChildsByType(state, composite, 'CompositeItem');
  return currentVisible === compositeItems[compositeItems.length - 1];
}

export default compositeIsInLastChild;
