import generateClass from '@auth/shared/src/utils/generateClass';

const enter = {
  none: () => ({}),
  fade: ({ transitionInDuration, transitionInTimingFunction }) => ({
    enter: generateClass({
      opacity: 0,
    }),
    enterActive: generateClass({
      opacity: 1,
      transition: `opacity ${transitionInDuration} ${transitionInTimingFunction}`,
    }),
  }),
  slideAndFade: ({ transitionInDuration, transitionInTimingFunction }, y) => ({
    enter: generateClass({
      opacity: 0,
      transform: `translate3d(0,${y === 'top' ? '-' : ''}20px ,0)`,
    }),
    enterActive: generateClass({
      opacity: 1,
      transform: 'translate3d(0,0,0)',
      transition: `all ${transitionInDuration}  ${transitionInTimingFunction}`,
    }),
  }),
};

const exit = {
  none: () => ({}),
  fade: ({ transitionOutDuration, transitionOutTimingFunction }) => ({
    exit: generateClass({
      opacity: 1,
    }),
    exitActive: generateClass({
      opacity: 0,
      transition: `opacity ${transitionOutDuration} ${transitionOutTimingFunction}`,
    }),
  }),
  slideAndFade: ({ transitionOutDuration, transitionOutTimingFunction }, y) => ({
    exit: generateClass({
      opacity: 1,
      transform: 'translate3d(0,0,0)',
    }),
    exitActive: generateClass({
      opacity: 0,
      transform: `translate3d(0,${y === 'top' ? '-' : ''}20px,0)`,
      transition: `all ${transitionOutDuration} ${transitionOutTimingFunction}`,
    }),
  }),
};

function getTransitionClassNamesByProps(props, y) {
  const {
    transitionTypeIn,
    transitionTypeOut,
  } = props;
  return {
    ...enter[transitionTypeIn](props, y),
    ...exit[transitionTypeOut](props, y),
  };
}

export default getTransitionClassNamesByProps;
