import selectorClosestType from '@auth/lib/src/selectors/selectorClosestType';
import selectorProp from '@auth/lib/src/selectors/selectorProp';

function quizMaxAttemptSelector(state, descendantId) {
  const quiz = selectorClosestType(state, descendantId, 'Quiz');
  if (!quiz) return 0;
  const unlimitedAttempts = selectorProp(state, quiz, 'unlimitedAttempts');
  if (unlimitedAttempts === true) return Infinity;
  return +(selectorProp(state, quiz, 'numberAttempts') || '3');
}

export default quizMaxAttemptSelector;
