import selectorChilds from './selectorChilds';
import selectorType from './selectorType';

function selectorAllDescendantIds(id, state, invalidTypes = []) {
  const childs = selectorChilds(state, id);
  return childs.reduce(
    (acc, childId) => {
      if (!childId) return acc;
      const type = selectorType(state, childId);
      if (invalidTypes.includes(type)) return acc;
      return [...acc, childId, ...selectorAllDescendantIds(childId, state, invalidTypes)];
    },
    [],
  );
}

export default selectorAllDescendantIds;
