import { useDispatch } from 'react-redux';
import selectorCompIdsByType from '@auth/lib/src/selectors/selectorCompIdsByType';
import useCheckQuizzes from '../../../hooks/useCheckQuizzes';

function useClickCheckAllQuizzes() {
  const dispatchThunk = useDispatch();
  const checkQuizzes = useCheckQuizzes();
  return () => {
    dispatchThunk(
      (dispatch, getState) => {
        const state = getState();
        const quizzes = selectorCompIdsByType(state, 'Quiz', true);
        checkQuizzes(quizzes);
      },
    );
  };
}

export default useClickCheckAllQuizzes;
