import { FLIPCARD_CHANGE_TO_FRONT, FLIPCARD_CHANGE_TO_REVERSE } from '@auth/lib/src/events/triggers';
import selectorExistInstance from '@auth/lib/src/selectors/selectorExistInstance';
import { useDispatch } from 'react-redux';
import suspendDataSelector from '../selectors/suspendDataSelector';
import useDispatchEvent from './useDispatchEvent';
import useSetSuspendData from './useSetSuspendData';

function useFlipCard() {
  const setSuspend = useSetSuspendData();
  const dispatchThunk = useDispatch();
  const dispatchEvent = useDispatchEvent();
  return (idFlipCard) => {
    dispatchThunk(
      (dispatch, getState) => {
        const state = getState();
        if (selectorExistInstance(state, idFlipCard)) {
          const suspendShowBack = suspendDataSelector(idFlipCard, 'showBack', state);
          setSuspend(idFlipCard, 'showBack', suspendShowBack ? 0 : 1);
          if (suspendShowBack) {
            dispatchEvent(
              FLIPCARD_CHANGE_TO_FRONT,
              idFlipCard,
            );
          } else {
            dispatchEvent(
              FLIPCARD_CHANGE_TO_REVERSE,
              idFlipCard,
            );
          }
        }
      },
    );
  };
}

export default useFlipCard;
