import React, { useReducer } from 'react';
import PropTypes from 'prop-types';
import Button from '@auth/components/src/Button/Button';
import ntxButtonActions from '@auth/components/src/Button/ntxButtonActions';
import useEnabled from '../../../hooks/useEnabled';
import useSelected from '../../../hooks/useSelected';
import interactiveClickReducer, { initialState } from './interactiveClickReducer';
import useInteractiveClick from './useInteractiveClick';
import usePatchOver from './usePatchOver';
import useUpdateActive from './useUpdateActive';
import useCallOnStateChange from './useCallOnStateChange';

const InteractiveClick = React.memo((props) => {
  const {
    Comp, onStateChange, id,
  } = props;
  const [state, dispatch] = useReducer(interactiveClickReducer, initialState);
  const { over, active } = state;
  const selected = useSelected(props);
  const enabled = useEnabled(props);
  usePatchOver(id, over, dispatch);
  useUpdateActive(active, dispatch);
  useCallOnStateChange(enabled, over, selected, active, onStateChange);
  const handleClick = useInteractiveClick(props);
  return (
    <Comp
      onMouseEnter={() => dispatch({ type: 'over', value: true, timestamp: Date.now() })}
      onMouseLeave={() => dispatch({ type: 'over', value: false, timestamp: Date.now() })}
      onMouseDown={() => { dispatch({ type: 'active', value: true }); }}
      {...props}
      selected={selected}
      disabled={!enabled}
      over={over}
      onClick={handleClick}
      onKeyPress={handleClick}
    />
  );
});

InteractiveClick.propTypes = {
  action: PropTypes.oneOf(Object.keys(ntxButtonActions)),
  Comp: PropTypes.elementType,
  onStateChange: PropTypes.func,
  id: PropTypes.string.isRequired,
};

InteractiveClick.defaultProps = {
  action: null,
  Comp: Button,
  onStateChange: () => null,
};

export default InteractiveClick;
