import { createSelector } from 'reselect';
import { useSelector } from 'react-redux';
import { useMemo } from 'react';
import selectorInstanceData from '@auth/lib/src/selectors/selectorInstanceData';
import selectorStateLangs from '@auth/lib/src/selectors/selectorStateLangs';
import selectorStateCurrentLang from '@auth/lib/src/selectors/selectorStateCurrentLang';
import selectorStateStructure from '@auth/lib/src/selectors/selectorStateStructure';
import selectorStateInstances from '@auth/lib/src/selectors/selectorStateInstances';
import areArraysEqual from '@auth/shared/src/utils/areArraysEqual';

const arrayProps = ['ascendants', 'childs', 'auxComps'];

function isEqualInstanceData(a, b) {
  const objKeysA = Object.keys(a);
  const objKeysB = Object.keys(b);
  const diferent = objKeysA.length !== objKeysB.length || Object.keys(a).find(
    (k) => {
      if (arrayProps.includes[k]) return !areArraysEqual(a[k], b[k]);
      return a[k] !== b[k];
    },
  );
  return !diferent;
}

const instanceSelector = (id) => createSelector(
  [
    selectorStateInstances,
  ],
  (instances) => instances[id],
);

const structureSelector = (id) => createSelector(
  [
    instanceSelector(id),
    selectorStateStructure,
  ],
  (instance, structure) => {
    if (instance?.type !== 'MenuList') {
      return null;
    }
    return structure;
  },
);

function getMySelector(id) {
  return createSelector(
    [
      instanceSelector(id),
      selectorStateLangs,
      selectorStateCurrentLang,
      structureSelector(id),
    ],
    (instance, langs, currentLang, structure) => selectorInstanceData(
      instance,
      langs,
      currentLang,
      structure,
      id,
    ),
  );
}

function useInstanceData(id) {
  const mySelector = useMemo(
    () => getMySelector(id),
    [id],
  );
  return useSelector(
    mySelector,
    isEqualInstanceData,
  );
}

export default useInstanceData;
