import selectorChilds from '@auth/lib/src/selectors/selectorChilds';
import { hasChildsContainer } from '../config';
import arrowsGroup from './getChildAreas/arrowsGroup';
import automaticOptions from './getChildAreas/automaticOptions';
import changeDisplay from './getChildAreas/changeDisplay';
import columnsByChilds from './getChildAreas/columnsByChilds';
import composite from './getChildAreas/composite';
import fixedColumns from './getChildAreas/fixedColumns';
import getAreasInRows from './getChildAreas/getAreasInRows';
import getFilteredChilds from './getChildAreas/getFilteredChilds';
import inColumns from './getChildAreas/inColumns';
import row from './getChildAreas/row';

const config = {
  Tabs: inColumns,
  Dots: inColumns,
  Row: row,
  SingleChoiceGroupOption: fixedColumns,
  LikertGroupOption: inColumns,
  MultipleChoiceGroupOption: fixedColumns,
  MenuList: fixedColumns,
  SequenceGroupOption: columnsByChilds,
  Composite: composite,
  ArrowsGroupOption: arrowsGroup,
  SingleChoiceOption: automaticOptions,
  MultipleChoiceOption: automaticOptions,
  SequenceOption: automaticOptions,
};

function getChildAreas(
  state,
  id,
  type,
  cssDisplay,
) {
  const childs = ['Tabs', 'Dots'].includes(type)
    ? selectorChilds(state, id)
    : getFilteredChilds(state, id);
  if (!childs.length) return {};
  if (config[type]) {
    return config[type]({
      state, id, type, cssDisplay, childs,
    });
  }
  if (hasChildsContainer(type)) {
    const ret = {
      display: changeDisplay(cssDisplay, type),
      flexDirection: 'column',
      displayChildWrapper: 'grid',
      widthChildWrapper: '100%',
      gridTemplateAreasWrapper: getAreasInRows(childs),
      heightChildWrapper: ['FlipCardFront', 'FlipCardBack'].includes(type) ? '100%' : 'auto',
      height: type === 'Column' ? '100%' : 'auto',
    };
    if (['FlipCardFront', 'FlipCardBack'].includes(type)) delete ret.height;
    return ret;
  }
  return {
    display: changeDisplay(cssDisplay, type),
    gridTemplateAreas: getAreasInRows(childs),
  };
}

export default getChildAreas;
