import activityTypes from '@auth/lib/src/data/activityTypes';
import selectorFirstAscendantByTypes from '@auth/lib/src/selectors/selectorFirstAscendantByTypes';
import selectorType from '@auth/lib/src/selectors/selectorType';
import rightAnswersCount from '../comps/rightAnswersCount';

function activityRightAnswersCount(state, descendantId) {
  const activityId = selectorFirstAscendantByTypes(state, descendantId, activityTypes);
  if (!activityId) return 0;
  const activityType = selectorType(state, activityId);
  return rightAnswersCount(state, activityId, activityType);
}

export default activityRightAnswersCount;
