import { hasChildsContainer } from '../../config';

const gridTypes = [
  'ArrowsGroupOption',
  'Dots',
  'LikertGroupOption',
  'MenuList',
  'MultipleChoiceGroupOption',
  'Row',
  'SequenceGroupOption',
  'SingleChoiceGroupOption',
  'Tabs',
];

const flexTypes = [
  'Composite',
];

const blockTypes = [
  'Iframe',
  'FlipCard',
];

function getDisplayByType(type) {
  if (gridTypes.includes(type)) return 'grid';
  if (flexTypes.includes(type)) return 'flex';
  if (blockTypes.includes(type)) return 'block';
  if (hasChildsContainer(type)) return 'flex';
  return 'grid';
}

export default getDisplayByType;
