import selectorClosestType from '@auth/lib/src/selectors/selectorClosestType';
import checkActivityTextInputWrittenCharsSelector from './checkActivityTextInputWrittenCharsSelector';
import activityTextInputMaxTextSizeSelector from './activityTextInputMaxTextSizeSelector';

function inputRemainingCharsSelector(state, descendantId) {
  const activityId = selectorClosestType(state, descendantId, 'ActivityTextInput');
  if (!activityId) return 0;
  const writtenChars = checkActivityTextInputWrittenCharsSelector(state, activityId);
  const maxChars = activityTextInputMaxTextSizeSelector(state, activityId);
  const remainig = maxChars - writtenChars;
  return Math.max(remainig, 0);
}

export default inputRemainingCharsSelector;
