import React from 'react';
import PropTypes from 'prop-types';
import Launcher from '@auth/components/src/Launcher/Launcher';
import InteractiveClick from './interactiveClick/InteractiveClick';
import useSetSuspendData from '../../hooks/useSetSuspendData';

function InteractiveRenderLauncher(props) {
  const { id } = props;
  const setSuspend = useSetSuspendData();

  const handleClick = () => {
    setSuspend(id, 'clicked', 1);
  };

  return (
    <InteractiveClick
      {...props}
      Comp={Launcher}
      onClick={handleClick}
    />
  );
}

InteractiveRenderLauncher.propTypes = {
  id: PropTypes.string.isRequired,
};

InteractiveRenderLauncher.defaultProps = {};

export default InteractiveRenderLauncher;
