export const SET_PAGE = 'SET_PAGE';

export function setPageAction(page, section) {
  return ({
    type: SET_PAGE,
    payload: {
      page,
      section,
    },
  });
}

const initialState = 'page1';

function pageReducer(state = initialState, action = {}) {
  const { type, payload } = action;
  if (type === SET_PAGE) {
    const { page } = payload;
    return page;
  }
  return state;
}

export default pageReducer;
