import selectorType from '@auth/lib/src/selectors/selectorType';
import retryQuizMantainCorrect from '../../comps/retryQuizMantainCorrect';
import quizActivitiesSelector from '../../selectors/quizActivitiesSelector';
import quizHasAttemptsSelector from '../../selectors/quizHasAttemptsSelector';
import { setOperation, unsetOperation } from '../useSuspendOperations';

function getResponseOperation(state, activityId) {
  const type = selectorType(state, activityId);
  return retryQuizMantainCorrect(state, activityId, type);
}

function getRetryQuizMantainCorrectOperations(quizId, state) {
  const hasAttempts = quizHasAttemptsSelector(state, quizId);
  if (!hasAttempts) return [];
  const activities = quizActivitiesSelector(state, quizId);
  if (!activities.length) return [];
  const gotoFirstActivityOperation = setOperation(quizId, 'visibleChilds', [activities[0]]);
  return activities.reduce(
    (memo, activityId) => {
      const operations = [unsetOperation(activityId, 'correct')];
      const responseOp = getResponseOperation(state, activityId);
      if (responseOp) operations.push(responseOp);
      return [...memo, ...operations];
    },
    [gotoFirstActivityOperation],
  );
}

export default getRetryQuizMantainCorrectOperations;
