import { useCallback } from 'react';
import { useDispatch } from 'react-redux';
import suspendDataSelector from '../selectors/suspendDataSelector';

function useGetSuspendDataThunk() {
  const dispatchHook = useDispatch();
  return useCallback(
    (id, propName) => dispatchHook(
      (dispatch, getState) => {
        const state = getState();
        return suspendDataSelector(id, propName, state);
      },
    ),
    [dispatchHook],
  );
}

export default useGetSuspendDataThunk;
