import React, { useEffect, useRef, useState } from 'react';
import PropTypes from 'prop-types';
import useComponentSize from '@auth/shared/src/hooks/useComponentSize';
import progressTransition from './progressTransition';

function LinearProgressBar(props) {
  const {
    value,
    initialValue,
    progressBarBaseStrokeWidth,
    progressBarBaseStroke,
    progressBarBaseStrokeLinecap,
    progressBarFillStrokeWidth,
    progressBarFillStroke,
    progressBarFillStrokeLinecap,
    transitionType,
    transitionDuration,
    transitionDelay,
  } = props;
  const myRef = useRef(null);
  const size = useComponentSize(myRef);
  const baseStrokeWidth = +progressBarBaseStrokeWidth;
  const fillStrokeWidth = +progressBarFillStrokeWidth;
  const realHeight = Math.max(baseStrokeWidth, fillStrokeWidth);
  const baseStrokeWidthDivide = baseStrokeWidth / 2;
  const realHeightDivide = realHeight / 2;
  const fillStrokeWidthDivide = fillStrokeWidth / 2;
  const realWidthFill = size.width - fillStrokeWidthDivide;
  const strokeDashoffset = realWidthFill - ((realWidthFill * value) / 100);
  const initialStrokeDashoffset = realWidthFill - ((realWidthFill * initialValue) / 100);

  const [showStrokeDashoffset, setShowStrokeDashoffset] = useState(false);
  useEffect(() => {
    /*
     * Este useEffect se mete para ver la animación la primera vez.
     * También es necesario para que la animación la primera vez no sea a la inversa.
    */
    if (size.width > 0 && strokeDashoffset >= 0) {
      setShowStrokeDashoffset(true);
    }
  }, [size.width, setShowStrokeDashoffset, strokeDashoffset, transitionType]);
  const patchLineCap = (type) => (type === 'butt' ? 'square' : type);
  const sdo = showStrokeDashoffset ? strokeDashoffset : initialStrokeDashoffset;
  return (
    <div ref={myRef} style={{ fontSize: 0 }}>
      {size.width > 0 && (
        <svg height={realHeight} style={{ width: '100%', height: `${realHeight}px` }}>
          <line
            x1={baseStrokeWidthDivide}
            y1={realHeightDivide}
            x2={size.width - baseStrokeWidthDivide}
            y2={realHeightDivide}
            stroke={progressBarBaseStroke}
            strokeWidth={baseStrokeWidth}
            strokeLinecap={patchLineCap(progressBarBaseStrokeLinecap)}
          />
          <line
            className={transitionType !== 'none' ? progressTransition(transitionDuration, transitionDelay, transitionType) : ''}
            x1={fillStrokeWidthDivide}
            y1={realHeightDivide}
            x2={realWidthFill}
            y2={realHeightDivide}
            stroke={progressBarFillStroke}
            strokeDasharray={realWidthFill}
            strokeDashoffset={sdo}
            strokeWidth={fillStrokeWidth}
            strokeLinecap={patchLineCap(progressBarFillStrokeLinecap)}
          />
        </svg>
      )}
    </div>
  );
}

LinearProgressBar.propTypes = {
  value: PropTypes.number,
  initialValue: PropTypes.number,
  progressBarBaseStrokeWidth: PropTypes.string,
  progressBarBaseStroke: PropTypes.string,
  progressBarBaseStrokeLinecap: PropTypes.string,
  progressBarFillStrokeWidth: PropTypes.string,
  progressBarFillStroke: PropTypes.string,
  progressBarFillStrokeLinecap: PropTypes.string,
  transitionType: PropTypes.string,
  transitionDuration: PropTypes.string,
  transitionDelay: PropTypes.string,
};

LinearProgressBar.defaultProps = {
  value: 50,
  initialValue: 0,
  progressBarBaseStrokeWidth: '10',
  progressBarBaseStroke: '#eee',
  progressBarBaseStrokeLinecap: 'round',
  progressBarFillStrokeWidth: '20',
  progressBarFillStroke: '#1AA9E2',
  progressBarFillStrokeLinecap: 'round',
  transitionType: 'linear',
  transitionDuration: '1000ms',
  transitionDelay: '0ms',
};

export default LinearProgressBar;
