import useGetPreserveAspectRatio from '@auth/shared/src/hooks/useGetPreserveAspectRatio';

function useMantainAspectRatioPatchVhVw(css) {
  const mantainAspectRatio = useGetPreserveAspectRatio();
  if (mantainAspectRatio === 'none') return css;
  const draftCss = { ...css };
  const [aspectRatioW, aspectRatioH] = mantainAspectRatio.split('/');
  Object.entries(css).forEach(
    ([key, val = '']) => {
      if (val && val.endsWith && val.endsWith('vh')) {
        const value = val.replace('vh', '');
        const numericValue = +value;
        if (!Number.isNaN(numericValue)) {
          draftCss[key] = `${(+aspectRatioH * value) / 100}px`;
        }
      }
      if (val && val.endsWith && val.endsWith('vw')) {
        const value = val.replace('vw', '');
        const numericValue = +value;
        if (!Number.isNaN(numericValue)) {
          draftCss[key] = `${(+aspectRatioW * value) / 100}px`;
        }
      }
    },
  );
  return draftCss;
}

export default useMantainAspectRatioPatchVhVw;
