import selectorStateInstances from './selectorStateInstances';

function selectorCompIdsByTypes(state, searchTypes = [], checkBranchType = true) {
  const instances = selectorStateInstances(state);
  return Object.keys(instances).filter(
    (id) => {
      const { type, branchType } = instances[id] || {};
      const validBranchType = checkBranchType ? branchType === 'content' : true;
      return searchTypes.includes(type) && validBranchType;
    },
  );
}

export default selectorCompIdsByTypes;
