import selectorProp from './selectorProp';
import selectorAllDescendantIds from './selectorAllDescendantIds';

function selectorFindDescendantByTypes(state, id, searchTypes = [], invalidTypes = []) {
  const allDescendants = selectorAllDescendantIds(id, state, invalidTypes);
  return allDescendants.find(
    (descendant) => searchTypes.includes(selectorProp(state, descendant, 'type')),
  );
}

export default selectorFindDescendantByTypes;
