import selectorClosestType from '@auth/lib/src/selectors/selectorClosestType';
import suspendDataSelector from './suspendDataSelector';

function singleChoiceOptionSelectedSelector(id, state) {
  const activity = selectorClosestType(state, id, 'ActivitySingleChoice');
  const response = suspendDataSelector(activity, 'response', state);
  return response === id;
}

export default singleChoiceOptionSelectedSelector;
