import selectorProp from '@auth/lib/src/selectors/selectorProp';
import selectorRealType from '@auth/lib/src/selectors/selectorRealType';

const feedbackScreenQuizSelector = (state, quiz) => {
  const show = selectorProp(state, quiz, 'showFeedbackScreen');
  if (show !== 'true') return null;
  const childs = selectorProp(state, quiz, 'childs') || [];
  return childs.find(
    (child) => selectorRealType(state, child) === 'FeedbackScreen',
  );
};

export default feedbackScreenQuizSelector;
