import { useEffect } from 'react';
import { CONTENT_INIT } from '@auth/lib/src/events/triggers';
import useDispatchEvent from '../hooks/useDispatchEvent';

function ContentInitDispatcher() {
  const dispatchEvent = useDispatchEvent();
  useEffect(() => {
    dispatchEvent(CONTENT_INIT);
  }, [dispatchEvent]);
  return null;
}

ContentInitDispatcher.propTypes = {};

ContentInitDispatcher.defaultProps = {};

export default ContentInitDispatcher;
