import { currentLangSetAction } from '@auth/lib/src/features/currentLang/currentLangReducerSet';
import { useCallback } from 'react';
import { useDispatch } from 'react-redux';

function useSetCurrentLang() {
  const dispatch = useDispatch();
  return useCallback(
    (newLang) => dispatch(currentLangSetAction(newLang)),
    [dispatch],
  );
}

export default useSetCurrentLang;
