import selectorClosestType from '@auth/lib/src/selectors/selectorClosestType';
import selectorAllDescendantIdsByType from '@auth/lib/src/selectors/selectorAllDescendantIdsByType';
import selectorProp from '@auth/lib/src/selectors/selectorProp';

function activityTextInputMaxTextSizeSelector(state, descendantId) {
  const activityId = selectorClosestType(state, descendantId, 'ActivityTextInput');
  if (!activityId) return 0;
  const option = selectorAllDescendantIdsByType(activityId, 'TextInputOption', state)[0];
  if (!option) return 0;
  return +(selectorProp(state, option, 'maxTextSize') || '0');
}

export default activityTextInputMaxTextSizeSelector;
