import React from 'react';
import { useSelector } from 'react-redux';
import { TransitionGroup } from 'react-transition-group';
import RenderContent from '@auth/components/src/RenderContent/RenderContent';
import useAppData from '@auth/shared/src/hooks/useAppData';
import RenderContentContext from '@auth/components/src/RenderContent/RenderContentContext';
import InteractiveRenderComp from './InteractiveRenderComp';
import renderPopupSelector from './renderPopupSelector';

function RenderPopup() {
  const { unitId } = useAppData();
  const popup = useSelector(renderPopupSelector);
  const style = {
    position: 'fixed',
    top: 0,
    left: 0,
    bottom: 0,
    right: 0,
    zIndex: 3,
    display: 'grid',
    alignContent: 'normal',
    gridTemplateAreas: '"popup"',
    pointerEvents: popup ? 'all' : 'none',
  };
  const styleOverlay = {
    position: 'fixed',
    top: 0,
    left: 0,
    bottom: 0,
    right: 0,
    zIndex: 2,
    pointerEvents: popup ? 'all' : 'none',
  };
  return (
    <>
      <div style={style} className="mantain-aspect-ratio">
        <RenderContentContext.Provider value={InteractiveRenderComp}>
          <TransitionGroup
            component={null}
          >
            {!!popup && (
              <RenderContent
                id={popup}
                unitId={unitId}
              />
            )}
          </TransitionGroup>
        </RenderContentContext.Provider>
      </div>
      <div id="popupOverlay" style={styleOverlay} />
    </>
  );
}

RenderPopup.propTypes = {};

export default RenderPopup;
