import selectorAllDescendantIdsByType from '@auth/lib/src/selectors/selectorAllDescendantIdsByType';
import selectorProp from '@auth/lib/src/selectors/selectorProp';
import suspendDataSelector from './suspendDataSelector';
import replaceAccents from '../utils/replaceAccents';

function checkActivityTextInputSelector(state, activityId) {
  const option = selectorAllDescendantIdsByType(activityId, 'TextInputOption', state)[0];
  const caseSensitive = selectorProp(state, option, 'caseSensitive');
  const accentSensitive = selectorProp(state, option, 'accentSensitive');
  const selectorRightAnswers = selectorProp(state, option, 'rightAnswers');
  const rightAnswers = selectorRightAnswers ? JSON.parse(selectorRightAnswers) : [];
  const response = suspendDataSelector(
    activityId,
    'response',
    state,
  ) || '';

  if (rightAnswers?.length) {
    let finalResponse = response;
    let finalSolution = rightAnswers;
    if (!caseSensitive) {
      finalResponse = response.toLowerCase();
      finalSolution = rightAnswers.map((item) => item.toLowerCase());
    }

    if (!accentSensitive) {
      finalResponse = replaceAccents(finalResponse);
      finalSolution = finalSolution.map((item) => replaceAccents(item));
    }
    const match = finalSolution.filter((item) => item === finalResponse);
    return match?.length ? 1 : 0;
  }
  return response?.length ? 1 : 0;
}

export default checkActivityTextInputSelector;
