import selectorAscendants from './selectorAscendants';
import selectorType from './selectorType';

function selectorClosestType(state, id, searchType) {
  const type = selectorType(state, id);
  if (type === searchType) return id;
  const ascendants = selectorAscendants(state, id);
  return ascendants.find((ascendantId) => selectorType(state, ascendantId) === searchType);
}

export default selectorClosestType;
