import isEnabledProp from './isEnabledProp';
import isValidProp from './isValidProp';

function deleteDisabledProps(mergeCss) {
  return Object.entries(mergeCss).reduce(
    (memo, [k, v]) => {
      const isCssProp = !isEnabledProp(k);
      if (!isCssProp) return memo;
      if (!isValidProp(k, mergeCss)) return memo;
      return {
        ...memo,
        [k]: v,
      };
    },
    {},
  );
}

export default deleteDisabledProps;
