import selectorChildsByType from '@auth/lib/src/selectors/selectorChildsByType';
import suspendDataSelector from '../suspendDataSelector';

function compositesCompleteSelector(state, id) {
  const items = selectorChildsByType(state, id, 'CompositeItem');
  return items.every(
    (item) => suspendDataSelector(item, 'visited', state) === 1,
  );
}

export default compositesCompleteSelector;
