import React from 'react';
import PropTypes from 'prop-types';
import useInteractiveProps from '../useInteractiveProps';

function ActivityArrows(props) {
  const {
    children = null,
    className,
  } = props;
  return (
    <div
      className={className}
      {...useInteractiveProps(props)}
    >
      {children}
    </div>
  );
}

ActivityArrows.propTypes = {
  className: PropTypes.string,
  children: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]),
  score: PropTypes.string,
};

ActivityArrows.defaultProps = {
  children: null,
  className: '',
  score: '100',
};

export default ActivityArrows;
