import React from 'react';
import PropTypes from 'prop-types';
import useInteractiveProps from '../useInteractiveProps';
import PopupOverlay from './PopupOverlay';

function Popup(props) {
  const {
    id,
    children = null,
    className,
    popupOverlayBackground,
  } = props;
  return (
    <>
      <div
        className={className}
        {...useInteractiveProps(props)}
        style={{ zIndex: 1 }}
      >
        {children}
      </div>
      <PopupOverlay popupOverlayBackground={popupOverlayBackground} id={id} />
    </>
  );
}

Popup.propTypes = {
  id: PropTypes.string,
  className: PropTypes.string,
  popupOverlayBackground: PropTypes.string,
  children: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]),
};

Popup.defaultProps = {
  id: '',
  children: null,
  className: '',
  popupOverlayBackground: null,
};

export default Popup;
