import React from 'react';
import PropTypes from 'prop-types';
import ActivityMultipleChoice from '@auth/components/src/ActivityMultipleChoice/ActivityMultipleChoice';
import ActivityContext from './ActivityContext';

function InteractiveRenderActivityMultipleChoice(props) {
  const { id } = props;
  return (
    <ActivityContext.Provider value={id}>
      <ActivityMultipleChoice {...props} />
    </ActivityContext.Provider>
  );
}

InteractiveRenderActivityMultipleChoice.propTypes = {
  id: PropTypes.string.isRequired,
};
InteractiveRenderActivityMultipleChoice.defaultProps = {
};

export default InteractiveRenderActivityMultipleChoice;
