export const SHOW_NOTIFICATION = 'SHOW_NOTIFICATION';

export const HIDE_NOTIFICATION = 'HIDE_NOTIFICATION';

export function showNotificationAction(id, notification) {
  return ({
    type: SHOW_NOTIFICATION,
    payload: {
      id,
      notification,
    },
  });
}

export function hideNotificationAction(notification) {
  return ({
    type: HIDE_NOTIFICATION,
    payload: {
      notification,
    },
  });
}

function notificationsReducer(state = [], action = {}) {
  const { type, payload = {} } = action;
  const { id, notification } = payload;
  if (type === SHOW_NOTIFICATION) {
    return [{ id, notification }, ...state];
  }
  if (type === HIDE_NOTIFICATION) {
    return state.filter(
      ({ notification: current }) => current !== notification,
    );
  }
  return state;
}

export default notificationsReducer;
