function getMantainAspectRatioClass(width, height, mantainAspectRatio, inRender = true) {
  if (mantainAspectRatio === 'none') {
    return {
      scale: 1,
      translateX: 0,
      translateY: 0,
    };
  }
  const [strCanvasW, strCanvasH] = mantainAspectRatio.split('/');
  const canvasW = +strCanvasW;
  const canvasH = +strCanvasH;
  const scaleX = width / canvasW;
  const scaleY = height / canvasH;
  const scale = Math.min(scaleX, scaleY);
  const realW = canvasW * scale;
  const realH = canvasH * scale;
  const translateX = `${(((width / realW) * 100) / 2) - 50}%`;
  const translateY = `${(((height / realH) * 100) / 2) - 50}%`;
  const transformOrigin = '0 0';
  const overflow = inRender ? 'auto' : 'visible';
  return {
    transform: `scale(${scale}) translateX(${translateX}) translateY(${translateY})`,
    scale,
    translateX,
    translateY,
    transformOrigin,
    width: `${canvasW}px`,
    height: `${canvasH}px`,
    overflowX: overflow,
    overflowY: overflow,
  };
}

export default getMantainAspectRatioClass;
