import Panel from './Panel/Panel';
import Popup from './Popup/Popup';
import ProgressBar from './ProgressBar/ProgressBar';
import Quiz from './Quiz/Quiz';
import Row from './Row/Row';
import Section from './Section/Section';
import SequenceGroupOption from './SequenceGroupOption/SequenceGroupOption';
import SequenceOption from './SequenceOption/SequenceOption';
import SingleChoiceGroupOption from './SingleChoiceGroupOption/SingleChoiceGroupOption';
import SingleChoiceOption from './SingleChoiceOption/SingleChoiceOption';
import Tabs from './Tabs/Tabs';
import Text from './Text/Text';
import TextInputOption from './TextInputOption/TextInputOption';
import Video from './Video/Video';

const config = {
  AccordionHeaders,
  ActionData: nullFunc,
  ActivityArrows,
  ActivityLikert,
  ActivityMultipleChoice,
  ActivitySequence,
  ActivitySingleChoice,
  ActivityTextInput,
  Area,
  ArrowsGroupOption,
  ArrowSource,
  ArrowTarget,
  Audio,
  Button,
  Column,
  Composite,
  CompositeItem,
  ContentBox,
  Dots,
  FeedbackScreen,
  FlipCard,
  FlipCardBack,
  FlipCardFront,
  Hotspot,
  Iframe,
  Image,
  IntroductionScreen,
  Launcher,
  LikertGroupOption,
  LikertOption,
  MasterContainer,
  MenuList,
  MenuListItem,
  MultipleChoiceGroupOption,
  MultipleChoiceOption,
  Notification,
  PageContent,
  Panel,
  Popup,
  ProgressBar,
  Quiz,
  Row,
  Section,
  SequenceGroupOption,
  SequenceOption,
  SingleChoiceGroupOption,
  SingleChoiceOption,
  Tabs,
  Text,
  TextInputOption,
  Video,
};

function getCompByType(type) {
  if (!config[type]) {
    // eslint-disable-next-line no-console
    console.error(`getCompByType: component ${type} not exist!`);
    return nullFunc;
  }
  return config[type];
}

export default getCompByType;
