import React from 'react';
import PropTypes from 'prop-types';
import { ClassNames } from '@auth/shared/src/utils/generateClassReact';
import useInteractiveProps from '../useInteractiveProps';

const cssDefault = {
  border: 'none',
  display: 'block',
  cursor: 'pointer',
  '&:disabled': {
    cursor: 'auto',
    pointerEvents: 'none',
  },
};

function MenuListItem(props) {
  const {
    className,
    click,
    isCurrent,
    title,
    disabled,
    visible,
  } = props;
  const interactiveProps = useInteractiveProps(props);
  const selectedAttr = isCurrent ? { 'data-selected': 'true' } : {};
  if (!visible) return null;
  return (
    <ClassNames>
      {({ css, cx }) => (
        <button
          type="button"
          onClick={click}
          disabled={disabled}
          className={cx(css(cssDefault), className)}
          {...selectedAttr}
          {...interactiveProps}
        >
          <span>{title}</span>
        </button>
      )}
    </ClassNames>
  );
}

MenuListItem.propTypes = {
  isCurrent: PropTypes.bool,
  click: PropTypes.func,
  title: PropTypes.string,
  className: PropTypes.string,
  disabled: PropTypes.bool,
  visible: PropTypes.bool,
};
MenuListItem.defaultProps = {
  isCurrent: false,
  click: () => null,
  title: '',
  className: '',
  disabled: false,
  visible: true,
};

export default MenuListItem;
