import { useCallback } from 'react';
import { useDispatch } from 'react-redux';
import { setSuspendDataAction } from '../reducers/suspendDataReducerSet';
import { setTempSuspendDataAction } from '../reducers/suspendDataTempReducerSet';
import suspendDataSelector from '../selectors/suspendDataSelector';

function useSetSuspendData() {
  const dispatchThunk = useDispatch();
  return useCallback((
    id,
    propName,
    value,
    temp,
  ) => {
    dispatchThunk((dispatch, getState) => {
      const state = getState();
      const currentValue = suspendDataSelector(id, propName, state);
      if (value !== currentValue) {
        const action = temp
          ? setTempSuspendDataAction
          : setSuspendDataAction;
        dispatch(action(id, propName, value));
      }
    });
  }, [dispatchThunk]);
}

export default useSetSuspendData;
