function equal(a, b) {
  if (a === Object(a)) return JSON.stringify(a) === JSON.stringify(b);
  return a === b;
}

export default function suspendComparator(a, b) {
  const aKeys = Object.keys(a);
  const bKeys = Object.keys(b);
  if (aKeys.length !== bKeys.length) return false;
  return aKeys.every(
    (id) => {
      if (!b[id]) return false;
      const aPropNames = Object.keys(a[id]);
      const bPropNames = Object.keys(b[id]);
      if (aPropNames.length !== bPropNames.length) return false;
      return aPropNames.every(
        (propName) => equal(a[id][propName], b[id][propName]),
      );
    },
  );
}
