import selectorInstance from './selectorInstance';

export const getTitle = (
  title, defaultTitle, type, unknowTitle = 'unknow',
) => title || defaultTitle || type || unknowTitle;

const selectorTitle = (state, id, unknowTitle = 'unknow') => {
  const { type, title, defaultTitle } = selectorInstance(state, id);
  return getTitle(title, defaultTitle, type, unknowTitle);
};

export default selectorTitle;
