import React from 'react';
import PropTypes from 'prop-types';
import { ClassNames } from '@auth/shared/src/utils/generateClassReact';
import useInteractiveProps from '../useInteractiveProps';
import nullFunc from '../nullFunc';

function FlipCard(props) {
  const {
    FlipCardFront,
    FlipCardBack,
    className,
    cssCustom,
    onClick,
  } = props;
  const interactiveProps = useInteractiveProps(props);
  return (
    <ClassNames>
      {({ css, cx }) => (
        <div
          className={cx(className, css(cssCustom))}
          {...interactiveProps}
          onClick={onClick}
          role="button"
          onFocus={nullFunc}
          onKeyDown={nullFunc}
          tabIndex="0"
        >
          <div>
            {FlipCardFront}
            {FlipCardBack}
          </div>
        </div>
      )}
    </ClassNames>
  );
}

FlipCard.propTypes = {
  className: PropTypes.string,
  FlipCardBack: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]),
  FlipCardFront: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]),
  cssCustom: PropTypes.objectOf(PropTypes.oneOfType([PropTypes.object, PropTypes.string])),
  onClick: PropTypes.func,
};

FlipCard.defaultProps = {
  FlipCardBack: null,
  FlipCardFront: null,
  className: '',
  cssCustom: {},
  onClick: nullFunc,
};

export default FlipCard;
