import React, { useContext } from 'react';
import PropTypes from 'prop-types';
import LikertOption from '@auth/components/src/LikertOption/LikertOption';
import ActivityContext from './ActivityContext';
import useGetSuspendData from '../../hooks/useGetSuspendData';
import useSuspendOperations, { setOperation, unsetOperation } from '../../hooks/useSuspendOperations';
import getChoiceOptionProps from './interactiveRenderChoiceOption/getChoiceOptionProps';
import useIsAsking from '../../hooks/useIsAsking';

function InteractiveRenderLikertOption(props) {
  const { id } = props;
  const activityId = useContext(ActivityContext);
  const response = useGetSuspendData(activityId, 'response');
  const checked = response === id;
  const asking = useIsAsking(activityId);
  const suspendOperations = useSuspendOperations();
  const handleOnChange = () => {
    if (asking && activityId) {
      suspendOperations([
        unsetOperation(activityId, 'correct'),
        setOperation(activityId, 'response', id),
      ]);
    }
  };
  const pseudoSelector = checked ? 'selected' : '';
  return (
    <LikertOption
      {...props}
      onChange={handleOnChange}
      checked={checked}
      pseudoSelector={pseudoSelector}
      {...getChoiceOptionProps(pseudoSelector, id, props)}
    />
  );
}

InteractiveRenderLikertOption.propTypes = {
  id: PropTypes.string.isRequired,
};
InteractiveRenderLikertOption.defaultProps = {
};

export default InteractiveRenderLikertOption;
