import { useContext } from 'react';
import { useSelector } from 'react-redux';
import QuizContext from '../QuizContext';
import getPseudoSelector from './getPseudoSelector';
import optionActivityIsFinished from './optionActivityIsFinished';

function useOptionPseudoSelector(asking, checked, correct) {
  const quizId = useContext(QuizContext);
  const activityFinished = useSelector(
    (state) => optionActivityIsFinished(state, quizId),
  );
  return getPseudoSelector(asking, activityFinished, checked, correct);
}

export default useOptionPseudoSelector;
