import selectorProp from '@auth/lib/src/selectors/selectorProp';
import quizActivitiesSelector from './quizActivitiesSelector';
import quizCorrectActivitiesPercentSelector from './quizCorrectActivitiesPercentSelector';
import quizScorePercentSelector from './quizScorePercentSelector';
import quizSuccessRatePercentSelector from './quizSuccessRatePercentSelector';
import suspendDataSelector from './suspendDataSelector';

function quizStatusSelector(state, id) {
  const activities = quizActivitiesSelector(state, id);
  const data = activities.reduce(
    (memo, activity) => ({
      ...memo,
      [activity]: suspendDataSelector(activity, 'correct', state),
    }),
    {},
  );
  const notAttempted = Object.values(data).every(
    (val) => val !== 0 && val !== 1,
  );
  if (notAttempted) return 'not attempted';
  const incomplete = Object.entries(data).find(
    ([, val]) => val === undefined,
  );
  if (incomplete) return 'incomplete';
  const criteria = selectorProp(state, id, 'successCriteria') || 'points';
  const scorePercent = criteria === 'points'
    ? quizScorePercentSelector(state, id)
    : quizCorrectActivitiesPercentSelector(state, id);
  const quizSuccessRatePercent = quizSuccessRatePercentSelector(state, id);
  if (scorePercent >= quizSuccessRatePercent) return 'passed';
  return 'failed';
}

export default quizStatusSelector;
