import selectorAllDescendantIdsByType from '@auth/lib/src/selectors/selectorAllDescendantIdsByType';
import selectorProp from '@auth/lib/src/selectors/selectorProp';
import selectorType from '@auth/lib/src/selectors/selectorType';
import { operationsSuspendDataAction } from '../../reducers/suspendDataReducerOperations';
import suspendDataSelector from '../../selectors/suspendDataSelector';
import getCurrent from './getCurrent';
import { setOperation, unsetTempOperation } from '../useSuspendOperations';

function setResponse(dispatch, activityId, id, state) {
  const current = getCurrent(activityId, state);
  const currentType = selectorType(state, current);
  const response = suspendDataSelector(activityId, 'response', state) || '';
  const relations = response ? response.split('[,]') : [];
  const currentIsSource = currentType === 'ArrowSource';
  const possibleRelation = currentIsSource ? `${current}[.]${id}` : `${id}[.]${current}`;
  const existRelation = relations.some(
    (relation) => relation === possibleRelation,
  );
  const groupId = selectorAllDescendantIdsByType(activityId, 'ArrowsGroupOption', state);
  const correct = selectorProp(state, groupId, 'correct');
  const sources = selectorAllDescendantIdsByType(activityId, 'ArrowSource', state);
  const getTargetsById = (textList, itemId) => textList.split('[,]')
    .filter((a) => a.includes(itemId))
    .map((b) => b.split('[.]')
      .filter((c) => c !== itemId)[0]) || [];
  const multipleTargets = sources.filter((item) => getTargetsById(correct, item).length > 1);
  const cleanRelations = multipleTargets.length === 0
    ? relations.filter((item) => item.split('[.]')[0] !== possibleRelation.split('[.]')[0])
    : relations;

  const newRelations = existRelation
    ? cleanRelations.filter((relation) => relation !== possibleRelation)
    : [...cleanRelations, possibleRelation];
  const newResponse = newRelations.join('[,]');
  return dispatch(operationsSuspendDataAction([
    unsetTempOperation(activityId, 'current'),
    setOperation(activityId, 'response', newResponse),
  ]));
}

export default setResponse;
