import ActivityArrows from './ActivityArrows/visibilityAndEnabledConditions';
import ActivityLikert from './ActivityLikert/visibilityAndEnabledConditions';
import ActivityMultipleChoice from './ActivityMultipleChoice/visibilityAndEnabledConditions';
import ActivitySequence from './ActivitySequence/visibilityAndEnabledConditions';
import ActivitySingleChoice from './ActivitySingleChoice/visibilityAndEnabledConditions';
import ActivityTextInput from './ActivityTextInput/visibilityAndEnabledConditions';
import ArrowSource from './ArrowSource/visibilityAndEnabledConditions';
import Audio from './Audio/visibilityAndEnabledConditions';
import Composite from './Composite/visibilityAndEnabledConditions';
import Image from './Image/visibilityAndEnabledConditions';
import LikertOption from './LikertOption/visibilityAndEnabledConditions';
import MultipleChoiceOption from './MultipleChoiceOption/visibilityAndEnabledConditions';
import PageContent from './PageContent/visibilityAndEnabledConditions';
import Quiz from './Quiz/visibilityAndEnabledConditions';
import SequenceOption from './SequenceOption/visibilityAndEnabledConditions';
import SingleChoiceOption from './SingleChoiceOption/visibilityAndEnabledConditions';
import Video from './Video/visibilityAndEnabledConditions';

const valueSelector = {
  ActivityArrows,
  ActivityLikert,
  ActivityMultipleChoice,
  ActivitySequence,
  ActivitySingleChoice,
  ActivityTextInput,
  ArrowSource,
  Audio,
  Composite,
  Image,
  LikertOption,
  MultipleChoiceOption,
  PageContent,
  Quiz,
  SequenceOption,
  SingleChoiceOption,
  Video,
};

function visibilityAndEnabledConditions(compId, type, field, state) {
  const selector = valueSelector[type]?.[field];
  if (!selector) return undefined;
  return selector(compId, state);
}

export default visibilityAndEnabledConditions;
