function patchAlignContentColumn(css, type) {
  const {
    alignContent, ...otherCss
  } = css;
  if (type === 'Column') {
    if (alignContent === 'end') {
      return {
        ...otherCss,
        marginChildWrapper: 'auto 0 0 0',
      };
    }
    if (alignContent === 'center') {
      return {
        ...otherCss,
        marginChildWrapper: 'auto 0 auto 0',
      };
    }
    return otherCss;
  }
  return css;
}

export default patchAlignContentColumn;
