import { ANSWERED } from '../tincanVerbs';
import { xApiInteractionType } from '../xapi/conts';

export function toInteractionArray(obj) {
  return Object.entries(obj).map(
    ([k, v]) => ({
      id: k,
      description: v.description,
    }),
  );
}

function choice(data, activityId, context) {
  const {
    id,
    response,
    wording,
    correctResponses,
    choices,
    success,
    courseId,
    scoreMin,
    scoreMax,
    scoreRaw,
  } = data;

  return {
    verb: ANSWERED,
    result: {
      success,
      response: response.split(',').join('[,]'),
      score: {
        min: scoreMin,
        max: scoreMax,
        raw: scoreRaw,
      },
    },
    context,
    object: {
      id: `${activityId || courseId}/interaction/${id}`,
      definition: {
        description: wording,
        type: xApiInteractionType,
        correctResponsesPattern: [correctResponses.split(',').join('[,]')],
        choices: toInteractionArray(choices),
        interactionType: 'choice',
      },
      objectType: 'Activity',
    },
  };
}

export default choice;
