import { useDispatch } from 'react-redux';
import selectorRealType from '@auth/lib/src/selectors/selectorRealType';
import pageSelector from '../selectors/pageSelector';
import { setPageAction } from '../reducers/pageReducer';
import canNavigateToPageSelector from '../selectors/canNavigateToPageSelector';
import sectionToScrollSelector from '../selectors/sectionToScrollSelector';

export function thunkSetPage(pageId, sectionId) {
  return (dispatch, getState) => {
    const state = getState();
    const realType = selectorRealType(state, pageId);
    if (realType !== 'PageContent') {
      return null;
    }
    const currentPage = pageSelector(state);
    const currentSection = sectionToScrollSelector(state);
    if (currentPage === pageId && sectionId === currentSection) {
      return null;
    }
    const canNavigate = canNavigateToPageSelector(state, pageId);
    if (!canNavigate) {
      return null;
    }
    return dispatch(setPageAction(pageId, sectionId));
  };
}

function useSetCurrentPage() {
  const dispatchThunk = useDispatch();
  return (pageId, sectionId) => dispatchThunk(thunkSetPage(pageId, sectionId));
}

export default useSetCurrentPage;
