import { HIDE_NOTIFICATION } from '../reducers/notificationsReducer';

const subscribers = [];

export function subscribeHideNotificationAction(callback) {
  subscribers.push(callback);
  return subscribers.length - 1;
}

export function hideNotificationMiddleware() {
  return (next) => (action) => {
    const { type } = action;
    if (type === HIDE_NOTIFICATION) {
      subscribers.forEach(
        (callback, index) => {
          callback(action, () => subscribers.splice(index, 1));
        },
      );
    }
    return next(action);
  };
}
