import React from 'react';
import PropTypes from 'prop-types';
import Image from '@auth/components/src/Image/Image';
import ImageContext from './ImageContext';

function InteractiveRenderImage(props) {
  const { id } = props;
  return (
    <ImageContext.Provider value={id}>
      <Image {...props} />
    </ImageContext.Provider>
  );
}

InteractiveRenderImage.propTypes = {
  id: PropTypes.string.isRequired,
};

InteractiveRenderImage.defaultProps = {
};

export default InteractiveRenderImage;
