import { applyMiddleware, compose, createStore } from 'redux';
import thunk from 'redux-thunk';
import { hideNotificationMiddleware } from './middlewares/hideNotificationMiddleware';
import { setPopupMiddleware } from './middlewares/setPopupMiddleware';
import reducer from './reducers';
import {
  completionChange,
  evaluableQuizzesDoneChange,
  locationChange,
  scoreChange,
  suspendChange,
  init,
} from './tracking/tracking';
import trackingStoreChange from './tracking/trackingStoreChange';

let unsubscribe;
let myState = {};

const getInitialLocationData = (myLocation) => {
  if (!myLocation) return {};
  const [page = '', popup = ''] = myLocation.split('/');
  const objPage = page ? { page } : {};
  const objPopup = popup ? { popup } : {};
  return { ...objPage, ...objPopup };
};

const getInitialStateByTracking = (trackingData) => {
  const {
    location,
    suspend,
  } = trackingData;
  const locationData = getInitialLocationData(location);
  if (suspend) return { suspendData: suspend, ...locationData };
  return locationData;
};

function getStudentLanguagePreference(languagePrefence, langs, defaultLang) {
  if (langs.includes(languagePrefence)) {
    return {
      currentLang: languagePrefence,
    };
  }
  return {
    currentLang: defaultLang,
  };
}

function onUnsubscribe(subscribers) {
  const index = subscribers.length - 1;
  return () => subscribers.splice(index, 1);
}

const onScoreChangeSubscribers = [];
export const subscribeScoreChange = (func) => {
  onScoreChangeSubscribers.push(func);
  return onUnsubscribe(onScoreChangeSubscribers);
};

function onScoreChange(data, config) {
  scoreChange(data, config);
  onScoreChangeSubscribers.forEach((subscriptor) => subscriptor(data.score));
}

const onCompletionChangeSubscribers = [];
export const subscribeCompletionChange = (func) => {
  onCompletionChangeSubscribers.push(func);
  return onUnsubscribe(onCompletionChangeSubscribers);
};

function onCompletionChange(data, config) {
  completionChange(data, config);
  onCompletionChangeSubscribers.forEach((subscriptor) => subscriptor(data.completion));
}

const changesFunc = {
  suspend: suspendChange,
  location: locationChange,
  score: onScoreChange,
  completion: onCompletionChange,
  evaluableQuizzesDone: evaluableQuizzesDoneChange,
};

const trackingSetStore = (store) => {
  if (unsubscribe) unsubscribe();
  unsubscribe = store.subscribe(
    () => {
      const storeState = store.getState();
      const {
        state,
        changes,
      } = trackingStoreChange(myState, storeState);

      if (changes.length) {
        myState = { ...state };
        changes.forEach(
          (change) => changesFunc[change](state, storeState.config),
        );
      }
    },
  );
};

function configureStore(initialState) {
  const { config } = initialState;
  const trakingInitialData = init(config);
  Object.entries(trakingInitialData).forEach(
    ([k, v]) => {
      myState[k] = v;
    },
  );

  // eslint-disable-next-line no-underscore-dangle
  const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;
  const store = createStore(
    reducer,
    {
      ...initialState,
      ...getInitialStateByTracking(trakingInitialData),
      ...getStudentLanguagePreference(
        trakingInitialData.languagePreference,
        Object.keys(initialState.langs),
        initialState.currentLang,
      ),
    },
    composeEnhancers(
      applyMiddleware(
        thunk,
        setPopupMiddleware,
        hideNotificationMiddleware,
      ),
    ),
  );
  trackingSetStore(store);
  return store;
}

export default configureStore;
