import selectorChilds from '@auth/lib/src/selectors/selectorChilds';
import selectorProp from '@auth/lib/src/selectors/selectorProp';

function getFilteredChilds(state, id) {
  const childs = selectorChilds(state, id);
  return childs.filter(
    (child) => !selectorProp(state, child, 'editorTagDisabled'),
  );
}

export default getFilteredChilds;
