import selectorCssInstanceData from '@auth/lib/src/selectors/selectorCssInstanceData';
import selectorPreserveAspectRatio from '@auth/lib/src/selectors/selectorPreserveAspectRatio';
import selectorProp from '@auth/lib/src/selectors/selectorProp';
import selectorType from '@auth/lib/src/selectors/selectorType';
import useAppData from '@auth/shared/src/hooks/useAppData';
import { shallowEqual, useSelector } from 'react-redux';
import { getPropsOverridesCss } from '../config';
import getChildAreas from './getChildAreas';
import getGridIdByType from './selectorGetCss/getGridIdByType';
import transform from './selectorGetCss/transform';

function selectorCssInstance(state, id, compProps) {
  const type = selectorProp(state, id, 'type');

  const cssDataInstance = selectorCssInstanceData(state, id);
  const cssDataIntanceWithPropsOverridesCss = (
    () => getPropsOverridesCss(type).reduce(
      (memo, propName) => {
        const propVal = selectorProp(state, id, propName);
        if (!propVal) return memo;
        return { ...memo, [propName]: propVal };
      },
      cssDataInstance,
    )
  )();
  const childsAreas = getChildAreas(state, id, type, cssDataIntanceWithPropsOverridesCss.display);

  return {
    ...cssDataIntanceWithPropsOverridesCss,
    gridArea: getGridIdByType(id, state, compProps),
    ...childsAreas,
  };
}

function selectorCssStyle(state, id) {
  const style = selectorProp(state, id, 'style');
  return selectorCssInstanceData(state, style);
}

function useGetCss(id, compProps, cssDataCustom) {
  const { unitId } = useAppData();
  const cssDataInstance = useSelector(
    (state) => selectorCssInstance(state, id, compProps),
    shallowEqual,
  );
  const cssDataStyle = useSelector(
    (state) => selectorCssStyle(state, id),
    shallowEqual,
  );

  const type = useSelector(
    (state) => selectorType(state, id),
  );
  const preserveAspectRatio = useSelector(
    selectorPreserveAspectRatio,
  );
  return transform({
    cssDataCustom,
    cssDataInstance,
    cssDataStyle,
    preserveAspectRatio,
    type,
    unitId,
  });
}

export default useGetCss;
