import React from 'react';
import PropTypes from 'prop-types';
import { ClassNames } from '@auth/shared/src/utils/generateClassReact';
import useInteractiveProps from '../useInteractiveProps';
import nullFunc from '../nullFunc';

const cssDefault = {
  overflow: 'hidden',
};

const cssFigureDefault = {
  overflow: 'hidden',
  position: 'relative',
  width: '100%',
};

const cssChildrenContainer = {
  position: 'absolute',
  top: 0,
  left: 0,
  right: 0,
  bottom: 0,
  width: '100%',
};

const calculatePaddingTop = (ratio) => ({
  paddingTop: `${ratio}%`,
});

function Image(props) {
  const {
    src,
    alt,
    title,
    translateX,
    translateY,
    zoom,
    aspectRatio,
    filter,
    originalWidth,
    originalHeight,
    className,
    children,
    refChildrenContainer,
    onLoad,
  } = props;
  const ratio = aspectRatio !== 'auto' ? aspectRatio : `${originalWidth}:${originalHeight}`;
  const paddingTop = (() => {
    const [w, h] = ratio.split(':');
    return (+h / +w) * 100;
  })();
  const cssImg = {
    position: 'absolute',
    top: 0,
    right: 0,
    bottom: 0,
    left: 0,
    transform: `scale(${zoom}) translateY(${translateY}) translateX(${translateX})`,
    width: '100%',
    transformOrigin: '0 0',
    margin: 'auto',
    filter,
  };
  const objRefChildrenContainer = refChildrenContainer ? { ref: refChildrenContainer } : {};
  const interactiveProps = useInteractiveProps(props);
  return (
    <ClassNames>
      {({ css, cx }) => (
        <div
          className={cx(css(cssDefault), className)}
          {...interactiveProps}
        >
          <div style={{ position: 'relative' }}>
            <figure
              className={cx(css(cssFigureDefault), css(calculatePaddingTop(paddingTop)))}
            >
              <img
                src={src || `${process.env.PUBLIC_URL}/resources/placeholder.png`}
                alt={alt}
                title={title}
                className={css(cssImg)}
                draggable="false"
                onLoad={(e) => { onLoad(e); }}
              />
            </figure>
            <div className={css(cssChildrenContainer)} {...objRefChildrenContainer}>
              {children}
            </div>
          </div>
        </div>
      )}
    </ClassNames>
  );
}

Image.propTypes = {
  className: PropTypes.string,
  src: PropTypes.string,
  alt: PropTypes.string,
  title: PropTypes.string,
  translateX: PropTypes.string,
  translateY: PropTypes.string,
  zoom: PropTypes.number,
  aspectRatio: PropTypes.string,
  filter: PropTypes.string,
  originalWidth: PropTypes.number,
  originalHeight: PropTypes.number,
  children: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]),
  refChildrenContainer: PropTypes.oneOfType([
    PropTypes.func,
    PropTypes.shape({ current: PropTypes.instanceOf(Element) }),
  ]),
  onLoad: PropTypes.func,
};

Image.defaultProps = {
  src: `${process.env.PUBLIC_URL}/resources/placeholder.png`,
  alt: 'Alt',
  title: 'Image',
  translateX: '0',
  translateY: '0',
  zoom: 1,
  aspectRatio: 'auto',
  filter: 'none',
  originalWidth: 160,
  originalHeight: 90,
  className: '',
  children: null,
  refChildrenContainer: null,
  onLoad: nullFunc,
};

export default Image;
