import selectorAllDescendantIdsByType from '@auth/lib/src/selectors/selectorAllDescendantIdsByType';
import selectorProp from '@auth/lib/src/selectors/selectorProp';
import activityPointsSelector from './activityPointsSelector';
import activitySuccessRatePercentSelector from './activitySuccessRatePercentSelector';
import suspendDataSelector from './suspendDataSelector';

export function isOptionCorrect(state, option, activityId) {
  const response = suspendDataSelector(activityId, 'response', state);
  const responseInArray = response ? response.split(',') : [];
  const correctProp = selectorProp(state, option, 'correct') === 'true';
  return correctProp === responseInArray.includes(option);
}

export function optionsCorrect(state, activityId) {
  const options = selectorAllDescendantIdsByType(activityId, 'MultipleChoiceOption', state);
  return options.reduce(
    (totCorrects, option) => (isOptionCorrect(state, option, activityId)
      ? totCorrects + 1
      : totCorrects),
    0,
  );
}

function percentCorrect(state, activityId) {
  const options = selectorAllDescendantIdsByType(activityId, 'MultipleChoiceOption', state);
  const totCorrect = optionsCorrect(state, activityId);
  return (totCorrect / options.length) * 100;
}

export function calculateMultipleChoiceTotPoints(state, activityId) {
  const activityPoints = activityPointsSelector(state, activityId);
  const percent = percentCorrect(state, activityId);
  const points = (percent / 100) * activityPoints;
  return points > 1 ? Math.round(points) : points;
}

function checkMultipleChoiceActivitySelector(state, activityId) {
  const currentPercent = percentCorrect(state, activityId);
  const successRatePercent = activitySuccessRatePercentSelector(state, activityId);
  return currentPercent >= successRatePercent;
}

export default checkMultipleChoiceActivitySelector;
