import React, { useEffect, useRef } from 'react';
import PropTypes from 'prop-types';
import RangeTouch from 'rangetouch';
import useInteractiveProps from '../useInteractiveProps';
import './skin1/VolumeControlSkin1.css';

// Get the number of decimal places
export function getDecimalPlaces(value) {
  const match = `${value}`.match(/(?:\.(\d+))?(?:[eE]([+-]?\d+))?$/);

  if (!match) {
    return 0;
  }

  return Math.max(
    0,
    // Number of digits right of decimal point.
    (match[1] ? match[1].length : 0)
    // Adjust for scientific notation.
    - (match[2] ? +match[2] : 0),
  );
}

// Round to the nearest step
export function round(number, step) {
  if (step < 1) {
    const places = getDecimalPlaces(step);
    return parseFloat(number.toFixed(places));
  }
  return Math.round(number / step) * step;
}

function VolumeControl(props) {
  const {
    className,
    volume,
    setVolume,
    mute,
    setMute,
  } = props;
  const newVolume = volume * 100;
  const componentEl = useRef(null);
  const componentElRange = useRef(null);
  const handleOnChangeVolume = (e) => {
    const calculateVolume = +((e.target.value) / 100);
    // Si estamos en mute pero modificamos el volumen: salimos del mute
    if (mute && calculateVolume !== 0) {
      setMute(false);
    }
    // Si no estamos mute pero ponemos el volumen a 0: entramos en el mute
    if (!mute && calculateVolume === 0) {
      setMute(true);
    }
    setVolume(calculateVolume);
  };
  const handleClickToggleMute = () => {
    // Si pulsamos para salir del modo mute pero no tenemos volumen: ponemos volumen al 50%
    if (mute && volume === 0) {
      setVolume(0.5);
    }
    setMute(!mute);
  };
  useEffect(() => {
    if (componentEl && componentEl.current) {
      componentEl.current.style.setProperty('--volume-percent', `${mute ? 0 : newVolume}%`);
    }
  }, [newVolume, mute, componentEl]);
  React.useEffect(() => {
    const range = new RangeTouch(componentElRange.current, { thumbWidth: 0 });
    range.get = (event) => {
      const input = event.target;
      const touch = event.changedTouches[0];
      const min = parseFloat(input.getAttribute('min')) || 0;
      const max = parseFloat(input.getAttribute('max')) || 100;
      const step = parseFloat(input.getAttribute('step')) || 1;
      const delta = max - min;

      // Calculate percentage
      let percent;
      const clientRect = input.getBoundingClientRect();
      const thumbWidth = ((100 / clientRect.height) * (15 / 2)) / 100;
      // Determine left percentage
      percent = (100 / clientRect.height) * (touch.clientY - clientRect.top);

      percent = 100 - percent;

      // Don't allow outside bounds
      if (percent < 0) {
        percent = 0;
      } else if (percent > 100) {
        percent = 100;
      }

      // Factor in the thumb offset
      if (percent < 50) {
        percent -= (100 - percent * 2) * thumbWidth;
      } else if (percent > 50) {
        percent += (percent - 50) * 2 * thumbWidth;
      }

      // Find the closest step to the mouse position
      return min + round(delta * (percent / 100), step);
    };
    return () => {
      range.destroy();
    };
  }, []);
  return (
    <div
      ref={componentEl}
      className={`ntx-multimedia-volume-control ${className}`}
      {...useInteractiveProps(props)}
    >
      <button
        type="button"
        aria-label="Mute/Unmute"
        onClick={handleClickToggleMute}
        className={`ntx-multimedia-btn-sound ${mute ? 'ntx-multimedia-btn-sound-mute' : 'ntx-multimedia-btn-sound-unmute'}`}
      />
      <div className="ntx-multimedia-wrapper-slider-sound">
        <div className="ntx-multimedia-container-slider-sound">
          <input
            ref={componentElRange}
            type="range"
            className="ntx-multimedia-range-sound"
            step="1"
            max="100"
            min="0"
            value={mute ? 0 : newVolume}
            onInput={handleOnChangeVolume}
          />
        </div>
      </div>
    </div>
  );
}

VolumeControl.propTypes = {
  id: PropTypes.string.isRequired,
  className: PropTypes.string,
  volume: PropTypes.number,
  setVolume: PropTypes.func,
  mute: PropTypes.bool,
  setMute: PropTypes.func,
};

VolumeControl.defaultProps = {
  className: '',
  volume: 1,
  setVolume: () => null,
  mute: false,
  setMute: () => null,
};

export default VolumeControl;
