import langValidKeys from '../../data/langValidKeys';
import { LANGS } from '../../editor/stateKeys';
import { add, remove } from '../../stateChanger/stateChanger';

export const LANGS_CHANGE = 'LANGS_CHANGE';

export const langsChangeSchema = {
  type: {
    type: 'string',
  },
  payload: {
    type: 'object',
    shape: {
      oldKey: {
        type: 'string',
      },
      newKey: {
        type: 'string',
      },
    },
  },
};

export const langsChangeAction = (oldKey, newKey) => ({
  type: LANGS_CHANGE,
  payload: {
    oldKey,
    newKey,
  },
});

export function changeLang(state, oldKey, newKey, mutable, allState) {
  if (!langValidKeys.includes(oldKey)) return state;
  if (!state[oldKey]) return state;
  let draftState = state;
  draftState = add(
    draftState,
    newKey,
    { ...state[oldKey] },
    mutable,
    allState,
    LANGS,
  );
  draftState = remove(
    draftState,
    oldKey,
    mutable,
    allState,
    LANGS,
  );
  return draftState;
}

const langsReducerChange = (state, action, allState) => {
  const oldKey = action?.payload?.oldKey;
  const newKey = action?.payload?.newKey;
  const mutable = action?.mutable;
  return changeLang(state, oldKey, newKey, mutable, allState);
};

export default langsReducerChange;
