import { useContext } from 'react';
import { useDispatch } from 'react-redux';
import selectorFirstAscendantByType from '@auth/lib/src/selectors/selectorFirstAscendantByType';
import nullFunc from '../../../utils/nullFunc';
import SequenceGroupOptionContext from '../SequenceGroupOptionContext';
import suspendDataSelector from '../../../selectors/suspendDataSelector';
import ActivityContext from '../ActivityContext';

function useClickSequenceMove() {
  const { movePrev = nullFunc, moveNext = nullFunc } = useContext(SequenceGroupOptionContext) || {};
  const activityId = useContext(ActivityContext);
  const dispatchThunk = useDispatch();
  return (props, moveType) => {
    const { id } = props;
    const optionId = dispatchThunk(
      (dispatch, getState) => {
        const state = getState();
        const correct = suspendDataSelector(activityId, 'correct', state);
        if ([0, 1].includes(correct)) return undefined;
        return selectorFirstAscendantByType(state, id, 'SequenceOption');
      },
    );
    if (!optionId) return;
    const func = moveType === 'next' ? moveNext : movePrev;
    func(optionId);
  };
}

export default useClickSequenceMove;
