import { useDispatch } from 'react-redux';
import selectorCompIdsByType from '@auth/lib/src/selectors/selectorCompIdsByType';
import { operationsSuspendDataAction } from '../reducers/suspendDataReducerOperations';
import getRetryQuizMantainCorrectOperations from './useRetryQuizMantainCorrect/getRetryQuizMantainCorrectOperations';

function useRetryAndMantainCorrectAllQuizzes() {
  const dispatchThunk = useDispatch();
  return () => {
    dispatchThunk((dispatch, getState) => {
      const state = getState();
      const quizzes = selectorCompIdsByType(state, 'Quiz', true);
      const operations = quizzes.reduce(
        (memo, quizId) => [...memo, ...getRetryQuizMantainCorrectOperations(quizId, state)],
        [],
      );
      if (operations.length) dispatch(operationsSuspendDataAction(operations));
    });
  };
}

export default useRetryAndMantainCorrectAllQuizzes;
