import create from 'zustand';
import {
  EDITOR_DRAWER_PROPERTIES,
} from '../const';
import roundTwoDecimals from '../utils/roundTwoDecimals';
import getEditorUserModeInLocalStorage from './useSetEditorUserMode/getEditorUserModeInLocalStorage';

const useEditorStore = create(
  (set) => ({
    overIds: [],
    setOverIds: (overIds) => set({ overIds }),
    overIdsTag: [],
    setOverIdsTag: (overIdsTag) => set({ overIdsTag }),
    buttonState: '',
    setButtonState: (buttonState) => set({ buttonState }),
    currentTag: null,
    setCurrentTag: (currentTag) => set({ currentTag, buttonState: '' }),
    currentColumn: null,
    setCurrentColumn: (currentColumn) => set({ currentColumn }),
    currentTab: EDITOR_DRAWER_PROPERTIES,
    setCurrentTab: (currentTab) => set({ currentTab }),
    enabledHelp: true,
    setEnabledHelp: (enabledHelp) => set({ enabledHelp }),
    breadcrumbsActive: true,
    setBreadcrumbsActive: (breadcrumbsActive) => set({ breadcrumbsActive }),
    currentTypeComposite: null,
    setCurrentTypeComposite: (currentTypeComposite) => set({ currentTypeComposite }),
    complexId: [],
    setComplexId: (complexId, keepArray = false) => {
      if (keepArray) {
        return set((state) => ({
          complexId: [complexId, ...state.complexId],
          currentTag: complexId,
          buttonState: '',
        }));
      }
      return set({
        complexId: [complexId],
        currentTag: complexId,
        buttonState: '',
      });
    },
    setComplexIdBack: () => set(
      (state) => {
        const newComplexId = state.complexId.filter((v, index) => index !== 0);
        return ({
          complexId: newComplexId,
          currentTag: newComplexId[0] || null,
          buttonState: '',
        });
      },
    ),
    ckEditorBlurDisabled: false,
    setCkEditorBlurDisabled: (ckEditorBlurDisabled) => {
      set({ ckEditorBlurDisabled });
    },
    HTMLTagName: '',
    setHTMLTagName: (HTMLTagName) => set({ HTMLTagName }),
    resizingImageArea: null,
    setResizingImageArea: (resizingImageArea) => set({ resizingImageArea }),
    snackbarMessage: '',
    setSnackbarMessage: (snackbarMessage) => set({ snackbarMessage }),
    showLoading: false,
    setShowLoading: (showLoading) => set({ showLoading }),
    googleFonts: {},
    setGoogleFonts: (googleFonts) => set({ googleFonts, googleFontsIsLoading: false }),
    googleFontsIsLoading: false,
    setGoogleFontsIsLoading: (googleFontsIsLoading) => set({ googleFontsIsLoading }),
    addComponentPanelOpen: false,
    addComponentPanelType: null,
    categoriesPanelOpen: false,
    categoriesPanelList: [],
    addComponentTarget: null,
    addComponentPosition: null,
    setOpenCategories: (list, target = null, position = null) => set({
      categoriesPanelOpen: true,
      categoriesPanelList: list,
      addComponentTarget: target,
      addComponentPosition: position,
    }),
    setCloseCategories: () => set({ categoriesPanelOpen: false }),
    setOpenAddComponent: (type, target = null, position = null) => set({
      addComponentPanelOpen: true,
      addComponentPanelType: type,
      addComponentTarget: target,
      addComponentPosition: position,
    }),
    setCloseAddComponent: () => set({ addComponentPanelOpen: false }),
    setCloseCategoriesAndAddComponent: () => set({
      categoriesPanelOpen: false,
      addComponentPanelOpen: false,
    }),
    setComponentAdded: (target) => set({ addComponentTarget: target }),
    editorUserMode: getEditorUserModeInLocalStorage(),
    setEditorUserMode: (editorUserMode) => set({ editorUserMode }),
    preserveAspectRatioCustom: '2560/1440',
    setPreserveAspectRatioCustom: (preserveAspectRatioCustom) => set({ preserveAspectRatioCustom }),
    zoom: 1,
    setZoom: (newZoom) => {
      const zoom = Math.min(Math.max(0.01, roundTwoDecimals(newZoom)), 4);
      set({ zoom });
    },
  }),
);

export default useEditorStore;
