import React from 'react';
import PropTypes from 'prop-types';
import { ClassNames } from '@auth/shared/src/utils/generateClassReact';
import { FontAwesomeIcon } from '@auth/shared/src/utils/integrationFortawesome';
import useInteractiveProps from '../useInteractiveProps';
import ButtonComp from './ButtonComp';
import DivComp from './DivComp';
import nullFunc from '../nullFunc';

const cssButtonDefault = {
  display: 'flex',
  border: 'none',
  margin: 0,
  textDecoration: 'none',
  cursor: 'pointer',
  textAlign: 'center',
  fontFamily: 'inherit',
  appearance: 'none',
  alignItems: 'center',
  '&:disabled': {
    cursor: 'auto',
    pointerEvents: 'none',
  },
  '& svg': {
    width: 'auto',
  },
};

const cssButtonTextDefault = {
  display: 'inline-block',
  width: '100%',
};

function getTextAlign(justifyContent) {
  if (['start', 'flex-start', 'left'].includes(justifyContent)) {
    return 'left';
  }
  if (['end', 'flex-end', 'right'].includes(justifyContent)) {
    return 'right';
  }
  if (['center'].includes(justifyContent)) {
    return 'center';
  }
  return 'left';
}

function Button(props) {
  const {
    text,
    iconSrc,
    selected,
    className,
    disabled,
    onMouseEnter,
    onMouseLeave,
    onMouseDown,
    onMouseUp,
    buttonTextDisplay,
    buttonIconDisplay,
    auxChildren,
    ckRef,
    onTextMouseEnter,
    rootButtonNodeName,
    id,
  } = props;
  const html = {
    __html: text,
  };
  const selectedAttr = selected ? { 'data-selected': 'true' } : {};
  const BaseNode = rootButtonNodeName === 'button' ? ButtonComp : DivComp;
  const interactiveProps = useInteractiveProps(props);
  const [textAlign, setTextAlign] = React.useState('left');
  React.useEffect(() => {
    const domElement = document.querySelector(`[data-ntx-component="${id}"`);
    const { justifyContent } = window.getComputedStyle(domElement);
    setTextAlign(getTextAlign(justifyContent));
  }, [id, className]);
  return (
    <ClassNames>
      {({ css, cx }) => (
        <BaseNode
          type="button"
          role="button"
          disabled={disabled}
          className={cx(css(cssButtonDefault), className)}
          onMouseEnter={onMouseEnter}
          onMouseLeave={onMouseLeave}
          onMouseDown={onMouseDown}
          onMouseUp={onMouseUp}
          onFocus={nullFunc}
          {...selectedAttr}
          {...interactiveProps}
        >
          {auxChildren}
          {buttonTextDisplay !== 'none' && (
            <div
              style={{ textAlign }}
              onMouseEnter={onTextMouseEnter}
              ref={ckRef}
            >
              <div
                style={{ textAlign }}
                className={`${css(cssButtonTextDefault)} ntx-ck-editor-container`}
                // eslint-disable-next-line react/no-danger
                dangerouslySetInnerHTML={html}
              />
            </div>
          )}
          {buttonIconDisplay !== 'none' && <FontAwesomeIcon icon={['fas', iconSrc]} className="ntx-apply-transition" />}
        </BaseNode>
      )}
    </ClassNames>
  );
}

Button.propTypes = {
  id: PropTypes.string.isRequired,
  className: PropTypes.string,
  text: PropTypes.string,
  iconSrc: PropTypes.string,

  auxChildren: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]),
  selected: PropTypes.bool,
  disabled: PropTypes.bool,
  onMouseEnter: PropTypes.func,
  onMouseLeave: PropTypes.func,
  onMouseDown: PropTypes.func,
  onMouseUp: PropTypes.func,
  onTextMouseEnter: PropTypes.func,
  buttonTextDisplay: PropTypes.oneOf(['none', 'initial']),
  buttonIconDisplay: PropTypes.oneOf(['none', 'initial']),
  ckRef: PropTypes.oneOfType([
    // Either a function
    PropTypes.func,
    // Or the instance of a DOM native element (see the note about SSR)
    PropTypes.shape({ current: PropTypes.instanceOf(Element) }),
  ]),
  rootButtonNodeName: PropTypes.oneOf(['div', 'button']),
};

Button.defaultProps = {
  text: '<p>Lorem ipsum</p>',
  iconSrc: 'chevron-right',
  auxChildren: null,
  selected: false,
  className: '',
  disabled: false,
  onMouseEnter: nullFunc,
  onMouseLeave: nullFunc,
  onMouseDown: nullFunc,
  onMouseUp: nullFunc,
  onTextMouseEnter: nullFunc,
  buttonTextDisplay: 'initial',
  buttonIconDisplay: 'initial',
  ckRef: null,
  rootButtonNodeName: 'button',
};

export default Button;
