import iso8601 from 'iso8601.js';

let SCORM = {};
let debug = false;
let successStatus;
let failedStatus;
let score = 0;
let completion = false;
let success = false;
let status = 'not attempted';

const getScore = () => {
  const lmsScore = SCORM.get('cmi.core.score.raw');
  if (lmsScore === '') return undefined;
  return +lmsScore;
};

const getCompletion = () => {
  const lmsStatus = SCORM.get('cmi.core.lesson_status');
  if (lmsStatus === 'not attempted') return undefined;
  return ['passed', 'failed', 'completed'].includes(lmsStatus);
};

const getMastery = () => {
  const lmsMastery = SCORM.get('cmi.student_data.mastery_score');
  if (lmsMastery === '') return undefined;
  return +lmsMastery;
};

let sendStatus;

const init = (config) => {
  SCORM = window.pipwerks.SCORM;
  debug = window.pipwerks.debug;
  const {
    scorm12SuccessLessonStatus,
    scorm12FailedLessonStatus,
    sendingSuccessStatusCriteria,
    sendComplete,
    debugMode,
  } = config;
  successStatus = scorm12SuccessLessonStatus;
  failedStatus = scorm12FailedLessonStatus;
  sendStatus = (() => {
    if ((scorm12SuccessLessonStatus === 'passed' || scorm12FailedLessonStatus === 'failed')) {
      return sendingSuccessStatusCriteria !== 'never';
    }
    return sendComplete;
  })();
  debug.isActive = debugMode;
  SCORM.handleExitMode = false;
  SCORM.handleCompletionStatus = false;
  const initSuccess = SCORM.init();
  if (initSuccess) {
    return [
      true,
      getScore(),
      getCompletion(),
      getMastery(),
    ];
  }
  return [false];
};

const setSuspend = (suspend) => {
  SCORM.set('cmi.suspend_data', JSON.stringify(suspend));
};

const getSuspend = () => {
  const val = SCORM.get('cmi.suspend_data');
  if (!val) return undefined;
  return JSON.parse(val);
};

const setLocation = (location) => {
  SCORM.set('cmi.core.lesson_location', location);
};

const getLocation = () => SCORM.get('cmi.core.lesson_location');

const exit = ({ sessionTime }) => {
  const isoSessionTime = iso8601.time(new Date(sessionTime), true);
  SCORM.set('cmi.core.session_time', isoSessionTime);
  SCORM.save();
  SCORM.quit();
};

const sendInteraction = () => null;

const sendMultipleInteractions = () => null;

const calculateStatus = () => {
  if (!completion) return 'incomplete';
  if (success) return successStatus;
  return failedStatus;
};

const updateStatus = () => {
  const newStatus = calculateStatus();
  if (newStatus !== status) {
    status = newStatus;
    if (sendStatus) SCORM.set('cmi.core.lesson_status', status);
  }
};

const setScore = (newScore) => {
  score = newScore;
  SCORM.set('cmi.core.score.raw', score);
  SCORM.set('cmi.core.score.min', 0);
  SCORM.set('cmi.core.score.max', 100);
  updateStatus();
};

const setCompletion = (newCompletion) => {
  completion = newCompletion;
  updateStatus();
};

const setSuccessStatus = (newSuccess) => {
  success = newSuccess;
  updateStatus();
};

const getName = () => SCORM.get('cmi.core.student_name');

const getId = () => SCORM.get('cmi.core.student_id');

const scorm12 = {
  init,
  setSuspend,
  getSuspend,
  setLocation,
  getLocation,
  exit,
  sendInteraction,
  sendMultipleInteractions,
  setScore,
  setCompletion,
  setSuccessStatus,
  getName,
  getId,
};

export default scorm12;
