import { useDispatch } from 'react-redux';
import selectorRealType from '@auth/lib/src/selectors/selectorRealType';
import { useCallback } from 'react';
import { setPopupAction } from '../reducers/popupReducer';

const isPopup = (state, id) => selectorRealType(state, id) === 'Popup';

function useSetCurrentPopup() {
  const dispatchThunk = useDispatch();
  return useCallback(
    (id) => dispatchThunk((dispatch, getState) => {
      if (id === null) return dispatch(setPopupAction(null));
      if (isPopup(getState(), id)) return dispatch(setPopupAction(id));
      return null;
    }),
    [dispatchThunk],
  );
}

export default useSetCurrentPopup;
