import selectorCompleteCriteria from '@auth/lib/src/selectors/selectorCompleteCriteria';
import selectorProp from '@auth/lib/src/selectors/selectorProp';
import selectorType from '@auth/lib/src/selectors/selectorType';
import completionSelectorConfig from './completionSelectorConfig';

function requirementsSelector(state, id) {
  const general = selectorCompleteCriteria(state);
  const compType = selectorType(state, id);
  const posibles = Object.keys(completionSelectorConfig[compType]);
  return posibles.filter(
    (v) => {
      const customCompleteCriteria = selectorProp(state, id, 'customCompleteCriteria') || 'inherit';
      if (general.includes(v) && customCompleteCriteria === 'inherit') return true;
      return v === customCompleteCriteria;
    },
  );
}

export default requirementsSelector;
