import getDisplayByType from '../getChildAreas/getDisplayByType';

const props = ['display:widescreen', 'display', 'display:tablet', 'display:mobile'];

function patchDisplay(css, type) {
  const needPatch = props.some(
    (prop) => css[prop] === 'none',
  );
  if (!needPatch) return css;
  return props.reduce(
    (memo, prop) => ({
      ...memo,
      [prop]: css[prop] || getDisplayByType(type),
    }),
    css,
  );
}

export default patchDisplay;
