import React from 'react';
import PropTypes from 'prop-types';
import CompositeItem from '@auth/components/src/CompositeItem/CompositeItem';
import useSetVisited from '../../hooks/useSetVisited';

function InteractiveRenderCompositeItem(props) {
  const { id } = props;
  useSetVisited(id);
  return (
    <CompositeItem {...props} />
  );
}

InteractiveRenderCompositeItem.propTypes = {
  id: PropTypes.string.isRequired,
};
InteractiveRenderCompositeItem.defaultProps = {
};

export default InteractiveRenderCompositeItem;
