import React from 'react';
import PropTypes from 'prop-types';
import useInteractiveProps from '../useInteractiveProps';
import pseudoSelectorProp from '../pseudoSelectorProp';

function SequenceOption(props) {
  const {
    children,
    className,
    pseudoSelector,
    attributes,
    listeners,
    dragStyle,
  } = props;
  return (
    <section
      className={className}
      {...pseudoSelectorProp(pseudoSelector)}
      {...useInteractiveProps(props)}
      {...attributes}
      {...listeners}
      style={dragStyle}
    >
      {children}
    </section>
  );
}

SequenceOption.propTypes = {
  className: PropTypes.string,
  children: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]),
  onChange: PropTypes.func,
  pseudoSelector: PropTypes.oneOf([
    '',
    'answeredright',
    'answeredwrong',
  ]),
  attributes: PropTypes.objectOf(PropTypes.oneOfType(
    [PropTypes.string, PropTypes.number, PropTypes.bool],
  )),
  listeners: PropTypes.objectOf(PropTypes.func),
  dragStyle: PropTypes.objectOf(PropTypes.string),
};

SequenceOption.defaultProps = {
  children: null,
  onChange: () => null,
  className: '',
  pseudoSelector: '',
  attributes: {},
  listeners: {},
  dragStyle: {},
};

export default SequenceOption;
