import React from 'react';
import PropTypes from 'prop-types';
import { CSSTransition } from 'react-transition-group';
import generateClass from '@auth/shared/src/utils/generateClass';
import Popup from '@auth/components/src/Popup/Popup';
import PopupOverlayContext from '@auth/components/src/Popup/PopupOverlayContext';

const contextValue = { id: 'popupOverlay' };
function InteractiveRenderPopup(props) {
  const {
    id,
    in: propIn,
    transitionTypeIn,
    transitionTypeOut,
    transitionInDuration,
    transitionOutDuration,
  } = props;
  const classNames = (() => {
    const objEnter = transitionTypeIn === 'none'
      ? {}
      : {
        enter: generateClass({
          opacity: 0,
        }),
        enterActive: generateClass({
          opacity: 1,
          transition: `opacity ${transitionInDuration} ease-in`,
        }),
      };
    const objExit = transitionTypeOut === 'none'
      ? {}
      : {
        exit: generateClass({
          opacity: 1,
        }),
        exitActive: generateClass({
          opacity: 0,
          transition: `opacity ${transitionOutDuration} ease-in`,
        }),
      };
    return {
      ...objEnter,
      ...objExit,
    };
  })();
  const timeout = (() => {
    const nDurationEnter = +(transitionInDuration.replace('ms', ''));
    const objEnter = transitionTypeIn === 'none'
      ? {}
      : { enter: nDurationEnter };
    const nDurationExit = +(transitionOutDuration.replace('ms', ''));

    const objExit = transitionTypeOut === 'none'
      ? {}
      : { exit: nDurationExit };
    return {
      ...objEnter,
      ...objExit,
    };
  })();
  return (
    <CSSTransition
      in={propIn}
      unmountOnExit
      key={id}
      timeout={timeout}
      classNames={classNames}
    >
      <PopupOverlayContext.Provider value={contextValue}>
        <Popup {...props} />
      </PopupOverlayContext.Provider>
    </CSSTransition>
  );
}

InteractiveRenderPopup.propTypes = {
  id: PropTypes.string.isRequired,
  in: PropTypes.bool.isRequired,
  popupOverlayBackground: PropTypes.string,
  transitionTypeIn: PropTypes.string,
  transitionTypeOut: PropTypes.string,
  transitionInDuration: PropTypes.string,
  transitionOutDuration: PropTypes.string,
};

InteractiveRenderPopup.defaultProps = {
  popupOverlayBackground: null,
  transitionTypeIn: 'none',
  transitionTypeOut: 'none',
  transitionInDuration: '1000ms',
  transitionOutDuration: '1000ms',
};

export default InteractiveRenderPopup;
