import selectorProp from '@auth/lib/src/selectors/selectorProp';
import quizHasAttemptsSelector from '../../../selectors/quizHasAttemptsSelector';

function optionActivityIsFinished(state, quizId) {
  const unlimitedAttempts = selectorProp(state, quizId, 'unlimitedAttempts');
  if (unlimitedAttempts === true) return true;
  const quizHasAttempts = quizHasAttemptsSelector(state, quizId);
  return !quizHasAttempts;
}

export default optionActivityIsFinished;
