import masterContainerContext from '@auth/components/src/MasterContainer/masterContainerContext';
import { useContext } from 'react';
import { useSelector } from 'react-redux';
import visibilitySelector from '../selectors/visibilitySelector';

function useVisibility(props) {
  const myMasterContainerId = useContext(masterContainerContext);
  return useSelector(
    (state) => visibilitySelector(state, props, myMasterContainerId),
  );
}

export default useVisibility;
