import activityTypes from '../data/activityTypes';
import selectorClosestType from './selectorClosestType';
import selectorClosestTypes from './selectorClosestTypes';

function selectorConditionId(state, id, instanceId, masterContainer) {
  if (id === 'currentActivity') return selectorClosestTypes(state, masterContainer || instanceId, activityTypes);
  if (id === 'currentQuiz') return selectorClosestType(state, masterContainer || instanceId, 'Quiz');
  if (id === 'currentPage') return selectorClosestType(state, masterContainer || instanceId, 'PageContent');
  if (id === 'currentComposite') return selectorClosestType(state, masterContainer || instanceId, 'Composite');
  return id;
}

export default selectorConditionId;
