import useSetSuspendData from './useSetSuspendData';

const useNextSlide = () => {
  const setSuspend = useSetSuspendData();
  return (componentId, childs, currentChild) => {
    if (childs.length) {
      const newPosition = Math.min(childs.indexOf(currentChild) + 1, childs.length - 1);
      const next = childs[newPosition];
      if (next !== currentChild) {
        setSuspend(componentId, 'visibleChilds', [next]);
      }
    }
  };
};

export default useNextSlide;
