import React, { useContext } from 'react';
import { useSelector } from 'react-redux';
import { TransitionGroup } from 'react-transition-group';
import generateClass from '@auth/shared/src/utils/generateClass';
import RenderContent from '@auth/components/src/RenderContent/RenderContent';
import selectorCompIdsByType from '@auth/lib/src/selectors/selectorCompIdsByType';
import useAppData from '@auth/shared/src/hooks/useAppData';
import RenderContentContext from '@auth/components/src/RenderContent/RenderContentContext';
import InteractiveRenderComp from './InteractiveRenderComp';
import InteractiveRenderStateContext from './InteractiveRenderStateContext';

function RenderPanel() {
  const { unitId } = useAppData();
  const panel = useSelector(
    (state) => {
      const [panelId] = selectorCompIdsByType(state, 'Panel');
      return panelId;
    },
  );
  const { isPanelOpen } = useContext(InteractiveRenderStateContext);
  const style = generateClass({
    position: 'fixed',
    zIndex: 3,
    top: 0,
    bottom: 0,
    width: '100%',
    pointerEvents: 'none',
    '> *': {
      pointerEvents: 'all',
    },
  });
  return (
    <div className={`${style} mantain-aspect-ratio`}>
      <RenderContentContext.Provider value={InteractiveRenderComp}>
        <TransitionGroup component={null}>
          {isPanelOpen && (
            <RenderContent
              id={panel}
              unitId={unitId}
            />
          )}
        </TransitionGroup>
      </RenderContentContext.Provider>
    </div>
  );
}

RenderPanel.propTypes = {};

export default RenderPanel;
