import { SET_PAGE } from './pageReducer';

export const SECTION_SCROLLED = 'SECTION_SCROLLED';

export function sectionScrolledAction() {
  return ({
    type: SECTION_SCROLLED,
    payload: {},
  });
}

const initialState = null;

function sectionToScrollReducer(state = initialState, action = {}) {
  const { type, payload } = action;
  if (type === SET_PAGE) {
    const { section } = payload;
    return section || null;
  }
  if (type === SECTION_SCROLLED) {
    return null;
  }
  return state;
}

export default sectionToScrollReducer;
