import evaluableQuizzesSelector from './evaluableQuizzesSelector';
import scoreByActivitiesScoreSelector from './scoreByActivitiesScoreSelector';
import scoreByViewPagesSelector from './scoreByViewPagesSelector';

function scoreByAutomaticSelector(state) {
  const evaluableQuizzes = evaluableQuizzesSelector(state);
  if (evaluableQuizzes.length) return scoreByActivitiesScoreSelector(state);
  return scoreByViewPagesSelector(state);
}

export default scoreByAutomaticSelector;
