import selectorCurrentLang from './selectorCurrentLang';
import selectorDefaultLang from './selectorDefaultLang';
import selectorInstanceData from './selectorInstanceData';
import selectorStateCurrentLang from './selectorStateCurrentLang';
import selectorStateInstances from './selectorStateInstances';
import selectorStateLangs from './selectorStateLangs';
import selectorStateStructure from './selectorStateStructure';

function getOrder(lang, currentLang, defautlLang) {
  if (lang === currentLang) return 0;
  if (lang === defautlLang) return 5;
  return 10;
}

export function getSortedLangKeys(state, customCurrentLang) {
  const langs = selectorStateLangs(state);
  const currentLang = customCurrentLang || selectorCurrentLang(state);
  const defaultLang = selectorDefaultLang(state);
  return Object.keys(langs).sort((a, b) => {
    const aOrder = getOrder(a, currentLang, defaultLang);
    const bOrder = getOrder(b, currentLang, defaultLang);
    if (aOrder > bOrder) return 1;
    if (aOrder < bOrder) return -1;
    return 0;
  });
}

export function getPropValue(searchId, propName, langKeys, instances) {
  const langProps = langKeys.map(
    (lang) => `${propName}__${lang}`,
  );
  const calcPropName = langProps.find(
    (prop) => instances[searchId][prop] !== undefined,
  );
  return instances[searchId][calcPropName] || instances[searchId][propName];
}

function selectorInstance(state, id, customCurrentLang) {
  const instances = selectorStateInstances(state);
  const instance = instances[id];
  const langs = selectorStateLangs(state);
  const currentLang = customCurrentLang || selectorStateCurrentLang(state);
  const structure = selectorStateStructure(state);
  return selectorInstanceData(instance, langs, currentLang, structure, id);
}

export default selectorInstance;
