import React, { useContext } from 'react';
import PropTypes from 'prop-types';
import SequenceOption from '@auth/components/src/SequenceOption/SequenceOption';
import { useSelector } from 'react-redux';
import { useSortable } from '@dnd-kit/sortable';
import { CSS } from '@dnd-kit/utilities';
import ActivityContext from './ActivityContext';
import sequenceOptionPseudoSelector from './interactiveRenderSequenceOption/sequenceOptionPseudoSelector';
import useGetSuccessActivity from '../../hooks/useGetSuccessActivity';

const InteractiveRenderSequenceOption = React.memo((props) => {
  const { id } = props;
  const success = useGetSuccessActivity();
  const {
    attributes,
    listeners,
    setNodeRef,
    transform,
    transition,
  } = useSortable({ id, disabled: success !== undefined });

  const activityId = useContext(ActivityContext);

  const pseudoSelector = useSelector(
    (state) => sequenceOptionPseudoSelector(state, activityId, id) || '',
  );

  const dragStyle = {
    transform: CSS.Transform.toString(transform),
    transition,
  };

  return (
    <SequenceOption
      componentRef={setNodeRef}
      pseudoSelector={pseudoSelector}
      dragStyle={dragStyle}
      attributes={attributes}
      listeners={listeners}
      {...props}
    />
  );
});

InteractiveRenderSequenceOption.propTypes = {
  id: PropTypes.string.isRequired,
};

InteractiveRenderSequenceOption.defaultProps = {
};

export default InteractiveRenderSequenceOption;
