import activityCheckedSelector from '../../selectors/activityCheckedSelector';
import activityIsLast from '../../selectors/activityIsLast';
import suspendDataSelector from '../../selectors/suspendDataSelector';

const visibilityAndEnabledConditions = {
  correct: (id, state) => suspendDataSelector(id, 'correct', state),
  answered: (id, state) => suspendDataSelector(id, 'response', state) || '',
  checked: (id, state) => activityCheckedSelector(id, state),
  activityIsLast: (id, state) => activityIsLast(state, id),
};

export default visibilityAndEnabledConditions;
