import selectorClosestType from '@auth/lib/src/selectors/selectorClosestType';
import quizActivitiesSelector from './quizActivitiesSelector';
import suspendDataSelector from './suspendDataSelector';

const quizCorrectActivitiesCount = (state, descendantId) => {
  const quiz = selectorClosestType(state, descendantId, 'Quiz');
  if (!quiz) return 0;
  const activities = quizActivitiesSelector(state, quiz);
  const correctActivities = activities.filter(
    (activity) => suspendDataSelector(activity, 'correct', state) === 1,
  );
  return correctActivities.length;
};

export default quizCorrectActivitiesCount;
