export function getSlots(type) {
  const { slots = [] } = config[type] || {};
  return slots;
}

function Div(props) {
  return <div {...props} />;
}

function getChildsContainerByType(type) {
  const { childsContainer = '' } = config[type] || {};
  return childsContainer;
}

export function hasChildsContainer(type) {
  const childsContainer = getChildsContainerByType(type);
  return childsContainer !== 'fragment';
}

export function getChildsContainer(type) {
  const childsContainer = getChildsContainerByType(type);
  if (childsContainer === 'div') { return Div; }
  return Fragment;
}

export function getPropsPassedToChilds(type) {
  const { propsPassedToChilds = [] } = config[type] || {};
  return propsPassedToChilds;
}

export function getPropsOverridesCss(type) {
  const { propsOverridesCss = [] } = config[type] || {};
  return propsOverridesCss;
}
