import PropTypes from 'prop-types';

function Fragment(props) {
  const { children = null } = props;
  return children;
}

Fragment.propTypes = {
  children: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]),
};

Fragment.defaultProps = {
  children: null,
};

export default Fragment;
