import selectorProp from './selectorProp';
import selectorAllDescendantIds from './selectorAllDescendantIds';

function selectorAllDescendantIdsByType(id, type, state) {
  const allDescendants = selectorAllDescendantIds(id, state);
  return allDescendants.filter(
    (descendant) => selectorProp(state, descendant, 'type') === type,
  );
}

export default selectorAllDescendantIdsByType;
