import selectorConfig from '@auth/lib/src/selectors/selectorConfig';
import scoreByActivitiesScoreSelector from './scoreByActivitiesScoreSelector';
import scoreByAutomaticSelector from './scoreByAutomaticSelector';
import scoreByPassedActivitySelector from './scoreByPassedActivitySelector';
import scoreByPassedQuizzesSelector from './scoreByPassedQuizzesSelector';
import scoreByViewPagesSelector from './scoreByViewPagesSelector';

const config = {
  viewed_pages: scoreByViewPagesSelector,
  activities_score: scoreByActivitiesScoreSelector,
  passed_quiz: scoreByPassedQuizzesSelector,
  passed_activity: scoreByPassedActivitySelector,
  automatic: scoreByAutomaticSelector,
};

function scoreSelector(state) {
  const contentScoreCalculationCriteria = selectorConfig(state, 'contentScoreCalculationCriteria');
  const score = config[contentScoreCalculationCriteria](state);
  if (score === undefined) return undefined;
  return Math.round(score);
}

export default scoreSelector;
