import resolutionWidthReducer from '@auth/lib/src/resolutionWidthReducer';
import currentLangReducer from '@auth/lib/src/features/currentLang/currentLangReducer';
import pageReducer from './pageReducer';
import suspendDataReducer from './suspendDataReducer';
import popupReducer from './popupReducer';
import suspendDataTempReducer from './suspendDataTempReducer';
import { suspendTempKey } from '../selectors/suspendTempSelector';
import { suspendKey } from '../selectors/suspendSelector';
import { pageKey } from '../selectors/pageSelector';
import { popupKey } from '../InteractiveRender/renderPopupSelector';
import { notificationsKey } from '../selectors/notificationsSelector';
import notificationsReducer from './notificationsReducer';
import { sectionToScrollKey } from '../selectors/sectionToScrollSelector';
import sectionToScrollReducer from './sectionToScrollReducer';
import { zoomKey } from '../selectors/zoomSelector';
import zoomReducer from './zoomReducer';

function constReducer(state) {
  return state;
}

function reducer(state = {}, action = {}) {
  return ({
    config: constReducer(state.config),
    css: constReducer(state.css),
    events: constReducer(state.events),
    instances: constReducer(state.instances),
    langs: constReducer(state.langs),
    structure: constReducer(state.structure),
    colors: constReducer(state.colors),
    currentLang: currentLangReducer(state.currentLang, action, state),
    editorResolutionWidth: resolutionWidthReducer(state.editorResolutionWidth, action, state),
    [sectionToScrollKey]: sectionToScrollReducer(state[sectionToScrollKey], action, state),
    [pageKey]: pageReducer(state[pageKey], action, state),
    [popupKey]: popupReducer(state[popupKey], action, state),
    [notificationsKey]: notificationsReducer(state[notificationsKey], action, state),
    [suspendKey]: suspendDataReducer(state[suspendKey], action, state),
    [suspendTempKey]: suspendDataTempReducer(state[suspendTempKey], action, state),
    [zoomKey]: zoomReducer(state[zoomKey], action, state),
  });
}

export default reducer;
