import React, { } from 'react';
import RenderCssVariables from '@auth/components/src/RenderContent/RenderCssVariables';
import RenderPage from './RenderPage';
import RenderPopup from './RenderPopup';
import useUrlHashHandler from '../hooks/useUrlHashHandler';
import RenderPanel from './RenderPanel';
import InteractiveRenderState from './InteractiveRenderState';
import useResolutionStoreUpdater from '../hooks/useResolutionStoreUpdater';
import RenderNotifications from './RenderNotifications';
import MaintainAspectRatio from './MaintainAspectRatio';
import HtmlLangUpdater from './HtmlLangUpdater';
import useCourseExited from './useCourseExited';
import ContentInitDispatcher from './ContentInitDispatcher';
import ContentCompleteDispatcher from './ContentCompleteDispatcher';
import ContentExceedingDispatcher from './ContentExceedingDispatcher';
import FavIconUpdater from './FavIconUpdater';
import RenderCssZoom from './RenderCssZoom';

function InteractiveRender() {
  useUrlHashHandler();
  useResolutionStoreUpdater();
  const courseExited = useCourseExited();
  if (courseExited) return null;
  return (
    <>
      <HtmlLangUpdater />
      <FavIconUpdater />
      <RenderCssVariables />
      <RenderCssZoom />
      <InteractiveRenderState>
        <MaintainAspectRatio>
          <RenderPage />
          <RenderPanel />
          <RenderPopup />
          <RenderNotifications />
        </MaintainAspectRatio>
      </InteractiveRenderState>
      <ContentInitDispatcher />
      <ContentCompleteDispatcher />
      <ContentExceedingDispatcher />
    </>
  );
}

export default React.memo(InteractiveRender);
