import { optionsCorrect } from '../../selectors/checkMultipleChoiceActivitySelector';
import suspendDataSelector from '../../selectors/suspendDataSelector';

function rightAnswersCount(state, activityId) {
  const checked = [0, 1].includes(suspendDataSelector(activityId, 'correct', state));
  if (!checked) return 0;
  return optionsCorrect(state, activityId);
}

export default rightAnswersCount;
