import getCssPropByButtonState from '../interactiveRenderButton/getCssPropByButtonState';

function getChoiceOptionProps(pseudoSelector, id, props) {
  const newValueOptionMarkDisplay = getCssPropByButtonState(pseudoSelector, 'optionMarkDisplay', props);
  const newValueOptionIconMarkDisplay = getCssPropByButtonState(pseudoSelector, 'optionIconMarkDisplay', props);
  const newValueoptionShowRadioButton = getCssPropByButtonState(pseudoSelector, 'optionShowRadioButton', props);

  const idHtmlFor = `htmlFor_${id}`;
  return {
    pseudoSelector,
    htmlFor: idHtmlFor,
    inputId: idHtmlFor,
    optionShowRadioButton: newValueoptionShowRadioButton,
    optionMarkDisplay: newValueOptionMarkDisplay,
    optionIconMarkDisplay: newValueOptionIconMarkDisplay,
  };
}

export default getChoiceOptionProps;
