import interactions2004 from './interactions2004';
import iso8601TimeInterval from './iso8601TimeInterval';

let SCORM = {};
let debug = false;
let sendInteractions = true;
let lmsCompletion = 'unknown';
let completionStatusAlreadySubmitted = false;

const getScore = () => {
  const lmsScore = SCORM.get('cmi.score.raw');
  if (lmsScore === '') return undefined;
  return +lmsScore;
};

const getCompletion = () => {
  lmsCompletion = SCORM.get('cmi.completion_status');
  if (lmsCompletion === 'not attempted' || lmsCompletion === 'unknown') return undefined;
  return lmsCompletion === 'completed';
};

const init = (config) => {
  SCORM = window.pipwerks.SCORM;
  debug = window.pipwerks.debug;
  const {
    scormSendInteractions,
    debugMode,
  } = config;
  debug.isActive = debugMode;
  SCORM.handleExitMode = false;
  SCORM.handleCompletionStatus = false;
  const initSuccess = SCORM.init();
  sendInteractions = scormSendInteractions;
  if (initSuccess) {
    return [
      true,
      getScore(),
      getCompletion(),
    ];
  }
  return [false];
};

const setSuspend = (suspend) => {
  SCORM.set('cmi.suspend_data', JSON.stringify(suspend));
};

const getSuspend = () => {
  const val = SCORM.get('cmi.suspend_data');
  if (!val) return undefined;
  return JSON.parse(val);
};

const setLocation = (location) => {
  SCORM.set('cmi.location', location);
};

const getLocation = () => SCORM.get('cmi.location');

const exit = ({ sessionTime }) => {
  if (!completionStatusAlreadySubmitted) {
    SCORM.set('cmi.completion_status', lmsCompletion);
  }
  SCORM.set('cmi.session_time', iso8601TimeInterval(sessionTime));
  SCORM.save();
  SCORM.quit();
};

const getTotInteractions = () => +(SCORM.get('cmi.interactions._count') || '0');

const sendInteraction = (data) => {
  if (sendInteractions) {
    const interaction = interactions2004(data);
    const prefix = `cmi.interactions.${getTotInteractions()}`;
    interaction.forEach(
      ([key, value]) => {
        SCORM.set(`${prefix}.${key}`, value);
      },
    );
  }
};

const sendMultipleInteractions = (data = []) => {
  if (sendInteractions) {
    data.forEach(
      (interactionData) => {
        sendInteraction(interactionData);
      },
    );
  }
};
const setScore = (score) => {
  SCORM.set('cmi.score.raw', score);
  SCORM.set('cmi.score.min', 0);
  SCORM.set('cmi.score.max', 100);
  SCORM.set('cmi.score.scaled', score / 100);
};

const setCompletion = (completion) => {
  SCORM.set(
    'cmi.completion_status',
    completion ? 'completed' : 'incomplete',
  );
  completionStatusAlreadySubmitted = true;
};

const setSuccessStatus = (success) => {
  SCORM.set(
    'cmi.success_status',
    success ? 'passed' : 'failed',
  );
};

const getName = () => SCORM.get('cmi.learner_name');

const getId = () => SCORM.get('cmi.learner_id');

const scorm2004 = {
  init,
  setSuspend,
  getSuspend,
  setLocation,
  getLocation,
  exit,
  sendInteraction,
  sendMultipleInteractions,
  setScore,
  setCompletion,
  setSuccessStatus,
  getName,
  getId,
};

export default scorm2004;
