import langValidKeys from '../../data/langValidKeys';
import { LANGS } from '../../editor/stateKeys';
import { set } from '../../stateChanger/stateChanger';

export const LANGS_SET_DEFAULT = 'LANGS_SET_DEFAULT';

export const langsSetDefaultSchema = {
  type: {
    type: 'string',
  },
  payload: {
    type: 'object',
    shape: {
      key: {
        type: 'string',
      },
    },
  },
};

export const langsSetDefaultAction = (
  key,
) => ({
  type: LANGS_SET_DEFAULT,
  payload: {
    key,
  },
});

export function setDefaultLang(state, newDefaultKey, mutable, allState) {
  if (!langValidKeys.includes(newDefaultKey)) return state;
  let draftState = state;
  Object.keys(state).forEach(
    (key) => {
      draftState = set(
        draftState,
        key,
        { default: key === newDefaultKey },
        mutable,
        allState,
        LANGS,
      );
    },
  );
  return draftState;
}

function langsReducerSetDefault(state, action, allState) {
  const key = action?.payload?.key;
  const mutable = action?.mutable;
  return setDefaultLang(state, key, mutable, allState);
}

export default langsReducerSetDefault;
