const getMqs = ({ desktop, tablet, mobile }) => ({
  widescreen: `@media (min-width: ${desktop})`,
  tablet: `@media (max-width: ${tablet})`,
  mobile: `@media (max-width: ${mobile})`,
});

function getSelector(pseudo, value) {
  if (!pseudo) { return value.selector; }
  if (realPseudoSelectors.includes(pseudo)) {
    return (value.selector || '').split(',').reduce(
      (m, v) => {
        const str = `:${pseudo}${v}`;
        if (m) { return `${m},${str}`; }
        return str;
      },
      '',
    );
  }
  return (value.selector || '').split(',').reduce(
    (m, v) => {
      const str = `&[data-${pseudo}] ${v}`;
      if (m) { return `${m},${str}`; }
      return str;
    },
    '',
  );
}

function getMqObj(mq, mqs) {
  if (!mq) { return {}; }
  return { mq: mqs[mq] };
}

function ntxCssProps(key, mqConfig = {}) {
  if (originalCssProps[key]) {
    return originalCssProps[key];
  }
  const mqs = getMqs(mqConfig);
  const pseudo = pseudoSelectorStates.find(
    (k) => key.startsWith(k),
  );
  const mq = Object.keys(mqs).find(
    (k) => key.endsWith(`:${k}`),
  );
  const prop = strFirstCharToLowerCase(
    key.replace(pseudo || '', '').replace(`:${mq}` || '', ''),
  );
  const value = originalCssProps[prop];
  if (!value) { return undefined; }
  const selector = getSelector(pseudo, value);
  const objMq = getMqObj(mq, mqs);
  if (!(pseudo || mq)) return undefined;
  const mqWitColon = mq ? `:${mq}` : '';
  const ret = {
    ...originalCssProps[prop],
    ...objMq,
    selector,
  };
  if (ret.family) { ret.family = getPropName(ret.family, pseudo, mqWitColon); }
  if (ret.weight) { ret.weight = getPropName(ret.weight, pseudo, mqWitColon); }
  if (ret.style) { ret.style = getPropName(ret.style, pseudo, mqWitColon); }
  return ret;
}

export default ntxCssProps;
