import selectorVirtualPropEnabled from '@auth/lib/src/selectors/selectorVirtualPropEnabled';
import { useSelector } from 'react-redux';

function useVirtualProp(id) {
  const enabledVirtual = useSelector(
    (state) => selectorVirtualPropEnabled(state, id),
  );
  if (!enabledVirtual) return {};
  return { enable_virtual: enabledVirtual };
}

export default useVirtualProp;
