import React from 'react';
import PropTypes from 'prop-types';
import './PageContent.css';
import useInteractiveProps from '../useInteractiveProps';
import getClassPositionArrows from './getClassPositionArrows';

function PageContent(props) {
  const {
    pageContentArrowsDisplay,
    pageContentArrowsPosition,
    pageContentMenuButtonDisplay,
    className,
    children = null,
    Button = null,
  } = props;
  const classArrows = getClassPositionArrows(pageContentArrowsPosition);
  const showArrows = pageContentArrowsDisplay === 'true';
  const showMenuButton = pageContentMenuButtonDisplay === 'true';
  const totButtons = React.Children.toArray(Button);
  const arrowsButton = totButtons.filter(
    (child) => child.key.endsWith('_prevButton') || child.key.endsWith('_nextButton'),
  );
  const menuButton = totButtons.filter(
    (child) => child.key.endsWith('_menuButton'),
  );
  return (
    <article
      className={`${className} ${classArrows}`}
      {...useInteractiveProps(props)}
    >
      {children}
      {
        showArrows && React.Children.map(arrowsButton, (ButtonItem, buttonIndex) => (
          <div
            className={`pageContent-arrow pageContent-arrow-${buttonIndex}`}
          >
            {ButtonItem}
          </div>
        ))
      }
      {
        showMenuButton && React.Children.map(menuButton, (ButtonItem) => (
          <div
            className="pageContent-menu-button"
          >
            {ButtonItem}
          </div>
        ))
      }
    </article>
  );
}

PageContent.propTypes = {
  children: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]),
  Button: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]),
  pageContentArrowsDisplay: PropTypes.string,
  pageContentArrowsPosition: PropTypes.string,
  pageContentMenuButtonDisplay: PropTypes.string,
  className: PropTypes.string,
  mantainAspectRatio: PropTypes.string,
};

PageContent.defaultProps = {
  children: null,
  Button: null,
  pageContentArrowsDisplay: 'false',
  pageContentArrowsPosition: '',
  pageContentMenuButtonDisplay: 'false',
  className: '',
  mantainAspectRatio: 'none',
};

export default PageContent;
