import activityTypes from '@auth/lib/src/data/activityTypes';
import selectorProp from '@auth/lib/src/selectors/selectorProp';
import selectorRealType from '@auth/lib/src/selectors/selectorRealType';

function quizActivitiesSelector(state, quiz) {
  const childs = selectorProp(state, quiz, 'childs') || [];
  return childs.filter(
    (child) => activityTypes.includes(selectorRealType(state, child)),
  );
}

export default quizActivitiesSelector;
