import { useContext } from 'react';
import useSuspendOperations, { unsetOperation } from './useSuspendOperations';
import ActivityContext from '../InteractiveRender/customComps/ActivityContext';
import useGetSuspendData from './useGetSuspendData';

function useClearResponse() {
  const activityId = useContext(ActivityContext);
  const suspendOperations = useSuspendOperations();
  const correct = useGetSuspendData(activityId, 'correct');
  const checkActivityAnswering = !(correct === 1 || correct === 0);
  return () => {
    if (activityId && checkActivityAnswering) {
      suspendOperations([unsetOperation(activityId, 'response')]);
    }
  };
}

export default useClearResponse;
