import getQueryParam from '@auth/shared/src/getQueryParam';

function getDefaultLang(data = {}) {
  const dataKeys = Object.keys(data);
  const param = getQueryParam('lang');
  if (dataKeys.includes(param)) return param;
  const defaultInData = dataKeys.find(
    (key) => data[key]?.default,
  );
  if (defaultInData) return defaultInData;
  return dataKeys[0];
}

export default getDefaultLang;
