import patchUrlResources from '@auth/components/src/RenderContent/patchUrlResources';
import selectorConfig from '@auth/lib/src/selectors/selectorConfig';
import useAppData from '@auth/shared/src/hooks/useAppData';
import { useEffect } from 'react';
import { useSelector } from 'react-redux';

function FavIconUpdater() {
  const faviconValue = useSelector(
    (state) => selectorConfig(state, 'favicon'),
  );
  const { unitId } = useAppData();
  useEffect(() => {
    if (faviconValue) {
      const { id, filename } = JSON.parse(faviconValue);
      const favImg = patchUrlResources(id, filename, unitId);
      const headTitle = document.querySelector('head');
      const setFavicon = document.createElement('link');
      setFavicon.setAttribute('rel', 'shortcut icon');
      setFavicon.setAttribute('href', favImg);
      headTitle.appendChild(setFavicon);
    }
  }, [faviconValue, unitId]);
  return null;
}

FavIconUpdater.propTypes = {};

FavIconUpdater.defaultProps = {};

export default FavIconUpdater;
