import activityTypes from '../data/activityTypes';
import selectorClosestTypes from './selectorClosestTypes';
import selectorRealType from './selectorRealType';
import selectorType from './selectorType';

function selectorConditionType(state, id, instanceId, masterContainerId) {
  if (id === 'currentActivity') {
    const act = selectorClosestTypes(state, masterContainerId || instanceId, activityTypes);
    return selectorType(state, act);
  }
  if (id === 'currentQuiz') return 'Quiz';
  if (id === 'currentPage') return 'PageContent';
  if (id === 'currentComposite') return 'Composite';
  return selectorRealType(state, id);
}
export default selectorConditionType;
