import React from 'react';
import PropTypes from 'prop-types';
import getCompByType from '@auth/components/src/getCompByType';
import { ClassNames, Global } from '@auth/shared/src/utils/generateClassReact';
import useGetCss from '@auth/components/src/RenderContent/useGetCss';
import useMantainAspectRatioPatchVhVw from '@auth/components/src/RenderContent/useMantainAspectRatioPatchVhVw';
import convertForEmotion from '@auth/components/src/RenderContent/convertForEmotion';
import useMqConfig from '@auth/editor/src/hooks/useMqConfig';
import useAppData from '@auth/shared/src/hooks/useAppData';
import InteractiveRenderActivityLikert from './customComps/InteractiveRenderActivityLikert';
import InteractiveRenderActivityMultipleChoice from './customComps/InteractiveRenderActivityMultipleChoice';
import InteractiveRenderActivitySequence from './customComps/InteractiveRenderActivitySequence';
import InteractiveRenderActivitySingleChoice from './customComps/InteractiveRenderActivitySingleChoice';
import InteractiveRenderAudio from './customComps/InteractiveRenderAudio';
import InteractiveRenderButton from './customComps/InteractiveRenderButton';
import InteractiveRenderComposite from './customComps/InteractiveRenderComposite';
import InteractiveRenderLauncher from './customComps/InteractiveRenderLauncher';
import InteractiveRenderLikertOption from './customComps/InteractiveRenderLikertOption';
import InteractiveRenderMenuList from './customComps/InteractiveRenderMenuList';
import InteractiveRenderMenuListItem from './customComps/InteractiveRenderMenuListItem';
import InteractiveRenderMultipleChoiceOption from './customComps/InteractiveRenderMultipleChoiceOption';
import InteractiveRenderPageContent from './customComps/InteractiveRenderPageContent';
import InteractiveRenderPanel from './customComps/InteractiveRenderPanel';
import InteractiveRenderPopup from './customComps/InteractiveRenderPopup';
import InteractiveRenderProgressBar from './customComps/InteractiveRenderProgressBar';
import InteractiveRenderQuiz from './customComps/InteractiveRenderQuiz';
import InteractiveRenderSingleChoiceGroupOption from './customComps/InteractiveRenderSingleChoiceGroupOption';
import InteractiveRenderSingleChoiceOption from './customComps/InteractiveRenderSingleChoiceOption';
import InteractiveRenderText from './customComps/InteractiveRenderText';
import InteractiveRenderVideo from './customComps/InteractiveRenderVideo';
import useNtxCssAnimations from '../hooks/useNtxCssAnimations';
import useVisibility from '../hooks/useVisibility';
import InteractiveRenderMultipleChoiceGroupOption from './customComps/InteractiveRenderMultipleChoiceGroupOption';
import InteractiveRenderCompositeItem from './customComps/InteractiveRenderCompositeItem';
import InteractiveRenderActivityTextInput from './customComps/InteractiveRenderActivityTextInput';
import InteractiveRenderTextInputOption from './customComps/InteractiveRenderTextInputOption';
import InteractiveRenderHotspot from './customComps/InteractiveRenderHotspot';
import InteractiveRenderImage from './customComps/InteractiveRenderImage';
import useVirtualProp from '../hooks/useVirtualProp';
import InteractiveRenderActivityArrows from './customComps/InteractiveRenderActivityArrows';
import InteractiveRenderArrowSource from './customComps/InteractiveRenderArrowSource';
import InteractiveRenderArrowTarget from './customComps/InteractiveRenderArrowTarget';
import InteractiveRenderArrowsGroupOption from './customComps/InteractiveRenderArrowsGroupOption';
import InteractiveRenderSequenceGroupOption from './customComps/InteractiveRenderSequenceGroupOption';
import InteractiveRenderSequenceOption from './customComps/InteractiveRenderSequenceOption';
import InteractiveRenderArea from './customComps/InteractiveRenderArea';
import InteractiveRenderNotification from './customComps/InteractiveRenderNotification';
import useRequirementsDispatchEvent from '../hooks/useRequirementsDispatchEvent';
import InteractiveRenderFlipCard from './customComps/InteractiveRenderFlipCard';

const customComps = {
  ActivityArrows: InteractiveRenderActivityArrows,
  ActivityLikert: InteractiveRenderActivityLikert,
  ActivityMultipleChoice: InteractiveRenderActivityMultipleChoice,
  ActivitySequence: InteractiveRenderActivitySequence,
  ActivitySingleChoice: InteractiveRenderActivitySingleChoice,
  ActivityTextInput: InteractiveRenderActivityTextInput,
  Area: InteractiveRenderArea,
  ArrowsGroupOption: InteractiveRenderArrowsGroupOption,
  ArrowSource: InteractiveRenderArrowSource,
  ArrowTarget: InteractiveRenderArrowTarget,
  Audio: InteractiveRenderAudio,
  Button: InteractiveRenderButton,
  Composite: InteractiveRenderComposite,
  CompositeItem: InteractiveRenderCompositeItem,
  FlipCard: InteractiveRenderFlipCard,
  Hotspot: InteractiveRenderHotspot,
  Image: InteractiveRenderImage,
  Launcher: InteractiveRenderLauncher,
  LikertOption: InteractiveRenderLikertOption,
  MenuList: InteractiveRenderMenuList,
  MenuListItem: InteractiveRenderMenuListItem,
  MultipleChoiceGroupOption: InteractiveRenderMultipleChoiceGroupOption,
  MultipleChoiceOption: InteractiveRenderMultipleChoiceOption,
  Notification: InteractiveRenderNotification,
  PageContent: InteractiveRenderPageContent,
  Panel: InteractiveRenderPanel,
  Popup: InteractiveRenderPopup,
  ProgressBar: InteractiveRenderProgressBar,
  Quiz: InteractiveRenderQuiz,
  SequenceGroupOption: InteractiveRenderSequenceGroupOption,
  SequenceOption: InteractiveRenderSequenceOption,
  SingleChoiceGroupOption: InteractiveRenderSingleChoiceGroupOption,
  SingleChoiceOption: InteractiveRenderSingleChoiceOption,
  Text: InteractiveRenderText,
  TextInputOption: InteractiveRenderTextInputOption,
  Video: InteractiveRenderVideo,
};

function InteractiveRenderComp(props) {
  const { type, id } = props;
  const cssData = useGetCss(id, props);
  const mqConfig = useMqConfig();
  const { unitId } = useAppData();
  const cssWithPatchVhVw = useMantainAspectRatioPatchVhVw(cssData);
  const {
    classData,
    globalData,
  } = convertForEmotion(id, cssWithPatchVhVw, mqConfig, type, unitId);

  const [
    classAnimations = '',
    propsAnimations,
  ] = useNtxCssAnimations(props);
  const virtualProp = useVirtualProp(id);
  useRequirementsDispatchEvent(id);
  const render = useVisibility(props);
  if (!render) return null;
  const Comp = customComps[type] || getCompByType(type);

  return (
    <>
      {!!globalData && (
        <Global
          styles={{
            [`.ntx-global-${type}`]: globalData,
          }}
        />
      )}
      <ClassNames>
        {({ css, cx }) => (
          <Comp
            {...props}
            {...propsAnimations}
            {...virtualProp}
            className={cx(css(classData), classAnimations)}
          />
        )}
      </ClassNames>
    </>
  );
}

InteractiveRenderComp.propTypes = {
  id: PropTypes.string.isRequired,
  type: PropTypes.string.isRequired,
};

InteractiveRenderComp.defaultProps = {
};

export default InteractiveRenderComp;
