import changeDisplay from './changeDisplay';
import getAreasInRows from './getAreasInRows';

function automaticOptions({ cssDisplay, type, childs }) {
  return {
    display: changeDisplay(cssDisplay, type),
    flexDirection: 'column',
    displayChildWrapper: 'grid',
    heightChildWrapper: '100%',
    widthChildWrapper: '100%',
    gridTemplateAreasWrapper: getAreasInRows(childs),
  };
}

export default automaticOptions;
