export const parseUrlOnlineResources = (strProp) => {
  if (!strProp) return {};
  return JSON.parse(strProp);
};

const getUrlOnlineResources = (strProp) => {
  const { id, origin } = parseUrlOnlineResources(strProp);
  if (origin === 'youtube') {
    return `https://www.youtube.com/watch?v=${id}`;
  }
  if (origin === 'vimeo') {
    return `https://vimeo.com/${id}`;
  }
  if (origin === 'web') {
    return id;
  }
  return null;
};

export default getUrlOnlineResources;
