import selectorClosestType from '@auth/lib/src/selectors/selectorClosestType';
import quizStatusSelector from './quizStatusSelector';

const quizDescendantStatusSelector = (state, descendantId) => {
  const quiz = selectorClosestType(state, descendantId, 'Quiz');
  if (!quiz) return null;
  return quizStatusSelector(state, quiz);
};

export default quizDescendantStatusSelector;
