import selectorCompIdsByType from '@auth/lib/src/selectors/selectorCompIdsByType';
import selectorProp from '@auth/lib/src/selectors/selectorProp';

function evaluableQuizzesSelector(state) {
  const quizzes = selectorCompIdsByType(state, 'Quiz');
  return quizzes.filter(
    (quiz) => selectorProp(state, quiz, 'evaluable'),
  );
}

export default evaluableQuizzesSelector;
