import selectorType from '@auth/lib/src/selectors/selectorType';

export const popupKey = 'popup';

function renderPopupSelector(state) {
  const popupId = state[popupKey];
  if (!popupId) return null;
  if (selectorType(state, popupId) !== 'Popup') return null;
  return popupId;
}

export default renderPopupSelector;
