import useSetSuspendData from './useSetSuspendData';

const usePrevSlide = () => {
  const setSuspend = useSetSuspendData();
  return (componentId, childs, currentChild) => {
    if (childs.length) {
      const newPosition = Math.max(childs.indexOf(currentChild) - 1, 0);
      const prev = childs[newPosition];
      if (prev !== currentChild) {
        setSuspend(componentId, 'visibleChilds', [prev]);
      }
    }
  };
};

export default usePrevSlide;
