import { useInView } from 'react-intersection-observer';
import { shallowEqual, useSelector } from 'react-redux';
import generateClass from '@auth/shared/src/utils/generateClass';
import selectorRealType from '@auth/lib/src/selectors/selectorRealType';

function fade(props) {
  const {
    transitionInDuration = '300ms', transitionInDelay = '0s', transitionInTimingFunction = 'ease',
  } = props;
  return generateClass({
    opacity: 0,
    '&.animateIn': {
      transition: `opacity ${transitionInDuration} ${transitionInTimingFunction} ${transitionInDelay}`,
      opacity: 1,
    },
  });
}

function slideAndFade(props) {
  const {
    transitionInDuration = '300ms', transitionInDelay = '0s', transitionInTimingFunction = 'ease', transitionInSlide = 'up',
  } = props;
  const animationBySlide = {
    up: () => generateClass({
      opacity: 0,
      transform: 'translate3d(0, 21px, 0)',
      '&.animateIn': {
        transition: `
            opacity ${transitionInDuration} ${transitionInTimingFunction} ${transitionInDelay},
            transform ${transitionInDuration} ${transitionInTimingFunction} ${transitionInDelay}
          `,
        opacity: 1,
        transform: 'translate3d(0, 0, 0)',
      },
    }),
    down: () => generateClass({
      opacity: 0,
      transform: 'translate3d(0, -21px, 0)',
      '&.animateIn': {
        transition: `
            opacity ${transitionInDuration} ${transitionInTimingFunction} ${transitionInDelay},
            transform ${transitionInDuration} ${transitionInTimingFunction} ${transitionInDelay}
          `,
        opacity: 1,
        transform: 'translate3d(0, 0, 0)',
      },
    }),
    right: () => generateClass({
      opacity: 0,
      transform: 'translate3d(-42px, 0, 0)',
      '&.animateIn': {
        transition: `
            opacity ${transitionInDuration} ${transitionInTimingFunction} ${transitionInDelay},
            transform ${transitionInDuration} ${transitionInTimingFunction} ${transitionInDelay}
          `,
        opacity: 1,
        transform: 'translate3d(0, 0, 0)',
      },
    }),
    left: () => generateClass({
      opacity: 0,
      transform: 'translate3d(42px, 0, 0)',
      '&.animateIn': {
        transition: `
            opacity ${transitionInDuration} ${transitionInTimingFunction} ${transitionInDelay},
            transform ${transitionInDuration} ${transitionInTimingFunction} ${transitionInDelay}
          `,
        opacity: 1,
        transform: 'translate3d(0, 0, 0)',
      },
    }),
  };
  const func = animationBySlide[transitionInSlide];
  if (func) {
    return func();
  }
  return '';
}

const config = {
  fade,
  slideAndFade,
};

function getTransitionClassName(props) {
  const { transitionTypeIn } = props;
  const func = config[transitionTypeIn];
  if (func) { return func(props); }
  return '';
}

const invalidTypes = ['Popup', 'Panel', 'Notification'];

function useNtxCssAnimations(props) {
  const {
    transitionTypeIn, id,
  } = props;
  const { ref, inView } = useInView({
    triggerOnce: true,
  });
  const realType = useSelector(
    (state) => selectorRealType(state, id),
    shallowEqual,
  );
  if (invalidTypes.includes(realType) || !transitionTypeIn || transitionTypeIn === 'none') { return ['', null]; }
  const transitionClassName = getTransitionClassName(props);
  const className = inView
    ? `animateIn ${transitionClassName}`
    : transitionClassName;
  return [
    className,
    {
      componentRef: ref,
    },
  ];
}

export default useNtxCssAnimations;
