import activityTypes from '@auth/lib/src/data/activityTypes';
import selectorClosestType from '@auth/lib/src/selectors/selectorClosestType';
import selectorCompIdsByTypes from '@auth/lib/src/selectors/selectorCompIdsByTypes';
import selectorDescendantsIdsByTypesFromIds from '@auth/lib/src/selectors/selectorDescendantsIdsByTypesFromIds';
import selectorProp from '@auth/lib/src/selectors/selectorProp';

function evaluableActivitiesSelector(state, customStructure) {
  const activities = customStructure
    ? selectorDescendantsIdsByTypesFromIds(state, customStructure, activityTypes)
    : selectorCompIdsByTypes(state, activityTypes);
  return activities.filter(
    (act) => {
      const myQuiz = selectorClosestType(state, act, 'Quiz');
      return selectorProp(state, myQuiz, 'evaluable');
    },
  );
}

export default evaluableActivitiesSelector;
