import selectorNextPageId from '@auth/lib/src/selectors/selectorNextPageId';
import { thunkSetPage } from '../hooks/useSetCurrentPage';

const goNextPageThunk = () => (dispatch, getState) => {
  const state = getState();
  const nextPage = selectorNextPageId(state);
  if (nextPage) dispatch(thunkSetPage(nextPage));
};

export default goNextPageThunk;
