import selectorRealType from '@auth/lib/src/selectors/selectorRealType';
import selectorType from '@auth/lib/src/selectors/selectorType';
import getGridId from '../getChildAreas/getGridId';

function getGridIdByType(id, state, compProps = {}) {
  const { parentId } = compProps;
  const type = selectorRealType(state, id);
  const parentType = selectorType(state, parentId);
  const patchId = parentType === 'MasterContainer'
    ? parentId
    : id;
  if (type === 'Popup') return 'popup';
  if (type === 'CompositeItem') {
    const { compositeNavigationType } = compProps;
    if (compositeNavigationType !== 'accordion') {
      return 'composite';
    }
  }
  return getGridId(patchId);
}

export default getGridIdByType;
