import getAreasInAutoColumns from './getAreasInAutoColumns';
import getAreasInRows from './getAreasInRows';

function getGridTemplateInAutoColumns(gridTemplateColumns, childs, display) {
  const itemsPerColumn = gridTemplateColumns.split(' ').length;
  const inRows = getAreasInRows(childs);
  const inCols = getAreasInAutoColumns(childs, itemsPerColumn);
  const objGridTemplateColumns = display === 'vertical' ? {} : {
    gridTemplateColumns,
  };
  return {
    gridTemplateAreas: display === 'vertical' ? inRows : inCols,
    ...objGridTemplateColumns,
  };
}

export default getGridTemplateInAutoColumns;
