function ensureHaveDefault(styles, defaultStyles) {
  const stylesKeys = Object.keys(styles);
  const defaultStylesKeys = Object.keys(defaultStyles);
  const notFound = defaultStylesKeys.filter(
    (style) => !stylesKeys.includes(style),
  );
  return notFound.reduce(
    (memo, k) => ({
      ...memo,
      [k]: { ...defaultStyles[k] },
    }),
    styles,
  );
}

export default ensureHaveDefault;
