import getDescriptionByPropName from './getDescriptionByPropName';

const invalidTypesToSearchInto = [
  'ArrowsGroupOption',
  'LikertGroupOption',
  'MultipleChoiceGroupOption',
  'SequenceGroupOption',
  'SingleChoiceGroupOption',
];

function getWording(state, id) {
  return getDescriptionByPropName(state, id, 'wording', invalidTypesToSearchInto);
}

export default getWording;
