import { useDispatch } from 'react-redux';
import selectorAllDescendantIdsByType from '@auth/lib/src/selectors/selectorAllDescendantIdsByType';
import pageSelector from '../../../selectors/pageSelector';
import useCheckQuizzes from '../../../hooks/useCheckQuizzes';

function useClickCheckQuizzesOnCurrentPage() {
  const dispatchThunk = useDispatch();
  const checkQuizzes = useCheckQuizzes();
  return () => {
    dispatchThunk(
      (dispatch, getState) => {
        const state = getState();
        const currentPage = pageSelector(state);
        const quizzes = selectorAllDescendantIdsByType(currentPage, 'Quiz', state);
        checkQuizzes(quizzes);
      },
    );
  };
}

export default useClickCheckQuizzesOnCurrentPage;
