import React, { forwardRef } from 'react';
import PropTypes from 'prop-types';
import useInteractiveProps from '../useInteractiveProps';

const ArrowsGroupOption = forwardRef((props, ref) => {
  const {
    className,
    ArrowSource,
    ArrowTarget,
    children,
  } = props;
  return (
    <div
      ref={ref}
      className={className}
      {...useInteractiveProps(props)}
    >
      <div style={{
        justifySelf: 'stretch',
        display: 'flex',
        justifyContent: 'center',
        flexDirection: 'column',
      }}
      >
        {ArrowSource}
      </div>
      <div style={{
        justifySelf: 'stretch',
        display: 'flex',
        justifyContent: 'center',
        flexDirection: 'column',
      }}
      >
        {ArrowTarget}
      </div>
      <div style={{ position: 'relative' }}>
        {children}
      </div>
    </div>
  );
});

ArrowsGroupOption.propTypes = {
  className: PropTypes.string,
  ArrowSource: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]),
  ArrowTarget: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]),
  children: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]),
};

ArrowsGroupOption.defaultProps = {
  ArrowSource: null,
  ArrowTarget: null,
  className: '',
  children: null,
};

export default ArrowsGroupOption;
