import mqStates from '@auth/lib/src/data/mqStates';
import pseudoSelectorStates from '@auth/lib/src/data/pseudoSelectorStates';
import patchUrlResources from '../patchUrlResources';
import getPropByRelated from './getPropByRelated';
import getRelatedProp from './getRelatedProp';
import propValue from './propValue';

function patchBgImage(css, unitId) {
  const bgProps = ['backgroundImageGradient', 'backgroundImageUrl'];
  const cssWithoutBGProps = Object.entries(css).reduce(
    (memo, [k, v]) => {
      const related = getRelatedProp(k);
      if (bgProps.includes(related)) { return memo; }
      return {
        ...memo,
        [k]: v,
      };
    },
    {},
  );
  const patched = {};
  ['', ...mqStates].forEach(
    (mq) => {
      ['', ...pseudoSelectorStates].forEach(
        (pseudo) => {
          const gradientPropName = getPropByRelated('backgroundImageGradient', pseudo, mq);
          const urlPropName = getPropByRelated('backgroundImageUrl', pseudo, mq);
          const bgImagePropName = getPropByRelated('backgroundImage', pseudo, mq);
          const backgroundImageGradient = propValue(gradientPropName, css);
          const backgroundImageUrl = propValue(urlPropName, css);
          if ((backgroundImageGradient || backgroundImageUrl)) {
            const patchUrl = (() => {
              if (!backgroundImageUrl) { return ''; }
              if (backgroundImageUrl === 'none') { return 'none'; }
              const { id, filename } = JSON.parse(backgroundImageUrl);
              return `url(${patchUrlResources(id, filename, unitId)})`;
            })();
            const backgroundImage = (() => {
              if (backgroundImageGradient && backgroundImageUrl) { return `${backgroundImageGradient}, ${patchUrl}`; }
              if (backgroundImageGradient) { return backgroundImageGradient; }
              return patchUrl;
            })();
            patched[bgImagePropName] = backgroundImage;
          }
        },
      );
    },
  );
  return {
    ...cssWithoutBGProps,
    ...patched,
  };
}

export default patchBgImage;
