import selectorClosestType from '@auth/lib/src/selectors/selectorClosestType';
import quizCorrectActivitiesCount from './quizCorrectActivitiesCount';
import quizTotActivitiesCount from './quizTotActivitiesCount';

export default function quizCorrectActivitiesPercentSelector(state, descendantId) {
  const quiz = selectorClosestType(state, descendantId, 'Quiz');
  if (!quiz) return 0;
  const correct = quizCorrectActivitiesCount(state, quiz);
  const totActivities = quizTotActivitiesCount(state, quiz);
  return (100 * correct) / totActivities;
}
