import selectorActivityMaxScore from '@auth/components/src/RenderContent/selectorActivityMaxScore';
import activityScoreSelector from './activityScoreSelector';
import evaluableActivitiesSelector from './evaluableActivitiesSelector';
import suspendDataSelector from './suspendDataSelector';

function scoreByActivitiesScoreSelector(state) {
  const scorableActivies = evaluableActivitiesSelector(state);
  if (!scorableActivies.length) return undefined;
  const hasSomeActivityChecked = scorableActivies.find(
    (act) => [0, 1].includes(suspendDataSelector(act, 'correct', state)),
  );
  if (!hasSomeActivityChecked) return undefined;
  const totActivitiesScore = scorableActivies.reduce(
    (memo, act) => memo + activityScoreSelector(state, act),
    0,
  );
  const maxActivitiesScore = scorableActivies.reduce(
    (memo, act) => memo + selectorActivityMaxScore(state, act),
    0,
  );
  if (!maxActivitiesScore) {
    return 100;
  }
  return Math.round((totActivitiesScore / maxActivitiesScore) * 100);
}

export default scoreByActivitiesScoreSelector;
