import selectorProp from '@auth/lib/src/selectors/selectorProp';
import selectorStateStructure from '@auth/lib/src/selectors/selectorStateStructure';
import pageSelector from './pageSelector';

const prevPageTitleSelector = (state) => {
  const id = pageSelector(state);
  const structure = selectorStateStructure(state) || [];
  const pos = structure.indexOf(id);
  if (pos === -1 || pos === 0) return '';
  const prevId = structure[pos - 1];
  return selectorProp(state, prevId, 'title');
};

export default prevPageTitleSelector;
