import selectorChilds from './selectorChilds';
import selectorProp from './selectorProp';

const selectorMasterContainerIdGetChildId = (state, id) => {
  const type = selectorProp(state, id, 'type');
  if (type !== 'MasterContainer') return id;
  const childs = selectorChilds(state, id);
  return childs[0];
};

export default selectorMasterContainerIdGetChildId;
