import { useContext } from 'react';
import PropTypes from 'prop-types';
import Button from '@auth/components/src/Button/Button';
import VisbleChildsContext from '../VisbleChildsContext';
import useNextSlide from '../../../hooks/useNextSlide';

function useClickNextSlide() {
  const {
    componentId,
    visibleChild,
    childs = [],
  } = useContext(VisbleChildsContext) || {};
  const goNextSlide = useNextSlide();
  return () => {
    goNextSlide(componentId, childs, visibleChild);
  };
}

useClickNextSlide.propTypes = {
  id: PropTypes.string.isRequired,
  Comp: PropTypes.elementType,
  onClick: PropTypes.func,
};

useClickNextSlide.defaultProps = {
  Comp: Button,
  onClick: () => null,
};

export default useClickNextSlide;
