import mergeCssData from './mergeCssData';
import addInheritValuesOnEnabled from './addInheritValuesOnEnabled';
import addValuesOnDisabled from './addValuesOnDisabled';
import deleteDisabledProps from './deleteDisabledProps';
import patchAlignContentColumn from './patchAlignContentColumn';
import patchBgImage from './patchBgImage';
import patchDisplay from './patchDisplay';
import patchZIndex from './patchZIndex';
import patchStretch from './patchStretch';
import patchAspectRatio from './patchAspectRatio';

function patchGridAutoRows(data) {
  return { ...data, gridAutoRows: 'min-content' };
}

function transform({
  cssDataCustom,
  cssDataInstance,
  cssDataStyle,
  preserveAspectRatio,
  type,
  unitId,
}) {
  const data = mergeCssData(cssDataInstance, cssDataStyle, cssDataCustom);
  const patchedAspectRatio = patchAspectRatio(preserveAspectRatio, data);
  const patchedStrech = patchStretch(patchedAspectRatio);
  const withInheritData = addInheritValuesOnEnabled(patchedStrech);
  const withDisabledData = addValuesOnDisabled(withInheritData);
  const withoutDisabledProps = deleteDisabledProps(withDisabledData);
  const patchedBgImage = patchBgImage(withoutDisabledProps, unitId);
  const patchedZIndex = patchZIndex(patchedBgImage);
  const patchedAlignContentColumn = patchAlignContentColumn(patchedZIndex, type);
  const patchedDisplay = patchDisplay(patchedAlignContentColumn, type);
  return patchGridAutoRows(patchedDisplay);
}

export default transform;
