import { useContext } from 'react';
import ActivityContext from '../InteractiveRender/customComps/ActivityContext';
import useSuspendOperations, { unsetOperation } from './useSuspendOperations';

export function getRetryActivityOperations(activityId, resetResponse) {
  const operations = [unsetOperation(activityId, 'correct')];
  if (resetResponse) {
    operations.push(unsetOperation(activityId, 'response'));
  }
  return operations;
}

function useRetryActivity(resetResponse = true) {
  const activityId = useContext(ActivityContext);
  const suspendOperations = useSuspendOperations();
  return () => {
    if (activityId) {
      suspendOperations(getRetryActivityOperations(activityId, resetResponse));
    }
  };
}

export default useRetryActivity;
