import activityTypes from '@auth/lib/src/data/activityTypes';
import selectorChildsByTypes from '@auth/lib/src/selectors/selectorChildsByTypes';
import selectorActivityMaxScore from './selectorActivityMaxScore';

export default function selectorQuizMaxScore(state, id) {
  const activities = selectorChildsByTypes(state, id, activityTypes);
  return activities.reduce(
    (memo, activity) => memo + selectorActivityMaxScore(state, activity),
    0,
  );
}
