import {
  ACTIVITY_FAILED, ACTIVITY_PASSED, QUIZ_FAILED, QUIZ_PASSED,
} from '@auth/lib/src/events/triggers';
import { useDispatch } from 'react-redux';
import quizActivitiesSelector from '../selectors/quizActivitiesSelector';
import quizDescendantStatusSelector from '../selectors/quizDescendantStatusSelector';
import suspendDataSelector from '../selectors/suspendDataSelector';
import useDispatchEvent from './useDispatchEvent';

function getCheckActivityEventType(state, activityId) {
  const correct = suspendDataSelector(activityId, 'correct', state);
  if (correct === 1) return ACTIVITY_PASSED;
  if (correct === 0) return ACTIVITY_FAILED;
  return null;
}

function useCheckQuizEvents() {
  const dispatchHook = useDispatch();
  const dispatchEvent = useDispatchEvent();
  return (id) => dispatchHook(
    (dispatch, getState) => {
      const state = getState();
      const activities = quizActivitiesSelector(state, id);
      activities.forEach(
        (activityId) => {
          const type = getCheckActivityEventType(state, activityId);
          if (type) dispatchEvent(type, activityId);
        },
      );
      const status = quizDescendantStatusSelector(state, id);
      if (status === 'failed') {
        dispatchEvent(QUIZ_FAILED, id);
      }
      if (status === 'passed') {
        dispatchEvent(QUIZ_PASSED, id);
      }
    },
  );
}

export default useCheckQuizEvents;
