function getPseudoSelector(asking, activityFinished, optionSelected, optionCorrect) {
  if (asking) {
    if (optionSelected) { return 'askingchosen'; }
    return '';
  }

  if (optionSelected) {
    if (optionCorrect === 'true') { return 'chosencorrect'; }
    return 'chosenincorrect';
  }

  if (activityFinished) {
    if (optionCorrect === 'true') { return 'notchosenincorrect'; }
    return 'notchosencorrect';
  }
  return '';
}

export default getPseudoSelector;
