import useEditorStore from './useEditorStore';

function getCurrentButtonState(state, id) {
  const { currentTag, buttonState } = state;
  if (id === currentTag) return buttonState;
  return '';
}

export function getButtonState(id) {
  const state = useEditorStore.getState();
  return getCurrentButtonState(state, id);
}

function useGetButtonState(id) {
  return useEditorStore(
    (state) => getCurrentButtonState(state, id),
  );
}

export default useGetButtonState;
