import PropTypes from 'prop-types';
import Button from '@auth/components/src/Button/Button';
import nullFunc from '@auth/components/src/nullFunc';
import useSetCurrentPage from '../../../hooks/useSetCurrentPage';

function useClickGotoPage() {
  const setPage = useSetCurrentPage();
  return ({ targetPage, targetSection }) => {
    setPage(targetPage, targetSection);
  };
}

useClickGotoPage.propTypes = {
  targetPage: PropTypes.string,
  targetSection: PropTypes.string,
  Comp: PropTypes.elementType,
  onClick: PropTypes.func,
};

useClickGotoPage.defaultProps = {
  targetPage: '',
  targetSection: '',
  Comp: Button,
  onClick: nullFunc,
};

export default useClickGotoPage;
