import { createSelector } from 'reselect';
import { useSelector } from 'react-redux';
import { useMemo } from 'react';
import selectorStateCss from '@auth/lib/src/selectors/selectorStateCss';
import selectorStateInstances from '@auth/lib/src/selectors/selectorStateInstances';
import useGetButtonState from '@auth/editor/src/hooks/useGetButtonState';
import selectorStateEditorResolutionWidth from '@auth/lib/src/selectors/selectorStateEditorResolutionWidth';
import selectorConfig from '@auth/lib/src/selectors/selectorConfig';
import selectorType from '@auth/lib/src/selectors/selectorType';
import areArraysEqual from '@auth/shared/src/utils/areArraysEqual';
import selectorCssPropsToProps from './selectorCssPropsToProps';

const arrayProps = ['ascendants', 'childs', 'auxComps'];

function isEqualInstanceData(a, b) {
  const aKeys = Object.keys(a);
  const bKeys = Object.keys(b);
  if (aKeys.length !== bKeys.length) return false;
  const diferent = aKeys.find(
    (k) => {
      if (arrayProps.includes[k]) return !areArraysEqual(a[k], b[k]);
      return a[k] !== b[k];
    },
  );
  return !diferent;
}

const instanceSelector = (id) => createSelector(
  [
    selectorStateCss,
  ],
  (css) => css[id],
);

const styleSelector = (id) => createSelector(
  [
    (state) => {
      const instances = selectorStateInstances(state);
      return instances[id]?.style;
    },
    selectorStateCss,
  ],
  (style, css) => css[style],
);

export function getMySelector(id, buttonState, unitId) {
  return createSelector(
    [
      instanceSelector(id),
      styleSelector(id),
      selectorStateEditorResolutionWidth,
      (state) => selectorConfig(state, 'resolutionDesktop'),
      (state) => selectorConfig(state, 'resolutionTablet'),
      (state) => selectorConfig(state, 'resolutionMobile'),
      (state) => selectorType(state, id),
    ],
    (
      instance,
      style,
      resolution,
      desktop,
      tablet,
      mobile,
      type,
    ) => selectorCssPropsToProps(
      instance,
      style,
      buttonState,
      resolution,
      unitId,
      desktop,
      tablet,
      mobile,
      type,
    ),
  );
}

function useCssProps(id, unitId) {
  const buttonState = useGetButtonState(id);
  const mySelector = useMemo(
    () => getMySelector(id, buttonState, unitId),
    [id, buttonState, unitId],
  );
  return useSelector(
    mySelector,
    isEqualInstanceData,
  );
}

export default useCssProps;
