import ActivityArrows from './ActivityArrows/rightAnswersCount';
import ActivityLikert from './ActivityLikert/rightAnswersCount';
import ActivityMultipleChoice from './ActivityMultipleChoice/rightAnswersCount';
import ActivitySequence from './ActivitySequence/rightAnswersCount';
import ActivitySingleChoice from './ActivitySingleChoice/rightAnswersCount';
import ActivityTextInput from './ActivityTextInput/rightAnswersCount';

const config = {
  ActivityArrows,
  ActivityLikert,
  ActivityMultipleChoice,
  ActivitySequence,
  ActivitySingleChoice,
  ActivityTextInput,
};

function rightAnswersCount(state, activityId, type) {
  return config[type](state, activityId);
}

export default rightAnswersCount;
