import progressVariables from '@auth/components/src/RenderContent/progressVariables';
import selectorProp from '@auth/lib/src/selectors/selectorProp';
import pageStructureCountSelector from './pageStructureCountSelector';
import pageNumberByDescendantIdSelector from './pageNumberByDescendantIdSelector';
import quizCorrectActivitiesCount from './quizCorrectActivitiesCount';
import quizElapsedTimeSelector from './quizElapsedTimeSelector';
import quizLimitTimeSelector from './quizLimitTimeSelector';
import quizRemainingTimeSelector from './quizRemainingTimeSelector';
import quizScorePercentSelector from './quizScorePercentSelector';
import quizTotActivitiesCount from './quizTotActivitiesCount';
import visitedByTypeSelector from './visitedByTypeSelector';

const getVarName = (val) => Object.keys(progressVariables).find(
  (v) => `|${v}` === val,
);

const selectors = {
  PAGE_NUMBER: pageNumberByDescendantIdSelector,
  PAGES_COUNT: pageStructureCountSelector,
  VISITED_PAGES: (state) => visitedByTypeSelector(state, 'PageContent'),
  QUIZ_TOT_ACTIVITIES: quizTotActivitiesCount,
  QUIZ_CORRECT_ACTIVITIES: quizCorrectActivitiesCount,
  QUIZ_SCORE_PERCENT: quizScorePercentSelector,
  QUIZ_LIMIT_TIME: quizLimitTimeSelector,
  QUIZ_REMAINING_TIME: quizRemainingTimeSelector,
  QUIZ_ELAPSED_TIME: quizElapsedTimeSelector,
};

const progressPropSelector = (state, id, propName) => {
  const propVal = selectorProp(state, id, propName);
  const varName = getVarName(propVal);
  if (!varName) return propVal;
  return selectors[varName](state, id);
};

export default progressPropSelector;
