import ntxCssProps from '@auth/lib/src/data/ntxCssProps';
import { injectCssGlobal } from '@auth/shared/src/utils/generateClass';
import getFontFace from './getFontFace';
import patchUrlResources from './patchUrlResources';

function injectFonts(data, unitId, mqConfig) {
  const validEntries = Object.entries(data).filter(
    ([k]) => {
      const ntxCssPropsK = ntxCssProps(k, mqConfig);
      return ntxCssPropsK && (ntxCssPropsK.propName === 'fontSrc');
    },
  );
  const css = validEntries.reduce(
    (memo, [k, v]) => {
      const ntxCssPropsK = ntxCssProps(k, mqConfig);
      const {
        family, style, weight, source, localSrc,
      } = ntxCssPropsK;
      const fontSource = data[source];
      if (fontSource === 'googleFonts') {
        const fontFamily = data[family] || '';
        const fontWeight = data[weight] || 'normal';
        const fontStyle = data[style] || 'normal';
        const urlFont = v;
        return `${memo}${getFontFace(urlFont, fontFamily, fontWeight, fontStyle)}`;
      }
      if (!data[localSrc]) { return memo; }
      const { id, filename } = JSON.parse(data[localSrc]);
      const localUrl = patchUrlResources(id, filename, unitId);
      return `${memo}${getFontFace(localUrl, id, 'normal', 'normal')}`;
    },
    '',
  );
  if (css.length) { injectCssGlobal(css); }
}

export default injectFonts;
