import arrowsResponse from './arrowsResponse';
import defaultResponse from './defaultResponse';
import multipleChoiceResponse from './multipleChoiceResponse';
import sequenceResponse from './sequenceResponse';
import singleChoiceResponse from './singleChoiceResponse';

const config = {
  ActivityArrows: arrowsResponse,
  ActivityLikert: singleChoiceResponse,
  ActivityMultipleChoice: multipleChoiceResponse,
  ActivitySequence: sequenceResponse,
  ActivitySingleChoice: singleChoiceResponse,
  ActivityTextInput: defaultResponse,
};

const getResponse = (state, id, type) => config[type](state, id);

export default getResponse;
