import evaluableQuizzesSelector from './evaluableQuizzesSelector';
import quizDescendantStatusSelector from './quizDescendantStatusSelector';

function scoreByPassedQuizzesSelector(state) {
  const evaluableQuizzes = evaluableQuizzesSelector(state);
  const havePassedOrFailedQuizz = evaluableQuizzes.find(
    (quiz) => ['passed', 'failed'].includes(quizDescendantStatusSelector(state, quiz)),
  );
  if (!havePassedOrFailedQuizz) { return undefined; }
  const correctQuizzes = evaluableQuizzes.reduce(
    (memo, quiz) => {
      const status = quizDescendantStatusSelector(state, quiz);
      if (status === 'passed') { return memo + 1; }
      return memo;
    },
    0,
  );
  if (!evaluableQuizzes.length) { return undefined; }
  return Math.round((correctQuizzes / evaluableQuizzes.length) * 100);
}

export default scoreByPassedQuizzesSelector;
