export const SET_SUSPEND_DATA = 'SET_SUSPEND_DATA';

export const setSuspendDataAction = (id, prop, value) => ({
  type: SET_SUSPEND_DATA,
  payload: {
    id,
    prop,
    value,
  },
});

const suspendDataReducerSet = (state, action) => {
  const { payload } = action;
  const {
    id,
    prop,
    value,
  } = payload;
  return {
    ...state,
    [id]: {
      ...state[id],
      [prop]: value,
    },
  };
};

export default suspendDataReducerSet;
