import { ANSWERED } from '../tincanVerbs';
import { xApiInteractionType } from '../xapi/conts';
import { toInteractionArray } from './choice';

function likert(data, activityId, context) {
  const {
    id,
    response,
    wording,
    choices,
    courseId,
    scoreMin,
    scoreMax,
    scoreRaw,
  } = data;

  return {
    verb: ANSWERED,
    result: {
      response,
      score: {
        min: scoreMin,
        max: scoreMax,
        raw: scoreRaw,
      },
    },
    context,
    object: {
      id: `${activityId || courseId}/interaction/${id}`,
      definition: {
        description: wording,
        type: xApiInteractionType,
        scale: toInteractionArray(choices),
        interactionType: 'likert',
      },
      objectType: 'Activity',
    },
  };
}

export default likert;
