import selectorProp from '@auth/lib/src/selectors/selectorProp';
import changeDisplay from './changeDisplay';
import getAreasInColumns from './getAreasInColumns';

function inColumns({
  state, id, type, cssDisplay, childs,
}) {
  const gridTemplateColumns = selectorProp(state, id, 'gridTemplateColumns');
  const gap = selectorProp(state, id, 'gap') || '0px';
  const rowGap = selectorProp(state, id, 'rowGap') || gap;
  const columnGap = selectorProp(state, id, 'columnGap') || gap;
  const ret = {
    display: changeDisplay(cssDisplay, type),
    rowGap,
    columnGap,
    gridTemplateAreas: getAreasInColumns(childs),
  };
  if (gridTemplateColumns) ret.gridTemplateColumns = gridTemplateColumns;
  return ret;
}

export default inColumns;
