import { useDispatch } from 'react-redux';
import checkQuizInteractionsSelector from '../selectors/checkQuizInteractionsSelector';
import checkQuizSuspendOperationsSelector from '../selectors/checkQuizSuspendOperationsSelector';
import quizHasAttemptsSelector from '../selectors/quizHasAttemptsSelector';
import { sendMultipleInteractions } from '../tracking/tracking';
import useCheckQuizEvents from './useCheckQuizEvents';
import useSuspendOperations from './useSuspendOperations';

function useCheckQuizzes() {
  const suspendOperations = useSuspendOperations();
  const checkQuizEvents = useCheckQuizEvents();
  const dispatchThunk = useDispatch();
  return (quizzes) => {
    dispatchThunk(
      (dispatch, getState) => {
        const state = getState();
        const operations = quizzes.reduce(
          (memo, quizId) => [
            ...memo,
            ...checkQuizSuspendOperationsSelector(state, quizId),
          ],
          [],
        );
        if (operations.length) suspendOperations(operations);
        const stateAfterCheck = getState();
        const interactions = quizzes.reduce(
          (memo, quizId) => {
            const hasAttempts = quizHasAttemptsSelector(state, quizId);
            if (hasAttempts) {
              checkQuizEvents(quizId);
              return [...memo, ...checkQuizInteractionsSelector(stateAfterCheck, quizId)];
            }
            return memo;
          },
          [],
        );
        if (interactions.length) sendMultipleInteractions(interactions);
      },
    );
  };
}

export default useCheckQuizzes;
