import suspendSelector from './suspendSelector';
import suspendTempSelector from './suspendTempSelector';

function suspendDataSelector(id, propName, state) {
  const temp = suspendTempSelector(state);
  const tempValue = temp?.[id]?.[propName];
  if (tempValue) return tempValue;
  const suspend = suspendSelector(state);
  return suspend?.[id]?.[propName];
}

export default suspendDataSelector;
