/* eslint-disable no-empty */
import React, { useEffect } from 'react';
import { Global, css } from '@auth/shared/src/utils/generateClassReact';
import zoomSelector from '@auth/render/src/selectors/zoomSelector';
import { useDispatch, useSelector } from 'react-redux';
import useGetPreserveAspectRatio from '@auth/shared/src/hooks/useGetPreserveAspectRatio';
import { isXapiInConfig } from '../tracking/lmsApi';
import useSetSuspendData from '../hooks/useSetSuspendData';
import suspendDataSelector from '../selectors/suspendDataSelector';
import { setZoomAction } from '../reducers/zoomReducer';

export const localStorageZoomId = `${(window.packageId || 'preview')}_zoom`;

function RenderCssZoom() {
  const zoom = useSelector(zoomSelector);
  const aspect = useGetPreserveAspectRatio();
  const setSuspend = useSetSuspendData();
  const dispatchThunk = useDispatch();

  useEffect(
    () => {
      try { localStorage.setItem(localStorageZoomId, zoom); } catch (err) { }
      if (isXapiInConfig) setSuspend('course', 'zoom', zoom);
    },
    [setSuspend, zoom],
  );

  useEffect(
    () => {
      if (!isXapiInConfig) return;
      dispatchThunk(
        (dispatch, getState) => {
          const state = getState();
          const value = +(suspendDataSelector('course', 'zoom', state));
          if (value) dispatch(setZoomAction(value));
        },
      );
    },
    [dispatchThunk],
  );
  const cssToApply = aspect === 'none'
    ? `html { font-size: ${zoom}%}`
    : `html {
        transform-origin: 0 0;
        overflow: auto;
        transform: scale(${zoom / 100});
      }`;

  return (
    <Global styles={css(cssToApply)} />
  );
}

RenderCssZoom.propTypes = {
};

RenderCssZoom.defaultProps = {
};

export default RenderCssZoom;
