import React, { useMemo } from 'react';
import PropTypes from 'prop-types';
import { TransitionGroup } from 'react-transition-group';
import RenderNotificationsContainerContext from './RenderNotificationsContainerContext';

const style = {
  pointerEvents: 'all',
  position: 'fixed',
};

const yStyle = {
  top: {
    top: 0,
  },
  bottom: {
    bottom: 0,
  },
};

const xStyle = {
  left: {
    left: 0,
  },
  center: {
    left: '50%',
    transform: 'translateX(-50%)',
  },
  right: {
    right: 0,
  },
};

function RenderNotificationsContainer(props) {
  const {
    children,
    x,
    y,
  } = props;
  const contexValue = useMemo(
    () => ({ x, y }),
    [x, y],
  );
  const myStyle = useMemo(
    () => ({
      ...style,
      ...xStyle[x],
      ...yStyle[y],
    }),
    [x, y],
  );
  return (
    <RenderNotificationsContainerContext.Provider value={contexValue}>
      <div style={myStyle}>
        <TransitionGroup component={null}>
          {children}
        </TransitionGroup>
      </div>
    </RenderNotificationsContainerContext.Provider>
  );
}

RenderNotificationsContainer.propTypes = {
  children: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]),
  y: PropTypes.oneOf(['top', 'bottom']),
  x: PropTypes.oneOf(['left', 'center', 'right']),
};

RenderNotificationsContainer.defaultProps = {
  children: null,
  y: 'top',
  x: 'right',
};

export default RenderNotificationsContainer;
