import React, { useContext, useMemo } from 'react';
import PropTypes from 'prop-types';
import { useSelector } from 'react-redux';
import getDisplayPropName from '@auth/components/src/RenderContent/getChildAreas/getDisplayPropName';
import columnsByChilds from '@auth/components/src/RenderContent/getChildAreas/columnsByChilds';
import {
  DndContext,
  PointerSensor,
  useSensor,
  useSensors,
} from '@dnd-kit/core';
import {
  arrayMove,
  SortableContext,
} from '@dnd-kit/sortable';
import SequenceGroupOption from '@auth/components/src/SequenceGroupOption/SequenceGroupOption';
import SequenceGroupOptionContext from './SequenceGroupOptionContext';
import ActivityContext from './ActivityContext';
import useGetSuspendData from '../../hooks/useGetSuspendData';
import useSetSuspendData from '../../hooks/useSetSuspendData';
import useGetOrderChilds from '../../hooks/useGetOrderChilds';

function InteractiveRenderSequenceGroupOption(props) {
  const displayPropName = useSelector(getDisplayPropName);
  const {
    [displayPropName]: display,
    id,
    type,
  } = props;
  const activityId = useContext(ActivityContext);
  const orderChilds = useGetOrderChilds(props);
  const responseInSuspend = useGetSuspendData(activityId, 'response');
  const response = responseInSuspend
    ? responseInSuspend.split('[,]')
    : orderChilds;

  const gridTemplateStyle = useSelector(
    (state) => columnsByChilds({
      state, id, childs: response, display, type,
    }),
  );
  const objStyle = {
    gridTemplateAreas: gridTemplateStyle.gridTemplateAreas,
  };

  const setSuspend = useSetSuspendData();

  const contextValue = useMemo(() => {
    const movePrev = (idToMove) => {
      const position = response.indexOf(idToMove);
      if (position > 0) {
        const newResponse = response.filter((child) => child !== idToMove);
        newResponse.splice(position - 1, 0, idToMove);
        setSuspend(activityId, 'response', newResponse.join('[,]'));
      }
    };
    const moveNext = (idToMove) => {
      const position = response.indexOf(idToMove);
      if (position < response.length - 1) {
        const newResponse = response.filter((child) => child !== idToMove);
        newResponse.splice(position + 1, 0, idToMove);
        setSuspend(activityId, 'response', newResponse.join('[,]'));
      }
    };
    return {
      display, movePrev, moveNext,
    };
  }, [activityId, display, response, setSuspend]);

  const handleDragEnd = (event) => {
    const { active, over } = event;
    if (active.id !== over.id) {
      const oldIndex = response.indexOf(active.id);
      const newIndex = response.indexOf(over.id);
      const newResponse = arrayMove(response, oldIndex, newIndex);
      setSuspend(activityId, 'response', newResponse.join('[,]'));
    }
  };

  const sensors = useSensors(
    useSensor(PointerSensor, {
      activationConstraint: {
        distance: 8,
      },
    }),
  );

  return (
    <React.Fragment key={response}>
      <DndContext
        onDragEnd={handleDragEnd}
        sensors={sensors}
      >
        <SortableContext
          items={response}
        >
          <SequenceGroupOptionContext.Provider value={contextValue}>
            <SequenceGroupOption
              {...props}
              gridStyle={objStyle}
            />
          </SequenceGroupOptionContext.Provider>
        </SortableContext>
      </DndContext>
    </React.Fragment>
  );
}

InteractiveRenderSequenceGroupOption.propTypes = {
  id: PropTypes.string.isRequired,
  type: PropTypes.string.isRequired,
  correct: PropTypes.string.isRequired,
};

InteractiveRenderSequenceGroupOption.defaultProps = {
};

export default InteractiveRenderSequenceGroupOption;
