import React from 'react';
import PropTypes from 'prop-types';
import { ClassNames } from '@auth/shared/src/utils/generateClassReact';

const cssDefault = {
  position: 'absolute',
  top: 0,
  right: 0,
  bottom: 0,
  left: 0,
  zIndex: -1,
};

function PanelOverlay(props) {
  const { onClick } = props;
  return (
    <ClassNames>
      {({ css }) => (
        <div
          className={css(cssDefault)}
          onClick={onClick}
          onKeyPress={() => null}
          role="button"
          tabIndex="0"
          aria-label="Close menu"
        />
      )}
    </ClassNames>
  );
}

PanelOverlay.propTypes = {
  onClick: PropTypes.func,
};

PanelOverlay.defaultProps = {
  onClick: () => null,
};

export default PanelOverlay;
