import { useCallback } from 'react';
import { useDispatch } from 'react-redux';
import { setZoomAction } from '../reducers/zoomReducer';
import zoomSelector from '../selectors/zoomSelector';

const types = ['in', 'out'];
const minZoom = 100;
const maxZoom = 200;
const delta = {
  in: 10,
  out: -10,
};

function useChangeZoom(type) {
  const dispatchThunk = useDispatch();
  return useCallback(
    () => {
      if (!types.includes(type)) throw new Error('invalid type');
      dispatchThunk(
        (dispatch, getState) => {
          const state = getState();
          const currentZoom = zoomSelector(state);
          const newZoom = Math.min(Math.max(currentZoom + delta[type], minZoom), maxZoom);
          if (newZoom !== currentZoom) dispatch(setZoomAction(newZoom));
        },
      );
    },
    [dispatchThunk, type],
  );
}

export default useChangeZoom;
