import { useEffect } from 'react';
import getButtonState from './getButtonState';

function useCallOnStateChange(enabled, over, selected, active, onStateChange) {
  const buttonState = getButtonState(enabled, over, selected, active);
  useEffect(
    () => {
      onStateChange(buttonState);
    },
    [buttonState, onStateChange],
  );
}

export default useCallOnStateChange;
