import ntxCssProps from '@auth/lib/src/data/ntxCssProps';
import { patchMqConfig } from '@auth/editor/src/hooks/useMqConfig';
import getValue from './selectorGetCss/getValue';
import transform from './selectorGetCss/transform';

function selectorCssPropsToProps(
  cssDataInstanceParam,
  cssDataStyleParam,
  buttonState,
  resolution,
  unitId,
  resolutionDesktop,
  resolutionTablet,
  resolutionMobile,
  type,
) {
  const cssDataInstance = cssDataInstanceParam || {};
  const cssDataStyle = cssDataStyleParam || {};
  const css = transform({
    cssDataInstance,
    cssDataStyle,
    type,
    unitId,
  });
  const data = Object.entries(css).reduce(
    (memo, [k, v]) => {
      const ntxCssPropsK = ntxCssProps(
        k,
        patchMqConfig(
          resolutionDesktop,
          resolutionTablet,
          resolutionMobile,
        ),
      );
      if (ntxCssPropsK && (ntxCssPropsK.propName === undefined || ntxCssPropsK.injectInProps)) {
        return { ...memo, [k]: v };
      }
      return memo;
    },
    {},
  );
  const propNames = Object.keys(data).reduce(
    (memo, k) => {
      const propName = k.replace(':widescreen', '').replace(':tablet', '').replace(':mobile', '');
      return !memo.includes(propName) ? [...memo, propName] : memo;
    },
    [],
  );
  return propNames.reduce(
    (memo, propName) => {
      const value = getValue(propName, buttonState, resolution, data);
      return {
        ...memo, [propName]: value,
      };
    },
    {},
  );
}

export default selectorCssPropsToProps;
