import React, { useContext } from 'react';
import PropTypes from 'prop-types';
import MultipleChoiceOption from '@auth/components/src/MultipleChoiceOption/MultipleChoiceOption';
import ActivityContext from './ActivityContext';
import useGetSuspendData from '../../hooks/useGetSuspendData';
import useSuspendOperations, { setOperation, unsetOperation } from '../../hooks/useSuspendOperations';
import getChoiceOptionProps from './interactiveRenderChoiceOption/getChoiceOptionProps';
import useOptionPseudoSelector from './interactiveRenderChoiceOption/useOptionPseudoSelector';
import useIsAsking from '../../hooks/useIsAsking';

function InteractiveRenderMultipleChoiceOption(props) {
  const {
    id,
    correct,
  } = props;
  const activityId = useContext(ActivityContext);
  const valueInSuspend = useGetSuspendData(activityId, 'response') || '';
  const response = valueInSuspend ? valueInSuspend.split(',') : [];
  const checked = response.includes(id);
  const asking = useIsAsking(activityId);
  const suspendOperations = useSuspendOperations();
  const handleOnChange = () => {
    if (activityId && asking) {
      if (checked) {
        const newSuspend = response.filter((responseItem) => responseItem !== id);
        suspendOperations([
          unsetOperation(activityId, 'correct'),
          setOperation(activityId, 'response', newSuspend.toString()),
        ]);
      } else {
        suspendOperations([
          unsetOperation(activityId, 'correct'),
          setOperation(activityId, 'response', ([...response, id]).toString()),
        ]);
      }
    }
  };

  const pseudoSelector = useOptionPseudoSelector(asking, checked, correct);

  return (
    <MultipleChoiceOption
      {...props}
      onChange={handleOnChange}
      checked={checked}
      pseudoSelector={pseudoSelector}
      {...getChoiceOptionProps(pseudoSelector, id, props)}
    />
  );
}

InteractiveRenderMultipleChoiceOption.propTypes = {
  id: PropTypes.string.isRequired,
  correct: PropTypes.string.isRequired,
};
InteractiveRenderMultipleChoiceOption.defaultProps = {
};

export default InteractiveRenderMultipleChoiceOption;
