import { EDITOR_USER_MODE_PROFESSIONAL } from '../../const';
import editorUserModeKeyInLocalStorage from './editorUserModeKeyInLocalStorage';
import isValidUserMode from './isValidUserMode';

function getEditorUserModeInLocalStorage() {
  try {
    const value = window.localStorage.getItem(editorUserModeKeyInLocalStorage);
    if (isValidUserMode(value)) return value;
    return EDITOR_USER_MODE_PROFESSIONAL;
  } catch (err) {
    return undefined;
  }
}

export default getEditorUserModeInLocalStorage;
