import { useContext } from 'react';
import VisbleChildsContext from '../VisbleChildsContext';
import useSetSuspendData from '../../../hooks/useSetSuspendData';

function useClickGotoSlide() {
  const {
    visibleChild,
    componentId,
  } = useContext(VisbleChildsContext) || {};
  const setSuspend = useSetSuspendData();
  return ({ isRelatedComp }) => {
    if (!componentId) return;
    if (isRelatedComp === visibleChild) return;
    setSuspend(componentId, 'visibleChilds', [isRelatedComp]);
  };
}

export default useClickGotoSlide;
