export const SET_TEMP_SUSPEND_DATA = 'SET_TEMP_SUSPEND_DATA';

export const setTempSuspendDataAction = (id, prop, value) => ({
  type: SET_TEMP_SUSPEND_DATA,
  payload: {
    id,
    prop,
    value,
  },
});

function suspendDataTempReducerSet(state, action) {
  const { payload } = action;
  const {
    id,
    prop,
    value,
  } = payload;
  return {
    ...state,
    [id]: {
      ...state[id],
      [prop]: value,
    },
  };
}

export default suspendDataTempReducerSet;
