const containerSelector = '> div';
const itemsSelector = '> div > div';
const itemBackSelector = '> div > div:nth-of-type(2)';

const cssRoot = {
  perspective: '1000px',
};

const cssContainer = {
  transformStyle: 'preserve-3d',
  position: 'relative',
  width: '100%',
  height: '100%',
};

const cssItems = {
  backfaceVisibility: 'hidden',
  transform: 'rotateX(0deg)', /* hack firefox https://bugzilla.mozilla.org/show_bug.cgi?id=1201471 */
};

const cssItemBack = {
  position: 'absolute',
  width: '100%',
  height: '100%',
  top: 0,
  left: 0,
};

const transforms = {
  ltr: 'rotateY(180deg)',
  rtl: 'rotateY(-180deg)',
  ttb: 'rotateX(-180deg)',
  btt: 'rotateX(180deg)',
};

function getFlipCardCss({
  showFront, transitionType, transitionDuration, transitionDelay, transitionTimingFunction,
}) {
  const cssCustom = {
    ...cssRoot,
    [containerSelector]: { ...cssContainer },
    [itemsSelector]: { ...cssItems },
    [itemBackSelector]: { ...cssItemBack },
  };
  cssCustom[containerSelector].transition = `transform ${transitionDuration} ${transitionDelay} ${transitionTimingFunction}`;
  if (!showFront) cssCustom[containerSelector].transform = transforms[transitionType];
  cssCustom[itemBackSelector].zIndex = showFront ? -1 : 'auto';
  cssCustom[itemBackSelector].transform = transforms[transitionType];
  return cssCustom;
}

export default getFlipCardCss;
