import selectorPrevPageId from '@auth/lib/src/selectors/selectorPrevPageId';
import { thunkSetPage } from '../hooks/useSetCurrentPage';

const goPreviousPageThunk = () => (dispatch, getState) => {
  const state = getState();
  const previousPage = selectorPrevPageId(state);
  if (previousPage) dispatch(thunkSetPage(previousPage));
};

export default goPreviousPageThunk;
