const set = (key, value) => {
  try {
    localStorage.setItem(`${(window.packageId || 'preview')}_${key}`, value);
    return true;
  } catch (err) {
    return false;
  }
};

const get = (key) => {
  try {
    return localStorage.getItem(`${window.packageId || 'preview'}_${key}`) || undefined;
  } catch (err) {
    return undefined;
  }
};

const init = () => [
  true,
  +get('score'),
  get('completion') === 'true',
];

const setSuspend = (suspend) => set('suspend', JSON.stringify(suspend));

const getSuspend = () => {
  const val = get('suspend');
  if (!val) return undefined;
  return JSON.parse(val);
};

const setLocation = (location) => set('location', location);

const getLocation = () => get('location');

const exit = () => null;

const sendInteraction = () => null;

const sendMultipleInteractions = () => null;

const setScore = (newScore) => set('score', newScore);

const setCompletion = (newCompletion) => set('completion', newCompletion);

const setSuccessStatus = () => null;

const getName = () => '';

const getId = () => '';

const localstorage = {
  init,
  setSuspend,
  getSuspend,
  setLocation,
  getLocation,
  exit,
  sendInteraction,
  sendMultipleInteractions,
  setScore,
  setCompletion,
  setSuccessStatus,
  getName,
  getId,
};

export default localstorage;
