import { REQUIREMENTS_MET } from '@auth/lib/src/events/triggers';
import selectorEventsIdsByTriggerCompIdAndTriggerType from '@auth/lib/src/selectors/selectorEventsIdsByTriggerCompIdAndTriggerType';
import completionSelector from '../../selectors/completionSelector';
import requirementsSelector from '../../selectors/requirementsSelector';

function launchRequirementsEventSelector(state, id) {
  const events = selectorEventsIdsByTriggerCompIdAndTriggerType(state, id, REQUIREMENTS_MET);
  if (!events.length) return undefined;
  const requirements = requirementsSelector(state, id);
  if (!requirements.length) return undefined;
  return completionSelector(state, [id]);
}

export default launchRequirementsEventSelector;
