import getBackRoute from '@auth/shared/src/getBackRoute';
import mode from '@auth/shared/src/mode';
import { patchResourceId } from '@auth/lib/src/resources';

function patchUrlResources(src, filename, unitId) {
  if (!unitId) { throw new Error('invalid unitId'); }
  const patchSrc = patchResourceId(src, filename);
  const currentMode = mode();
  if (currentMode === 'publish' || currentMode === 'development') {
    return `${process.env.PUBLIC_URL}/resources/${patchSrc}`;
  }
  const data = { id: unitId, hash: patchSrc };
  return getBackRoute(
    'api_authoring_unit_contents_resource_url_item',
    data,
  );
}

export default patchUrlResources;
