import defaultResponse from './defaultResponse';
import getId from './getId';

function multipleChoiceResponse(state, id) {
  const responseInSuspend = defaultResponse(state, id);
  const response = responseInSuspend ? responseInSuspend.split(',') : [];
  const mapResponse = response.map((v) => getId(state, v));
  return mapResponse.toString();
}

export default multipleChoiceResponse;
