import React from 'react';
import PropTypes from 'prop-types';

import ActivityTextInput from '@auth/components/src/ActivityTextInput/ActivityTextInput';
import ActivityContext from './ActivityContext';

function InteractiveRenderActivityTextInput(props) {
  const { id } = props;
  return (
    <ActivityContext.Provider value={id}>
      <ActivityTextInput {...props} />
    </ActivityContext.Provider>
  );
}

InteractiveRenderActivityTextInput.propTypes = {
  id: PropTypes.string.isRequired,
};
InteractiveRenderActivityTextInput.defaultProps = {
};

export default InteractiveRenderActivityTextInput;
