import { useDispatch } from 'react-redux';
import selectorRealType from '@auth/lib/src/selectors/selectorRealType';
import { v4 } from 'uuid';
import { useCallback } from 'react';
import { showNotificationAction } from '../reducers/notificationsReducer';

function isNotification(state, id) {
  return selectorRealType(state, id) === 'Notification';
}

function useShowNotification() {
  const dispatchThunk = useDispatch();
  return useCallback((id) => dispatchThunk((dispatch, getState) => {
    const state = getState();
    if (!isNotification(state, id)) return null;

    return dispatch(showNotificationAction(id, v4()));
  }), [dispatchThunk]);
}

export default useShowNotification;
