import selectorClosestType from '@auth/lib/src/selectors/selectorClosestType';
import suspendDataSelector from './suspendDataSelector';

function multipleChoiceOptionSelectedSelector(id, state) {
  const activity = selectorClosestType(state, id, 'ActivityMultipleChoice');
  const strResponse = suspendDataSelector(activity, 'response', state) || '';
  const responses = strResponse.split(',');
  return responses.includes(id);
}

export default multipleChoiceOptionSelectedSelector;
