import addParamsToUrl from '@auth/shared/src/addParamsToUrl';
import patchUrlResources from './patchUrlResources';

function getPatchUrl(id, name, mimeType, unitId) {
  const url = patchUrlResources(id, name, unitId);
  return addParamsToUrl(url, [
    { key: 'id', value: id },
    { key: 'mimeType', value: mimeType },
    { key: 'filename', value: name },
  ]);
}

export default getPatchUrl;
