import { useEffect } from 'react';
import { useSelector } from 'react-redux';
import selectorCurrentLang from '@auth/lib/src/selectors/selectorCurrentLang';

/* es un componente para evitar renderizados provocados al cambiar el valor de currentLang */

function HtmlLangUpdater() {
  const currentLang = useSelector(selectorCurrentLang);
  useEffect(
    () => {
      document.querySelector('html').setAttribute('lang', currentLang);
    },
    [currentLang],
  );
  return null;
}

HtmlLangUpdater.propTypes = {};

HtmlLangUpdater.defaultProps = {};

export default HtmlLangUpdater;
