import getQueryParam from '@auth/shared/src/getQueryParam';

function getPage(data = {}) {
  const { structure = [] } = data;
  if (window.ntxData) { return structure[0]; }
  const queryParamPage = getQueryParam('pageId');
  if (structure.includes(queryParamPage)) { return queryParamPage; }
  return structure[0];
}

export default getPage;
