import React, {
  useCallback, useEffect, useRef, useState,
} from 'react';
import PropTypes from 'prop-types';
import { WebVTT } from 'videojs-vtt.js';
import './skin1/VideoSkin1.css';

const SubtitleNoNative = React.memo((props) => {
  const {
    url,
    currentTime,
    subtitlesNoNatives,
    setSubtitles,
  } = props;
  const componentEl = useRef(null);
  const [cuesSubtitlesNoNative, setCuesSubtitlesNoNative] = useState([]);

  const processCuepoints = useCallback(() => {
    if (cuesSubtitlesNoNative.length) {
      const activeCues = cuesSubtitlesNoNative.filter(
        (cue) => cue.startTime <= currentTime && cue.endTime >= currentTime,
      );
      WebVTT.processCues(window, activeCues, componentEl.current);
    } else {
      WebVTT.processCues(window, [], componentEl.current);
    }
  }, [cuesSubtitlesNoNative, currentTime]);

  const parserSubtitle = (text) => {
    const parser = new WebVTT.Parser(window, WebVTT.StringDecoder());
    const cues = [];
    parser.oncue = (cue) => {
      cues.push(cue);
    };
    parser.onflush = () => {
      setCuesSubtitlesNoNative(cues);
    };
    parser.parse(text);
    parser.flush();
  };

  useEffect(() => {
    const newSubtitles = Object.entries(JSON.parse(subtitlesNoNatives)).reduce((m, [srcLang]) => ({
      ...m,
      [srcLang]: srcLang,
    }), {});
    setSubtitles(newSubtitles);
  }, [setSubtitles, subtitlesNoNatives]);

  useEffect(() => {
    processCuepoints();
  }, [cuesSubtitlesNoNative, currentTime, componentEl, processCuepoints]);

  useEffect(() => {
    const redrawSubtitles = () => {
      WebVTT.processCues(window, [], componentEl.current);
      cuesSubtitlesNoNative.forEach((cue) => {
        delete cue.displayState;
      });
      setTimeout(() => {
        processCuepoints();
      }, 300);
    };
    const afterFullScreenEvent = (e) => {
      if (e.target.contains(componentEl.current)) {
        redrawSubtitles();
      }
    };
    const handleMsFullscreenChange = (e) => {
      afterFullScreenEvent(e);
    };
    const handleMozFullscreenChange = (e) => {
      afterFullScreenEvent(e);
    };
    const handleWebkitFullscreenChange = (e) => {
      afterFullScreenEvent(e);
    };
    const handleFullscreenChange = (e) => {
      afterFullScreenEvent(e);
    };
    const handleCustomFullscreenChange = (e) => {
      afterFullScreenEvent(e);
    };
    const afterResizeEvent = () => {
      redrawSubtitles();
    };
    document.addEventListener('msfullscreenchange', handleMsFullscreenChange);
    document.addEventListener('mozfullscreenchange', handleMozFullscreenChange);
    document.addEventListener('webkitfullscreenchange', handleWebkitFullscreenChange);
    document.addEventListener('fullscreenchange', handleFullscreenChange);
    document.addEventListener('customfullscreenchange', handleCustomFullscreenChange);
    window.addEventListener('resize', afterResizeEvent);
    return () => {
      document.removeEventListener('msfullscreenchange', handleMsFullscreenChange);
      document.removeEventListener('mozfullscreenchange', handleMozFullscreenChange);
      document.removeEventListener('webkitfullscreenchange', handleWebkitFullscreenChange);
      document.removeEventListener('fullscreenchange', handleFullscreenChange);
      document.removeEventListener('customfullscreenchange', handleCustomFullscreenChange);
      window.removeEventListener('resize', afterResizeEvent);
    };
  }, [cuesSubtitlesNoNative, processCuepoints, componentEl]);

  useEffect(() => {
    if (url) {
      fetch(url, {
      })
        .then((response) => {
          if (!response.ok) throw Error(response.statusText);
          return response;
        })
        .then((response) => response.text())
        .then((response) => {
          parserSubtitle(response);
          return response;
        });
    }
  }, [url]);
  if (!url) {
    return null;
  }
  return (
    <div
      ref={componentEl}
      className="ntx-video-subtitle-no-native"
    />
  );
});

SubtitleNoNative.propTypes = {
  url: PropTypes.string,
  currentTime: PropTypes.number,
  subtitlesNoNatives: PropTypes.string,
  setSubtitles: PropTypes.func,
};

SubtitleNoNative.defaultProps = {
  url: '',
  currentTime: 0,
  subtitlesNoNatives: '{}',
  setSubtitles: () => null,
};

export default SubtitleNoNative;
