import React, { useContext } from 'react';
import PropTypes from 'prop-types';
import Xarrow, { Xwrapper } from 'react-xarrows';
import ArrowsGroupOption from '@auth/components/src/ArrowsGroupOption/ArrowsGroupOption';
import useGetCss from '@auth/components/src/RenderContent/useGetCss';
import selectorStateEditorResolutionWidth from '@auth/lib/src/selectors/selectorStateEditorResolutionWidth';
import { useSelector } from 'react-redux';
import getValue from '@auth/components/src/RenderContent/selectorGetCss/getValue';
import selectorType from '@auth/lib/src/selectors/selectorType';
import MaintainAspectRatioContext from '@auth/shared/src/contexts/MaintainAspectRatioContext';
import useMantainAspectRatioPatchVhVw from '@auth/components/src/RenderContent/useMantainAspectRatioPatchVhVw';
import ActivityContext from './ActivityContext';
import useGetSuspendData from '../../hooks/useGetSuspendData';
import GroupOptionContext from './GroupOptionContext';
import useGetOrderChilds from '../../hooks/useGetOrderChilds';

function sort(children, ids) {
  return React.Children.toArray(children).sort(
    (a, b) => {
      const aId = a.props?.children?.props?.id;
      const bId = b.props?.children?.props?.id;
      const aOrder = ids.indexOf(aId);
      const bOrder = ids.indexOf(bId);
      if (aOrder > bOrder) return 1;
      if (aOrder < bOrder) return -1;
      return 0;
    },
  );
}

function InteractiveRenderArrowsGroupOption(props) {
  const {
    id,
    correct,
    ArrowSource,
    ArrowTarget,
  } = props;

  const orderChilds = useGetOrderChilds(props);

  const sources = useSelector(
    (state) => orderChilds.filter(
      (orderChild) => selectorType(state, orderChild) === 'ArrowSource',
    ),
  );
  const sortSources = sort(ArrowSource, sources);
  const targets = useSelector(
    (state) => orderChilds.filter(
      (orderChild) => selectorType(state, orderChild) === 'ArrowTarget',
    ),
  );
  const sortTargets = sort(ArrowTarget, targets);
  const activityId = useContext(ActivityContext);
  const response = useGetSuspendData(activityId, 'response');
  const activityChecked = useGetSuspendData(activityId, 'correct') !== undefined;
  const correctRelations = correct ? correct.split('[,]') : [];
  const allRelations = response ? response.split('[,]') : [];
  const cssData = useGetCss(id, props);
  const patchedCssData = useMantainAspectRatioPatchVhVw(cssData);
  const resolution = useSelector((state) => selectorStateEditorResolutionWidth(state));
  const {
    scale, translateX, translateY,
  } = useContext(MaintainAspectRatioContext);
  return (
    <Xwrapper>
      <GroupOptionContext.Provider value={id}>
        <ArrowsGroupOption
          {...props}
          ArrowSource={sortSources}
          ArrowTarget={sortTargets}
        >
          {
            allRelations.map(
              (relation, index) => {
                const [start, end] = relation.split('[.]');
                const pseudoState = (() => {
                  if (!activityChecked) return '';
                  if (correctRelations.includes(relation)) return 'answeredright';
                  return 'answeredwrong';
                })();
                const color = getValue(`arrowColor${index + 1}`, pseudoState, resolution, patchedCssData) || '#000000';
                const arrowHeadShape = getValue('arrowHeadShape', pseudoState, resolution, patchedCssData) || 'arrow1';
                const showHead = arrowHeadShape !== 'none';
                const headShape = arrowHeadShape === 'none' ? 'arrow1' : arrowHeadShape;
                const arrowTailShape = getValue('arrowTailShape', pseudoState, resolution, patchedCssData) || 'none';
                const showTail = arrowTailShape !== 'none';
                const tailShape = arrowTailShape === 'none' ? 'arrow1' : arrowTailShape;
                const arrowStrokeWidth = +(getValue('arrowStrokeWidth', pseudoState, resolution, patchedCssData)) || 4;
                const arrowStrokeStyle = getValue('arrowStrokeStyle', pseudoState, resolution, patchedCssData) || 'solid';
                const dashness = arrowStrokeStyle === 'dashed';
                return (
                  <Xarrow
                    key={relation}
                    start={start}
                    end={end}
                    color={color}
                    showHead={showHead}
                    headShape={headShape}
                    showTail={showTail}
                    tailShape={tailShape}
                    strokeWidth={arrowStrokeWidth * scale}
                    dashness={dashness}
                    startAnchor="right"
                    endAnchor="left"
                    divContainerStyle={{
                      transform: `scale(${1 / scale}) translateX(${translateX}) translateY(${translateY})`,
                    }}
                  />
                );
              },
            )
          }
        </ArrowsGroupOption>
      </GroupOptionContext.Provider>
    </Xwrapper>
  );
}

InteractiveRenderArrowsGroupOption.propTypes = {
  id: PropTypes.string.isRequired,
  correct: PropTypes.string.isRequired,
  ArrowSource: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]).isRequired,
  ArrowTarget: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]).isRequired,
};
InteractiveRenderArrowsGroupOption.defaultProps = {

};

export default InteractiveRenderArrowsGroupOption;
