import selectorConfig from './selectorConfig';
import selectorStateInstances from './selectorStateInstances';
import selectorStateStructure from './selectorStateStructure';

function gotoPage({ instance }) {
  const { targetPage } = instance;
  if (!targetPage) return null;
  return JSON.stringify({
    compId: targetPage,
    trigger: 'canNavigate',
    disabled: true,
  });
}

function next({ currentPage, structure }) {
  const index = structure.findIndex(
    (pageId) => pageId === currentPage,
  );
  if (index === -1) return null;
  const nextPage = structure[index + 1];
  if (!nextPage) return null;
  return JSON.stringify({
    compId: nextPage,
    trigger: 'canNavigate',
    disabled: true,
  });
}

function previous({ currentPage, structure }) {
  const index = structure.findIndex(
    (pageId) => pageId === currentPage,
  );

  if (index === -1) return null;
  const prevPage = structure[index - 1];
  if (!prevPage) return null;
  return JSON.stringify({
    compId: prevPage,
    trigger: 'canNavigate',
    disabled: true,
  });
}

function menuListItem({ id }) {
  const [, pageId] = (id || '').split('_');
  return JSON.stringify({
    compId: pageId,
    trigger: 'canNavigate',
    disabled: true,
  });
}

const validComps = ['Button', 'MenuListItem', 'Launcher'];

const config = {
  Button_gotoPage: gotoPage,
  Button_next: next,
  Button_previous: previous,
  Launcher_gotoPage: gotoPage,
  Launcher_next: next,
  Launcher_previous: previous,
  MenuListItem: menuListItem,
};

function virtualPropEnabled(id, instance, contentNavigation, structure, currentPage) {
  if (!instance) return null;
  const { type, action = '' } = instance;
  if (!validComps.includes(type)) return null;
  if (contentNavigation !== 'sequential') return null;
  const key = action ? `${type}_${action}` : type;
  if (!config[key]) return null;
  return config[key]({
    id,
    instance,
    structure,
    currentPage,
  });
}

function selectorVirtualPropEnabled(state, id, complexId) {
  const instances = selectorStateInstances(state);
  const contentNavigation = selectorConfig(state, 'contentNavigation');
  const structure = selectorStateStructure(state);
  const currentPage = complexId || state?.page;
  return virtualPropEnabled(id, instances[id], contentNavigation, structure, currentPage);
}

export default selectorVirtualPropEnabled;
