import React from 'react';
import PropTypes from 'prop-types';
import ActivityLikert from '@auth/components/src/ActivityLikert/ActivityLikert';
import ActivityContext from './ActivityContext';

function InteractiveRenderActivityLikert(props) {
  const { id } = props;
  return (
    <ActivityContext.Provider value={id}>
      <ActivityLikert {...props} />
    </ActivityContext.Provider>
  );
}

InteractiveRenderActivityLikert.propTypes = {
  id: PropTypes.string.isRequired,
};
InteractiveRenderActivityLikert.defaultProps = {
};

export default InteractiveRenderActivityLikert;
