import { useEffect } from 'react';

function usePatchOver(id, over, dispatch) {
  useEffect(
    () => {
      const timeoutId = setTimeout(() => {
        const domElement = document.querySelector(`[data-ntx-component="${id}"`);
        const isOver = domElement.matches(':hover');
        if (isOver !== over) {
          dispatch({
            type: 'over', value: isOver, timestamp: Date.now(),
          });
        }
      }, 400);
      return () => {
        clearTimeout(timeoutId);
      };
    },
    [dispatch, id, over],
  );
}

export default usePatchOver;
