import React from 'react';
import PropTypes from 'prop-types';
import { ClassNames } from '@auth/shared/src/utils/generateClassReact';
import useInteractiveProps from '../useInteractiveProps';
import pseudoSelectorProp from '../pseudoSelectorProp';

const cssDefault = {
  input: {
    width: '100%',
    padding: '0 2px',
    border: 'none',
    '&:focus,&:focus-visible': {
      outline: 'none',
    },
  },
  textarea: {
    width: '100%',
    padding: '0 2px',
    resize: 'none',
    '&:focus,&:focus-visible': {
      outline: 'none',
    },
  },
};

function TextInputOption(props) {
  const {
    className,
    inputType,
    inputId,
    inputPlaceholder,
    inputRows,
    onBlur,
    onChange,
    value,
    pseudoSelector,
    disabled,
    maxTextSize,
  } = props;
  const commonProps = {
    id: inputId,
    placeholder: inputPlaceholder,
    onBlur,
    onChange,
    value,
    disabled,
    maxLength: maxTextSize,
  };
  const interactiveProps = useInteractiveProps(props);
  return (
    <ClassNames>
      {({ css, cx }) => (
        <div
          className={cx(css(cssDefault), className)}
          {...interactiveProps}
          {...pseudoSelectorProp(pseudoSelector)}
        >
          {inputType === 'input' && (
            <input
              type="text"
              {...commonProps}
            />
          )}
          {inputType === 'textArea' && (
            <textarea
              rows={+inputRows}
              {...commonProps}
            />
          )}
        </div>
      )}
    </ClassNames>
  );
}

TextInputOption.propTypes = {
  inputId: PropTypes.string,
  className: PropTypes.string,
  inputType: PropTypes.oneOf(['input', 'textArea']),
  inputPlaceholder: PropTypes.string,
  inputRows: PropTypes.string,
  onBlur: PropTypes.func,
  onChange: PropTypes.func,
  value: PropTypes.string,
  pseudoSelector: PropTypes.oneOf([
    '',
    'askingchosen',
    'answeredright',
    'answeredwrong',
  ]),
  disabled: PropTypes.bool,
  maxTextSize: PropTypes.string,
};

TextInputOption.defaultProps = {
  inputId: null,
  className: '',
  inputType: 'input',
  inputPlaceholder: 'Write your answer here',
  inputRows: '3',
  onBlur: () => null,
  onChange: () => null,
  value: '',
  pseudoSelector: '',
  disabled: false,
  maxTextSize: '',
};

export default TextInputOption;
