import React from 'react';
import PropTypes from 'prop-types';
import allAvailableLanguages from '@auth/render/src/allAvailableLanguages';

function SubtitleButton(props) {
  const {
    onClick,
    lang,
    defaultPlaceholder,
    selected,
  } = props;
  const label = allAvailableLanguages[lang];
  return (
    <button
      type="button"
      aria-label={`${label} subtitles` || 'subtitles'}
      onClick={() => onClick(lang)}
      className={selected ? 'ntx-subtitle-button-selected' : ''}
    >
      {label || defaultPlaceholder || lang}
    </button>
  );
}

SubtitleButton.propTypes = {
  lang: PropTypes.string,
  defaultPlaceholder: PropTypes.string,
  onClick: PropTypes.func,
  selected: PropTypes.bool,
};

SubtitleButton.defaultProps = {
  lang: '',
  defaultPlaceholder: '',
  onClick: () => null,
  selected: false,
};

export default SubtitleButton;
