import selectorConfig from '@auth/lib/src/selectors/selectorConfig';
import nextPageTitleSelector from './nextPageTitleSelector';
import pageStructureCountSelector from './pageStructureCountSelector';
import pageNumberSelector from './pageNumberSelector';
import pageTitleSelector from './pageTitleSelector';
import prevPageTitleSelector from './prevPageTitleSelector';
import quizCorrectActivitiesCount from './quizCorrectActivitiesCount';
import quizCurrentAttemptSelector from './quizCurrentAttemptSelector';
import quizDescendantMaxScoreSelector from './quizDescendantMaxScoreSelector';
import quizDescendantScoreSelector from './quizDescendantScoreSelector';
import quizDescendantStatusSelector from './quizDescendantStatusSelector';
import quizDescendantSuccessRatePercentSelector from './quizDescendantSuccessRatePercentSelector';
import quizMaxAttemptSelector from './quizMaxAttemptSelector';
import quizNumCurrentActivitySelector from './quizNumCurrentActivitySelector';
import quizRemainingAttemptsSelector from './quizRemainingAttemptsSelector';
import quizScorePercentSelector from './quizScorePercentSelector';
import quizTotActivitiesCount from './quizTotActivitiesCount';
import scoreSelector from './scoreSelector';
import textProgressByPropNameSelector from './textProgressByPropNameSelector';
import visitedByTypeSelector from './visitedByTypeSelector';
import inputRemainingCharsSelector from './inputRemainingCharsSelector';
import quizLimitTimeSelector from './quizLimitTimeSelector';
import quizElapsedTimeSelector from './quizElapsedTimeSelector';
import quizRemainingTimeSelector from './quizRemainingTimeSelector';
import activityRightAnswersCount from './activityRightAnswersCount';
import lmsApi from '../tracking/lmsApi';

const toStringWithInfinityChar = (v) => (v === Infinity ? '∞' : v.toString());

const selectors = {
  CONTENT_CUT_OFF_SCORE: (state) => selectorConfig(state, 'contentCutOffScore'),
  CONTENT_SCORE: (state) => scoreSelector(state) || 0,
  PAGE_NUMBER: (state) => pageNumberSelector(state).toString(),
  PAGE_TITLE: pageTitleSelector,
  PAGES_COUNT: (state) => pageStructureCountSelector(state).toString(),
  NEXT_PAGE_TITLE: nextPageTitleSelector,
  PREV_PAGE_TITLE: prevPageTitleSelector,
  VISITED_PAGES: (state) => visitedByTypeSelector(state, 'PageContent').toString(),
  PROGRESS_VALUE: (state, id) => textProgressByPropNameSelector(state, id, 'value').toString(),
  PROGRESS_MIN: (state, id) => textProgressByPropNameSelector(state, id, 'minValue').toString(),
  PROGRESS_MAX: (state, id) => textProgressByPropNameSelector(state, id, 'maxValue').toString(),
  PROGRESS_INITIAL: (state, id) => textProgressByPropNameSelector(state, id, 'initialValue').toString(),
  QUIZ_STATUS: quizDescendantStatusSelector,
  QUIZ_SCORE: (state, id) => (quizDescendantScoreSelector(state, id) || 0).toString(),
  QUIZ_MAX_SCORE: (state, id) => (quizDescendantMaxScoreSelector(state, id) || 0).toString(),
  QUIZ_SCORE_PERCENT: (state, id) => Math.round(
    quizScorePercentSelector(state, id) || 0,
  ).toString(),
  QUIZ_TOT_ACTIVITIES: (state, id) => quizTotActivitiesCount(state, id).toString(),
  QUIZ_NUM_CURRENT_ACTIVITY: (state, id) => quizNumCurrentActivitySelector(state, id).toString(),
  QUIZ_CORRECT_ACTIVITIES: (state, id) => quizCorrectActivitiesCount(state, id).toString(),
  QUIZ_CURRENT_ATTEMPT: (state, id) => quizCurrentAttemptSelector(state, id).toString(),
  QUIZ_MAX_ATTEMPTS: (state, id) => toStringWithInfinityChar(quizMaxAttemptSelector(state, id)),
  QUIZ_REMAINING_ATTEMPTS: (state, id) => toStringWithInfinityChar(
    quizRemainingAttemptsSelector(state, id),
  ),
  QUIZ_SUCCESS_RATE_PERCENT: (state, id) => (`${quizDescendantSuccessRatePercentSelector(state, id)}%`),
  ACTIVITY_RIGHT_ANSWERS_COUNT: (state, id) => activityRightAnswersCount(state, id).toString(),
  INPUT_REMAINING_CHARS: (state, id) => inputRemainingCharsSelector(state, id).toString(),
  QUIZ_LIMIT_TIME: (state, id) => (quizLimitTimeSelector(state, id) || 0).toString(),
  QUIZ_ELAPSED_TIME: (state, id) => (quizElapsedTimeSelector(state, id) || 0).toString(),
  QUIZ_REMAINING_TIME: (state, id) => (quizRemainingTimeSelector(state, id) || 0).toString(),
  STUDENT_NAME: () => lmsApi.getName(),
  STUDENT_ID: () => lmsApi.getId(),
};

function replaceTextVariableSelector(state, variable, id) {
  if (selectors[variable]) return selectors[variable](state, id);
  return variable;
}

export default replaceTextVariableSelector;
