import { CURRENTLANG } from '../../editor/stateKeys';
import { setAllState } from '../../stateChanger/stateChanger';

function currentLangReducerChange(state, action, allState) {
  const oldKey = action?.payload?.oldKey;
  const newKey = action?.payload?.newKey;
  if (oldKey !== state) return state;
  return setAllState(state, newKey, allState, CURRENTLANG);
}

export default currentLangReducerChange;
