import { useContext } from 'react';
import { useDispatch } from 'react-redux';
import QuizContext from '../InteractiveRender/customComps/QuizContext';
import quizActivitiesSelector from '../selectors/quizActivitiesSelector';
import quizHasAttemptsSelector from '../selectors/quizHasAttemptsSelector';
import { getRetryActivityOperations } from './useRetryActivity';
import useSuspendOperations, { setOperation } from './useSuspendOperations';

export function getRetryQuizOperations(quizId, state, resetResponse) {
  const hasAttempts = quizHasAttemptsSelector(state, quizId);
  if (!hasAttempts) return [];
  const activities = quizActivitiesSelector(state, quizId);
  if (!activities.length) return [];
  const gotoFirstActivityOperation = setOperation(quizId, 'visibleChilds', [activities[0]]);
  return activities.reduce(
    (memo, activityId) => [
      ...memo,
      ...getRetryActivityOperations(activityId, resetResponse),
    ],
    [gotoFirstActivityOperation],
  );
}

function useRetryQuiz(resetResponse = true) {
  const quizId = useContext(QuizContext);
  const suspendOperations = useSuspendOperations();
  const dispatchThunk = useDispatch();
  return () => {
    const operations = dispatchThunk(
      (dispatch, getState) => getRetryQuizOperations(quizId, getState(), resetResponse),
    );
    if (operations.length) suspendOperations(operations);
  };
}

export default useRetryQuiz;
