import selectorChildsByTypes from '@auth/lib/src/selectors/selectorChildsByTypes';
import suspendDataSelector from '../suspendDataSelector';

function imageInteractOneHotspotSelector(state, id) {
  const items = selectorChildsByTypes(state, id, ['Hotspot', 'Area']);
  if (!items.length) return true;
  return items.some(
    (item) => suspendDataSelector(item, 'interact', state) === 1,
  );
}

export default imageInteractOneHotspotSelector;
