import React from 'react';
import RenderContent from '@auth/components/src/RenderContent/RenderContent';
import useAppData from '@auth/shared/src/hooks/useAppData';
import RenderContentContext from '@auth/components/src/RenderContent/RenderContentContext';
import usePage from '../hooks/usePage';
import InteractiveRenderComp from './InteractiveRenderComp';

function RenderPage() {
  const { unitId } = useAppData();
  const page = usePage();
  return (
    <div className="mantain-aspect-ratio">
      <RenderContentContext.Provider value={InteractiveRenderComp}>
        <RenderContent id={page} unitId={unitId} />
      </RenderContentContext.Provider>
    </div>
  );
}

RenderPage.propTypes = {};

export default RenderPage;
