import { resolutionToNumber } from './useEditorCanvasSize';
import useGetConfig from './useGetConfig';

export function patchMqConfig(desktop, tablet, mobile) {
  const patchDesktop = `${resolutionToNumber(desktop) + 1}px`;
  return {
    desktop: patchDesktop,
    tablet,
    mobile,
  };
}

const useMqConfig = () => {
  const desktop = useGetConfig('resolutionDesktop');
  const tablet = useGetConfig('resolutionTablet');
  const mobile = useGetConfig('resolutionMobile');

  return patchMqConfig(desktop, tablet, mobile);
};

export default useMqConfig;
