import suspendDataTempReducerSet, { SET_TEMP_SUSPEND_DATA } from './suspendDataTempReducerSet';
import suspendDataTempReducerUnset, { UNSET_TEMP_SUSPEND_DATA } from './suspendDataTempReducerUnset';

const config = {
  [SET_TEMP_SUSPEND_DATA]: suspendDataTempReducerSet,
  [UNSET_TEMP_SUSPEND_DATA]: suspendDataTempReducerUnset,
};

function suspendDataTempReducerOperations(state, action) {
  const { payload } = action;
  const { operations } = payload;
  return operations.reduce(
    (memo, operation) => {
      const { type } = operation;
      if (config[type]) return config[type](memo, operation);
      return memo;
    },
    state,
  );
}

export default suspendDataTempReducerOperations;
