import React, { useMemo } from 'react';
import PropTypes from 'prop-types';
import { shallowEqual, useSelector } from 'react-redux';
import Composite from '@auth/components/src/Composite/Composite';
import selectorRealType from '@auth/lib/src/selectors/selectorRealType';
import useGetAccordionGridTemplateAreas from '@auth/shared/src/hooks/useGetAccordionGridTemplateAreas';
import useGetAccordionHeaderId from '@auth/shared/src/hooks/useGetAccordionHeaderId';
import useGetAccordionFirstCompositeItem from '@auth/shared/src/hooks/useGetAccordionFirstCompositeItem';
import VisbleChildsContext from './VisbleChildsContext';
import useGetCompositeVisibleChilds from '../../hooks/useGetCompositeVisibleChilds';
import useNextSlide from '../../hooks/useNextSlide';
import usePrevSlide from '../../hooks/usePrevSlide';

function InteractiveRenderComposite(props) {
  const { id, childs } = props;
  const visibleChilds = useGetCompositeVisibleChilds(id);

  const compositeItems = useSelector(
    (state) => childs.filter(
      (child) => selectorRealType(state, child) === 'CompositeItem',
    ),
    shallowEqual,
  );
  const goPrevSlide = usePrevSlide();
  const goNextSlide = useNextSlide();
  const handlePrev = () => {
    goPrevSlide(id, compositeItems, visibleChilds[0]);
  };
  const handleNext = () => {
    goNextSlide(id, compositeItems, visibleChilds[0]);
  };
  const contextValue = useMemo(() => ({
    componentId: id,
    visibleChild: visibleChilds[0],
    childs: compositeItems,
  }), [id, visibleChilds, compositeItems]);
  return (
    <VisbleChildsContext.Provider value={contextValue}>
      <Composite
        {...props}
        visibleChilds={visibleChilds}
        accordionGridTemplateAreas={useGetAccordionGridTemplateAreas(id)}
        accordionHeaderId={useGetAccordionHeaderId(childs)}
        firstCompositeItem={useGetAccordionFirstCompositeItem(childs)}
        goPrev={handlePrev}
        goNext={handleNext}
      />
    </VisbleChildsContext.Provider>
  );
}

InteractiveRenderComposite.propTypes = {
  id: PropTypes.string.isRequired,
  childs: PropTypes.arrayOf(PropTypes.string).isRequired,
};
InteractiveRenderComposite.defaultProps = {
};

InteractiveRenderComposite.slots = Composite.slots;

export default InteractiveRenderComposite;
