import { useDispatch } from 'react-redux';
import { useCallback } from 'react';
import { hideNotificationAction } from '../reducers/notificationsReducer';

function useHideNotification() {
  const dispatchThunk = useDispatch();
  return useCallback(
    (id) => dispatchThunk((dispatch) => dispatch(hideNotificationAction(id))),
    [dispatchThunk],
  );
}

export default useHideNotification;
