import { createSelector } from 'reselect';
import { useSelector } from 'react-redux';
import selectorStateColors from '@auth/lib/src/selectors/selectorStateColors';

const colorsToEmotion = createSelector(
  [
    selectorStateColors,
  ],
  (
    colors,
  ) => Object.entries(colors).reduce((m, [key, value]) => `${m}--ntx-custom-${key}: ${value.color};`, ''),
);

function useConvertColorsToEmotion() {
  return useSelector(colorsToEmotion);
}

export default useConvertColorsToEmotion;
