import shuffleArray from '../utils/shuffleArray';
import useGetSuspendData from './useGetSuspendData';
import useSetSuspendData from './useSetSuspendData';

function useGetOrderChilds(props) {
  const {
    id,
    randomChildsOrder,
    childs,
  } = props;
  const childsInSuspend = useGetSuspendData(id, 'orderChilds');
  const setSuspend = useSetSuspendData();
  if (!randomChildsOrder) return childs;
  if (childsInSuspend) return childsInSuspend;
  const orderChilds = [...childs];
  shuffleArray(orderChilds);
  setSuspend(id, 'orderChilds', orderChilds);
  return orderChilds;
}

export default useGetOrderChilds;
