import React, { useContext, useState } from 'react';
import PropTypes from 'prop-types';
import Hotspot from '@auth/components/src/Hotspot/Hotspot';
import { useSelector } from 'react-redux';
import getValue from '@auth/components/src/RenderContent/selectorGetCss/getValue';
import selectorStateEditorResolutionWidth from '@auth/lib/src/selectors/selectorStateEditorResolutionWidth';
import InteractiveClick from './interactiveClick/InteractiveClick';
import useSetSuspendData from '../../hooks/useSetSuspendData';
import ImageContext from './ImageContext';
import useSelected from '../../hooks/useSelected';
import getCssPropByButtonState from './interactiveRenderButton/getCssPropByButtonState';

function InteractiveRenderHotspot(props) {
  const [buttonState, setButtonState] = useState('');
  const handleOnStateChange = (newButtonState) => {
    setButtonState(newButtonState);
  };
  const iconSrc = useSelector(
    (state) => getValue(
      'iconSrc',
      buttonState,
      selectorStateEditorResolutionWidth(state),
      props,
    ),
  );
  const x = useSelector(
    (state) => getValue(
      'x',
      buttonState,
      selectorStateEditorResolutionWidth(state),
      props,
    ),
  );
  const y = useSelector(
    (state) => getValue(
      'y',
      buttonState,
      selectorStateEditorResolutionWidth(state),
      props,
    ),
  );
  const { id } = props;
  const setSuspend = useSetSuspendData();
  const myImageId = useContext(ImageContext);
  const handleClick = () => {
    setSuspend(id, 'interact', 1);
    setSuspend(myImageId, 'sel', id);
  };
  const selected = useSelected(props);

  const buttonTextDisplay = getCssPropByButtonState(buttonState, 'buttonTextDisplay', props);
  const buttonIconDisplay = getCssPropByButtonState(buttonState, 'buttonIconDisplay', props);

  return (
    <InteractiveClick
      {...props}
      onStateChange={handleOnStateChange}
      iconSrc={iconSrc}
      x={x}
      y={y}
      Comp={Hotspot}
      onClick={handleClick}
      selected={selected}
      buttonTextDisplay={buttonTextDisplay}
      buttonIconDisplay={buttonIconDisplay}
    />
  );
}

InteractiveRenderHotspot.propTypes = {
  id: PropTypes.string.isRequired,
  buttonTextDisplay: PropTypes.oneOf(['none', 'initial']),
};

InteractiveRenderHotspot.defaultProps = {
  buttonTextDisplay: 'none',
};

export default InteractiveRenderHotspot;
