import selectorProp from '@auth/lib/src/selectors/selectorProp';
import areArraysEqual from '@auth/shared/src/utils/areArraysEqual';
import { useContext } from 'react';
import { useSelector } from 'react-redux';
import ActivityContext from '../InteractiveRender/customComps/ActivityContext';
import GroupOptionContext from '../InteractiveRender/customComps/GroupOptionContext';
import optionActivityIsFinished from '../InteractiveRender/customComps/interactiveRenderChoiceOption/optionActivityIsFinished';
import QuizContext from '../InteractiveRender/customComps/QuizContext';
import suspendDataSelector from '../selectors/suspendDataSelector';

function getTargets(rels, id) {
  return rels.reduce(
    (memo, rel) => {
      const [source, target] = rel.split('[.]');
      if (source === id) { return [...memo, target]; }
      return memo;
    },
    [],
  ).sort();
}

function getSources(rels, id) {
  return rels.reduce(
    (memo, rel) => {
      const [source, target] = rel.split('[.]');
      if (target === id) { return [...memo, source]; }
      return memo;
    },
    [],
  ).sort();
}

export function selector(state, activityId, groupOptionId, id, isSource, activityFinished) {
  const activityChecked = suspendDataSelector(activityId, 'correct', state) !== undefined;
  if (!activityChecked) {
    return suspendDataSelector(activityId, 'current', state) === id ? 'askingchosen' : '';
  }
  const response = suspendDataSelector(activityId, 'response', state);
  const correct = selectorProp(state, groupOptionId, 'correct');
  const correctRelations = correct ? correct.split('[,]') : [];
  const allRelations = response ? response.split('[,]') : [];
  const get = isSource ? getTargets : getSources;
  const myCorrectRels = get(correctRelations, id);
  const myRels = get(allRelations, id);
  const isCorrect = areArraysEqual(myCorrectRels, myRels);
  if (!myRels.length && !activityFinished) {
    return '';
  }
  if (isCorrect) return 'answeredright';
  return 'answeredwrong';
}

function useArrowPseudoselector(id, type) {
  const quizId = useContext(QuizContext);
  const activityId = useContext(ActivityContext);
  const groupOptionId = useContext(GroupOptionContext);
  const isSource = type === 'ArrowSource';
  return useSelector(
    (state) => {
      const activityFinished = optionActivityIsFinished(state, quizId);
      return selector(state, activityId, groupOptionId, id, isSource, activityFinished);
    },
  );
}

export default useArrowPseudoselector;
