import { useContext } from 'react';
import { useDispatch } from 'react-redux';
import { sendMultipleInteractions } from '../../../tracking/tracking';
import QuizContext from '../QuizContext';
import useSuspendOperations from '../../../hooks/useSuspendOperations';
import useCheckQuizEvents from '../../../hooks/useCheckQuizEvents';
import checkQuizSuspendOperationsSelector from '../../../selectors/checkQuizSuspendOperationsSelector';
import checkQuizInteractionsSelector from '../../../selectors/checkQuizInteractionsSelector';
import quizHasAttemptsSelector from '../../../selectors/quizHasAttemptsSelector';

function useClickCheckQuiz() {
  const quizId = useContext(QuizContext);
  const checkQuizEvents = useCheckQuizEvents();
  const dispatchThunk = useDispatch();
  const suspendOperations = useSuspendOperations();
  return () => {
    dispatchThunk(
      (dispatch, getState) => {
        const state = getState();
        suspendOperations(checkQuizSuspendOperationsSelector(state, quizId));
        const hasAttempts = quizHasAttemptsSelector(state, quizId);
        if (hasAttempts) {
          sendMultipleInteractions(checkQuizInteractionsSelector(getState(), quizId));
          checkQuizEvents(quizId);
        }
      },
    );
  };
}

export default useClickCheckQuiz;
