import nullFunc from '@auth/components/src/nullFunc';
import { useCallback } from 'react';
import { useDispatch } from 'react-redux';
import getTodo from './useArrows/getTodo';
import setCurrent from './useArrows/setCurrent';
import setResponse from './useArrows/setResponse';

const config = {
  none: nullFunc,
  setCurrent,
  setResponse,
};

function useArrows(id, activityId) {
  const dispatchThunk = useDispatch();
  return useCallback(
    () => dispatchThunk(
      (dispatch, getState) => {
        const state = getState();
        const todo = getTodo(id, activityId, state);
        return config[todo](dispatch, activityId, id, state);
      },
    ),
    [activityId, dispatchThunk, id],
  );
}

export default useArrows;
