import myAdd from './stateChangerAdd';
import mySet from './stateChangerSet';
import myUnset from './stateChangerUnset';
import myRemove from './stateChangerRemove';
import mySetAllState from './stateChangerSetAllState';
import undoAdd from './undoAdd';
import undoSet from './undoSet';
import undoUnset from './undoUnset';
import undoRemove from './undoRemove';
import undoSetAllState from './undoSetAllState';
import { allStateKeys } from '../editor/stateKeys';

const validateStateKey = (key) => allStateKeys.includes(key);

export function add(state, id, data, mutable, allState, stateKey) {
  if (!state) throw new Error('stateChanger:add invalid state');
  if (!id) throw new Error('stateChanger:add invalid id');
  if (!data) throw new Error('stateChanger:add invalid data');
  if (!validateStateKey(stateKey)) throw new Error('stateChanger:add invalid stateKey');
  if (state[id]) return mySet(state, id, data, mutable, allState, stateKey);
  return myAdd(state, id, data, mutable, allState, stateKey);
}

export function set(state, id, data, mutable, allState, stateKey) {
  if (!state) throw new Error('stateChanger:set invalid state');
  if (!id) throw new Error('stateChanger:set invalid id');
  if (!validateStateKey(stateKey)) throw new Error('stateChanger:set invalid stateKey');
  return mySet(state, id, data, mutable, allState, stateKey);
}

export function unset(state, id, data, mutable, allState, stateKey) {
  if (!state) throw new Error('stateChanger:unset invalid state');
  if (!id) throw new Error('stateChanger:unset invalid id');
  if (!validateStateKey(stateKey)) throw new Error('stateChanger:unset invalid stateKey');
  return myUnset(state, id, data, mutable, allState, stateKey);
}

export function remove(state, id, mutable, allState, stateKey) {
  if (!state) throw new Error('stateChanger:remove invalid state');
  if (!id) throw new Error('stateChanger:remove invalid id');
  if (!validateStateKey(stateKey)) throw new Error('stateChanger:remove invalid stateKey');
  return myRemove(state, id, mutable, allState, stateKey);
}

export function setAllState(state, data, allState, stateKey) {
  if (!validateStateKey(stateKey)) throw new Error('stateChanger:setAllState invalid stateKey');
  return mySetAllState(state, data, allState, stateKey);
}

const undoFuncs = {
  add: undoAdd,
  set: undoSet,
  unset: undoUnset,
  remove: undoRemove,
  setAllState: undoSetAllState,
};

export function undo(state, operations, mutable, allState, stateKey) {
  if (!state) throw new Error(`stateChanger:undo invalid state ${stateKey}`);
  if (!operations?.length) throw new Error('stateChanger:undo invalid operations');
  return [...operations].reverse().reduce(
    (memo, operation) => undoFuncs[operation.type](memo, operation, mutable, allState, stateKey),
    state,
  );
}

export function redo(state, operations, mutable, allState, statekey) {
  if (!state) throw new Error(`stateChanger:redo invalid state ${statekey}`);
  if (!operations?.length) throw new Error('stateChanger:redo invalid operations');
  return operations.reduce(
    (memo, operation) => {
      const { type, id, data } = operation;
      if (type === 'add') return add(memo, id, data, mutable, allState, statekey);
      if (type === 'set') return set(memo, id, data, mutable, allState, statekey);
      if (type === 'unset') return unset(memo, id, data, mutable, allState, statekey);
      if (type === 'remove') return remove(memo, id, mutable, allState, statekey);
      if (type === 'setAllState') return setAllState(state, data, allState, statekey);
      return memo;
    },
    state,
  );
}
