const sec = 1000;
const min = sec * 60;
const hou = min * 60;
const day = hou * 24;
const mon = day * 30;
const yea = mon * 12;

const iso8601TimeInterval = (miliseconds) => {
  const years = Math.floor(miliseconds / yea);
  const months = Math.floor((miliseconds % yea) / mon);
  const days = Math.floor((miliseconds % mon) / day);
  const hours = Math.floor((miliseconds % day) / hou);
  const minutes = Math.floor((miliseconds % hou) / min);
  const seconds = Math.floor((miliseconds % min) / sec);
  let ret = 'P';
  if (years > 0) ret += `${years}Y`;
  if (months > 0) ret += `${months}M`;
  if (days > 0) ret += `${days}D`;
  ret += 'T';
  if (hours > 0) ret += `${hours}H`;
  if (minutes > 0) ret += `${minutes}M`;
  if (seconds >= 0) ret += `${seconds}S`;
  return ret;
};

export default iso8601TimeInterval;
