import nullFunc from '../../utils/nullFunc';
import choicesActivityLikert from './choicesActivityLikert';
import choicesActivityMultipleChoice from './choicesActivityMultipleChoice';
import choicesActivitySequence from './choicesActivitySequence';
import choicesActivitySingleChoice from './choicesActivitySingleChoice';

const config = {
  ActivityArrows: nullFunc,
  ActivityLikert: choicesActivityLikert,
  ActivityMultipleChoice: choicesActivityMultipleChoice,
  ActivitySequence: choicesActivitySequence,
  ActivitySingleChoice: choicesActivitySingleChoice,
  ActivityTextInput: nullFunc,
};

const getChoices = (state, id, type) => config[type](state, id);

export default getChoices;
