import React from 'react';
import PropTypes from 'prop-types';
import useInteractiveProps from '../useInteractiveProps';

function MenuList(props) {
  const {
    className, children, cssData,
  } = props;
  const { alignSelf } = cssData;
  const newStyle = alignSelf !== 'start' ? { alignSelf: 'start' } : {};
  return (
    <nav
      className={`${className} menuList`}
      style={{ ...newStyle }}
      {...useInteractiveProps(props)}
    >
      {children}
    </nav>
  );
}

MenuList.propTypes = {
  id: PropTypes.string.isRequired,
  currentId: PropTypes.string,
  className: PropTypes.string.isRequired,
  click: PropTypes.func,
  children: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]),
  // eslint-disable-next-line react/forbid-prop-types
  cssData: PropTypes.object,
};
MenuList.defaultProps = {
  click: () => null,
  currentId: null,
  children: null,
  cssData: {},
};

export default MenuList;
