import React, {
  useCallback, useEffect, useRef, useState,
} from 'react';
import PropTypes from 'prop-types';
import useComponentSize from '@auth/shared/src/hooks/useComponentSize';
import ToolbarControl from './ToolbarControl';
import useInteractiveProps from '../useInteractiveProps';
import VideoPlayer from './VideoPlayer';
import SubtitleNoNative from './SubtitleNoNative';

const supportFullScreen = (() => {
  const docElm = document.documentElement;
  if (
    docElm.requestFullscreen
    || docElm.mozRequestFullScreen
    || docElm.webkitRequestFullScreen
    || docElm.msRequestFullscreen
  ) {
    return true;
  }
  return false;
})();

const getUrl = (srcOrigin, src, time) => {
  if (srcOrigin === 'youtube') {
    const timeParam = `#t=${time}`;
    return `${src}${timeParam}`;
  }
  return src;
};

const canHaveSubtitles = (srcOrigin) => (
  window.location.protocol !== 'file:'
  && !['youtube', 'vimeo', 'web'].includes(srcOrigin)
);

const filterPropsByPrefix = (props, prefix) => Object.entries(props).reduce(
  (m, [k, v]) => {
    if (k.startsWith(prefix)) {
      return {
        ...m,
        [k.replace(prefix, '')]: v,
      };
    }
    return m;
  },
  {},
);

export const getSubtitlesVideo = (props) => filterPropsByPrefix(props, 'subtitle_');

const getBookmarks = (props) => Object.entries(filterPropsByPrefix(props, 'bookmark_')).reduce(
  (m, [id, v]) => ({ ...m, [id]: v }),
  {},
);

const openFullscreen = (element) => {
  if (element.requestFullscreen) {
    element.requestFullscreen();
  } else if (element.webkitRequestFullscreen) { /* Safari */
    element.webkitRequestFullscreen();
  } else if (element.msRequestFullscreen) { /* IE11 */
    element.msRequestFullscreen();
  } else { /* Unsupported */
    const event = new Event('customfullscreenchange', { bubbles: true });
    element.dispatchEvent(event);
  }
};

const closeFullscreen = (element, withoutSupporForceNative = false, videoElement = null) => {
  const isFullScreen = document.fullscreenElement
    || document.webkitFullscreenElement
    || document.mozFullscreenElement
    || document.msFullscreenElement
    || element.classList.contains('element-fullscreen')
    || videoElement.webkitDisplayingFullscreen;
  if (isFullScreen) {
    if (document.exitFullscreen) {
      document.exitFullscreen();
    } else if (document.webkitExitFullscreen) { /* Safari */
      document.webkitExitFullscreen();
    } else if (document.msExitFullscreen) { /* IE11 */
      document.msExitFullscreen();
    } else if (
      withoutSupporForceNative
      && videoElement.webkitSupportsFullscreen
      && videoElement.webkitExitFullscreen
    ) { /* iOS */
      videoElement.webkitExitFullscreen();
    } else { /* Unsupported */
      const event = new Event('customfullscreenchange', { bubbles: true });
      element.dispatchEvent(event);
    }
  }
};

function Video(props) {
  const {
    id,
    className,
    allowFullscreenOS,
    src,
    srcOrigin,
    poster,
    currentTime,
    onTimeUpdate,
    onPlay,
    onPause,
    onEnded,
    onReady,
    timeEvents,
    fastFordward,
    subtitleDefaultLanguage,
    subtitlesInCCR,
    listenChangeSubtitles,
    subtitlesSource,
  } = props;
  const playedSometime = useRef(false);
  const componentEl = useRef(null);
  const componentPlayer = useRef(null);
  const nativeSubtitles = subtitlesSource !== 'local' || srcOrigin === 'ccr';
  const size = useComponentSize(componentEl);
  const interactiveProps = useInteractiveProps(props);
  const [currentSubtitles, setCurrentSubtitles] = useState(
    (srcOrigin === 'ccr' || subtitlesSource === 'local') ? subtitleDefaultLanguage : 'none',
  );
  const [duration, setDuration] = useState(0);
  const [playing, setPlaying] = useState(false);
  const [volume, setVolume] = useState(1);
  const [muted, setMuted] = useState(false);
  const [totalLoaded, setTotalLoaded] = useState(0);
  const [totalPlayed, setTotalPlayed] = useState(0);
  const [seeking, setSeeking] = useState(false);
  const [ready, setReady] = useState(false);
  const [styleResponsive, setStyleResponsive] = useState({
    paddingBottom: '56.25%',
    height: 0,
  });
  const [idTimeoutHover, setIdTimeoutHover] = useState(null);
  const hideToolbarOnYoutube = srcOrigin === 'youtube' && !playedSometime.current;
  const [showToolbar, setShowToolbar] = useState(!hideToolbarOnYoutube);
  const [classFullscreen, setClassFullscreen] = useState('');

  const getInternalPlayer = () => (
    componentPlayer?.current?.getInternalPlayer
      ? componentPlayer.current.getInternalPlayer() || {}
      : {});

  const seek = (percent) => {
    setTotalPlayed(percent);
    componentPlayer.current.seekTo(percent);
  };

  const url = getUrl(srcOrigin, src, currentTime);

  const handleSetSubtitle = (lang) => {
    setCurrentSubtitles(lang);
  };

  const handleSetFullscreen = () => {
    const goToFullScreen = !document.fullscreenElement
      && !document.webkitFullscreenElement
      && !document.mozFullscreenElement
      && !document.msFullscreenElement
      && !componentEl.current.classList.contains('element-fullscreen');
    if (goToFullScreen) {
      if (!supportFullScreen && allowFullscreenOS) {
        const internalPlayer = getInternalPlayer();
        if (
          internalPlayer.webkitSupportsFullscreen
          && internalPlayer.webkitEnterFullscreen
        ) {
          // escuchar webkitendfullscreen para enterarse de que salio de ese modo
          getInternalPlayer().webkitEnterFullscreen();
        } else {
          setPlaying(true);
        }
      } else {
        openFullscreen(componentEl.current);
      }
    } else {
      closeFullscreen(componentEl.current);
    }
  };

  const setValuesForAspectRatio = useCallback(() => {
    if (['ccr', 'web'].includes(srcOrigin)) {
      const { videoWidth, videoHeight } = getInternalPlayer();
      setStyleResponsive({
        paddingBottom: `${100 / (videoWidth / videoHeight)}%`,
        height: 0,
      });
    } else {
      setStyleResponsive({
        paddingBottom: '56.25%',
        height: 0,
      });
    }
  }, [srcOrigin, setStyleResponsive]);

  useEffect(() => {
    if (duration) {
      setTotalPlayed(currentTime / duration);
    }
  }, [duration, setTotalPlayed, currentTime]);

  useEffect(
    () => {
      if (playing) {
        playedSometime.current = true;
        onPlay();
      }
    },
    [playing, onPlay],
  );

  useEffect(
    () => {
      if (!playing) {
        onPause();
      }
    },
    [playing, onPause],
  );

  useEffect(
    () => {
      setReady(false);
    },
    [src],
  );

  useEffect(
    () => {
      if (ready) {
        setValuesForAspectRatio();
      }
    },
    [ready, src, srcOrigin, setValuesForAspectRatio],
  );

  useEffect(() => {
    if (ready) {
      const rootElement = componentEl.current;
      const afterFullScreenEvent = (enter, isNative = true) => {
        if (enter) {
          setStyleResponsive({
            paddingBottom: 0,
            height: isNative ? '100vh' : '100%',
          });
        } else {
          setValuesForAspectRatio();
        }
      };
      const handleMsFullscreenChange = () => {
        afterFullScreenEvent(document.msFullscreenElement);
      };
      const handleMozFullscreenChange = () => {
        afterFullScreenEvent(document.mozFullscreenElement);
      };
      const handleWebkitFullscreenChange = () => {
        afterFullScreenEvent(document.webkitFullscreenElement);
      };
      const handleFullscreenChange = () => {
        afterFullScreenEvent(document.fullscreenElement);
      };
      const handleCustomFullscreenChange = () => {
        if (rootElement.classList.contains('element-fullscreen')) {
          setClassFullscreen('');
          afterFullScreenEvent(false, false);
        } else {
          setClassFullscreen('element-fullscreen');
          afterFullScreenEvent(true, false);
        }
      };
      rootElement.addEventListener('msfullscreenchange', handleMsFullscreenChange);
      rootElement.addEventListener('mozfullscreenchange', handleMozFullscreenChange);
      rootElement.addEventListener('webkitfullscreenchange', handleWebkitFullscreenChange);
      rootElement.addEventListener('fullscreenchange', handleFullscreenChange);
      rootElement.addEventListener('customfullscreenchange', handleCustomFullscreenChange);
      return () => {
        rootElement.removeEventListener('msfullscreenchange', handleMsFullscreenChange);
        rootElement.removeEventListener('mozfullscreenchange', handleMozFullscreenChange);
        rootElement.removeEventListener('webkitfullscreenchange', handleWebkitFullscreenChange);
        rootElement.removeEventListener('fullscreenchange', handleFullscreenChange);
        rootElement.removeEventListener('customfullscreenchange', handleCustomFullscreenChange);
      };
    }
    return undefined;
  }, [ready, setValuesForAspectRatio, allowFullscreenOS]);

  const playsinline = (() => {
    if (supportFullScreen || srcOrigin === 'ccr') {
      return true;
    }
    if (allowFullscreenOS) {
      return false;
    }
    return true;
  })();
  useEffect(
    () => {
      if (ready) {
        onReady({
          play: () => {
            setPlaying(true);
          },
          pause: () => {
            setPlaying(false);
          },
          exitFullscreen: () => {
            closeFullscreen(componentEl.current, allowFullscreenOS, getInternalPlayer());
          },
          seek: (time) => {
            componentPlayer.current.seekTo(time, 'seconds');
          },
          getCurrentTime: () => (
            componentPlayer.current ? componentPlayer.current.getCurrentTime() : 0
          ),
        }, playsinline);
      }
    },
    [ready, setPlaying, onReady, allowFullscreenOS, playsinline],
  );

  const initIdTimeoutHover = () => {
    setIdTimeoutHover(setTimeout(() => {
      setShowToolbar(false);
    }, 3000));
  };

  useEffect(() => {
    if (!playing && !hideToolbarOnYoutube) {
      setShowToolbar(true);
    }
    if (playing) {
      initIdTimeoutHover();
    }
  }, [playing, hideToolbarOnYoutube]);

  if (!playing) {
    clearTimeout(idTimeoutHover);
  }

  const classNameState = playing
    ? 'ntx-video-playing'
    : 'ntx-video-paused';

  const classNameShowToolbar = showToolbar
    ? 'ntx-video-show-toolbar'
    : 'ntx-video-hide-toolbar';

  const [subtitles, setSubtitles] = useState({});
  const handleOnMouseMove = () => {
    if (playing) {
      clearTimeout(idTimeoutHover);
      setShowToolbar(true);
      initIdTimeoutHover();
    }
  };

  const bookmarks = getBookmarks(props);
  const bookmarksSeconds = Object.values(bookmarks).map(
    (strObj) => +JSON.parse(strObj).second,
  );
  const timeEventsAndBookmarks = [...new Set([...timeEvents, ...bookmarksSeconds])];

  const handleOnReady = useCallback(
    () => {
      setReady(true);
    },
    [],
  );
  const handleOnPlay = useCallback(
    () => {
      setPlaying(true);
    },
    [],
  );
  const handleOnPause = useCallback(
    () => {
      setPlaying(false);
    },
    [],
  );

  const handleOnProgress = useCallback(
    ({ loaded, played, playedSeconds }) => {
      if (!seeking) {
        setTotalPlayed(played);
        setTotalLoaded(loaded);
        onTimeUpdate(playedSeconds);
      }
    },
    [onTimeUpdate, seeking],
  );
  const handleOnEnded = useCallback(
    () => {
      onEnded();
      setPlaying(false);
    },
    [onEnded],
  );
  return (
    <div
      className={className}
      {...interactiveProps}
    >
      <figure
        ref={componentEl}
        className={`ntx-video-skin-1 ${classNameState} ${classNameShowToolbar} ${classFullscreen}`}
      >
        <div className="ntx-video-player-wrapper" style={styleResponsive}>
          <VideoPlayer
            key={url}
            ref={componentPlayer}
            url={url}
            typeResource={srcOrigin}
            poster={poster}
            playsinline={playsinline}
            loadNativeSubtitles={nativeSubtitles}
            listenChangeSubtitles={nativeSubtitles ? listenChangeSubtitles : false}
            currentSubtitles={nativeSubtitles ? currentSubtitles : 'none'}
            subtitlesInCCR={
              (() => {
                if (nativeSubtitles) {
                  return canHaveSubtitles(srcOrigin) ? subtitlesInCCR : JSON.stringify({});
                }
                return JSON.stringify({});
              })()
            }
            setSubtitles={setSubtitles}
            playing={playing}
            muted={muted}
            volume={volume}
            onReady={handleOnReady}
            onDuration={setDuration}
            onPlay={handleOnPlay}
            onPause={handleOnPause}
            onProgress={handleOnProgress}
            onEnded={handleOnEnded}
          />
          {
            !nativeSubtitles && (
              <SubtitleNoNative
                url={JSON.parse(subtitlesInCCR)[currentSubtitles]}
                currentTime={totalPlayed * duration}
                subtitlesNoNatives={subtitlesInCCR}
                setSubtitles={setSubtitles}
              />
            )
          }
        </div>
        <ToolbarControl
          id={id}
          duration={duration}
          loaded={totalLoaded}
          played={totalPlayed}
          paused={!playing}
          volume={volume}
          muted={muted}
          play={() => {
            setPlaying(true);
          }}
          pause={() => {
            setPlaying(false);
          }}
          setVolume={(newVolume) => {
            setVolume(newVolume);
          }}
          mute={() => {
            setMuted(true);
          }}
          unmute={() => {
            setMuted(false);
          }}
          setSeeking={(isSeeking) => {
            setSeeking(isSeeking);
          }}
          seek={seek}
          setFullscreen={handleSetFullscreen}
          rootEl={componentEl}
          subtitles={subtitles}
          setSubtitle={handleSetSubtitle}
          currentSubtitle={currentSubtitles}
          timeEvents={timeEventsAndBookmarks}
          fastFordward={fastFordward}
          bookmarks={bookmarks}
          heightVideo={size.height}
        />
        <div
          className="ntx-video-cover-hack"
          onMouseMove={handleOnMouseMove}
        />
        <div
          className="ntx-video-gradient"
        />
      </figure>
    </div>
  );
}

Video.propTypes = {
  id: PropTypes.string.isRequired,
  className: PropTypes.string,
  allowFullscreenOS: PropTypes.bool,
  src: PropTypes.string,
  srcOrigin: PropTypes.string,
  poster: PropTypes.string,
  currentTime: PropTypes.number,
  onTimeUpdate: PropTypes.func,
  onPlay: PropTypes.func,
  onPause: PropTypes.func,
  onEnded: PropTypes.func,
  onReady: PropTypes.func,
  timeEvents: PropTypes.arrayOf(PropTypes.number),
  fastFordward: PropTypes.bool,
  subtitleDefaultLanguage: PropTypes.string,
  subtitlesInCCR: PropTypes.string,
  listenChangeSubtitles: PropTypes.bool,
  subtitlesSource: PropTypes.string,
};

Video.defaultProps = {
  allowFullscreenOS: false,
  src: `${process.env.PUBLIC_URL}/resources/video.mp4`,
  srcOrigin: 'ccr',
  className: '',
  poster: '',
  currentTime: 0,
  onTimeUpdate: () => null,
  onPlay: () => null,
  onPause: () => null,
  onEnded: () => null,
  onReady: () => null,
  timeEvents: [],
  fastFordward: true,
  subtitleDefaultLanguage: 'none',
  subtitlesInCCR: '{}',
  listenChangeSubtitles: true,
  subtitlesSource: 'online',
};

export default Video;
