import React, { useEffect, useMemo, useState } from 'react';
import { library, fas } from '@auth/shared/src/utils/integrationFortawesome';
import ProgressLine from '@auth/shared/src/components/ProgressLine';
import getBackRoute from '@auth/shared/src/getBackRoute';
import authHeaders from '@auth/shared/src/authHeaders';
import { AppContext } from '@auth/shared/src/hooks/useAppData';
import getQueryParam from '@auth/shared/src/getQueryParam';
import StoreProvider from './StoreProvider';

library.add(fas);

const timesWaitLoading = 5000;

function App() {
  const [state, setState] = useState(window.ntxData);
  const [unitId, setUnitId] = useState(getQueryParam('id') || window.envVars?.authoringUnitId);
  const [isRoutesLoaded, setIsRoutesLoaded] = useState(!!(window.ntxData));
  const [responseData, setResponseData] = useState({});
  useEffect(() => {
    if (!state && !isRoutesLoaded) {
      fetch(
        `${process.env.PUBLIC_URL}/resources/fos_js_routes.json`,
        {
          cache: 'no-store',
        },
      )
        .then((response) => {
          if (!response.ok) throw Error(response.statusText);
          return response;
        })
        .then((response) => response.json())
        .then((routes) => {
          window.Routing.setRoutingData(routes);
          setIsRoutesLoaded(true);
        });
    }
  }, [state, isRoutesLoaded]);

  useEffect(() => {
    if (!state && isRoutesLoaded && !unitId) {
      const url = getBackRoute(
        'api_content_managers_get_collection',
        {},
        { themeId: process.env.REACT_APP_THEME_0_ID },
      );
      fetch(url, {
        headers: {
          ...authHeaders,
          accept: 'application/json',
        },
      })
        .then((response) => {
          if (!response.ok) throw Error(response.statusText);
          return response;
        })
        .then((response) => response.json())
        .then((data) => {
          setUnitId(getQueryParam('id') || data[+getQueryParam('i') || 0].id);
        });
    }
  }, [state, isRoutesLoaded, unitId]);

  useEffect(() => {
    if (unitId && !state && isRoutesLoaded && !responseData.status) {
      const url = getBackRoute(
        'api_authoring_unit_contents_get_projection_item',
        {
          aggregateId: unitId,
          type: 'review',
        },
      );
      fetch(url, {
        headers: {
          ...authHeaders,
          accept: 'application/json',
        },
      })
        .then((response) => {
          if (!response.ok) throw Error(response.statusText);
          return response;
        })
        .then((response) => response.json())
        .then((response) => {
          setResponseData(response);
        });
    }
  }, [state, unitId, isRoutesLoaded, responseData]);

  useEffect(() => {
    let timeoutId;

    if (responseData.status === 'ready') {
      setState(responseData.state);
    }
    if (responseData.status === 'pending') {
      timeoutId = setTimeout(
        () => { setResponseData({}); },
        timesWaitLoading,
      );
    }

    return () => {
      if (timeoutId) clearTimeout(timeoutId);
    };
  }, [responseData]);
  const contextValue = useMemo(() => ({
    unitId,
  }), [unitId]);
  if (!state) return <ProgressLine />;
  return (
    <AppContext.Provider value={contextValue}>
      <StoreProvider data={state} />
    </AppContext.Provider>
  );
}

export default App;
