import selectorClosestType from '@auth/lib/src/selectors/selectorClosestType';
import selectorQuizMaxScore from '@auth/components/src/RenderContent/selectorQuizMaxScore';

const quizDescendantMaxScoreSelector = (state, descendantId) => {
  const quiz = selectorClosestType(state, descendantId, 'Quiz');
  if (!quiz) return undefined;
  return selectorQuizMaxScore(state, quiz);
};

export default quizDescendantMaxScoreSelector;
