import quizHasAttemptsSelector from '../quizHasAttemptsSelector';
import quizStatusSelector from '../quizStatusSelector';

function quizzesOvercomeOrFinalizeSelector(state, id) {
  const status = quizStatusSelector(state, id);
  if (status === 'passed') return true;
  const hasAttempts = quizHasAttemptsSelector(state, id);
  if (!hasAttempts) return true;
  return false;
}

export default quizzesOvercomeOrFinalizeSelector;
