import React from 'react';
import PropTypes from 'prop-types';

const MilestoneMultimediaProgressBar1 = (props) => {
  const {
    onClick,
    left,
  } = props;
  return (
    <button
      type="button"
      aria-label="milestone"
      onClick={() => { onClick(left); }}
      className="ntx-milestone-multimedia"
      style={{
        left: `${(100 * left)}%`,
      }}
    >
      <span />
    </button>
  );
};

MilestoneMultimediaProgressBar1.propTypes = {
  onClick: PropTypes.func,
  left: PropTypes.number,
};

MilestoneMultimediaProgressBar1.defaultProps = {
  onClick: () => null,
  left: 0,
};

export default MilestoneMultimediaProgressBar1;
