import getIntlPropsByCompType from '../data/getIntlPropsByCompType';

const getOrder = (lang, currentLang, defautlLang) => {
  if (lang === currentLang) return 0;
  if (lang === defautlLang) return 5;
  return 10;
};

const getSortedLangKeys = (
  langs, currentLang, defaultLang,
) => Object.keys(langs).sort((a, b) => {
  const aOrder = getOrder(a, currentLang, defaultLang);
  const bOrder = getOrder(b, currentLang, defaultLang);
  if (aOrder > bOrder) return 1;
  if (aOrder < bOrder) return -1;
  return 0;
});

const getPropValue = (propName, langKeys, instance) => {
  const langProps = langKeys.map(
    (lang) => `${propName}__${lang}`,
  );
  langProps.push(propName);
  const instanceKeys = Object.keys(instance);
  const calcPropName = langProps.find(
    (prop) => instanceKeys.includes(prop),
  );
  return instance[calcPropName];
};

function sortMenuListChilds(structure, childs) {
  if (!childs || !childs.sort) return childs;
  return childs.sort((a = '', b = '') => {
    const aIndex = structure.indexOf(a.split('_')[1]);
    const aOrder = aIndex === -1 ? Infinity : aIndex;
    const bIndex = structure.indexOf(b.split('_')[1]);
    const bOrder = bIndex === -1 ? Infinity : bIndex;
    if (aOrder > bOrder) return 1;
    if (aOrder < bOrder) return -1;
    return 0;
  });
}

const selectorDefaultLang = (langs) => {
  const defaultLang = Object.entries(langs).find(
    ([, v]) => !!v.default,
  );
  if (defaultLang) return defaultLang[0];
  return null;
};

function selectorInstanceData(instance, langs, currentLang, structure, id) {
  const defaultLang = selectorDefaultLang(langs);
  const langKeys = getSortedLangKeys(langs, currentLang, defaultLang);
  if (!instance) return {};
  const { type } = instance;
  const intlPropsNames = getIntlPropsByCompType(type);

  const noIntlProps = Object.entries(instance).reduce(
    (memo, [k, v]) => {
      const [propName] = k.split('__');
      if (intlPropsNames.includes(propName)) return memo;
      if (type === 'MenuList' && propName === 'childs') {
        return {
          ...memo,
          [k]: sortMenuListChilds(structure, v),
        };
      }
      return {
        ...memo,
        [k]: v,
      };
    },
    { id },
  );

  if (!intlPropsNames.length) return noIntlProps;
  const intlProps = intlPropsNames.reduce(
    (memo, propName) => {
      const value = getPropValue(propName, langKeys, instance);
      if (value === undefined) return memo;
      return {
        ...memo,
        [propName]: value,
      };
    },
    {},
  );

  return {
    ...noIntlProps,
    ...intlProps,
  };
}

export default selectorInstanceData;
