import React from 'react';
import PropTypes from 'prop-types';
import ProgressBar from '@auth/components/src/ProgressBar/ProgressBar';
import useProgressProp from '../../hooks/useProgressProp';

function InteractiveRenderProgressBar(props) {
  const { id } = props;
  const value = useProgressProp(id, 'value');
  const minValue = useProgressProp(id, 'minValue');
  const maxValue = useProgressProp(id, 'maxValue');
  const initialValue = useProgressProp(id, 'initialValue');
  return (
    <ProgressBar
      {...props}
      value={value}
      minValue={minValue}
      maxValue={maxValue}
      initialValue={initialValue}
    />
  );
}

InteractiveRenderProgressBar.propTypes = {
  id: PropTypes.string.isRequired,
  value: PropTypes.string,
  minValue: PropTypes.string,
  maxValue: PropTypes.string,
};
InteractiveRenderProgressBar.defaultProps = {
  value: '30',
  minValue: '0',
  maxValue: '100',
};

export default InteractiveRenderProgressBar;
