import getMq from './getMq';

function patchAspectRatio(preserveAspectRatio = 'none', data = {}) {
  if (preserveAspectRatio === 'none') return data;
  const valids = Object.entries(data).filter(
    ([propName]) => getMq(propName) === '',
  );
  return valids.reduce(
    (memo, [propName, propValue]) => ({
      ...memo,
      [propName]: propValue,
    }),
    {},
  );
}

export default patchAspectRatio;
