import React, { useContext, useMemo } from 'react';
import { shallowEqual, useSelector } from 'react-redux';
import { createSelector } from 'reselect';
import MenuList from '@auth/components/src/MenuList/MenuList';
import selectorStateInstances from '@auth/lib/src/selectors/selectorStateInstances';
import selectorStateStructure from '@auth/lib/src/selectors/selectorStateStructure';
import { getTitle } from '@auth/lib/src/selectors/selectorTitle';
import useSetCurrentPage from '../../hooks/useSetCurrentPage';
import usePage from '../../hooks/usePage';
import InteractiveRenderStateContext from '../InteractiveRenderStateContext';

const makeDataSelector = () => createSelector(
  selectorStateStructure,
  selectorStateInstances,
  (structure, instances) => structure.reduce(
    (memo, id) => {
      const { title, type, defaultTitle } = instances[id];
      return {
        ...memo,
        [id]: getTitle(title, defaultTitle, type),
      };
    },
    {},
  ),
);

function InteractiveRenderMenuList(props) {
  const dataSelector = useMemo(makeDataSelector, []);
  const data = useSelector(
    dataSelector,
    shallowEqual,
  );
  const { closePanel } = useContext(InteractiveRenderStateContext);
  const setPage = useSetCurrentPage();
  const handleOnClick = (id) => {
    setPage(id);
    closePanel();
  };
  const page = usePage();
  return (
    <MenuList
      {...props}
      data={data}
      click={handleOnClick}
      currentId={page}
    />
  );
}

InteractiveRenderMenuList.propTypes = {
};

InteractiveRenderMenuList.defaultProps = {};

export default InteractiveRenderMenuList;
