export default function registerOperation(state, operation, stateKey) {
  if (!state.patchOperations) {
    state.patchOperations = {};
  }
  if (!state.patchOperations[stateKey]) {
    state.patchOperations = {
      ...state.patchOperations,
      [stateKey]: [],
    };
  }
  state.patchOperations[stateKey].push(operation);
}
