import { useContext } from 'react';
import { useDispatch } from 'react-redux';
import QuizContext from '../InteractiveRender/customComps/QuizContext';
import useSuspendOperations from './useSuspendOperations';
import getRetryQuizMantainCorrectOperations from './useRetryQuizMantainCorrect/getRetryQuizMantainCorrectOperations';

function useRetryQuizMantainCorrect(resetResponse = true) {
  const quizId = useContext(QuizContext);
  const suspendOperations = useSuspendOperations();
  const dispatchThunk = useDispatch();
  return () => {
    const operations = dispatchThunk(
      (dispatch, getState) => getRetryQuizMantainCorrectOperations(
        quizId,
        getState(),
        resetResponse,
      ),
    );
    if (operations.length) suspendOperations(operations);
  };
}

export default useRetryQuizMantainCorrect;
