import { useEffect, useRef } from 'react';
import { EXCEEDING_CONTENT } from '@auth/lib/src/events/triggers';
import { useSelector } from 'react-redux';
import selectorConfig from '@auth/lib/src/selectors/selectorConfig';
import useDispatchEvent from '../hooks/useDispatchEvent';
import scoreSelector from '../selectors/scoreSelector';

function ContentExceedingDispatcher() {
  const dispatchEvent = useDispatchEvent();
  const exceding = useSelector((state) => {
    const mastery = selectorConfig(state, 'contentCutOffScore');
    const score = scoreSelector(state);
    return score && score >= mastery;
  });
  const excedingRef = useRef(false);
  useEffect(
    () => {
      if (!excedingRef.current && exceding) {
        dispatchEvent(EXCEEDING_CONTENT);
        excedingRef.current = true;
      }
    },
    [exceding, dispatchEvent],
  );
  return null;
}

ContentExceedingDispatcher.propTypes = {};

ContentExceedingDispatcher.defaultProps = {};

export default ContentExceedingDispatcher;
