import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import { ClassNames } from '@auth/shared/src/utils/generateClassReact';
import useInteractiveProps from '../useInteractiveProps';

const cssDefault = {
  fontSize: 0,
  lineHeight: 0,
  overflow: 'auto',
  WebkitOverflowScrolling: 'touch',
};

const cssIframe = {
  width: '100%',
  height: '100%',
};

function isCorrectUrl(url) {
  // eslint-disable-next-line no-useless-escape
  const regex = /https?:\/\/(www\.)?[-a-zA-Z0-9@:%._\+~#=]{1,256}\.[a-zA-Z0-9()]{1,6}\b([-a-zA-Z0-9()@:%_\+.~#?&//=]*)/gm;
  return regex.test(url);
}

function Iframe(props) {
  const {
    className,
    srcUrl,
    alt,
    iframePointerEvents,
  } = props;
  const [urlInIframe, setUrlInIframe] = useState(srcUrl);
  useEffect(() => {
    const isCorrect = isCorrectUrl(srcUrl);
    if (isCorrect) {
      setUrlInIframe(srcUrl);
    } else {
      setUrlInIframe(`${process.env.PUBLIC_URL}/resources/iframe.html`);
    }
  }, [srcUrl]);
  const interactiveProps = useInteractiveProps(props);
  return (
    <ClassNames>
      {({ css, cx }) => (
        <div
          className={cx(css(cssDefault), className)}
          {...interactiveProps}
        >
          <iframe
            className={css(cssIframe)}
            src={urlInIframe}
            title={alt}
            style={{
              pointerEvents: iframePointerEvents,
            }}
          />
        </div>
      )}
    </ClassNames>
  );
}

Iframe.propTypes = {
  className: PropTypes.string,
  srcUrl: PropTypes.string,
  alt: PropTypes.string,
  iframePointerEvents: PropTypes.string,
};

Iframe.defaultProps = {
  className: '',
  srcUrl: null,
  alt: '',
  iframePointerEvents: 'all',
};

export default Iframe;
