import selectorFindDescendantByTypes from '@auth/lib/src/selectors/selectorFindDescendantByTypes';
import selectorProp from '@auth/lib/src/selectors/selectorProp';

function correctResponsesActivityTextInput(state, id) {
  const option = selectorFindDescendantByTypes(state, id, ['TextInputOption']);
  const selectorRightAnswers = selectorProp(state, option, 'rightAnswers');
  const rightAnswers = selectorRightAnswers ? JSON.parse(selectorRightAnswers) : [];
  return rightAnswers.length ? rightAnswers.join('[,]') : '';
}

export default correctResponsesActivityTextInput;
