import selectorConfig from '@auth/lib/src/selectors/selectorConfig';
import completionSelector from '../selectors/completionSelector';
import evaluableQuizzesDoneSelector from '../selectors/evaluableQuizzesDoneSelector';
import locationSelector from '../selectors/locationSelector';
import scoreSelector from '../selectors/scoreSelector';
import suspendSelector from '../selectors/suspendSelector';
import suspendComparator from './suspendComparator';

function trackingStoreChange(oldState = {}, storeState = {}) {
  const {
    completion, score, suspend = '{}', location = '/', evaluableQuizzesDone = false,
  } = oldState;
  const ret = {
    state: {
      completion, score, suspend, location, evaluableQuizzesDone,
    },
    changes: [],
  };

  const newCompletion = completionSelector(storeState);
  if (newCompletion !== completion) {
    ret.state.completion = newCompletion;
    ret.changes.push('completion');
  }

  const newScore = scoreSelector(storeState);
  const scoreConfig = selectorConfig(storeState, 'contentScoreCalculationMethod');
  const mastery = selectorConfig(storeState, 'contentCutOffScore');

  if (newScore && newScore !== score) {
    const valid = (() => {
      if (scoreConfig === 'best_score' && score !== undefined && newScore <= score) { return false; }
      if (scoreConfig === 'best_score_until_passing' && score !== undefined && newScore <= score) { return false; }
      if (scoreConfig === 'best_score_until_passing' && score >= mastery) { return false; }
      return true;
    })();
    if (valid) {
      ret.state.score = newScore;
      ret.changes.push('score');
    }
  }

  const newSuspend = suspendSelector(storeState);
  if (!suspendComparator(newSuspend, suspend)) {
    ret.state.suspend = newSuspend;
    ret.changes.push('suspend');
  }

  const newLocation = locationSelector(storeState);
  if (newLocation !== location) {
    ret.state.location = newLocation;
    ret.changes.push('location');
  }

  const newEvaluableQuizzesDone = evaluableQuizzesDoneSelector(storeState);
  if (newEvaluableQuizzesDone !== evaluableQuizzesDone) {
    ret.state.evaluableQuizzesDone = newEvaluableQuizzesDone;
    ret.changes.push('evaluableQuizzesDone');
  }

  return ret;
}

export default trackingStoreChange;
