import { SET_POPUP } from '../reducers/popupReducer';

const subscribersSetPopup = [];

export const subscribeSetPopupAction = (callback) => {
  subscribersSetPopup.push(callback);
  return subscribersSetPopup.length - 1;
};

export const setPopupMiddleware = () => (next) => (action) => {
  const { type } = action;
  if (type === SET_POPUP) {
    subscribersSetPopup.forEach(
      (callback, index) => {
        callback(action, () => subscribersSetPopup.splice(index, 1));
      },
    );
  }
  return next(action);
};
