import selectorProp from '@auth/lib/src/selectors/selectorProp';
import {
  useEffect,
} from 'react';
import { useDispatch } from 'react-redux';
import renderPopupSelector from '../InteractiveRender/renderPopupSelector';
import { operationsSuspendDataAction } from '../reducers/suspendDataReducerOperations';
import { setSuspendDataAction } from '../reducers/suspendDataReducerSet';
import { setTempSuspendDataAction } from '../reducers/suspendDataTempReducerSet';
import { unsetTempSuspendDataAction } from '../reducers/suspendDataTempReducerUnset';
import quizRemainingTimeSelector from '../selectors/quizRemainingTimeSelector';
import quizStatusSelector from '../selectors/quizStatusSelector';
import suspendDataSelector from '../selectors/suspendDataSelector';
import useFinaliseQuizzes from './useFinaliseQuizzes';

function useTimeLimit(id, strTimeLimit) {
  const dispatchThunk = useDispatch();
  const finalise = useFinaliseQuizzes();
  useEffect(
    () => {
      let intervalId;
      const valid = dispatchThunk(
        (dispatch, getState) => {
          const timeLimit = +strTimeLimit;
          if (!timeLimit) return false;
          const state = getState();
          const started = suspendDataSelector(id, 'started', state);
          if (!started) return false;
          const isFinalised = suspendDataSelector(id, 'finalised', state) === 1;
          if (isFinalised) return false;
          const remaining = +quizRemainingTimeSelector(getState(), id);
          if (!remaining) {
            finalise([id]);
            return false;
          }
          return true;
        },
      );

      if (valid) {
        intervalId = setInterval(
          () => {
            dispatchThunk(
              (dispatch, getState) => {
                const state = getState();
                const status = quizStatusSelector(state, id);
                if (!['passed', 'failed'].includes(status)) {
                  const quizAvaliable = suspendDataSelector(id, 'avaliable', state) || 0;
                  const remaining = +quizRemainingTimeSelector(state, id);
                  if (!remaining) {
                    dispatch(operationsSuspendDataAction([
                      setSuspendDataAction(id, 'avaliable', quizAvaliable + 1),
                      unsetTempSuspendDataAction(id, 'avaliable'),
                    ]));
                    finalise([id]);
                    clearInterval(intervalId);
                    intervalId = null;
                  } else {
                    const pauseTimer = selectorProp(state, id, 'pauseTimer');
                    const increase = pauseTimer !== 'onPopupOpen' || !renderPopupSelector(state);
                    if (increase) dispatch(setTempSuspendDataAction(id, 'avaliable', quizAvaliable + 1));
                  }
                }
              },
            );
          },
          1000,
        );
      }
      return () => {
        if (intervalId) {
          clearInterval(intervalId);
          dispatchThunk(
            (dispatch, getState) => {
              const state = getState();
              const quizAvaliable = suspendDataSelector(id, 'avaliable', state);
              dispatch(operationsSuspendDataAction([
                setSuspendDataAction(id, 'avaliable', quizAvaliable),
                unsetTempSuspendDataAction(id, 'avaliable'),
              ]));
            },
          );
        }
      };
    },
    [dispatchThunk, finalise, id, strTimeLimit],
  );
}

export default useTimeLimit;
