import React, { useMemo } from 'react';
import PropTypes from 'prop-types';
import { shallowEqual, useSelector } from 'react-redux';
import Quiz from '@auth/components/src/Quiz/Quiz';
import selectorQuizValidChilds from '@auth/lib/src/selectors/selectorQuizValidChilds';
import VisbleChildsContext from './VisbleChildsContext';
import useGetSuspendData from '../../hooks/useGetSuspendData';
import QuizContext from './QuizContext';
import usePrevSlide from '../../hooks/usePrevSlide';
import useNextSlide from '../../hooks/useNextSlide';
import useTimeLimit from '../../hooks/useTimeLimit';

function InteractiveRenderQuiz(props) {
  const {
    id,
    childs,
    showIntroductionScreen,
    showFeedbackScreen,
    timeLimit,
  } = props;
  const filterChilds = useSelector(
    (state) => selectorQuizValidChilds(state, childs, showIntroductionScreen, showFeedbackScreen),
    shallowEqual,
  );
  const visibleChildsTemp = useGetSuspendData(id, 'visibleChilds');
  const visibleChilds = useMemo(
    () => visibleChildsTemp || [filterChilds[0]],
    [visibleChildsTemp, filterChilds],
  );
  const goPrevSlide = usePrevSlide();
  const goNextSlide = useNextSlide();

  useTimeLimit(id, timeLimit);
  const handlePrev = () => {
    goPrevSlide(id, filterChilds, visibleChilds[0]);
  };
  const handleNext = () => {
    goNextSlide(id, filterChilds, visibleChilds[0]);
  };
  const visibleChildsContextValue = useMemo(() => ({
    componentId: id,
    visibleChild: visibleChilds[0],
    childs: filterChilds,
  }), [filterChilds, id, visibleChilds]);
  return (
    <QuizContext.Provider value={id}>
      <VisbleChildsContext.Provider value={visibleChildsContextValue}>
        <Quiz
          {...props}
          visibleChilds={visibleChilds}
          goPrevSlide={handlePrev}
          goNextSlide={handleNext}
        />
      </VisbleChildsContext.Provider>
    </QuizContext.Provider>
  );
}

InteractiveRenderQuiz.propTypes = {
  id: PropTypes.string.isRequired,
  childs: PropTypes.arrayOf(PropTypes.string),
  showIntroductionScreen: PropTypes.string,
  showFeedbackScreen: PropTypes.string,
  timeLimit: PropTypes.string,
};

InteractiveRenderQuiz.defaultProps = {
  childs: [],
  showFeedbackScreen: null,
  showIntroductionScreen: null,
  timeLimit: '',
};

export default InteractiveRenderQuiz;
