import React from 'react';
import PropTypes from 'prop-types';
import useInteractiveProps from '../useInteractiveProps';

function Text(props) {
  const {
    text,
    className,
  } = props;

  const interactiveProps = useInteractiveProps(props);
  const html = {
    __html: text,
  };
  return (
    <div
      className={className}
      {...interactiveProps}
    >
      <div
        className="ntx-ck-editor-container"
        // eslint-disable-next-line react/no-danger
        dangerouslySetInnerHTML={html}
      />
    </div>
  );
}

Text.propTypes = {
  text: PropTypes.string,
  className: PropTypes.string,
};

Text.defaultProps = {
  text: '<p>Lorem ipsum</p>',
  className: '',
};

export default Text;
