import { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import selectorType from '@auth/lib/src/selectors/selectorType';
import selectorParentId from '@auth/lib/src/selectors/selectorParentId';
import useSetCurrentPage from './useSetCurrentPage';
import useSetCurrentPopup from './useSetCurrentPopup';

function clearHash() {
  if ('replaceState' in window.history) {
    window.history.replaceState(null, '', window.location.href.replace(window.location.hash, ''));
  } else {
    if (window.location.hash !== '') {
      window.history.back();
    }
    window.location.hash = '';
  }
}

function useUrlHashHandler() {
  const setPopup = useSetCurrentPopup();
  const setPage = useSetCurrentPage();
  const dispatchThunk = useDispatch();
  useEffect(
    () => {
      const handleHashChange = () => {
        const hash = window.location.hash.replace('#', '');
        clearHash();
        dispatchThunk((dispatch, getState) => {
          const state = getState();
          const type = selectorType(state, hash);
          if (type === 'Popup') setPopup(hash);
          if (type === 'PageContent') setPage(hash);
          if (type === 'Section') setPage(selectorParentId(state, hash), hash);
        });
      };
      window.addEventListener('hashchange', handleHashChange, false);
      return () => window.removeEventListener('hashchange', handleHashChange);
    },
    [setPopup, setPage, dispatchThunk],
  );
}

export default useUrlHashHandler;
