import { changeLang } from './langsReducerChange';
import { setDefaultLang } from './langsReducerSetDefault';
import langValidKeys from '../../data/langValidKeys';

export const LANGS_SET_DEFAULT_ON_UNIT_CREATION = 'LANGS_SET_DEFAULT_ON_UNIT_CREATION';

export const langsSetDefaultOnUnitCreationSchema = {
  type: {
    type: 'string',
  },
  payload: {
    type: 'object',
    shape: {
      key: {
        type: 'string',
      },
    },
  },
};

export const langsSetDefaultOnUnitCreationAction = (
  key,
) => ({
  type: LANGS_SET_DEFAULT_ON_UNIT_CREATION,
  payload: {
    key,
  },
});

export function langsSetDefaultOnUnitCreationType(state, key) {
  if (state[key]?.default) return { type: 'nothing' };
  if (state[key]) return { type: 'setDefaultLang' };
  const defaultKey = Object.keys(state).find(
    (k) => state[k].default,
  );
  return { type: 'changeLang', defaultKey };
}

function langsReducerSetDefaultOnUnitCreation(state, action, allState) {
  const key = action?.payload?.key;
  const mutable = action?.mutable;
  if (!langValidKeys.includes(key)) return state;
  const { type, defaultKey } = langsSetDefaultOnUnitCreationType(state, key);
  if (type === 'nothing') return state;
  if (type === 'setDefaultLang') return setDefaultLang(state, key, mutable, allState);
  return changeLang(
    state,
    defaultKey,
    key,
    mutable,
    allState,
  );
}

export default langsReducerSetDefaultOnUnitCreation;
