import arrows from './interactionsXapi/arrows';
import choice from './interactionsXapi/choice';
import likert from './interactionsXapi/likert';
import patchContext from './interactionsXapi/patchContext';
import sequence from './interactionsXapi/sequence';
import textInput from './interactionsXapi/textInput';

const config = {
  ActivityArrows: arrows,
  ActivityLikert: likert,
  ActivityMultipleChoice: choice,
  ActivitySequence: sequence,
  ActivitySingleChoice: choice,
  ActivityTextInput: textInput,
};

function interactionsXapi(data, activityId, context) {
  const { type } = data;

  return config[type](
    data,
    activityId,
    patchContext(context, activityId, data),
  );
}

export default interactionsXapi;
