import { setSuspendDataAction } from '../reducers/suspendDataReducerSet';
import isCorrectActivitySelector from './isCorrectActivitySelector';

function checkActivityCorrectSuspendValue(state, activityId) {
  const correct = isCorrectActivitySelector(state, activityId);
  return setSuspendDataAction(activityId, 'correct', correct ? 1 : 0);
}

export default checkActivityCorrectSuspendValue;
