import { setOperation } from '../../hooks/useSuspendOperations';
import { isOptionCorrect } from '../../selectors/checkMultipleChoiceActivitySelector';
import suspendDataSelector from '../../selectors/suspendDataSelector';

function retryQuizMantainCorrect(state, activityId) {
  const response = suspendDataSelector(activityId, 'response', state) || '';
  const responseInArray = response.split(',');
  const newResponse = responseInArray.filter(
    (option) => isOptionCorrect(state, option, activityId),
  ).join(',');
  return setOperation(activityId, 'response', newResponse);
}

export default retryQuizMantainCorrect;
