import React from 'react';
import PropTypes from 'prop-types';
import ActivitySequence from '@auth/components/src/ActivitySequence/ActivitySequence';
import ActivityContext from './ActivityContext';

function InteractiveRenderActivitySequence(props) {
  const { id } = props;
  return (
    <ActivityContext.Provider value={id}>
      <ActivitySequence {...props} />
    </ActivityContext.Provider>
  );
}

InteractiveRenderActivitySequence.propTypes = {
  id: PropTypes.string.isRequired,
};
InteractiveRenderActivitySequence.defaultProps = {
};

export default InteractiveRenderActivitySequence;
