import { OPERATIONS_SUSPEND_DATA } from './suspendDataReducerOperations';
import suspendDataTempReducerOperations from './suspendDataTempReducerOperations';
import suspendDataTempReducerSet, { SET_TEMP_SUSPEND_DATA } from './suspendDataTempReducerSet';
import suspendDataTempReducerUnset, { UNSET_TEMP_SUSPEND_DATA } from './suspendDataTempReducerUnset';

const initialState = {};

const reducerConfig = {
  [SET_TEMP_SUSPEND_DATA]: suspendDataTempReducerSet,
  [OPERATIONS_SUSPEND_DATA]: suspendDataTempReducerOperations,
  [UNSET_TEMP_SUSPEND_DATA]: suspendDataTempReducerUnset,
};

function suspendDataTempReducer(state = initialState, action = {}) {
  const { type } = action;
  if (reducerConfig[type]) return reducerConfig[type](state, action);
  return state;
}

export default suspendDataTempReducer;
