import { CURRENTLANG } from '../../editor/stateKeys';
import { setAllState } from '../../stateChanger/stateChanger';

export const CURRENTLANG_SET = 'CURRENTLANG_SET';

export const currentLangSetAction = (lang) => ({
  type: CURRENTLANG_SET,
  payload: {
    lang,
  },
});

function currentLangReducerSet(state, action, allState) {
  const lang = action?.payload?.lang;
  if (!lang) return state;
  return setAllState(state, lang, allState, CURRENTLANG);
}

export default currentLangReducerSet;
