import activityTypes from '@auth/lib/src/data/activityTypes';
import selectorClosestType from '@auth/lib/src/selectors/selectorClosestType';
import selectorClosestTypes from '@auth/lib/src/selectors/selectorClosestTypes';
import quizActivitiesSelector from './quizActivitiesSelector';

function activityIsLast(state, descendantId) {
  const closestQuiz = selectorClosestType(state, descendantId, 'Quiz');
  const closestActivity = selectorClosestTypes(state, descendantId, activityTypes);
  const quizActivities = quizActivitiesSelector(state, closestQuiz);
  return closestActivity === quizActivities[quizActivities.length - 1];
}

export default activityIsLast;
