import React from 'react';
import PropTypes from 'prop-types';
import Button from '../Button/Button';

function Area(props) {
  const {
    resizeRef, x, y, width, height, ...rest
  } = props;

  return (
    <div
      ref={resizeRef}
      style={{
        position: 'absolute',
        left: `${x}%`,
        top: `${y}%`,
        width: `${width}%`,
        height: `${height}%`,
      }}
    >
      <Button
        {...rest}
        buttonTextDisplay="none"
        buttonIconDisplay="none"
      />
    </div>
  );
}

Area.propTypes = {
  resizeRef: PropTypes.oneOfType([
    PropTypes.func,
    PropTypes.shape({
      current: PropTypes.instanceOf(Element),
    }),
  ]),
  x: PropTypes.number.isRequired,
  y: PropTypes.number.isRequired,
  width: PropTypes.number.isRequired,
  height: PropTypes.number.isRequired,
  buttonDisabled: PropTypes.bool,
  rootButtonNodeName: PropTypes.oneOf(['div', 'button']),
};

Area.defaultProps = {
  resizeRef: null,
  buttonDisabled: false,
  rootButtonNodeName: 'button',
};

export default Area;
