import localstorage from './localstorage';
import none from './none';
import scorm12 from './scorm12';
import scorm2004 from './scorm2004';
import xapi from './xapi';

export const isLocalInConfig = window.envVars?.trackingSpec === 'local';

export const isScorm12InConfig = window.envVars?.trackingSpec === 'scorm_1_2';

export const isScorm2004InConfig = window.envVars?.trackingSpec === 'scorm_2004';

export const isXapiInConfig = window.envVars?.trackingSpec === 'tincan';

export const isNone = window.envVars?.trackingSpec === 'zip' || (!isLocalInConfig && !isScorm12InConfig && !isScorm2004InConfig && !isXapiInConfig);

let currentLmsApi = none;

const lmsApi = () => currentLmsApi;

function init(config) {
  if (isLocalInConfig) {
    currentLmsApi = localstorage;
    return localstorage.init(config);
  }
  if (isScorm12InConfig) {
    currentLmsApi = scorm12;
    return scorm12.init(config);
  }
  if (isScorm2004InConfig) {
    currentLmsApi = scorm2004;
    return scorm2004.init(config);
  }
  if (isXapiInConfig) {
    currentLmsApi = xapi;
    return xapi.init(config);
  }
  return none.init(config);
}

export const lmsApiInit = (config) => {
  const [isCorrect, ...initData] = init(config);
  if (!isCorrect) {
    const [, ...initDataNone] = none.init(config);
    currentLmsApi = none;
    return initDataNone;
  }
  return initData;
};

export default lmsApi;
