import React, { useContext } from 'react';
import PropTypes from 'prop-types';
import { useSelector, shallowEqual } from 'react-redux';
import selectorChildTypes from '@auth/lib/src/selectors/selectorChildTypes';
import { isInstanceResourceProp } from '@auth/lib/src/resources';
import getUrlOnlineResources, { parseUrlOnlineResources } from '@auth/shared/src/getUrlOnlineResources';
import patchUrlResources from './patchUrlResources';
import {
  getChildsContainer,
  getPropsPassedToChilds,
  getSlots,
  hasChildsContainer,
} from '../config';
import RenderContentContext from './RenderContentContext';
import useInstanceData from './useInstanceData';
import useCssProps from './useCssProps';
import calculatePatchUrl from './calculatePatchUrl';

const RenderContent = React.memo((props) => {
  const {
    unitId, id, parentId, index = 0, ...rest
  } = props;
  const RenderComp = useContext(RenderContentContext);
  const instanceData = useInstanceData(id);
  const { type, childs = [] } = instanceData;
  const childTypes = useSelector(
    (state) => selectorChildTypes(state, id),
    shallowEqual,
  );
  const cssPropsToProps = useCssProps(id, unitId);
  if (!type) return null;
  const objSrc = Object.entries(instanceData).reduce(
    (m, [k, v]) => {
      if (isInstanceResourceProp(k) && v && v !== 'none') {
        const { id: resourceId, filename } = JSON.parse(v);
        return {
          ...m,
          [k]: resourceId ? patchUrlResources(resourceId, filename, unitId) : '',
        };
      }
      return m;
    },
    {},
  );
  const objSrcUrl = {};
  if (instanceData.srcSource === 'srcUrl') {
    if (!instanceData.srcUrl) {
      objSrcUrl.src = undefined;
      objSrcUrl.srcOrigin = 'ccr';
    } else {
      const { origin } = parseUrlOnlineResources(instanceData.srcUrl);
      objSrcUrl.src = getUrlOnlineResources(instanceData.srcUrl);
      objSrcUrl.srcOrigin = origin;
    }
  }
  if (instanceData.srcSource === 'src') {
    objSrcUrl.srcOrigin = 'ccr';
  }
  const objTextWithPatchUrls = instanceData.text
    ? { text: calculatePatchUrl(instanceData.text, unitId) }
    : {};
  const slots = getSlots(type);
  const slotsTypes = slots.map(({ slot }) => slot);
  const getType = (searchId) => childTypes[childs.indexOf(searchId)];
  const objSlots = slots.reduce(
    (memo, { slot, wrapperInEdit, wrapperInPreview }) => {
      const wrapper = process.env.REACT_APP_BUILD_MODE === 'edit' ? wrapperInEdit : wrapperInPreview;
      const slotchilds = childs.filter((childId) => {
        const childType = getType(childId);
        return childType === slot;
      });
      const slotComps = slotchilds.map(
        (child, i) => {
          const comp = (
            <RenderContent
              key={child}
              id={child}
              index={i}
              parentId={id}
              unitId={unitId}
            />
          );
          if (!wrapper) return comp;
          return (
            <div
              key={child}
              style={{
                display: 'grid',
                gridTemplateAreas: `"grid_${child}"`,
              }}
            >
              {comp}
            </div>
          );
        },
      );
      return {
        ...memo,
        [slot]: slotComps,
      };
    },
    {},
  );
  const ChildWrapper = getChildsContainer(type);
  const childrenIds = childs.filter(
    (childId) => {
      const childType = getType(childId);
      return !slotsTypes.includes(childType);
    },
  );
  const objHasChildsContainer = hasChildsContainer(type)
    ? {}
    : { 'data-child-wrapper': id };
  const compProps = {
    ...instanceData,
    ...objSrc,
    ...objSrcUrl,
    ...objSlots,
    ...cssPropsToProps,
    ...objHasChildsContainer,
    ...objTextWithPatchUrls,
    ...rest,
    id,
    index,
    parentId,
    type,
  };
  const propsPassedToChilds = getPropsPassedToChilds(type).reduce(
    (memo, propName) => (compProps[propName]
      ? ({ ...memo, [propName]: compProps[propName] })
      : memo),
    {},
  );

  return (
    <RenderComp {...compProps} key={id}>
      <ChildWrapper data-child-wrapper={id}>
        {childrenIds.map(
          (child, i) => (
            <RenderContent
              {...propsPassedToChilds}
              key={child}
              id={child}
              index={i}
              parentId={id}
              componentType={getType(child)}
              unitId={unitId}
            />
          ),
        )}
      </ChildWrapper>
    </RenderComp>
  );
});

RenderContent.propTypes = {
  id: PropTypes.string.isRequired,
  unitId: PropTypes.string.isRequired,
  parentId: PropTypes.string,
  index: PropTypes.number,
};

RenderContent.defaultProps = {
  index: 0,
  parentId: null,
};

RenderContent.displayName = 'RenderContent';

export default RenderContent;
