import selectorFindDescendantByTypes from '@auth/lib/src/selectors/selectorFindDescendantByTypes';
import selectorProp from '@auth/lib/src/selectors/selectorProp';
import getId from './getId';

function mapIds(state, sequence = '') {
  return sequence.split('[,]').map(
    (current) => getId(state, current),
  ).join('[,]');
}

function correctResponsesActivitySequence(state, id) {
  const groupOption = selectorFindDescendantByTypes(state, id, ['SequenceGroupOption']);
  const correct = selectorProp(state, groupOption, 'correct');
  return mapIds(state, correct);
}

export default correctResponsesActivitySequence;
