import { filterProps } from '@auth/lib/src/data/conditions';
import checkCondition from '../conditions/checkCondition';

function visibilitySelector(state, props, masterContainer) {
  const { id } = props;
  const visibilityProps = filterProps('visibility', props);
  return Object.values(visibilityProps).every(
    (condition) => checkCondition(condition, state, id, masterContainer),
  );
}

export default visibilitySelector;
