import { useDispatch } from 'react-redux';
import { setSuspendDataAction } from '../reducers/suspendDataReducerSet';
import quizCheckedSelector from '../selectors/quizCheckedSelector';
import useCheckQuizzes from './useCheckQuizzes';
import useSuspendOperations from './useSuspendOperations';

function useFinaliseQuizzes() {
  const setSuspend = useSuspendOperations();
  const dispatchThunk = useDispatch();
  const checkQuizzes = useCheckQuizzes();
  return (quizzes) => {
    dispatchThunk(
      (dispatch, getState) => {
        const state = getState();
        const notCheckedQuizzes = quizzes.filter(
          (quizId) => !quizCheckedSelector(state, quizId),
        );
        if (notCheckedQuizzes.length) checkQuizzes(notCheckedQuizzes);
        setSuspend(
          quizzes.map(
            (quizId) => setSuspendDataAction(quizId, 'finalise', '1'),
          ),
        );
      },
    );
  };
}

export default useFinaliseQuizzes;
