import selectorClosestType from '@auth/lib/src/selectors/selectorClosestType';
import quizLimitTimeSelector from './quizLimitTimeSelector';
import suspendDataSelector from './suspendDataSelector';

function quizElapsedTimeSelector(state, descendantId) {
  const quiz = selectorClosestType(state, descendantId, 'Quiz');
  if (!quiz) return undefined;
  const timeLimit = quizLimitTimeSelector(state, quiz);
  if (!timeLimit) return undefined;
  const started = suspendDataSelector(quiz, 'started', state);
  if (!started) return 0;
  const avaliable = suspendDataSelector(quiz, 'avaliable', state) || 0;
  return Math.min(avaliable, timeLimit);
}

export default quizElapsedTimeSelector;
