import { filterProps } from '@auth/lib/src/data/conditions';
import checkCondition from '../conditions/checkCondition';

function actionEnableSelector(state, props) {
  const { id } = props;
  const enabledProps = filterProps('actionEnable', props);
  return Object.values(enabledProps).every(
    (condition) => checkCondition(condition, state, id),
  );
}

export default actionEnableSelector;
