import React from 'react';
import PropTypes from 'prop-types';
import MasterContainerContext from './masterContainerContext';

function MasterContainer(props) {
  const { id, children = null } = props;
  return (
    <MasterContainerContext.Provider value={id}>
      {children}
    </MasterContainerContext.Provider>
  );
}

MasterContainer.propTypes = {
  id: PropTypes.string.isRequired,
  children: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]),
};

MasterContainer.defaultProps = {
  children: null,
};

export default MasterContainer;
