import ntxCssProps from '@auth/lib/src/data/ntxCssProps';
import injectFonts from './injectFonts';

function getOrder(propName) {
  let ret = 0;
  if (propName.startsWith('active')) { ret += 1; }
  if (propName.includes(':mobile')) { ret += 30; }
  if (propName.includes(':tablet')) { ret += 20; }
  if (propName.includes(':widescreen')) { ret += 10; }
  return ret;
}

function convertForEmotion(id, props, mqConfig = {}, type = 'unknow', unitId = '') {
  const classData = {};
  const globalData = {};
  injectFonts(props, unitId, mqConfig);
  Object.keys(props)
    .filter(
      (k) => {
        const ntxCssPropsK = ntxCssProps(k, mqConfig);
        if (!(ntxCssPropsK && ntxCssPropsK.propName)) { return false; }
        if (['fontSrc'].includes(ntxCssPropsK.propName)) { return false; }
        if (ntxCssPropsK.propName === 'fontFamily') {
          const { source, sourceValue } = ntxCssPropsK;
          return props[source] === sourceValue;
        }
        return true;
      },
    )
    .sort((a, b) => {
      const aOrder = getOrder(a);
      const bOrder = getOrder(b);
      if (aOrder > bOrder) { return 1; }
      if (aOrder < bOrder) { return -1; }
      return 0;
    })
    .forEach((k) => {
      const ntxCssPropsK = ntxCssProps(k, mqConfig);
      const {
        propName, auxSelector = '', mq = '',
      } = ntxCssPropsK;
      const mySelector = ntxCssPropsK[`selector${type}`] || ntxCssPropsK.selector;
      const patchSelector = mySelector.split('"id"').join(`"${id}"`);
      const calcSelector = `${patchSelector}${auxSelector}`;

      const patchValue = (() => {
        if (propName === 'fontFamily') {
          return `"${props[k]}"`;
        }
        return props[k];
      })();
      const currentData = ntxCssPropsK[`selectorGlobal${type}`] !== undefined
        ? globalData
        : classData;
      if (mq) {
        currentData[mq] = currentData[mq] || {};
      }
      if (mq && calcSelector) {
        currentData[mq][calcSelector] = currentData[mq][calcSelector] || {};
        currentData[mq][calcSelector][propName] = patchValue;
      }
      if (mq && !calcSelector) {
        currentData[mq][propName] = patchValue;
      }
      if (calcSelector && !mq) {
        currentData[calcSelector] = currentData[calcSelector] || {};
        currentData[calcSelector][propName] = patchValue;
      }
      if (!mq && !calcSelector) {
        currentData[propName] = patchValue;
      }
    });
  const ret = {};
  ret.classData = classData;
  if (Object.keys(globalData).length) {
    ret.globalData = globalData;
  }
  return ret;
}

export default convertForEmotion;
