import { useDispatch } from 'react-redux';
import { operationsSuspendDataAction } from '../reducers/suspendDataReducerOperations';
import { setSuspendDataAction } from '../reducers/suspendDataReducerSet';
import { unsetSuspendDataAction } from '../reducers/suspendDataReducerUnSet';
import { setTempSuspendDataAction } from '../reducers/suspendDataTempReducerSet';
import { unsetTempSuspendDataAction } from '../reducers/suspendDataTempReducerUnset';

export const setOperation = (
  id,
  propName,
  propValue,
) => setSuspendDataAction(id, propName, propValue);

export const unsetOperation = (
  id,
  propName,
) => unsetSuspendDataAction(id, propName);

export const setTempOperation = (
  id,
  propName,
  propValue,
) => setTempSuspendDataAction(id, propName, propValue);

export const unsetTempOperation = (
  id,
  propName,
) => unsetTempSuspendDataAction(id, propName);

function useSuspendOperations() {
  const dispatch = useDispatch();
  return (operations) => dispatch(operationsSuspendDataAction(operations));
}

export default useSuspendOperations;
