import selectorFirstAscendantByType from '@auth/lib/src/selectors/selectorFirstAscendantByType';
import pageNumberByIdSelector from './pageNumberByIdSelector';

const pageNumberByDescendantIdSelector = (state, id) => {
  const pageId = selectorFirstAscendantByType(state, id, 'PageContent');
  return pageNumberByIdSelector(state, pageId);
};

export default pageNumberByDescendantIdSelector;
