import { unsetOperation } from '../../hooks/useSuspendOperations';
import checkActivityTextInputSelector from '../../selectors/checkActivityTextInputSelector';

function retryQuizMantainCorrect(state, activityId) {
  const correct = checkActivityTextInputSelector(state, activityId);
  if (correct) return null;
  return unsetOperation(activityId, 'response');
}

export default retryQuizMantainCorrect;
