import { AUDIO_INDICATED_TIME, VIDEO_INDICATED_TIME } from './triggers';

export const SHOW_POPUP = 'SHOW_POPUP';
export const SHOW_NOTIFICATION = 'SHOW_NOTIFICATION';
export const PAUSE_MEDIA_AND_SHOW_POPUP = 'PAUSE_MEDIA_AND_SHOW_POPUP';
export const PAUSE_MEDIA_AND_SHOW_NOTIFICATION = 'PAUSE_MEDIA_AND_SHOW_NOTIFICATION';

const actionsData = {
  [SHOW_POPUP]: {
  },
  [SHOW_NOTIFICATION]: {
  },
  [PAUSE_MEDIA_AND_SHOW_POPUP]: {
    types: ['Video', 'Audio'],
    triggers: [VIDEO_INDICATED_TIME, AUDIO_INDICATED_TIME],
  },
  [PAUSE_MEDIA_AND_SHOW_NOTIFICATION]: {
    types: ['Video', 'Audio'],
    triggers: [VIDEO_INDICATED_TIME, AUDIO_INDICATED_TIME],
  },
};

const actions = Object.keys(actionsData);

export const isValidAction = (action) => actions.includes(action);

export const getActionsByTypeAndTrigger = (type, triggerType) => actions.filter(
  (action) => (
    (!actionsData[action].types
      || actionsData[action].types.includes(type))
      && (!actionsData[action].triggers
      || actionsData[action].triggers.includes(triggerType))
  ),
);

export default actions;
