import strFirstCharToLowerCase from '@auth/lib/src/utils/strFirstCharToLowerCase';
import getMq from './getMq';
import getPseudo from './getPseudo';

function getRelatedProp(prop) {
  const pseudo = getPseudo(prop);
  const mq = getMq(prop);
  return strFirstCharToLowerCase(
    prop.replace(pseudo, '').replace(mq, ''),
  );
}

export default getRelatedProp;
