import React from 'react';
import PropTypes from 'prop-types';
import ActivityArrows from '@auth/components/src/ActivityArrows/ActivityArrows';
import ActivityContext from './ActivityContext';

function InteractiveRenderActivityArrows(props) {
  const { id } = props;
  return (
    <ActivityContext.Provider value={id}>
      <ActivityArrows {...props} />
    </ActivityContext.Provider>
  );
}

InteractiveRenderActivityArrows.propTypes = {
  id: PropTypes.string.isRequired,
};

InteractiveRenderActivityArrows.defaultProps = {
};

export default InteractiveRenderActivityArrows;
