const textIntlProps = {
  ActivityArrows: ['wording'],
  ActivityLikert: ['wording'],
  ActivityMultipleChoice: ['wording'],
  ActivitySequence: ['wording'],
  ActivitySingleChoice: ['wording'],
  ActivityTextInput: ['wording'],
  ArrowSource: ['description'],
  ArrowTarget: ['description'],
  Audio: ['alt'],
  Button: ['text'],
  Image: ['alt'],
  Iframe: ['alt'],
  LikertOption: ['description'],
  MultipleChoiceOption: ['description'],
  PageContent: ['title'],
  Popup: ['title'],
  SequenceOption: ['description'],
  SingleChoiceOption: ['description'],
  Text: ['text'],
  Video: ['alt'],
};

export function getTextIntlPropsByType(type) {
  return [...(textIntlProps[type] || [])];
}

const resourcesIntlProps = {
  Audio: ['src', 'srcUrl'],
  Button: ['targetLang', 'src', 'url', 'textResources'],
  Iframe: ['srcUrl'],
  Image: ['src', 'originalWidth', 'originalHeight'],
  Launcher: ['targetLang', 'src', 'url'],
  Section: ['backgroundImageUrl'],
  Text: ['textResources'],
  Video: ['src', 'srcUrl', 'videoCover', 'poster'],
};

function merge(a, b) {
  const ret = { ...a };
  Object.entries(b).forEach(
    ([k, v]) => {
      ret[k] = ret[k]
        ? [...ret[k], ...v]
        : [...v];
    },
  );
  return ret;
}

const intlProps = merge(textIntlProps, resourcesIntlProps);

export default intlProps;
