import React, { useContext } from 'react';
import PropTypes from 'prop-types';
import { useSelector } from 'react-redux';
import MenuListItem from '@auth/components/src/MenuList/MenuListItem';
import selectorTitle from '@auth/lib/src/selectors/selectorTitle';
import pageSelector from '../../selectors/pageSelector';
import useSetCurrentPage from '../../hooks/useSetCurrentPage';
import InteractiveRenderStateContext from '../InteractiveRenderStateContext';
import useEnabled from '../../hooks/useEnabled';

function InteractiveRenderMenuListItem(props) {
  const { id } = props;
  const { closePanel } = useContext(InteractiveRenderStateContext);
  const setCurrentPage = useSetCurrentPage();
  const myPage = id.split('_')[1];
  const isCurrent = useSelector((state) => {
    const page = pageSelector(state);
    return page === myPage;
  });
  const handleClick = () => {
    setCurrentPage(myPage);
    closePanel();
  };
  const title = useSelector(
    (state) => {
      const myPageId = id.split('_')[1];
      return selectorTitle(state, myPageId);
    },
  );
  const enabled = useEnabled(props);
  return (
    <MenuListItem
      {...props}
      isCurrent={isCurrent}
      click={handleClick}
      title={title}
      disabled={!enabled}
    />
  );
}

InteractiveRenderMenuListItem.propTypes = {
  id: PropTypes.string.isRequired,
};

InteractiveRenderMenuListItem.defaultProps = {};

export default InteractiveRenderMenuListItem;
