import React from 'react';
import PropTypes from 'prop-types';
import useInteractiveProps from '../useInteractiveProps';

function FlipCardFront(props) {
  const {
    children = null,
    className = '',
  } = props;
  const interactiveProps = useInteractiveProps(props);
  return (
    <div
      className={className}
      {...interactiveProps}
    >
      {children}
    </div>
  );
}

FlipCardFront.propTypes = {
  className: PropTypes.string,
  children: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]),
};

FlipCardFront.defaultProps = {
  children: null,
  className: '',
};

export default FlipCardFront;
