import selectorRealType from './selectorRealType';

const getFilterTypes = (showIntroductionScreen, showFeedbackScreen) => {
  if (
    showIntroductionScreen === 'false'
    && showFeedbackScreen === 'false'
  ) return ['IntroductionScreen', 'FeedbackScreen'];
  if (
    showIntroductionScreen === 'false'
  ) return ['IntroductionScreen'];
  if (
    showFeedbackScreen === 'false'
  ) return ['FeedbackScreen'];
  return [];
};

const selectorQuizValidChilds = (state, childs, showIntroductionScreen, showFeedbackScreen) => {
  const filterTypes = getFilterTypes(showIntroductionScreen, showFeedbackScreen);
  return childs.filter(
    (child) => !filterTypes.includes(selectorRealType(state, child)),
  );
};

export default selectorQuizValidChilds;
