import selectorProp from '@auth/lib/src/selectors/selectorProp';
import suspendDataSelector from './suspendDataSelector';

function checkSingleChoiceActivitySelector(state, activityId) {
  const response = suspendDataSelector(
    activityId,
    'response',
    state,
  );
  return selectorProp(state, response, 'correct') === 'true' ? 1 : 0;
}

export default checkSingleChoiceActivitySelector;
