import selectorAscendants from './selectorAscendants';
import selectorType from './selectorType';

function selectorClosestTypes(state, id, searchTypes = []) {
  const type = selectorType(state, id);
  if (searchTypes.includes(type)) { return id; }
  const ascendants = selectorAscendants(state, id);
  return ascendants.find((ascendantId) => searchTypes.includes(selectorType(state, ascendantId)));
}

export default selectorClosestTypes;
