import selectorConfig from './selectorConfig';

export const getType = (val) => {
  const [type] = val.split('_');
  return type;
};

function selectorCompleteCriteria(state) {
  const criteria = selectorConfig(state, 'contentCompletionCalculationSubCriteria') || [];
  const enableds = selectorConfig(state, 'contentCompletionCalculationCriteria') || [];
  return criteria.filter(
    (el) => enableds.includes(getType(el)),
  );
}

export default selectorCompleteCriteria;
