import selectorConfig from '@auth/lib/src/selectors/selectorConfig';

function resolutionStoreUpdaterThunk(dispatch, getState) {
  const state = getState();
  return {
    configDesktop: selectorConfig(state, 'resolutionDesktop'),
    configTablet: selectorConfig(state, 'resolutionTablet'),
    configMobile: selectorConfig(state, 'resolutionMobile'),
  };
}

export default resolutionStoreUpdaterThunk;
