import completionSelector from '../../selectors/completionSelector';
import quizCheckedSelector from '../../selectors/quizCheckedSelector';
import quizDescendantStatusSelector from '../../selectors/quizDescendantStatusSelector';
import quizRemainingAttemptsSelector from '../../selectors/quizRemainingAttemptsSelector';

const visibilityAndEnabledConditions = {
  status: (id, state) => quizDescendantStatusSelector(state, id),
  hasAttempts: (id, state) => (quizRemainingAttemptsSelector(state, id) > 0 ? 'true' : 'false'),
  lastAttempt: (id, state) => (quizRemainingAttemptsSelector(state, id) === 1 ? 'true' : 'false'),
  checked: (id, state) => quizCheckedSelector(state, id),
  completionRequirementsMet: (id, state) => completionSelector(state, [id]),
};

export default visibilityAndEnabledConditions;
