import { getAffectedProps } from './cssEnabledProps';
import getMq from './getMq';
import getPropByRelated from './getPropByRelated';
import getPseudo from './getPseudo';
import getRelatedProp from './getRelatedProp';
import isEnabledProp from './isEnabledProp';
import propValue from './propValue';

function addInheritValuesOnEnabled(cssData) {
  const cssDataEnabledProps = Object.keys(cssData).filter(
    (propName) => {
      if (!isEnabledProp(propName)) return false;
      if (cssData[propName] !== 'true') return false;
      const mq = getMq(propName);
      const pseudo = getPseudo(propName);
      if (!mq && !pseudo) return false;
      return true;
    },
  );
  return cssDataEnabledProps.reduce(
    (memo, enabledProp) => {
      const relatedProp = getRelatedProp(enabledProp);
      const inheritProps = getAffectedProps(relatedProp).reduce(
        (m, propName) => {
          const mq = getMq(enabledProp);
          const pseudo = getPseudo(enabledProp);
          const prop = getPropByRelated(propName, pseudo, mq);
          const inheritValue = propValue(prop, cssData);
          if (inheritValue) {
            return {
              ...m,
              [prop]: inheritValue,
            };
          }
          return m;
        },
        {},
      );

      return {
        ...memo,
        ...inheritProps,
      };
    },
    cssData,
  );
}

export default addInheritValuesOnEnabled;
