import suspendDataReducerOperations, { OPERATIONS_SUSPEND_DATA } from './suspendDataReducerOperations';
import suspendDataReducerSet, { SET_SUSPEND_DATA } from './suspendDataReducerSet';
import suspendDataReducerUnset, { UNSET_SUSPEND_DATA } from './suspendDataReducerUnSet';

const initialState = {};

const reducerConfig = {
  [SET_SUSPEND_DATA]: suspendDataReducerSet,
  [OPERATIONS_SUSPEND_DATA]: suspendDataReducerOperations,
  [UNSET_SUSPEND_DATA]: suspendDataReducerUnset,
};

function suspendDataReducer(state = initialState, action = {}) {
  const { type } = action;
  if (reducerConfig[type]) return reducerConfig[type](state, action);
  return state;
}

export default suspendDataReducer;
