function getClassPositionArrows(position) {
  switch (position) {
    case 'middle':
      return 'pageContent pageContent-arrows-middle pageContent-arrows-middle-middle';
    case 'top':
      return 'pageContent pageContent-arrows-top pageContent-arrows-top-middle';
    case 'bottom':
      return 'pageContent pageContent-arrows-bottom pageContent-arrows-bottom-middle';
    case 'middle inner':
      return 'pageContent pageContent-arrows-middle pageContent-arrows-middle-inner';
    case 'top inner':
      return 'pageContent pageContent-arrows-top pageContent-arrows-top-inner';
    case 'bottom inner':
      return 'pageContent pageContent-arrows-bottom pageContent-arrows-bottom-inner';
    case 'middle outer':
      return 'pageContent pageContent-arrows-middle pageContent-arrows-middle-outer';
    case 'top outer':
      return 'pageContent pageContent-arrows-top pageContent-arrows-top-outer';
    case 'bottom outer':
      return 'pageContent pageContent-arrows-bottom pageContent-arrows-bottom-outer';
    default:
      return 'pageContent pageContent-arrows-middle pageContent-arrows-middle-middle';
  }
}

export default getClassPositionArrows;
