import React from 'react';
import RenderContentContext from '@auth/components/src/RenderContent/RenderContentContext';
import { shallowEqual, useSelector } from 'react-redux';
import useAppData from '@auth/shared/src/hooks/useAppData';
import RenderContent from '@auth/components/src/RenderContent/RenderContent';
import InteractiveRenderComp from './InteractiveRenderComp';
import notificationsSelector from '../selectors/notificationsSelector';
import RenderNotificationsContainer from './RenderNotificationsContainer';

function RenderNotifications() {
  const { unitId } = useAppData();
  const notificationsData = useSelector(notificationsSelector, shallowEqual);
  const notificationsComps = notificationsData.map(
    ({ id, notification }) => (
      <RenderContent
        key={notification}
        id={id}
        unitId={unitId}
        notificationId={notification}
      />
    ),
  );
  if (!notificationsComps.length) return null;
  const notificationsCompsReverse = [...notificationsComps].reverse();
  return (
    <div
      style={{
        position: 'fixed', top: 0, pointerEvents: 'none', zIndex: 4,
      }}
      className="mantain-aspect-ratio"
    >
      <RenderContentContext.Provider value={InteractiveRenderComp}>
        <RenderNotificationsContainer y="top" x="left">
          {notificationsComps}
        </RenderNotificationsContainer>
        <RenderNotificationsContainer y="top" x="center">
          {notificationsComps}
        </RenderNotificationsContainer>
        <RenderNotificationsContainer y="top" x="right">
          {notificationsComps}
        </RenderNotificationsContainer>
        <RenderNotificationsContainer y="bottom" x="left">
          {notificationsCompsReverse}
        </RenderNotificationsContainer>
        <RenderNotificationsContainer y="bottom" x="center">
          {notificationsCompsReverse}
        </RenderNotificationsContainer>
        <RenderNotificationsContainer y="bottom" x="right">
          {notificationsCompsReverse}
        </RenderNotificationsContainer>
      </RenderContentContext.Provider>
    </div>
  );
}

RenderNotifications.propTypes = {};

export default RenderNotifications;
