import selectorStateEvents from './selectorStateEvents';

function selectorEventsIdsByTriggerCompIdAndTriggerType(state, triggerCompId, triggerType) {
  const events = selectorStateEvents(state);
  const keys = Object.keys(events);
  return keys.filter(
    (eventId) => events[eventId].triggerCompId === triggerCompId
      && events[eventId].triggerType === triggerType,
  );
}

export default selectorEventsIdsByTriggerCompIdAndTriggerType;
