import getAreasInColumns from './getAreasInColumns';

function getAreasInAutoColumns(ids, itemsPerColumn) {
  const rows = Math.ceil(ids.length / itemsPerColumn);
  const aRows = Array.from(Array(rows).keys());
  return aRows.reduce(
    (memo, rowIndex) => {
      const columnItems = ids.filter(
        (id, index) => Math.floor(index / itemsPerColumn) === rowIndex,
      );
      const patchColItems = Array.from(Array(itemsPerColumn - columnItems.length).keys()).reduce(
        (m) => [...m, '.'],
        columnItems,
      );
      return `${memo}${getAreasInColumns(patchColItems)} `;
    },
    '',
  );
}

export default getAreasInAutoColumns;
