import React, { useContext } from 'react';
import PropTypes from 'prop-types';

import ArrowTarget from '@auth/components/src/ArrowTarget/ArrowTarget';
import useArrows from '../../hooks/useArrows';
import ActivityContext from './ActivityContext';
import useArrowPseudoselector from '../../hooks/useArrowPseudoselector';

function InteractiveRenderArrowTarget(props) {
  const { id, type } = props;
  const activityId = useContext(ActivityContext);
  const onClick = useArrows(id, activityId);
  const pseudoSelector = useArrowPseudoselector(id, type);
  return (
    <ArrowTarget
      {...props}
      onClick={onClick}
      pseudoSelector={pseudoSelector}
    />
  );
}

InteractiveRenderArrowTarget.propTypes = {
  id: PropTypes.string.isRequired,
  type: PropTypes.string.isRequired,
};

InteractiveRenderArrowTarget.defaultProps = {
};

export default InteractiveRenderArrowTarget;
