import React from 'react';
import { Global, css } from '@auth/shared/src/utils/generateClassReact';
import useConvertColorsToEmotion from './useConvertColorsToEmotion';

function RenderCssVariables() {
  const colors = useConvertColorsToEmotion();
  return (
    <Global
      styles={
        css`
          :root {
            ${colors};
          }`
      }
    />
  );
}

RenderCssVariables.propTypes = {
};

RenderCssVariables.defaultProps = {
};

export default RenderCssVariables;
