import React, { useContext } from 'react';
import PropTypes from 'prop-types';
import ArrowSource from '@auth/components/src/ArrowSource/ArrowSource';
import useArrows from '../../hooks/useArrows';
import ActivityContext from './ActivityContext';
import useArrowPseudoselector from '../../hooks/useArrowPseudoselector';

function InteractiveRenderArrowSource(props) {
  const { id, type } = props;
  const activityId = useContext(ActivityContext);
  const onClick = useArrows(id, activityId);
  const pseudoSelector = useArrowPseudoselector(id, type);
  return (
    <ArrowSource
      {...props}
      onClick={onClick}
      pseudoSelector={pseudoSelector}
    />
  );
}

InteractiveRenderArrowSource.propTypes = {
  id: PropTypes.string.isRequired,
  type: PropTypes.string.isRequired,
};

InteractiveRenderArrowSource.defaultProps = {
};

export default InteractiveRenderArrowSource;
