import React from 'react';
import PropTypes from 'prop-types';
import { ClassNames } from '@auth/shared/src/utils/generateClassReact';
import useInteractiveProps from '../useInteractiveProps';
import nullFunc from '../nullFunc';

const cssDefault = {
  cursor: 'pointer',
};

function Launcher(props) {
  const {
    children = null,
    className,
    disabled,
    onMouseEnter,
    onMouseLeave,
    onMouseDown,
    onMouseUp,
    selected,
  } = props;
  const selectedAttr = selected ? { 'data-selected': 'true' } : {};
  const interactiveProps = useInteractiveProps(props);
  return (
    <ClassNames>
      {({ css, cx }) => (
        <div
          role="button"
          tabIndex="0"
          disabled={disabled}
          className={cx(css(cssDefault), className)}
          onMouseEnter={onMouseEnter}
          onMouseLeave={onMouseLeave}
          onMouseDown={onMouseDown}
          onMouseUp={onMouseUp}
          onFocus={nullFunc}
          {...selectedAttr}
          {...interactiveProps}
        >
          {children}
        </div>
      )}
    </ClassNames>
  );
}

Launcher.propTypes = {
  className: PropTypes.string,
  children: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]),
  disabled: PropTypes.bool,
  onMouseEnter: PropTypes.func,
  onMouseLeave: PropTypes.func,
  onMouseDown: PropTypes.func,
  onMouseUp: PropTypes.func,
  selected: PropTypes.bool,
};

Launcher.defaultProps = {
  children: null,
  className: '',
  disabled: false,
  onMouseEnter: nullFunc,
  onMouseLeave: nullFunc,
  onMouseDown: nullFunc,
  onMouseUp: nullFunc,
  selected: false,
};

export default Launcher;
