import selectorProp from './selectorProp';
import selectorAllDescendantIdsByTypes from './selectorAllDescendantIdsByTypes';

function selectorAllDescendantIdsAndSelfByTypes(state, id, types = []) {
  const descendants = selectorAllDescendantIdsByTypes(id, types, state);
  const isValidId = types.includes(selectorProp(state, id, 'type'));
  if (isValidId) return [id, ...descendants];
  return descendants;
}

export default selectorAllDescendantIdsAndSelfByTypes;
