import { useContext } from 'react';
import { useDispatch } from 'react-redux';
import VisbleChildsContext from '../VisbleChildsContext';
import useNextSlide from '../../../hooks/useNextSlide';
import QuizContext from '../QuizContext';
import suspendDataSelector from '../../../selectors/suspendDataSelector';
import { setSuspendDataAction } from '../../../reducers/suspendDataReducerSet';

function useClickStartQuiz() {
  const {
    componentId,
    visibleChild,
    childs = [],
  } = useContext(VisbleChildsContext) || {};

  const goNextSlide = useNextSlide();
  const quizId = useContext(QuizContext);
  const dispatchThunk = useDispatch();

  return () => {
    dispatchThunk(
      (dispatch, getState) => {
        const state = getState();
        const started = suspendDataSelector(quizId, 'started', state);
        if (!started) dispatch(setSuspendDataAction(quizId, 'started', 1));
      },
    );
    goNextSlide(componentId, childs, visibleChild);
  };
}

export default useClickStartQuiz;
