import React from 'react';
import PropTypes from 'prop-types';
import CompositeSlides from './CompositeSlides';
import CompositeTabs from './CompositeTabs';
import CompositeAccordion from './CompositeAccordion';
import nullFunc from '../nullFunc';

function Composite(props) {
  const {
    id,
    type,
    childs,
    visibleChilds = [],
    children = null,
    Dots = null,
    Tabs = null,
    Button = null,
    compositeSlidesDotsDisplay,
    compositeSlidesDotsPosition,
    compositeSlidesArrowsDisplay,
    compositeSlidesArrowsPosition,
    transitionType,
    transitionDuration,
    transitionDelay,
    compositeNavigationType,
    relatedIdsAccordion,
    className,
    accordionHeaderId,
    firstCompositeItem,
    accordionGridTemplateAreas,
    goNext,
    goPrev,
    ...otherProps
  } = props;
  if (compositeNavigationType === 'tabs') {
    return (
      <CompositeTabs
        id={id}
        type={type}
        visibleChilds={visibleChilds}
        Tabs={Tabs}
        transitionType={transitionType}
        transitionDuration={transitionDuration}
        transitionDelay={transitionDelay}
        className={className}
        goNextSlide={goNext}
        goPrevSlide={goPrev}
        {...otherProps}
      >
        {children}
      </CompositeTabs>
    );
  }
  if (compositeNavigationType === 'accordion') {
    return (
      <CompositeAccordion
        id={id}
        type={type}
        childs={childs}
        visibleChilds={visibleChilds}
        relatedIdsAccordion={relatedIdsAccordion}
        className={className}
        accordionHeaderId={accordionHeaderId}
        firstCompositeItem={firstCompositeItem}
        accordionGridTemplateAreas={accordionGridTemplateAreas}
        goNextSlide={goNext}
        goPrevSlide={goPrev}
        {...otherProps}
      >
        {children}
      </CompositeAccordion>
    );
  }

  return (
    <CompositeSlides
      id={id}
      type={type}
      childs={childs}
      visibleChilds={visibleChilds}
      Dots={Dots}
      Button={Button}
      showDots={compositeSlidesDotsDisplay === 'true'}
      compositeSlidesDotsPosition={compositeSlidesDotsPosition}
      showArrows={compositeSlidesArrowsDisplay === 'true'}
      compositeSlidesArrowsPosition={compositeSlidesArrowsPosition}
      transitionType={transitionType}
      transitionDuration={transitionDuration}
      transitionDelay={transitionDelay}
      className={className}
      goNextSlide={goNext}
      goPrevSlide={goPrev}
      {...otherProps}
    >
      {children}
    </CompositeSlides>
  );
}

Composite.propTypes = {
  id: PropTypes.string.isRequired,
  type: PropTypes.string.isRequired,
  childs: PropTypes.arrayOf(PropTypes.string).isRequired,
  className: PropTypes.string,
  visibleChilds: PropTypes.arrayOf(PropTypes.string),
  children: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]),
  Dots: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]),
  Tabs: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]),
  Button: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]),
  compositeSlidesDotsDisplay: PropTypes.string,
  compositeSlidesDotsPosition: PropTypes.string,
  compositeSlidesArrowsDisplay: PropTypes.string,
  compositeSlidesArrowsPosition: PropTypes.string,
  accordionGridTemplateAreas: PropTypes.string,
  transitionType: PropTypes.string,
  transitionDuration: PropTypes.string,
  transitionDelay: PropTypes.string,
  accordionHeaderId: PropTypes.string.isRequired,
  firstCompositeItem: PropTypes.string.isRequired,
  compositeNavigationType: PropTypes.string.isRequired,
  relatedIdsAccordion: PropTypes.string,
  goNext: PropTypes.func,
  goPrev: PropTypes.func,
};

Composite.defaultProps = {
  visibleChilds: [],
  children: null,
  Dots: null,
  Tabs: null,
  Button: null,
  compositeSlidesDotsDisplay: 'true',
  compositeSlidesDotsPosition: 'bottom',
  compositeSlidesArrowsDisplay: 'true',
  compositeSlidesArrowsPosition: '',
  transitionType: 'fade',
  transitionDuration: '1000ms',
  transitionDelay: '0ms',
  className: '',
  accordionGridTemplateAreas: '',
  relatedIdsAccordion: '',
  goNext: nullFunc,
  goPrev: nullFunc,
};

export default Composite;
