import stringToHTML from '@auth/lib/src/utils/stringToHtml';
import { useSelector } from 'react-redux';
import replaceTextVariableSelector from '../selectors/replaceTextVariableSelector';

function mySelector(id, textPatch, nodes, state) {
  const currentVals = nodes.reduce((memo, node) => {
    const value = node.getAttribute('data-value');
    const cleanValue = replaceTextVariableSelector(state, value, id);
    return [
      ...memo,
      {
        oldValue: node.outerHTML,
        oldValueInner: node.innerHTML,
        originalValue: value,
        cleanValue,
      },
    ];
  }, []);
  return currentVals.reduce((m, v) => {
    const newValue = (() => {
      const freePipe = v.oldValueInner.replace('|', '');
      if (freePipe.search(v.originalValue) !== -1) {
        return freePipe.replace(v.originalValue, v.cleanValue);
      }
      return v.cleanValue;
    })();
    return m.replace(v.oldValue, newValue);
  }, textPatch);
}

function useTextVariables(id, text = '') {
  const textInHtml = stringToHTML(text);
  const textPatch = textInHtml.innerHTML;
  const nodes = Array.from(textInHtml.querySelectorAll('[data-mention]'));
  return useSelector(
    (state) => mySelector(id, textPatch, nodes, state),
  );
}

export default useTextVariables;
