import getDisplayPropName from '@auth/components/src/RenderContent/getChildAreas/getDisplayPropName';
import getGridTemplateInAutoColumns from '@auth/components/src/RenderContent/getChildAreas/getGridTemplateInAutoColumns';
import { useSelector } from 'react-redux';
import useGetOrderChilds from './useGetOrderChilds';

const useGridStyleByRandomChildsProp = (props) => {
  const {
    gridTemplateColumns,
  } = props;
  const displayPropName = useSelector(getDisplayPropName);
  const orderChilds = useGetOrderChilds(props);
  const gridTemplateStyle = getGridTemplateInAutoColumns(
    gridTemplateColumns,
    orderChilds,
    props[displayPropName],
  );
  const objStyle = {
    gridTemplateAreas: gridTemplateStyle.gridTemplateAreas,
  };
  if (gridTemplateStyle.gridTemplateColumns) {
    objStyle.gridTemplateColumns = gridTemplateStyle.gridTemplateColumns;
  }
  return objStyle;
};

export default useGridStyleByRandomChildsProp;
