import { setOperation } from '../hooks/useSuspendOperations';
import checkActivityCorrectSuspendValue from './checkActivityCorrectSuspendValue';
import checkQuizTimesSelector from './checkQuizTimesSelector';
import feedbackScreenQuizSelector from './feedbackScreenQuizSelector';
import quizActivitiesSelector from './quizActivitiesSelector';
import quizHasAttemptsSelector from './quizHasAttemptsSelector';

function checkQuizSuspendOperationsSelector(state, quizId) {
  const operations = [];
  const hasAttempts = quizHasAttemptsSelector(state, quizId);
  if (hasAttempts) {
    const checkQuizTimes = checkQuizTimesSelector(state, quizId);
    const updateQuizTimes = setOperation(quizId, 'checkQuizTimes', (checkQuizTimes + 1).toString());
    operations.push(updateQuizTimes);
    const activities = quizActivitiesSelector(state, quizId);
    activities.forEach(
      (activityId) => {
        operations.push(checkActivityCorrectSuspendValue(state, activityId));
      },
    );
  }
  const feedbackScreen = feedbackScreenQuizSelector(state, quizId);
  if (feedbackScreen) {
    const gotoFeedBack = setOperation(quizId, 'visibleChilds', [feedbackScreen]);
    operations.push(gotoFeedBack);
  }
  return operations;
}

export default checkQuizSuspendOperationsSelector;
