/**
 * La distribución de las columnas y su tamaño está en la propiedad css de la row
 */

import React from 'react';
import PropTypes from 'prop-types';
import useInteractiveProps from '../useInteractiveProps';

function Row(props) {
  const {
    children = null,
    className,
  } = props;
  return (
    <div
      className={className}
      {...useInteractiveProps(props)}
    >
      {children}
    </div>
  );
}

Row.propTypes = {
  children: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]),
  className: PropTypes.string,
};

Row.defaultProps = {
  children: null,
  className: '',
};

export default Row;
