import selectorCompIdsByType from '@auth/lib/src/selectors/selectorCompIdsByType';
import visitedByTypeSelector from './visitedByTypeSelector';

function scoreByViewPagesSelector(state) {
  const pageIds = selectorCompIdsByType(state, 'PageContent');
  const totPages = pageIds.length;
  if (totPages === 0) return undefined;
  const viewPages = visitedByTypeSelector(state, 'PageContent');
  if (!viewPages) return undefined;
  return (viewPages / totPages) * 100;
}

export default scoreByViewPagesSelector;
