import selectorStateEditorResolutionWidth from '@auth/lib/src/selectors/selectorStateEditorResolutionWidth';
import selectorGetCalcPropName from '@auth/lib/src/selectors/selectorGetCalcPropName';
import selectorCssInstanceData from '@auth/lib/src/selectors/selectorCssInstanceData';
import selectorCssStyleData from '@auth/lib/src/selectors/selectorCssStyleData';
import getValue from './selectorGetCss/getValue';

function selectorPlaceholder(state, instanceId, propName, defaultPlaceholder, buttonState) {
  const propNameCalculated = selectorGetCalcPropName(propName, state, buttonState);
  const resolution = selectorStateEditorResolutionWidth(state);
  const instance = selectorCssInstanceData(state, instanceId);
  const deleteCalculatedPropName = Object.entries(instance).reduce(
    (m, [k, v]) => {
      if (k !== propNameCalculated) return { ...m, [k]: v };
      return m;
    },
    {},
  );
  const style = selectorCssStyleData(state, instanceId);
  const data = { ...style, ...deleteCalculatedPropName };
  const placeholder = getValue(propName, buttonState, resolution, data);
  return placeholder || defaultPlaceholder;
}

export default selectorPlaceholder;
