import React, {
  useContext, useEffect, useRef,
} from 'react';
import PropTypes from 'prop-types';
import Notification from '@auth/components/src/Notification/Notification';
import { CSSTransition } from 'react-transition-group';
import useHideNotification from '../../hooks/useHideNotification';
import RenderNotificationsContainerContext from '../RenderNotificationsContainerContext';
import getTransitionClassNamesByProps from './InteractiveRenderNotification/getTransitionClassNamesByProps';
import getTransitionTimeoutProps from './InteractiveRenderNotification/getTransitionTimeoutProps';

const yData = {
  top: 'start',
  bottom: 'end',
};

const xData = {
  left: 'start',
  center: 'center',
  right: 'end',
};

function InteractiveRenderNotification(props) {
  const {
    id,
    duration,
    notificationId,
    placeSelf,
    in: propIn,
  } = props;
  const {
    x = 'top',
    y = 'right',
  } = useContext(RenderNotificationsContainerContext) || {};
  const hide = useHideNotification();
  const timeoutRef = useRef();
  const place = placeSelf.split(' ');
  const render = yData[y] === place[0] && xData[x] === place[1];
  useEffect(() => {
    if (!render) return undefined;
    timeoutRef.current = setTimeout(() => {
      hide(notificationId);
    }, +duration);
    return () => {
      clearTimeout(timeoutRef.current);
    };
  }, [duration, hide, notificationId, render]);
  if (!render) return null;
  const classNames = getTransitionClassNamesByProps(props, y);
  const timeout = getTransitionTimeoutProps(props);
  const handleOnClick = () => {
    clearTimeout(timeoutRef.current);
    hide(notificationId);
  };
  return (
    <CSSTransition
      in={propIn}
      unmountOnExit
      key={id}
      timeout={timeout}
      classNames={classNames}
    >
      <Notification
        {...props}
        onClick={handleOnClick}
      />
    </CSSTransition>
  );
}

InteractiveRenderNotification.propTypes = {
  duration: PropTypes.string,
  id: PropTypes.string.isRequired,
  notificationId: PropTypes.string.isRequired,
  index: PropTypes.number.isRequired,
  in: PropTypes.bool.isRequired,
  placeSelf: PropTypes.string,
};

InteractiveRenderNotification.defaultProps = {
  duration: '3000',
  placeSelf: 'start end',
};

export default InteractiveRenderNotification;
