import currentLangReducerChange from './currentLangReducerChange';
import currentLangReducerRemove from './currentLangReducerRemove';
import currentLangReducerSet, { CURRENTLANG_SET } from './currentLangReducerSet';
import currentLangReducerSetDefaultLangOnUnitCreation from './currentLangReducerSetDefaultLangOnUnitCreation';
import { LANGS_CHANGE } from '../langs/langsReducerChange';
import { LANGS_REMOVE } from '../langs/langsReducerRemove';
import { LANGS_SET_DEFAULT_ON_UNIT_CREATION } from '../langs/langsReducerSetDefaultOnUnitCreation';

const reducerConfig = {
  [CURRENTLANG_SET]: currentLangReducerSet,
  [LANGS_CHANGE]: currentLangReducerChange,
  [LANGS_REMOVE]: currentLangReducerRemove,
  [LANGS_SET_DEFAULT_ON_UNIT_CREATION]: currentLangReducerSetDefaultLangOnUnitCreation,
};

export const currentLangActions = Object.keys(reducerConfig);

function currentLangReducer(state = null, action = {}, allState = {}) {
  const reducer = reducerConfig[action?.type];
  if (!reducer) return state;
  return reducer(state, action, allState);
}

export default currentLangReducer;
