import selectorInstance from './selectorInstance';

function selectorProp(state, id, propName, customCurrentLang) {
  if (state.editorInstancesProps) {
    const tempVal = state.editorInstancesProps[id] && state.editorInstancesProps[id][propName];
    if (tempVal) return tempVal;
  }
  const instance = selectorInstance(state, id, customCurrentLang);
  return instance[propName];
}

export default selectorProp;
