import selectorProp from './selectorProp';
import selectorAllDescendantIds from './selectorAllDescendantIds';

function selectorAllDescendantIdsByTypes(id, types, state, invalidTypes = []) {
  const allDescendants = selectorAllDescendantIds(id, state, invalidTypes);
  return allDescendants.filter(
    (descendant) => types.includes(selectorProp(state, descendant, 'type')),
  );
}

export default selectorAllDescendantIdsByTypes;
