import { useSelector } from 'react-redux';
import selectorRealType from '@auth/lib/src/selectors/selectorRealType';

const selectorAccordionId = (state, childs) => childs.find(
  (child) => selectorRealType(state, child) === 'AccordionHeaders',
);

const useGetAccordionHeaderId = (childs) => useSelector(
  (state) => selectorAccordionId(state, childs),
);

export default useGetAccordionHeaderId;
