import { useEffect, useState } from 'react';

function useCourseExited() {
  const [courseExited, setCourseExited] = useState(false);
  useEffect(() => {
    const onExit = () => {
      setCourseExited(true);
    };
    window.addEventListener('saveAndExitContent', onExit);
    return () => {
      window.removeEventListener('saveAndExitContent', onExit);
    };
  }, []);

  return courseExited;
}

export default useCourseExited;
