import stringToHTML from '@auth/lib/src/utils/stringToHtml';
import getPatchUrl from './getPatchUrl';

function getPatchValue(value, unitId) {
  if (!value) return `${process.env.PUBLIC_URL}/resources/placeholder.png`;
  const { id, filename, mimeType } = JSON.parse(value);
  return getPatchUrl(id, filename, mimeType, unitId);
}

function calculatePatchUrl(data, unitId) {
  const textInHtml = stringToHTML(data);
  const nodes = Array.from(textInHtml.querySelectorAll('img[src], a[download]'));
  nodes.forEach(
    (node) => {
      const { nodeName } = node;
      if (nodeName === 'IMG') {
        const nodeValue = node.getAttribute('src');
        node.setAttribute('src', getPatchValue(nodeValue, unitId));
      }
      if (nodeName === 'A') {
        const nodeValue = node.getAttribute('href');
        node.setAttribute('href', getPatchValue(nodeValue, unitId));
      }
    },
  );
  return textInHtml.innerHTML;
}

export default calculatePatchUrl;
