import selectorAllDescendantIdsByType from '@auth/lib/src/selectors/selectorAllDescendantIdsByType';
import { useDispatch } from 'react-redux';
import { operationsSuspendDataAction } from '../reducers/suspendDataReducerOperations';
import pageSelector from '../selectors/pageSelector';
import { getRetryQuizOperations } from './useRetryQuiz';

function getThunk(resetResponse) {
  return (dispatch, getState) => {
    const state = getState();
    const currentPage = pageSelector(state);
    const quizzes = selectorAllDescendantIdsByType(currentPage, 'Quiz', state);
    const operations = quizzes.reduce(
      (memo, quizId) => [...memo, ...getRetryQuizOperations(quizId, state, resetResponse)],
      [],
    );
    if (operations.length) { dispatch(operationsSuspendDataAction(operations)); }
  };
}

function useRetryQuizzesOnCurrentPage(resetResponse = true) {
  const dispatchThunk = useDispatch();
  return () => {
    dispatchThunk(getThunk(resetResponse));
  };
}

export default useRetryQuizzesOnCurrentPage;
