import { useContext } from 'react';
import { useDispatch } from 'react-redux';
import interactionSelector from '../../../selectors/interactionSelector';
import { sendInteraction } from '../../../tracking/tracking';
import ActivityContext from '../ActivityContext';
import QuizContext from '../QuizContext';
import useCheckQuizEvents from '../../../hooks/useCheckQuizEvents';
import checkActivityCorrectSuspendValue from '../../../selectors/checkActivityCorrectSuspendValue';

function thunkGetCorrectAction(activitiyId) {
  return (dispatch, getState) => {
    dispatch(checkActivityCorrectSuspendValue(getState(), activitiyId));
  };
}

function thunkSendInteraction(activityId) {
  return (dispatch, getState) => {
    const state = getState();
    const interaction = interactionSelector(state, activityId);
    sendInteraction(interaction);
  };
}

function useClickCheck() {
  const checkQuizEvents = useCheckQuizEvents();
  const activityId = useContext(ActivityContext);
  const quizId = useContext(QuizContext);
  const dispatch = useDispatch();
  return () => {
    if (activityId) {
      dispatch(thunkGetCorrectAction(activityId));
      dispatch(thunkSendInteraction(activityId));
      checkQuizEvents(quizId);
    }
  };
}

export default useClickCheck;
