import selectorStateLangs from './selectorStateLangs';

function selectorDefaultLang(state) {
  const langs = selectorStateLangs(state);
  const defaultLang = Object.entries(langs).find(
    ([, v]) => !!v.default,
  );
  if (defaultLang) return defaultLang[0];
  return null;
}

export default selectorDefaultLang;
