function getResult(correctResponses, success) {
  if (!correctResponses) return 'neutral';
  if (success) return 'correct';
  return 'incorrect';
}

function textInput(data) {
  const {
    id,
    response,
    correctResponses,
    wording,
    scoreMax,
    success,
  } = data;

  const ret = [
    ['id', id],
    ['type', correctResponses ? 'fill-in' : 'long-fill-in'],
    ['weighting', scoreMax],
    ['result', getResult(correctResponses, success)],
    ['learner_response', response],
    ['description', wording ? Object.values(wording)[0] : ''],
  ];
  if (correctResponses) {
    correctResponses.split('[,]').forEach(
      (correct, index) => {
        ret.push([`correct_responses.${index}.pattern`, correct]);
      },
    );
  }
  return ret;
}

export default textInput;
