import suspendDataReducerSet, { SET_SUSPEND_DATA } from './suspendDataReducerSet';
import suspendDataReducerUnset, { UNSET_SUSPEND_DATA } from './suspendDataReducerUnSet';

export const OPERATIONS_SUSPEND_DATA = 'OPERATIONS_SUSPEND_DATA';

export const operationsSuspendDataAction = (operations = []) => ({
  type: OPERATIONS_SUSPEND_DATA,
  payload: {
    operations,
  },
});

const config = {
  [SET_SUSPEND_DATA]: suspendDataReducerSet,
  [UNSET_SUSPEND_DATA]: suspendDataReducerUnset,
};

const suspendDataReducerOperations = (state, action) => {
  const { payload } = action;
  const { operations } = payload;
  return operations.reduce(
    (memo, operation) => {
      const { type } = operation;
      if (config[type]) return config[type](memo, operation);
      return memo;
    },
    state,
  );
};

export default suspendDataReducerOperations;
