import { useContext } from 'react';
import { useDispatch } from 'react-redux';
import VisbleChildsContext from '../VisbleChildsContext';
import useSetSuspendData from '../../../hooks/useSetSuspendData';
import quizActivitiesSelector from '../../../selectors/quizActivitiesSelector';
import QuizContext from '../QuizContext';

function useClickGotoFirstActivity() {
  const quizId = useContext(QuizContext);
  const { componentId } = useContext(VisbleChildsContext) || {};
  const dispatchThunk = useDispatch();
  const setSuspend = useSetSuspendData();
  return () => {
    const activities = dispatchThunk(
      (dispatch, getState) => quizActivitiesSelector(getState(), quizId),
    );
    if (activities.length) setSuspend(componentId, 'visibleChilds', [activities[0]]);
  };
}

export default useClickGotoFirstActivity;
