export const UNSET_TEMP_SUSPEND_DATA = 'UNSET_TEMP_SUSPEND_DATA';

export const unsetTempSuspendDataAction = (id, propName) => ({
  type: UNSET_TEMP_SUSPEND_DATA,
  payload: {
    id,
    propName,
  },
});

const suspendDataTempReducerUnset = (state, action) => {
  const { payload } = action;
  const {
    id,
    propName,
  } = payload;
  if (state[id] === undefined || state[id][propName] === undefined) return state;
  const draftState = {
    ...state,
    [id]: {
      ...state[id],
    },
  };
  delete draftState[id][propName];
  return Object.keys(draftState).reduce((m, k) => {
    if (Object.keys(draftState[k]).length) {
      return {
        ...m,
        [k]: {
          ...draftState[k],
        },
      };
    }
    return m;
  }, {});
};

export default suspendDataTempReducerUnset;
