import React from 'react';
import PropTypes from 'prop-types';
import Text from '@auth/components/src/Text/Text';
import useTextVariables from '../../hooks/useTextVariables';

function InteractiveRenderText(props) {
  const { id, text } = props;
  const patchText = useTextVariables(id, text);
  return (
    <Text {...props} text={patchText} />
  );
}

InteractiveRenderText.propTypes = {
  id: PropTypes.string.isRequired,
  text: PropTypes.string.isRequired,
};
InteractiveRenderText.defaultProps = {
};

export default InteractiveRenderText;
