import React from 'react';
import PropTypes from 'prop-types';
import { ClassNames } from '@auth/shared/src/utils/generateClassReact';
import './ProgressBar.css';
import LinearProgressBar from './LinearProgressBar';
import CircleProgressBar from './CircleProgressBar';
import useInteractiveProps from '../useInteractiveProps';

const cssDefault = {
  position: 'relative',
};

function getTextClass(position = '') {
  const [y = 'center', x = 'center'] = position.split(' ');
  return `progress-text progress-text-y-${y} progress-text-x-${x}`;
}

function convertPoint(val, omin, omax, nmin, nmax) {
  let newVal;
  const oldRange = (omax - omin);
  let newRange;
  if (oldRange === 0) {
    newVal = nmin;
  } else {
    newRange = (nmax - nmin);
    newVal = (((val - omin) * newRange) / oldRange) + nmin;
  }
  return newVal;
}

function convertToNumber(strVal, defaultVal) {
  if (typeof strVal !== 'string') { return defaultVal; }
  if (strVal.includes('|')) { return defaultVal; }
  if (strVal === '') { return defaultVal; }
  return +strVal;
}

function ProgressBar(props) {
  const {
    progressBarFormat,
    value,
    initialValue,
    maxValue,
    minValue,
    progressBarCircleStartAngle,
    progressBarCircleEndAngle,
    progressBarTextDisplay,
    progressBarTextPosition,
    Text,
    progressBarBaseStrokeWidth,
    progressBarBaseStroke,
    progressBarBaseStrokeLinecap,
    progressBarFillStrokeWidth,
    progressBarFillStroke,
    progressBarFillStrokeLinecap,
    transitionType,
    transitionDuration,
    transitionDelay,
    className,
  } = props;
  const v = convertToNumber(value, 30);
  const maxV = convertToNumber(maxValue, 100);
  const minV = convertToNumber(minValue, 0);
  const initV = convertToNumber(initialValue, 0);
  const patchV = Math.min(Math.max(v, minV), maxV);
  const patchInitV = Math.min(Math.max(initV, minV), maxV);
  const calcValue = convertPoint(patchV, minV, maxV, 0, 100);
  const calcInitialValue = convertPoint(patchInitV, minV, maxV, 0, 100);
  const barProps = {
    progressBarBaseStrokeWidth: progressBarBaseStrokeWidth.split('px').join(''),
    progressBarBaseStroke,
    progressBarBaseStrokeLinecap,
    progressBarFillStrokeWidth: progressBarFillStrokeWidth.split('px').join(''),
    progressBarFillStroke,
    progressBarFillStrokeLinecap,
    transitionType,
    transitionDuration,
    transitionDelay,
    initialValue: calcInitialValue,
  };
  const bar = (() => {
    if (progressBarFormat === 'linear') {
      return (
        <LinearProgressBar
          {...barProps}
          value={calcValue}
        />
      );
    }
    return (
      <CircleProgressBar
        {...barProps}
        value={calcValue}
        start={progressBarCircleStartAngle}
        end={progressBarCircleEndAngle}
      />
    );
  })();
  const interactiveProps = useInteractiveProps(props);
  return (
    <ClassNames>
      {({ css }) => (
        <div
          className={className}
          {...interactiveProps}
        >
          <div className={css(cssDefault)}>
            {bar}
            {
              progressBarTextDisplay === 'true'
              && (
                <div className={getTextClass(progressBarTextPosition)}>
                  <div>
                    {Text}
                  </div>
                </div>
              )
            }
          </div>
        </div>
      )}
    </ClassNames>
  );
}

ProgressBar.propTypes = {
  className: PropTypes.string,
  value: PropTypes.string,
  maxValue: PropTypes.string,
  minValue: PropTypes.string,
  initialValue: PropTypes.string,
  progressBarFormat: PropTypes.string,
  progressBarBaseStrokeWidth: PropTypes.string,
  progressBarBaseStroke: PropTypes.string,
  progressBarBaseStrokeLinecap: PropTypes.string,
  progressBarFillStrokeWidth: PropTypes.string,
  progressBarFillStroke: PropTypes.string,
  progressBarFillStrokeLinecap: PropTypes.string,
  transitionType: PropTypes.string,
  transitionDuration: PropTypes.string,
  transitionDelay: PropTypes.string,
  progressBarTextDisplay: PropTypes.string,
  progressBarTextPosition: PropTypes.string,
  progressBarCircleStartAngle: PropTypes.string,
  progressBarCircleEndAngle: PropTypes.string,
  Text: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]),
};

ProgressBar.defaultProps = {
  className: '',
  progressBarFormat: 'linear',
  progressBarCircleStartAngle: '270',
  progressBarCircleEndAngle: '90',
  progressBarBaseStrokeWidth: '10',
  progressBarBaseStroke: '#eee',
  progressBarBaseStrokeLinecap: 'butt',
  progressBarFillStrokeWidth: '20',
  progressBarFillStroke: '#1AA9E2',
  progressBarFillStrokeLinecap: 'butt',
  transitionType: 'linear',
  transitionDuration: '1000ms',
  transitionDelay: '0ms',
  value: '30',
  maxValue: '100',
  minValue: '0',
  initialValue: '0',
  progressBarTextDisplay: 'false',
  progressBarTextPosition: 'center center',
  Text: null,
};

export default ProgressBar;
