import selectorAllDescendantIdsAndSelfByTypes from '@auth/lib/src/selectors/selectorAllDescendantIdsAndSelfByTypes';
import selectorCompIdsByTypes from '@auth/lib/src/selectors/selectorCompIdsByTypes';
import selectorType from '@auth/lib/src/selectors/selectorType';
import completionSelectorConfig from './completionSelectorConfig';
import requirementsSelector from './requirementsSelector';

const types = Object.keys(completionSelectorConfig);

function completionSelector(state, ids) {
  const myIds = ids || selectorCompIdsByTypes(state, ['PageContent', 'Popup', 'Notification']);
  const allIds = myIds.reduce(
    (memo, id) => [
      ...memo,
      ...selectorAllDescendantIdsAndSelfByTypes(state, id, types),
    ],
    [],
  );
  return allIds.every(
    (id) => requirementsSelector(state, id).every(
      (completeMode) => {
        const compType = selectorType(state, id);
        return completionSelectorConfig[compType][completeMode](state, id);
      },
    ),
  );
}

export default completionSelector;
