import selectorAllDescendantIdsByTypes from './selectorAllDescendantIdsByTypes';

function selectorDescendantsIdsByTypesFromIds(state, ids = [], types = []) {
  return ids.reduce(
    (memo, id) => {
      const act = selectorAllDescendantIdsByTypes(id, types, state);
      return [...memo, ...act];
    },
    [],
  );
}

export default selectorDescendantsIdsByTypesFromIds;
