import selectorAscendants from './selectorAscendants';
import selectorProp from './selectorProp';

function selectorFirstAscendantByTypes(state, id, types = []) {
  const ascendants = selectorAscendants(state, id);
  return [...ascendants].reverse().find(
    (ascendant) => types.includes(selectorProp(state, ascendant, 'type')),
  );
}

export default selectorFirstAscendantByTypes;
